// util/axios.js
import Vue from "vue";
import axios from "axios";
import CONFIG from "./config";
import { createHeader } from "./header";
import Router from "../router/index";
import Store from "../store";
const pending = {};
const CancelToken = axios.CancelToken;
const removePending = (key, isRequest = false) => {
  if (pending[key] && isRequest) {
    // console.log(decodeURIComponent(key))
    pending[key]("取消重复请求");
  }
  delete pending[key];
};
const getRequestIdentify = (config, isReuest = false) => {
  let url = config.url;
  if (isReuest) {
    url = config.baseURL + config.url.substring(1, config.url.length);
  }
  return config.method === "get"
    ? encodeURIComponent(url + JSON.stringify(config.params))
    : encodeURIComponent(config.url + JSON.stringify(config.data));
};

// 请求拦截器
axios.interceptors.request.use(
  config => {
    // 拦截重复请求(即当前正在进行的相同请求)
    let requestData = getRequestIdentify(config, true);
    removePending(requestData, true);

    config.cancelToken = new CancelToken(c => {
      pending[requestData] = c;
    });

    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 异常处理
axios.interceptors.response.use(
  response => {
    // 把已经完成的请求从 pending 中移除
    let requestData = getRequestIdentify(response.config);
    removePending(requestData);

    return {
      code: response.status,
      message: response.statusText,
      headers: response.headers,
      data: response.data
    };
  },
  err => {
    // console.log("网络错误", err,  err.response)

    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = "错误请求";
          break;
        case 401:
          err.message = "未授权，请重新登录";
          break;
        case 403:
          err.message = "拒绝访问";
          break;
        case 404:
          err.message = "请求错误,未找到该资源";
          break;
        case 405:
          err.message = "请求方法未允许";
          break;
        case 408:
          err.message = "请求超时";
          break;
        case 500:
          err.message = "服务器端出错";
          break;
        case 501:
          err.message = "网络未实现";
          break;
        case 502:
          err.message = "网络错误";
          break;
        case 503:
          err.message = "服务不可用";
          break;
        case 504:
          err.message = "网络超时";
          break;
        case 505:
          err.message = "http版本不支持该请求";
          break;
        default:
          err.message = `连接错误${err.response.status}`;
      }
      let errData = {
        code: err.response.status,
        message: err.message
      };
      // 统一错误处理可以放这，例如页面提示错误...
      console.log("统一错误处理: ", errData);
      // console.error(errData)
    } else {
      // Vue.prototype.$message({
      //   message: "网络错误，请重试",
      //   type: "error"
      // });
    }

    return Promise.reject(err);
  }
);

// axios.defaults.baseURL = 'http://47.103.114.22:8083/quote-service/api/v1'
axios.defaults.baseURL = CONFIG.host;

export default instance => {
  instance.prototype.axios = data => {

    // 行情VIP配置
    // if (!Store.state.AccountLogin.identityData.hasHkValue) {
    //   data.params.delay = 1;
    // } else {
    //   data.params.delay = 0;
    // }
    if(data.type){
      data.url = CONFIG[data.type] + data.url;
    } else {
      // data.url = CONFIG["host"] + data.url;

    }

    var _params = {
      method: !data.method ? "get" : data.method.toLowerCase(),
      url: data.url,
      headers: { ...createHeader(), ...data.headers }
    };
    if (data.type == "HQ") {
      _params.headers["Content-Type"] = "text/html;charset=UTF-8";
    }

    // let customerId = localStorage.getItem("customerId");
    // if (customerId) {
    //   data.params.customerId = customerId;
    // }
    if (!data.params) {
      data.params = {};
    }
    data.params._from = "PC";
    if (!data.params.customerId) {
      let customerId = localStorage.getItem("customerId") || "";
      data.params.customerId = customerId;
    }
    if (data.type == "HQ") {
      delete data.params.customerId;
    }
    if (data.withCredentials) {
      _params.withCredentials = true;
    }
    if (_params.method === "get") {
      _params.params = data.params || {};
    } else {
      _params.data = data.params || {};
    }

    return new Promise((resolve, reject) => {
      axios(_params)
        .then(response => {
          // console.log(response);
          // code 84  交易账户登录返回的code
          if (
            response.data.code == 0 ||
            response.data.Code == 0 ||
            response.data.code == 84
          ) {
            resolve(response);
           
          } else {
            if (response.data.code === -12) {
              // ebroker  账户失效， 需重新登录
              Store.dispatch("updateAccountData", {});
              Store.dispatch("SET_CURRENT_TYPE", 1);
              Vue.prototype.$message({
                message: "标准账户已过期，请重新登录",
                type: "error"
              });
              if (!Store.state.AccountLogin.organizationData.ID) {
                // erbroker登录无效后判断机构是否已登录
                Store.dispatch("closeDeal");
                // Store.dispatch("showAccountLogin");
              }

              reject(response);
              return;
            }
            if (!data.hideMessage) {
              let errorMsg = response.data.message || response.data.msg;
              if (response.data.code == 70006) {
                errorMsg = "登录已过期，请重新登录";
                localStorage.setItem("token", "");
              }
              Vue.prototype.$message({
                showClose: true,
                message: errorMsg,
                type: "error"
              });
            }
            if (response.data.code == 20001 || response.data.code == 70006) {
              /*
            20001  用户需重新登录
            70006 token无效 用户需重新登录
            */
              Store.dispatch("updateAccountData", {});
              Router.push("login");
            }
            console.log("其他异常");
            reject(response);
          }
        })
        .catch(error => {
          reject(error);
        });
    });
  };
};
