
<template>
  <div class="bank-select" :class="{'show-all': showBank}">
    <div class="bank-item" hover :class="{'no-card': noCard}" @click="showBankUl">
      <img class="bank-icon" :src="bankData.bankPicUrl" alt />
      <div class="bank-label">
        <div class="bank-name">{{bankData.bankName}}</div>
        <div class="bank-account">{{bankData.accountNo | bankCardFilter}}</div>
      </div>
      <i class="bank-arrow el-icon-arrow-down"></i>
    </div>
    <transition name="bank">
      <div class="bank-ul" v-show="showBank">
        <div
          class="bank-item"
          hover
          v-for="(bank, i) in bankDataArr"
          :key="i"
          @click="changeBank(bank)"
        >
          <img class="bank-icon" :src="bank.bankPicUrl" alt />
          <div class="bank-label">
            <div class="bank-name">{{bank.bankName}}</div>
            <div class="bank-account">{{bank.accountNo | bankCardFilter}}</div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import bankLogo from "@/assets/images/base/logo.png";
export default {
  name: "bank-select",
  components: {},
  props: {
    bankDataArr: Array
  },
  model: {
    prop: "bankData",
    event: "on-change-bank"
  },
  data() {
    return {
      bankData: {},
      showBank: false,
      noCard: false
    };
  },
  activated() {},
  mounted() {
    // this.setCard();
  },
  watch: {
    bankDataArr() {
      this.setCard();
    }
  },
  methods: {
    setCard() {
      // console.log(this.bankDataArr);
      if (this.bankDataArr && this.bankDataArr.length > 0) {
        this.noCard = false;
        this.changeBank(this.bankDataArr[0]);
      } else {
        this.noCard = true;
        this.bankData = {
          bankName: "无银行卡",
          accountNo: "点击绑定银行卡",
          bankPicUrl: bankLogo
        };
      }
    },
    changeBank(bank) {
      this.bankData = bank;
      this.showBank = false;
      this.$emit("change", bank); 
      this.$emit("on-change-bank", bank);
    },
    showBankUl() {
      if (!this.noCard) {
        this.showBank = !this.showBank;
      } else {
        this.$emit("add-card");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.bank-select {
  position: relative;
  width: 415px;
  margin: 0 auto;
  .bank-item {
    width: 100%;
    height: 90px;
    border-radius: 6px;
    background: #252e3b;
    @include flex-box;
    align-items: center;
    padding: 0 23px 0 25px;
    line-height: 1;
    cursor: pointer;
    &.no-card {
      .bank-name {
        @include add-size($font_size_20);
      }
      .bank-account {
        @include add-size($font_size_18);
        color: #ff3b3b;
      }
    }
    .bank-icon {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      margin-right: 20px;
    }
    .bank-name {
      @include add-size($font_size_16);
      color: $white;
    }
    .bank-account {
      @include add-size($font_size_14);
      margin-top: 13px;
    }
    .bank-arrow {
      margin-left: auto;
      transition: all 0.3s ease;
    }
  }

  .bank-ul {
    position: absolute;
    z-index: 10;
    width: 100%;
    max-height: 284px;
    padding: 6px 0;
    overflow-y: auto;
    overflow-x: hidden;
    left: 0;
    top: 100px;
    box-shadow: 0 0 10px rgba(37, 46, 59, 1);
    background: #252e3b;
    transform-origin: 50% 0;
    border: 1px solid $defaultColor;
    border-radius: 6px;
  }
  .bank-enter-active {
    animation: bank-in 0.3s ease;
  }
  .bank-leave-active {
    animation: bank-in 0.3s ease reverse;
  }

  @keyframes bank-in {
    0% {
      opacity: 0;
      transform: scaleY(0);
    }
    100% {
      opacity: 1;
      transform: scaleY(1);
    }
  }
  &.show-all {
    .bank-arrow {
      transform: rotate(180deg);
    }
    &::after {
      content: "";
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-top: 0;
      border-bottom: 12px solid #252e3b;
      position: absolute;
      left: 64px;
      bottom: -13px;
    }
  }
}
</style>
