<template>
  <div
    class="contextmenu"
    :style="{left: left+'px',top: top+'px'}"
    v-if="visible"
    @click.stop="stop"
  >
    <ul class="menu-ul">
      <li v-for="(menu, i) in menuType[type]" :key="i" @click="menuAction(menu)">{{menu.label}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type: "deal",
      left: 0,
      top: 0,
      menuType: {
        deal: [
          {
            label: "买入",
            value: 0
          },
          {
            label: "卖出",
            value: 1
          },
          {
            label: "个股详情",
            value: 2
          }
        ],
        entrust: [
          {
            label: "撤单",
            value: 3
          },
          {
            label: "个股详情"
          }
        ]
      },
      visible: false,
      stockData: {}
    };
  },
  props: {
    requestConfig: Object
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.AccountLogin.currentType;
    }
  },
  methods: {
    show(e) {
      this.left = e.left;
      this.top = e.top;
      this.type = e.type;
      this.stockData = e.row;
      this.visible = true;
      if (this.type === "entrust" && window.innerHeight - this.top < 60) {
        this.top -= 60;
      } else if (this.type === "deal" && window.innerHeight - this.top < 92) {
        this.top -= 92;
      }
    },
    menuAction(menu) {
      setTimeout(() => {
        this.$store.dispatch("CHANGE_SEARCH_DATA", {
          code: 'hk'  + this.stockData.StockCode,
          timestamp: new Date().getTime()
        });
      }, 500);

      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 0,
        timestamp: new Date().getTime()
      });
      if (menu.value !== 2) {
        this.$store.dispatch("SHOW_TARGET_DEAL", {
          target: menu.value,
          timestamp: new Date().getTime()
        });
      }
      if (menu.value === 3) {
        // 撤单
        this.cancelOrder();
      }
      this.close();
    },
    cancelOrder() {
      let orderID = this.stockData.OrderID;
      let StockCode = this.stockData.StockCode;
      /*  url: this.requestConfig.addOrder[this.currentType],
        method: "post",
        params: this.dealForm,
        type: this.requestConfig.type[this.currentType] */
      let params = {};
      // if (this.currentType == 0) {
      //   params = {
      //     accountID: this.requestConfig.account[this.currentType],
      //     orderID: orderID,
      //     symbol: StockCode
      //   };
      // } else {
        params = {
          orderID: orderID
        };
      // }
      this.axios({
        url: this.requestConfig.cancelOrder[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.$message({
          message: "撤销成功！",
          type: "success"
        });
        this.$emit("cancelOrder");
      });
    },
    close() {
      this.visible = false;
    },
    stop() {}
  }
};
</script>


<style lang="scss" scoped>
.contextmenu {
  position: fixed;
  left: 100px;
  top: 100px;
  border: 1px solid $borderColor;
  background-color: $blockBgColor;
  width: 100px;
  .menu-ul {
    li {
      line-height: 30px;
      cursor: pointer;
      @include add-size($font_size_14);
      text-align: center;
      &:hover {
        color: $white;
        background-color: $activeColor;
      }
    }
  }
}
</style>