<template>
  <div class="company-info" v-show="isShow">
    <div class="largerNewsList">
      <el-tabs v-model="activeName" type="border-card">
        <el-tab-pane label="新闻" name="first">
          <newList
            :stockData="stockData"
            :activeName="activeName"
            v-if="activeName == 'first'"
          />
        </el-tab-pane>
        <el-tab-pane label="公告" name="second">
          <noticeList
            :activeName="activeName"
            :stockData="stockData"
            v-if="activeName == 'second'"
          />
        </el-tab-pane>
        <el-tab-pane label="简况" name="third">
          <!-- <newList :newData="simpleCase" /> -->
          <template>
            <companyBrief v-if="activeName == 'third'" />
          </template>
        </el-tab-pane>
        <el-tab-pane label="财务" name="fourth">
          <template>
            <finance :stockData="stockData" v-if="activeName === 'fourth'" />
          </template>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import newList from "./news-list";
import noticeList from "./notice-list";
import companyBrief from "./company-brief";
import finance from "./finance";
export default {
  components: {
    newList,
    noticeList,
    companyBrief,
    finance
  },
  data() {
    return {
      activeName: "first"
    };
  },
  computed: {
    stockData() {
      return this.$store.state.GlobalData.currentStock;
    },
    isShow(){
      return this.$store.state.system.showCompanyInfo
    }
  },
  watch: {},
  methods: {},
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang="scss" scoped>
.largerNewsList {
  margin-top: 5px;
  height: calc(100% - 646px);
  width: 1022px;
  position: absolute;
  right: 397px;
  bottom: 34px;
  z-index: 10;
  /deep/.el-tabs {
    border: none;
    background: #1d242e;
    height: 100%;
    .el-tabs__nav-scroll {
      background: #131920;
    }
    .el-tabs__content {
      padding: 0 0 0 15px;
      overflow: auto;
      height: calc(100% - 39px);
    }
    .el-tabs__item {
      border: 4px solid #131920;
      border-top: none;
      background: #1d242e;
      &:first-child {
        margin-left: -4px;
      }
    }
    .el-tabs__item.is-active {
      border-bottom: 1px solid #1d242e;
      border-color: #1d242e;
      background: #1d242e;
    }

    .el-tab-pane {
      height: 100%;
    }
  }
}
</style>
