<template>
  <div class="order-detail">
    <el-dialog
      class="order-dialog"
      theame
      center
      :title="title"
      :visible="isShow"
      width="360px"
      top="10%"
      :modal="false"
      @close="close"
    >
      <div class="order-content">
        <div class="item-ul" :origin="origin">
          <div class="item-li">
            <div class="li-label">股票名称</div>
            <div class="li-value">{{orderData.stockName}} ({{orderData.symbol}})</div>
          </div>
          <div class="item-li">
            <div class="li-label">类型</div>
            <div class="li-value">{{orderTypeLabel}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">方向</div>
            <div
              class="li-value direction"
              :class="{buy: orderData.buySell == 0}"
            >{{orderData.buySell == 0 ? "买入" : "卖出"}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">价格</div>
            <div class="li-value">{{orderData.price }}</div>
          </div>
          <div class="item-li">
            <div class="li-label">数量</div>
            <div class="li-value">{{orderData.quantity}}</div>
          </div>

          <div class="item-li">
            <div class="li-label">佣金</div>
            <div class="li-value">{{commission | moneyFormat}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">印花税</div>
            <div class="li-value">{{stampPro | moneyFormat}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">交易费</div>
            <div class="li-value">{{tradePro | moneyFormat}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">交收费</div>
            <div class="li-value">{{payfeesPro | moneyFormat}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">交易征费</div>
            <div class="li-value">{{levyPro | moneyFormat}}</div>
          </div>
          <div class="item-li">
            <div class="li-label">金额</div>
            <div class="li-value">{{ price | moneyFormat}}</div>
          </div>
        </div>
        <div class="action-bar">
          <div class="btn quit" hover @click="close">取消</div>
          <div class="btn confirm" hover @click="setOrder">确定</div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    orderData: Object,
    origin: String
  },
  data() {
    return {
      title: "确认订单",
      isShow: false,
      orderType: [
        {
          value: "1",
          label: "限价单",
          OrderType: "L",
          limit: "0,1,2,3"
        },
        {
          value: "2",
          label: "竞价单",
          OrderType: "A",
          tips: "不带价格",
          limit: "4,5"
        },
        {
          value: "3",
          label: "竞价限价单",
          OrderType: "L",
          tips: "带价格",
          limit: "4,5"
        },
        {
          value: "4",
          label: "增强限价单",
          OrderType: "E",
          limit: "0,1,2,3"
        },
        {
          value: "5",
          label: "特别限价单",
          OrderType: "S",
          limit: "0,1,2,3"
        }
      ],
      taxData: {}
    };
  },
  computed: {
    orderTypeLabel() {
      let label = "";
      this.orderType.forEach(a => {
        if (a.OrderType === this.orderData.orderType) {
          label = a.label;
        }
      });
      return label;
    },
    commission() {
      let orderData = this.orderData;
      let taxData = this.taxData;
      let fee =
        (orderData.price * orderData.quantity * taxData.commissionUser) / 100;
      return fee > Number(taxData.commissionMin)
        ? fee
        : Number(taxData.commissionMin);
    },
    // stampPro
    stampPro() {
      // 印花税
      let orderData = this.orderData;
      let taxData = this.taxData;
      let fee = (orderData.price * orderData.quantity * taxData.stampPro) / 100;
      return fee > 1 ? fee : 1;
    },
    tradePro() {
      // 交易费
      let orderData = this.orderData;
      let taxData = this.taxData;
      let fee = (orderData.price * orderData.quantity * taxData.tradePro) / 100;
      return fee;
    },
    payfeesPro() {
      // 交收费
      let orderData = this.orderData;
      let taxData = this.taxData;
      let fee =
        (orderData.price * orderData.quantity * taxData.payfeesPro) / 100;
      return fee > 5 ? fee : 5;
    },
    levyPro() {
      // 交易征费
      let orderData = this.orderData;
      let taxData = this.taxData;
      let fee = (orderData.price * orderData.quantity * taxData.levyPro) / 100;
      return fee;
    },
    price() {
      let price = 0;
      if (this.orderData.buySell == 0) {
        price =
          this.orderData.price * this.orderData.quantity +
          this.commission +
          this.stampPro +
          this.tradePro +
          this.payfeesPro +
          this.levyPro;
      } else {
         price =
          this.orderData.price * this.orderData.quantity -
          this.commission -
          this.stampPro -
          this.tradePro -
          this.payfeesPro -
          this.levyPro;
      }

      return price;
    }
  },
  methods: {
    getTaxData() {
      this.taxData = JSON.parse(localStorage.getItem("taxData"));
    },
    setOrder() {
      this.$emit("setOrder");
      this.close();
    },
    close() {
      this.isShow = false;
    },
    show() {
      this.isShow = true;
      this.getTaxData();
    }
  }
};
</script>

<style lang="scss" scoped>
.order-detail {
  /deep/ .el-dialog {
    margin-bottom: 0;
    .el-dialog__title {
      color: $defaultColor !important;
      @include add-size($font_size_14);
    }
  }
  .order-content {
    .item-ul {
      padding: 15px 0;
      margin: 0 30px;
      @include add-size($font_size_14);
      color: $white;
      border-bottom: 1px solid #323840;
      &[origin="win"] {
        .item-li {
          padding: 4px 0;
        }
      }
      .item-li {
        padding: 15px 0;
        @include flex-center;
        justify-content: space-between;
        .direction {
          color: $green;
        }
        .buy {
          color: $red;
        }
      }
    }
    .action-bar {
      height: 45px;
      padding: 0 30px;
      @include flex-center;
      .btn {
        @include add-size($font_size_14);
        width: 58px;
        height: 24px;
        text-align: center;
        line-height: 24px;
        cursor: pointer;
        &.quit {
          color: $defaultColor;
          margin-left: auto;
          line-height: 22px;
          border: 1px solid #767a92;
          border-radius: 4px;
        }
        &.confirm {
          color: $activeColor;
          @include linear;
          border-radius: 4px;
          color: $white;
          margin-left: 24px;
        }
      }
    }
  }
}
</style>


