<template>
  <el-dialog
    theame
    center
    class="notice-detail"
    title="消息详情"
    :visible="dialogVisiblea"
    width="690px"
    top="10%"
    :modal="false"
    @close="close"
  >
    <div class="detail-content">
      <div class="detail-title">{{detailInfo.title}}：</div>
      <div class="detail-text">{{detailInfo.content}}</div>
    </div>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      dialogVisiblea: false,
      detailInfo: {}
    };
  },
  methods: {
    show(li) {
      this.detailInfo = li;
      this.dialogVisiblea = true;
    },
    close() {
      this.dialogVisiblea = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.notice-detail {
  .detail-content {
    padding: 30px 20px;
    color: $white;
    height: 671px;
    overflow-y: auto;
    .detail-title {
      @include add-size($font_size_18);
      padding-bottom: 20px;
    }
    .detail-text {
      line-height: 30px;
      @include add-size($font_size_14);
    }
  }
}
</style>