<template>
  <div class="reward-center">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="未使用" name="newCard">
        <!-- <span slot="label" class="section-tab-name-first">未使用</span> -->
        <div class="ticket-ul">
          <div class="ticket-box" v-for="(ticket, i) in ticketData.newCard" :key="i">
            <div class="ticket-li">
              <div class="ticket-info">
                <div class="info-base">
                  <div class="ticket-name">{{ticket.stockName}}</div>
                  <div class="ticket-limit">有效期至{{ticket.expireTime | dateFormat("yyyy-MM-dd")}}</div>
                </div>
                <div class="price">
                  HK$
                  <span>{{ticket.curPirce}}</span>
                </div>
              </div>
              <div class="ticket-intro">
                <div class="intro-de">
                  <div class="intro-de-t" @click="ticket.showIntro = !ticket.showIntro">
                    卡片说明
                    <i class="el-icon-arrow-down" :class="{active: ticket.showIntro}"></i>
                  </div>
                  <div class="intro-text" :class="{active: ticket.showIntro}">{{intro}}</div>
                </div>
                <div class="use-btn" @click="useCard(ticket)">立即使用</div>
              </div>
            </div>
          </div>
          <div class="no-data" v-if="ticketData.newCard.length <= 0">暂无数据</div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="已使用" name="usedCard">
        <!-- <span slot="label" class="section-tab-name">已使用</span> -->
        <div class="ticket-ul">
          <div class="ticket-box" v-for="(ticket, i) in ticketData.usedCard" :key="i">
            <div class="ticket-li">
              <div class="ticket-info">
                <div class="info-base">
                  <div class="ticket-name">{{ticket.stockName}}</div>
                  <div class="ticket-limit">有效期至{{ticket.expireTime | dateFormat("yyyy-MM-dd")}}</div>
                </div>
                <div class="price">
                  HK$
                  <span>{{ticket.curPirce}}</span>
                </div>
              </div>
              <div class="ticket-intro">
                <div class="intro-de">
                  <div class="intro-de-t" @click="ticket.showIntro = !ticket.showIntro">
                    卡片说明
                    <i class="el-icon-arrow-down" :class="{active: ticket.showIntro}"></i>
                  </div>
                  <div class="intro-text" :class="{active: ticket.showIntro}">{{intro}}</div>
                </div>
                <div class="use-btn used">已使用</div>
              </div>
            </div>
          </div>
          <div class="no-data" v-if="ticketData.usedCard.length <= 0">暂无数据</div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="已过期" name="expireCard">
        <div class="ticket-ul">
          <div class="ticket-box" v-for="(ticket, i) in ticketData.expireCard" :key="i">
            <div class="ticket-li">
              <div class="ticket-info">
                <div class="info-base">
                  <div class="ticket-name">{{ticket.stockName}}</div>
                  <div class="ticket-limit">有效期至{{ticket.expireTime | dateFormat("yyyy-MM-dd")}}</div>
                </div>
                <div class="price">
                  HK$
                  <span>{{ticket.curPirce}}</span>
                </div>
              </div>
              <div class="ticket-intro">
                <div class="intro-de">
                  <div class="intro-de-t" @click="ticket.showIntro = !ticket.showIntro">
                    卡片说明
                    <i class="el-icon-arrow-down" :class="{active: ticket.showIntro}"></i>
                  </div>
                  <div class="intro-text" :class="{active: ticket.showIntro}">{{intro}}</div>
                </div>
                <div class="use-btn used">已过期</div>
              </div>
            </div>
          </div>
          <div class="no-data" v-if="ticketData.expireCard.length <= 0">暂无数据</div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      activeName: "newCard",
      ticketData: {
        newCard: [],
        usedCard: [],
        expireCard: []
      },
      intro:
        "卡券需要交易账户余额＞1才可使用。使用后3个工作日内以股票等值现金发放至您的钱包账户内"
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  activated() {
    this.$nextTick(() => {
      this.getAwardList();
    });
  },
  watch: {
    activeName() {
      this.getAwardList();
    }
  },
  methods: {
    handleClick() {},
    getAwardList() {
      // POST /customer/points/selectRewardOverview
      let status =
        this.activeName === "newCard"
          ? 0
          : this.activeName === "usedCard"
          ? 1
          : "";
      this.axios({
        url: "/draw/info/selectCardCoupon",
        params: {
          receiveStatus: status // 0 未使用 1 使用 不填为已过期
        },
        type: "host3"
      }).then(res => {
        res.data.ret.forEach(a => {
          a.showIntro = false;
        });
        if (this.activeName === "newCard") {
          res.data.ret = res.data.ret.filter(b => {
            return b.boo;
          });
        }
        this.ticketData[this.activeName] = res.data.ret;
      });
    },
    useCard(ticket) {
      this.$confirm("确定使用此该卡券？", "提示", {
        cancelButtonText: "取消"
      }).then(() => {
        // GET /draw/info/updateCardStatus
        this.axios({
          url: "http://39.106.134.104:9006/draw/info/updateCardStatus",
          params: {
            id: ticket.id
          }
          // type: "host3"
        })
          .then(res => {
            this.$message({
              message: "使用成功！",
              type: "success"
            });
          })
          .catch(err => {
            ticket.showIntro = true;
          });
      });
    }
  }
};
</script> 

<style lang="scss" scoped>
$cardColor: #370704;
.reward-center {
  height: 100%;
  width: 100%;
  /deep/ .el-tabs {
    height: 57px;
    .el-tabs__item {
      color: $defaultColor;
      @include add-size($font_size_18);
      line-height: 57px;
      height: auto;
      &.is-active,
      &:hover {
        color: $activeColor;
      }
      .section-tab-name {
        padding: 0 128px;
      }
      .section-tab-name-first {
        padding: 0 20px;
      }
    }
    .el-tabs__nav-wrap {
      &::after {
        background: #323840;
        height: 1px;
      }
      .el-tabs__active-bar {
        background-color: transparent;
        &::after {
          background-color: $activeColor;
          width: 50px;
          height: 4px;
          border-radius: 2px;
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          margin-top: -1px;
          transform: translate(-50%, -50%);
        }
      }
    }
    .el-tabs__content {
      position: relative;
      padding-bottom: 30px;
    }
  }
  .ticket-ul {
    padding-top: 30px;
    @include flex-box;
    flex-wrap: wrap;
    width: 1340px;
    margin: 0 auto;
    .ticket-box {
      height: 200px;
      margin-right: 100px;
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    .ticket-li {
      width: 380px;
      background: linear-gradient(
        124deg,
        rgba(247, 237, 215, 1) 0%,
        rgba(233, 206, 147, 1) 100%
      );
      border: 2px solid $activeColor;
      // border-image: linear-gradient(
      //     180deg,
      //     rgba(255, 249, 237, 1),
      //     rgba(247, 236, 211, 1)
      //   )
      //   10 10;
      border-radius: 14px;
      box-shadow: 0px 8px 22px 0px rgba(213, 213, 213, 0.09);

      .ticket-info {
        color: $cardColor;
        @include flex-center;
        height: 96px;
        border-bottom: 1px dashed $white;
        padding: 22px 22px 24px 24px;
        .info-base {
          .ticket-name {
            @include add-size($font_size_20);
          }
          .ticket-limit {
            @include add-size($font_size_14);
            margin-top: 16px;
          }
        }
        .price {
          @include add-size($font_size_20);
          margin-left: auto;
          span {
            @include add-size(26px);
            @include medium;
          }
        }
      }
      .ticket-intro {
        padding: 0 24px;
        @include flex-box;
        color: $white;
        min-height: 52px;
        .intro-de {
          .intro-de-t {
            line-height: 52px;
            cursor: pointer;
            color: $cardColor;
          }
          i {
            margin-left: 8px;
            transition: all 0.3s ease;
            &.active {
              transform: rotate(180deg);
            }
          }
          .intro-text {
            width: 204px;
            height: 0;
            overflow: hidden;
            transition: all 0.3s ease;
            color: $cardColor;
            line-height: 18px;
            &.active {
              height: 60px;
            }
          }
        }
        .use-btn {
          width: 80px;
          height: 24px;
          margin-top: 14px;
          border-radius: 12px;
          background-color: #b03200;
          @include add-size($font_size_14);
          text-align: center;
          line-height: 24px;
          margin-left: auto;
          cursor: pointer;
          &.used {
            cursor: default;
            background-color: #aaa;
          }
        }
      }
    }
    .no-data {
      line-height: 336px;
      text-align: center;
      width: 100%;
    }
  }
}
</style>
