<template>
  <div class="statement-field">
    <div class="statement-block daily">
      <div class="sort-bar">
        <div class="table-title">日结单</div>
        <label class="picker-label" for="monthPicker">
          {{daySettle.settleym}}
          <i
            class="el-icon-caret-bottom arrow"
            :class="{active: monthSelect}"
          ></i>
          <el-date-picker
            value-format="yyyy-MM"
            class="picker"
            id="monthPicker"
            v-model="daySettle.settleym"
            type="month"
            placeholder="选择月"
            @blur="monthSelect = false"
            @focus="monthSelect = true"
            @change="changeMonth"
          ></el-date-picker>
        </label>

        <!-- <div class="batch">
          <img src="~@/assets/images/jy/batch-icon.png" class="batch-icon" alt />批量发送
        </div>-->
      </div>
      <div class="statement-ul">
        <div class="order-title">
          <div class="column-name">时间</div>
          <div class="column-name">操作</div>
        </div>
        <div class="order-ul">
          <div class="order-li" v-for="(li, i) in lstCustomerAssetsSettleDay" :key="i">
            <div class="li-date">{{li.settleDate}}</div>
            <div class="li-check" @click="showDetail(li, 'day')">
              <img src="~@/assets/images/jy/check-icon.png" class="check-icon icon" alt /> 查看
            </div>
            <!-- <div class="li-email" @click="sendEmail">
              <img src="~@/assets/images/jy/email-icon.png" class="check-icon icon" alt />
              发送邮箱
            </div>-->
          </div>
          <div class="no-data" v-if="lstCustomerAssetsSettleDay.length <= 0">暂无数据</div>
        </div>
      </div>
    </div>

    <div class="statement-block">
      <div class="sort-bar">
        <div class="table-title">月结单</div>
        <label class="picker-label" for="yearPicker">
          {{monthSettle.settley}}
          <i
            class="el-icon-caret-bottom arrow"
            :class="{active: yearSelect}"
          ></i>
          <el-date-picker
            value-format="yyyy"
            class="picker"
            id="yearPicker"
            v-model="monthSettle.settley"
            type="year"
            placeholder="选择年"
            @blur="yearSelect = false"
            @focus="yearSelect = true"
            @change="changeYear"
          ></el-date-picker>
        </label>
      </div>
      <div class="statement-ul">
        <div class="order-title">
          <div class="column-name">时间</div>
          <div class="column-name">操作</div>
        </div>
        <div class="order-ul">
          <div class="order-li" v-for="(li, i) in lstCustomerSettleMonth" :key="i">
            <div class="li-date">{{li.settleDate}}</div>
            <div class="li-check" @click="showDetail(li, 'month')">
              <img src="~@/assets/images/jy/check-icon.png" class="check-icon icon" alt /> 查看
            </div>
          </div>
          <div class="no-data" v-if="lstCustomerSettleMonth.length <= 0">暂无数据</div>
        </div>
      </div>
    </div>

    <StatementDetail ref="StatementDetail" />
  </div>
</template>

<script>
import StatementDetail from "@/components/deal/statement-detail";
import { hostEbroker } from "@/utils/config";
export default {
  components: {
    StatementDetail
  },
  data() {
    return {
      monthSelect: false,
      yearSelect: false,
      daySettle: {
        settleym: "",
        market: "hk",
        depositymd: "",
        pageSize: 10,
        pageStart: 1,
        url: "/settle/getAssetsDaySettle"
      },
      monthSettle: {
        settley: "",
        market: "hk",
        depositym: "",
        pageSize: 10,
        pageStart: 1,
        url: "/settle/getAssetsMonthSettle"
      },
      lstCustomerSettleMonth: [],
      lstCustomerAssetsSettleDay: []
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  activated() {
    this.changeMonth(new Date().Format("yyyy-MM"));
    this.changeYear(new Date().Format("yyyy"));
  },
  methods: {
    showDetail(li, flag) {
      this.$refs.StatementDetail.show(li, flag);
    },
    changeMonth(e) {
      this.daySettle.settleym = e;
      this.getDaySettleList();
    },
    changeYear(e) {
      this.monthSettle.settley = e;
      this.getMonthSettleList();
    },
    getDaySettleList() {
      // 日结列表
      let daySettle = this.daySettle;
      this.requestSettle(daySettle, res => {
        console.log(res);
        this.lstCustomerAssetsSettleDay =
          res.data.data.lstCustomerAssetsSettleDay;
      });
    },
    getMonthSettleList() {
      // 月结列表
      let monthSettle = this.monthSettle;
      this.requestSettle(monthSettle, res => {
        console.log(res);
        this.lstCustomerSettleMonth = res.data.data.lstCustomerSettleMonth;
      });
    },
    requestSettle(data, cb) {
      this.axios({
        url: data.url,
        params: data,
        type: "hostEbroker"
      }).then(res => {
        cb(res);
      });
    },
    handleClick() {}
  }
};
</script>

<style scoped lang="scss">
.statement-field {
  @include flex-box;
  .statement-block {
    margin-top: 20px;
    padding: 0 20px;
    width: 600px;
    &.daily {
      border-right: 1px solid #323840;
    }
    .sort-bar {
      @include flex-center;
      .table-title {
        // @include add-size($font_size_14);
        color: $activeColor;
        margin-right: 145px;
      }
      .picker {
        position: absolute;
        width: 0;
        height: 0;
        overflow: hidden;
      }
      .picker-label {
        cursor: pointer;
        position: relative;
        .arrow {
          margin-left: 5px;
          transition: all 0.3s ease;
          &.active {
            transform: rotate(180deg);
          }
        }
      }
      .batch {
        margin-left: 33px;
        @include flex-center;
        .batch-icon {
          margin-right: 9px;
        }
      }
    }
    .statement-ul {
      .order-title {
        @include flex-center;
        margin-top: 16px;
        justify-content: space-around;
        .column-name {
          // width: 38%;
        }
      }
      .order-ul {
        height: 180px;
        overflow-y: auto;
        padding-right: 5px;
      }
      .order-li {
        @include flex-center;
        color: $white;
        justify-content: space-around;
        // @include add-size($font_size_14);
        line-height: 34px;
        &:hover {
          background-color: #323840;
        }
        .li-check {
          @include flex-center;
          cursor: pointer;
          &:hover {
            color: $activeColor;
          }
        }
        .li-email {
          @include flex-center;
          cursor: pointer;
        }
        .icon {
          margin-right: 10px;
        }
      }
    }
  }
  /deep/ .el-dialog__wrapper {
    .el-dialog {
      .message-content {
        line-height: 24px;
        @include add-size($font_size_12);
        padding-left: 30px;
        padding-bottom: 26px;
        .email-c {
          color: #40a9ff;
        }
      }
    }
  }
  .no-data {
    text-align: center;
    line-height: 50px;
  }
}
</style>