<template>
  <div class="main-zx">
    <div class="left-list">
      <el-tabs v-model="activeName" :stretch="true" @tab-click="handleClick">
        <el-tab-pane label="推荐" name="first">
          <div class="news-ul first">
            <newsList
              :liData="li"
              v-for="li in selectData.first.list"
              :key="li.id"
              @target="getNewsDetail"
            />
            <infinite-loading
              ref="infinite"
              @infinite="getList"
              :distance="20"
              forceUseInfiniteWrapper=".news-ul.first"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results">暂无数据</span>
            </infinite-loading>
          </div>
        </el-tab-pane>
        <el-tab-pane label="自选" name="second">
          <div class="news-ul second">
            <newsList
              :liData="li"
              v-for="li in selectData.second.list"
              :key="li.id"
              @target="getNewsDetail"
            />
            <infinite-loading
              ref="infinite"
              @infinite="getList"
              :distance="20"
              forceUseInfiniteWrapper=".news-ul.second"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results">暂无数据</span>
            </infinite-loading>
          </div>
        </el-tab-pane>
        <el-tab-pane label="港股" name="third">
          <div class="news-ul third">
            <newsList
              :liData="li"
              v-for="li in selectData.third.list"
              :key="li.id"
              @target="getNewsDetail"
            />
            <infinite-loading
              ref="infinite"
              @infinite="getList"
              :distance="20"
              forceUseInfiniteWrapper=".news-ul.third"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results">暂无数据</span>
            </infinite-loading>
          </div>
        </el-tab-pane>
        <el-tab-pane label="美股" name="fourth">
          <div class="news-ul fourth">
            <newsList
              :liData="li"
              v-for="li in selectData.fourth.list"
              :key="li.id"
              @target="getNewsDetail"
            />
            <infinite-loading
              ref="infinite"
              @infinite="getList"
              :distance="20"
              forceUseInfiniteWrapper=".news-ul.fourth"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results">暂无数据</span>
            </infinite-loading>
          </div>
        </el-tab-pane>
        <el-tab-pane label="沪深" name="fifth">
          <div class="news-ul fifth">
            <newsList
              :liData="li"
              v-for="li in selectData.fifth.list"
              :key="li.id"
              @target="getNewsDetail"
            />
            <infinite-loading
              ref="infinite"
              @infinite="getList"
              :distance="20"
              forceUseInfiniteWrapper=".news-ul.fifth"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results">暂无数据</span>
            </infinite-loading>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="right-detail">
      <div class="news-detail">
        <div class="news-info">
          <div class="news-title" @click="onPreview">{{currentInfo.newsTitle}}</div>
          <div class="news-field_time">
            <span class="news-field">{{currentInfo.newsSource}}</span>
            <span class="news-time">{{currentInfo.newsPubTime}}</span>
          </div>
        </div>
        <div class="news-article text01" v-html="currentInfo.content" ref="newsContent"></div>
      </div>
    </div>
    <el-image-viewer v-if="showViewer" :on-close="closeViewer" :url-list="imgUrls" />
  </div>
</template>

<script>
import newsList from "./news-list";
import infiniteLoading from "vue-infinite-loading";
import ElImageViewer from "@/components/element-ui/image/image-viewer";
export default {
  components: {
    newsList,
    infiniteLoading,
    ElImageViewer
  },
  data() {
    return {
      activeName: "first",
      showViewer: false,
      currentInfo: {},
      selectData: {
        first: {
          url: "/news/title/selectHold",
          page: 1,
          size: 10,
          list: []
        },
        second: {
          url: "/news/title/selectOptional",
          type: "application/gzip",
          page: 1,
          size: 10,
          list: []
        },
        third: {
          url: "/news/title/selectNewList",
          market: "hk",
          page: 1,
          size: 10,
          list: []
        },
        fourth: {
          url: "/news/title/selectNewList",
          market: "us",
          page: 1,
          size: 10,
          list: []
        },
        fifth: {
          url: "/news/title/selectNewList",
          market: "cn",
          page: 1,
          size: 10,
          list: []
        }
      },
      imgUrls: [],
      imgPreviewIndex: 0,
      isRouterAlive: true
    };
  },
  mounted() {
    this.$nextTick(() => {
      // this.getList();
    });
  },
  activated() {},
  methods: {
    reload() {
      this.activeName = "first";
      this.$nextTick(() => {
        for (let key in this.selectData) {
          this.selectData[key].page = 1;
          this.selectData[key].list = [];
        }
        this.getList();
      });
    },
    onPreview() {
      this.showViewer = true;
    },
    // 关闭查看器
    closeViewer() {
      this.showViewer = false;
    },
    setImgPreview() {
      let imgs = this.$refs.newsContent.getElementsByTagName("img");
      let _this = this;
      for (let i = 0; i < imgs.length; i++) {
        imgs[i].setAttribute("index", i);
        imgs[i].onclick = function() {
          let index = Number(this.getAttribute("index"));
          let src = this.src;
          let left = [];
          let right = [];
          // 重置当前显示图片
          for (let m = 0; m < imgs.length; m++) {
            let _index = Number(imgs[m].getAttribute("index"));
            if (_index > index) {
              left.push(imgs[m].src);
            } else if (_index < index) {
              right.push(imgs[m].src);
            }
          }
          _this.imgUrls = [src, ...left, ...right];
          _this.onPreview();
        };
      }
    },
    getData() {
      // this.axios({
      //   url: "http://quote.lc913.com/quote-service/api/v2/selfstock",
      //   method: "get",
      //   params: {
      //     userid: "130027"
      //   }
      // }).then(res => {
      //   console.log(res, 123456);
      // });
    },
    handleClick() {
      if (this.selectData[this.activeName].list.length <= 0) {
        this.getList();
      }
    },
    getNewsDetail(li) {
      // GET /news/title/selectNewListInfo
      this.axios({
        url: "/news/title/selectNewListInfo",
        method: "get",

        params: {
          newsOwnId: li.newsId
        },
        type: "hostNews"
      }).then(res => {
        this.currentInfo = { ...li, ...res.data.ret };
        this.$nextTick(() => {
          this.setImgPreview();
        });
      });
    },
    getList($state) {
      let selectData = this.selectData;
      let activeName = this.activeName;
      let isFirst = selectData[activeName].list.length <= 0;
      this.axios({
        url: selectData[activeName].url,
        method: "get",
        headers: {
          "Content-Type": "application/zip"
        },
        params: {
          page: selectData[activeName].page,
          size: selectData[activeName].size,
          market: selectData[activeName].market
        },
        type: "hostNews"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          selectData[activeName].list = selectData[activeName].list.concat(
            res.data.ret.list
          );
          if (isFirst) {
            this.getNewsDetail(selectData[activeName].list[0]);
          }
          selectData[activeName].page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    }
  },
  watch: {
    "$store.state.GlobalData.needRelogin"(val) {
      if (!val && this.$route.name === "zx") {
        this.reload();
        this.$store.dispatch("SET_NEED_RELOGIN", true);
      }
    }
  }
};
</script> 

<style lang="scss" scoped>
.main-zx {
  height: 100%;
  // width: calc(100% - 84px);
  @include flex-box;
  width: 100%;
  .left-list {
    width: 600px;
    height: 100%;
    background-color: $blockBgColor;
    border-radius: 4px;
    margin-right: 4px;
    /deep/ .el-tabs {
      height: 100%;
      .el-tabs__item {
        color: $defaultColor;
        @include add-size($font_size_18);
        line-height: 57px;
        height: auto;
        &.is-active,
        &:hover {
          color: $activeColor;
        }
      }
      .el-tabs__header {
        margin-bottom: 0;
      }
      .el-tabs__nav-wrap {
        &::after {
          background: #323840;
          height: 1px;
        }
        .el-tabs__active-bar {
          background-color: transparent;
          &::after {
            background-color: $activeColor;
            width: 36px;
            height: 4px;
            border-radius: 2px;
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: -1px;
            transform: translate(-50%, -50%);
          }
        }
      }
      .el-tabs__content {
        height: calc(100% - 80px);
        .news-ul {
          height: 100%;
          overflow-y: auto;
        }
      }
      .el-tab-pane {
        animation: show-tab 0.5s ease forwards;
        height: 100%;
      }
      @keyframes show-tab {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
    }
  }
  .right-detail {
    width: 1312px;
    height: 100%;
    background-color: $blockBgColor;
    border-radius: 4px;
    position: relative;
    .news-detail {
      @include add-size($font_size_16);
      height: 100%;
      .news-info {
        padding: 43px 50px 32px;
        padding-left: 50px;
        border-bottom: 1px solid #323840;
        .news-title {
          @include add-size($font_size_22);
          color: $white;
          line-height: 36px;
        }
        .news-field_time {
          margin-top: 37px;
          .news-field {
            margin-right: 30px;
          }
        }
      }
      /deep/.news-article {
        padding: 20px 50px;
        overflow-y: auto;
        height: 80%;
        &.text01 {
          p {
            line-height: 30px;
            text-indent: 0em;
            font-size: 1em;
            padding-top: 20px;
          }
        }
        img {
          max-width: 100%;
        }
      }
    }
  }
}
</style>
