<template>
  <div class="hqListChild" :class="{ allInfo: allInfo }" ref="hqListChild">
    <div
      v-if="noShow"
      class="control"
      :class="{ controlImg: isShow }"
      @click="shouHide"
    >
      <img src="@/assets/images/hq/right.png" alt />
    </div>
    <div class="filter-key" v-if="hqList === 4 || hqList === 5 || hqList === 8">
      <el-select
        v-if="hqList != 8"
        v-model="category.type.value"
        placeholder="类型"
        @change="changeCategroy($event, 'type')"
      >
        <el-option
          :label="type.label"
          :value="type.value"
          v-for="type in category.type.children"
          :key="type.value"
        ></el-option>
      </el-select>
      <el-select
        v-model="category.endDate.value"
        placeholder="到期日"
        @change="changeCategroy($event, 'endDate')"
      >
        <el-option
          :label="endDate.label"
          :value="endDate.value"
          v-for="endDate in category.endDate.children"
          :key="endDate.value"
        ></el-option>
      </el-select>
      <el-select
        v-model="category.publisher.value"
        placeholder="发行人"
        @change="changeCategroy($event, 'publisher')"
      >
        <el-option
          :label="publisher.label"
          :value="publisher.value"
          v-for="publisher in category.publisher.children"
          :key="publisher.value"
        ></el-option>
      </el-select>
    </div>
    <!-- :height="maxHeight" -->
    <el-table
      style="padding-right:9px;"
      :data="tableData"
      :row-class-name="tableRowClassName"
      empty-text=" "
      :height="maxHeight"
      highlight-current-row
      ref="multipleTable"
      @sort-change="changeSortable"
      @row-click="goDetail"
      @row-contextmenu="rowContextmenu"
    >
      <!-- fixed="left" -->
      <el-table-column
        label="序号"
        type="index"
        width="50"
        align="center"
      ></el-table-column>
      <el-table-column prop="name" label="股票名称" align="left" width="120">
        <template slot-scope="scope">
          <div
            :data-selfStock="scope.row.selfStock"
            :title="scope.row.name"
            class="stock-name"
          >
            {{ scope.row.name }}
          </div>
          <div>
            <span style="background: #FA541C;font-size:10px;padding:2px;"
              >HK</span
            >
            <!-- style="color:#9195AC;" -->
            <span>{{ scope.row.symbol.slice(2) }}</span>
          </div>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="name" label align="center" width="150">
        <template slot-scope="scope">
          <sCharts v-if="scope.row.data" :sChartsData="scope.row.data" />
        </template>
      </el-table-column> -->
      <el-table-column prop="last" label="最新价" align="center" width="86">
        <template slot-scope="scope">
          <span v-if="scope.row.chg > 0" class="red">{{ scope.row.last }}</span>
          <span v-else-if="scope.row.chg < 0" class="greed">{{
            scope.row.last
          }}</span>
          <span v-else class="gay">{{ scope.row.last }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="chg_pct"
        sortable="custom"
        label="涨跌幅"
        align="center"
        width="86"
      >
        <template slot-scope="scope">
          <div v-if="scope.row.yd_chg">
            <span v-if="scope.row.yd_chg > 0" class="red"
              >+{{ scope.row.yd_chg_pct }}%</span
            >
            <span v-else-if="scope.row.yd_chg < 0" class="greed"
              >{{ scope.row.yd_chg_pct }}%</span
            >
            <span v-else class="gay">{{ scope.row.yd_chg_pct }}%</span>
          </div>
          <div v-else>
            <span v-if="scope.row.chg > 0" class="red"
              >+{{ scope.row.chg_pct }}%</span
            >
            <span v-else-if="scope.row.chg < 0" class="greed"
              >{{ scope.row.chg_pct }}%</span
            >
            <span v-else class="gay">{{ scope.row.chg_pct }}%</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="chg" label="涨跌额" align="center" width="86">
        <template slot-scope="scope">
          <div v-if="scope.row.yd_chg">
            <span v-if="scope.row.yd_chg > 0" class="red"
              >+{{ scope.row.yd_chg }}</span
            >
            <span v-else-if="scope.row.yd_chg < 0" class="greed">{{
              scope.row.yd_chg
            }}</span>
            <span v-else class="gay">{{ scope.row.yd_chg }}</span>
          </div>
          <div v-else>
            <span v-if="scope.row.chg > 0" class="red"
              >+{{ scope.row.chg }}</span
            >
            <span v-else-if="scope.row.chg < 0" class="greed">{{
              scope.row.chg
            }}</span>
            <span v-else class="gay">{{ scope.row.chg }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-if="isShow"
        prop="turnoverrate"
        label="换手率"
        align="center"
        width="96"
      ></el-table-column>
      <el-table-column
        v-if="isShow"
        prop="pe"
        label="市盈率"
        align="center"
        width="96"
      ></el-table-column>
      <el-table-column
        v-if="isShow"
        prop="bookvalueratio"
        label="市净率"
        align="center"
        width="96"
      ></el-table-column>
      <el-table-column
        v-if="isShow"
        prop="amplitude"
        label="日振幅"
        align="center"
        width="96"
      ></el-table-column>
      <el-table-column
        v-if="isShow"
        prop="volume"
        label="成交量"
        align="center"
        width="96"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.volume | digitUnit }}</span>
        </template>
      </el-table-column>
      <el-table-column
        v-if="isShow"
        prop="amount"
        label="成交额"
        align="center"
        width="96"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.amount | digitUnit }}</span>
        </template>
      </el-table-column>
      <el-table-column
        v-if="isShow"
        prop="ttm"
        label="市净率(动)"
        align="center"
        width="100"
      ></el-table-column>
      <el-table-column
        v-if="isShow"
        prop="flowmarketCap"
        label="流通市值"
        align="center"
        width="96"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.flowmarketCap | digitUnit }}</span>
        </template>
      </el-table-column>
      <el-table-column
        v-if="isShow"
        prop="marketCap"
        show-overflow-tooltip
        label="总市值"
        align="center"
        width="120"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.marketCap | digitUnit }}</span>
        </template>
      </el-table-column>
      <infinite-loading
        v-if="loading"
        slot="append"
        @infinite="appendTable"
        :identifier="infiniteId"
        force-use-infinite-wrapper=".el-table__body-wrapper"
        spinner="bubbles"
      >
        <span slot="no-more" class="no-more">—————— 加载完成 ———————</span>
        <span slot="no-results" class="no-result">—————— 暂无数据 ———————</span>
      </infinite-loading>
    </el-table>
    <!-- <rightMenu ref="rightMenu" /> -->
  </div>
</template>

<script>
import { initMOption } from "@/utils/k-line";
import sCharts from "@/components/common/sCharts.vue";
import InfiniteLoading from "vue-infinite-loading";
// import rightMenu from "@/components/popup/right-menu";

import { initArray, showDetailComponents } from "@/utils";
export default {
  name: "hqList",
  components: {
    InfiniteLoading,
    sCharts
    // rightMenu
  },
  data() {
    return {
      tableData: [],
      isOK: true,
      count: 0,
      index: 1,
      loading: true,
      isShow: false,
      allInfo: false, //是否显示全部信息
      direction: 1,
      infiniteId: 0,
      type: 0,
      maxHeight: 899,
      category: {
        type: {
          value: "",
          label: "全部",
          children: [],
          children4: [
            {
              value: "default",
              label: "全部"
            },
            {
              label: "认购",
              value: "WORM.C"
            },
            {
              label: "认沽",
              value: "WORM.P"
            }
          ],
          children5: [
            {
              label: "全部",
              value: "default"
            },
            {
              label: "牛证",
              value: "CBBC.C"
            },
            {
              label: "熊证",
              value: "CBBC.P"
            }
          ]
        },
        publisher: {
          value: "",
          label: "全部",
          children: [
            {
              value: "default",
              label: "全部"
            },
            {
              value: "BC",
              label: "巴克莱"
            },
            {
              value: "BI",
              label: "中银"
            },
            {
              value: "BP",
              label: "法巴"
            },
            {
              value: "CS",
              label: "瑞信"
            },
            {
              value: "CT",
              label: "花旗"
            },
            {
              value: "DB",
              label: "德银"
            },
            {
              value: "DC",
              label: "大和"
            },
            {
              value: "EA",
              label: "东亚"
            },
            {
              value: "GS",
              label: "高盛"
            },
            {
              value: "HS",
              label: "汇丰"
            },
            {
              value: "HT",
              label: "海通"
            },
            {
              value: "JP",
              label: "摩通"
            },
            {
              value: "KC",
              label: "比联金融"
            },
            {
              value: "MB",
              label: "卖银"
            },
            {
              value: "ML",
              label: "美林"
            },
            {
              value: "MS",
              label: "摩利"
            },
            {
              value: "NM",
              label: "野村"
            },
            {
              value: "RB",
              label: "荷合"
            },
            {
              value: "RS",
              label: "苏皇"
            },
            {
              value: "SC",
              label: "渣打"
            },
            {
              value: "SG",
              label: "法兴"
            },
            {
              value: "UB",
              label: "瑞银"
            },
            {
              value: "VT",
              label: "瑞通"
            }
          ]
        },
        endDate: {
          value: "",
          label: "全部",
          children: [
            {
              value: "default",
              label: "全部"
            },
            {
              value: "<3",
              label: "<3个月"
            },
            {
              value: "3-6",
              label: "3-6个月"
            },
            {
              value: "6-12",
              label: "6-12个月"
            },
            {
              value: ">12",
              label: ">12个月"
            }
          ]
        }
      },
      noShow: false,
      industryIdType: true,
      tableIndex: 0,
      timer: "",
      listTimer: "",
      showRight: false,
      personStock: "",
      listTimeOut: "",
      needData: "",
      neddUrl: "",
      newTimeOut: "",
      interTime: ""
    };
  },
  props: {
    hqList: {
      default: 1
    },
    industryId: {
      default: 1
    }
  },
  mounted() {
    this.$nextTick(() => {
      let that = this;
      this.setHeight();
      let box = document.getElementsByClassName("el-table__body-wrapper")[0];
      box.addEventListener("scroll", () => {
        this.setTimeFn();
      });
      document.addEventListener("keyup", function(e) {
        //进来默认是收起状态开始
        that.isShow = false;
        that.allInfo = false;
        that.$emit("getStatus", that.allInfo);
        //进来默认是收起状态结束
        if (!that.showRight) {
          return;
        }
        // 下40 上38
        // e.preventDefault()
        if (e.keyCode == 38) {
          if (that.tableIndex == 0) {
            return;
          } else {
            clearTimeout(that.timer);
            that.tableIndex -= 1;
            if (that.tableIndex > 7) {
              that.$refs.multipleTable.bodyWrapper.scrollTop -= 57;
            } else {
              that.$refs.multipleTable.bodyWrapper.scrollTop = 0;
            }
            that.$refs.multipleTable.setCurrentRow(
              that.tableData[that.tableIndex]
            );
            that.timer = setTimeout(() => {
              that.$store.dispatch("SET_CURRENT_STOCK", {
                code: that.tableData[that.tableIndex].symbol
              });
            }, 500);
          }
        } else if (e.keyCode == 40) {
          if (that.tableIndex < that.tableData.length - 1) {
            that.tableIndex += 1;
          } else {
            return;
          }
          clearTimeout(that.timer);
          if (that.tableIndex > 7) {
            that.$refs.multipleTable.bodyWrapper.scrollTop += 57;
          }
          that.$refs.multipleTable.setCurrentRow(
            that.tableData[that.tableIndex]
          );

          that.timer = setTimeout(() => {
            that.$store.dispatch("SET_CURRENT_STOCK", {
              code: that.tableData[that.tableIndex].symbol
            });
          }, 500);
        }
      });
    });
  },
  activated() {
    this.tableData = [];
    this.infiniteId++;
    this.showRight = true;
    //进来默认是收起状态开始
    this.isShow = false;
    this.allInfo = false;
    this.$emit("getStatus", this.allInfo);
    //进来默认是收起状态结束
    this.setHeight();
    this.index = 1;
    // if (this.tableData.length > 0) {
    //   this.$store.dispatch("CHANGE_SEARCH_DATA", {
    //     code: this.tableData[0].symbol,
    //     timestamp: new Date().getTime()
    //   });
    // }
    // let stockCodeList = window.stockCodeList || {};
    // if (new Date().getTime() - stockCodeList.timestamp < 1000) {
    //   this.personStock = stockCodeList.stockCode;
    // }
    // this.listTimeOut = setInterval(() => {
    //   this.tableData = [];
    //   this.infiniteId++;
    //   this.index = 1;
    // },5000)
  },
  deactivated() {
    // clearInterval(this.listTimeOut)
    this.showRight = false;
    this.tableData = [];
    clearTimeout(this.newTimeOut);
  },
  methods: {
    rowContextmenu(row, column, event) {
      // 列表右键菜单操作
      row.x = event.clientX;
      row.y = event.clientY;
      this.$refs.rightMenu.show(row);
    },
    setHeight() {
      let height = this.$refs.hqListChild.offsetHeight || 899;
      this.noShow = true;
      // console.log(this.hqList);
      if (this.hqList == 4 || this.hqList == 5 || this.hqList == 8) {
        this.maxHeight = height - 50;
        this.allInfo = false;
        this.isShow = false;
        this.noShow = false;
        this.$emit("getStatus", this.allInfo);
      } else {
        this.maxHeight = height;
      }
      if (this.industryId == 5) {
        this.noShow = false;
      }
    },
    changeCategroy(e, m) {
      if (e === "default") {
        this.category[m].value = "";
      }
      this.tableData = [];
      this.infiniteId++;
      this.index = 1;
    },
    appendTable(state) {
      let url = "/quote-service/api/v1/hk/range";
      let data = {
        direction: this.direction, //1涨幅排行，0跌幅排行 或价格大小排序
        index: this.index, //当前最后一只股票索引+1
        stockcode: this.personStock, //个股的股票代码
        type: this.type, //type为0按涨跌幅排序，为1按价格排序
        count: 20 //一次获取股票的数量
      };

      if (this.hqList == 1) {
        data.instrument = "EQTY"; //全部
      } else if (this.hqList == 2) {
        data.market = "MAIN"; //主板
        console.log(data);
      } else if (this.hqList == 3) {
        data.market = "GEM"; //创业板
      } else if (this.hqList == 4) {
        data.instrument = "WRNT.WORM"; //窝轮
        this.category.type.children = this.category.type.children4;
        data.searchtype = this.category.type.value;
        data.jingjishang = this.category.publisher.value;
        data.enddate = this.category.endDate.value;
      } else if (this.hqList == 5) {
        data.instrument = "WRNT.CBBC"; // 牛熊
        this.category.type.children = this.category.type.children5;
        data.searchtype = this.category.type.value;
        data.jingjishang = this.category.publisher.value;
        data.enddate = this.category.endDate.value;
      } else if (this.hqList == 8) {
        data.instrument = "WRNT.INLINE"; // 界内证
        this.category.type.children = this.category.type.children5;
        // data.searchtype = this.category.type.value;
        data.jingjishang = this.category.publisher.value;
        data.enddate = this.category.endDate.value;
      } else if (this.industryId == 4) {
        // 热门行业
        url = "/quote-service/api/v1/industrystock";
        data = {
          industry_id: this.hqList,
          index: this.index,
          direction: this.direction,
          count: 20,
          delay: 0
        };
      } else if (this.industryId == 5) {
        // state.complete();
        // 异动榜单
        if (this.industryIdType) {
          state.complete();
        }
        // this.industryIdType = false;
        url = "/quote-service/api/v1/stockyidong";
        if (this.hqList == "five00") {
          data = {
            type: 0,
            direction: this.direction,
            count: 20,
            delay: 0
          };
        } else if (this.hqList == "five01") {
          data = {
            type: 0,
            direction: this.direction,
            count: 20,
            delay: 0
          };
        } else {
          data = {
            type: 1,
            direction: this.direction,
            count: 20,
            delay: 0
          };
        }
      }
      this.needData = data;
      this.neddUrl = url;
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        let data = res.data.Data;
        if (!data) {
          state.complete();
        }

        if (data.length > 0) {
          if (this.tableData.length <= 0) {
            if (this.$store.state.GlobalData.currentStock.recover) {
              this.$store.dispatch("SET_CURRENT_STOCK", {
                recover: false
              });
            } else {
              this.$store.dispatch("SET_CURRENT_STOCK", {
                code: data[0].query.symbol
              });
            }
            showDetailComponents({ show: true });
          }

          data.forEach(a => {
            this.tableData.push(a.query);
          });
          initArray(this.tableData);
          // sessionStorage.setItem('listData',this.tableData)
          // bus.$emit('data',this.tableData[0])
          // if (
          //   this.$store.state.GlobalData.searchData.recover === false &&
          //   this.index < 20
          // ) {
          //   this.$store.dispatch("CHANGE_SEARCH_DATA", {
          //     code: this.tableData[0].symbol,
          //     timestamp: new Date().getTime()
          //   });
          // } else {
          //   if (this.$store.state.GlobalData.searchData.recover) {
          //     this.$store.dispatch("CHANGE_SEARCH_DATA", {
          //       recover: false
          //     });
          //   }
          // }

          // if (this.index < 20) {
          //   let currentCode = this.$store.state.GlobalData.searchData.code;
          //   this.tableData.map((value, index) => {
          //     if (currentCode == value.symbol) {
          //       this.$refs.multipleTable.setCurrentRow(value);
          //       this.tableIndex = index;
          //     }
          //   });
          // }
          state.loaded();
          this.index += 20;
        } else {
          state.complete();
        }
        this.setTimeFn();
      });
    },
    //定时方法
    setTimeFn() {
      clearTimeout(this.newTimeOut);
      this.newTimeOut = setTimeout(() => {
        // clearInterval(this.interTime)
        // this.interTime = setInterval(() => {
        //   this.getCurData()
        // },2000)
        this.getCurData();
        this.setTimeFn();
      }, 20000);
    },
    //定时需要刷的函数
    getCurData() {
      let box = document.getElementsByClassName("el-table__body-wrapper")[0];
      let index = Math.ceil(box.scrollTop / 50) + 1;
      this.needData.index = index;
      this.axios({
        url: this.neddUrl,
        method: "get",
        params: this.needData,
        type: "HQ"
      }).then(res => {
        let data = res.data.Data;
        data.map((value, i) => {
          let dataset = {
            data: [],
            yestclose: 330
          };
          if (value.Data.length != 0) {
            dataset.yestclose = value.Data[0].Hst_close;
          }
          value.Data.map((val, i) => {
            let listAry = new Array();
            let t = new Date(val.Minute * 60 * 1000);
            listAry.push(t.getHours().toString() + t.getMinutes().toString());
            listAry.push(val.Price);
            listAry.push(val.Avg_price);
            listAry.push(val.Volume);
            dataset.data.push(listAry);
          });
          value.query.data = dataset;
          // this.tableData.push(value.query);
          this.tableData.map((v, j) => {
            if (j == index + i) {
              // this.tableData[index+i - 1] = value.query
              this.$set(this.tableData, index + i - 1, value.query);
            }
          });
          initArray(this.tableData);
        });
        let currentCode = this.$store.state.GlobalData.currentStock.code;
        this.tableData.map((value, k) => {
          if (currentCode == value.symbol) {
            this.$refs.multipleTable.setCurrentRow(value);
            this.tableIndex = k;
          }
        });
      });
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (rowIndex % 2 === 0) {
        return "success-row";
      } else if (rowIndex % 2 === 1) {
        return "warning-row";
      }
      return "";
    },
    changeSortable(value) {
      console.log(value);
      if (value.order == null) {
        // 默认排序
        this.type = 0;
        this.direction = 1;
        //进来默认是收起状态开始
        this.isShow = false;
        this.allInfo = false;
        this.$emit("getStatus", this.allInfo);
        //进来默认是收起状态结束
      } else if (value.order == "ascending") {
        //升序
        this.type = 0;
        this.direction = 0;
      } else {
        //降序
        this.type = 0;
        this.direction = 1;
      }
      this.tableData = [];
      this.infiniteId++;
      this.index = 1;
    },
    goDetail(row, column, event) {
      // clearTimeout(this.listTimer);
      this.tableIndex = row.index;
      if (event) {
        this.allInfo = false;
        this.isShow = false;
        this.$emit("getStatus", this.allInfo);
      }
      this.$store.dispatch("SET_CURRENT_STOCK", { code: row.symbol });
      // this.listTimer = setTimeout(() => {
      //   this.$store.dispatch("CHANGE_SEARCH_DATA", {
      //     code: row.symbol,
      //     timestamp: new Date().getTime()
      //   });
      // }, 100);
    },
    shouHide() {
      this.isShow = !this.isShow;
      this.allInfo = !this.allInfo;
      this.$emit("getStatus", this.allInfo);
    }
  },
  computed: {
    // isLogin() {
    //   let isLogin =
    //     this.$store.state.AccountLogin.accountData.SessionKey ||
    //     this.$store.state.AccountLogin.organizationData.ID;
    //   return isLogin;
    // }
  },
  watch: {
    hqList(val) {
      this.setHeight();
      this.category.type.value = "";
      this.category.publisher.value = "";
      this.category.endDate.value = "";
      this.tableData = [];
      this.infiniteId++;
      this.index = 1;
      this.personStock = "";
      //进来默认是收起状态开始
      this.isShow = false;
      this.allInfo = false;
      this.$emit("getStatus", this.allInfo);
      //进来默认是收起状态结束
      if (val == 4 || val == 5 || val == 8) {
        let stockCodeList = window.stockCodeList || {};
        if (new Date().getTime() - stockCodeList.timestamp < 1000) {
          this.personStock = stockCodeList.stockCode;
        }
      }
    },
    // isLogin(val) {
    //   this.setHeight();
    // },
    "$store.state.GlobalData.currentStock.code"(val) {
      //进来默认是收起状态开始
      this.isShow = false;
      this.allInfo = false;
      this.$emit("getStatus", this.allInfo);
      //进来默认是收起状态结束
    }
  }
};
</script>

<style lang="scss" scoped>
