  //   overflow: auto;
  background: #1d242e;
  .control {
    width: 6px;
    height: 180px;
    position: absolute;
    background: #9195ac;
    border-radius: 2px;
    top: 36%;
    right: 0;
    z-index: 99;
    > img {
      width: 100%;
      position: absolute;
      top: 48%;
    }
  }
  .filter-key {
    @include flex-center;
    justify-content: space-around;
    width: 500px;
    padding: 10px 0;
    /deep/ .el-select {
      width: 110px;
      .el-input__inner {
        background-color: inherit;
        color: $defaultColor;
        // padding: 0;
        border-color: #636f81;
        @include add-size($font_size_14);
        line-height: 30px;
        height: 30px;
      }
      .el-input__icon {
        line-height: 30px;
      }
    }
  }
  .controlImg {
    > img {
      transform: rotate(180deg);
      transition: all 0.2s ease;
    }
  }
  /deep/.el-table {
    @include add-size($font_size_14);
    background: #1d242e;
    .cell {
      white-space: nowrap;
      color: #fff;
      padding: 0;
    }
    .stock-name {
      @include single-line-ellipsis;
    }
    .el-table__empty-block {
      background: #1d242e;
    }
    .warning-row {
      background: #1d242e;
    }
    .success-row {
      background: #252e3b;
    }
    .current-row {
      > td {
        background: rgb(78, 77, 77);
      }
    }
    th {
      background: #1d242e;
      border: none;
      // text-align: center;
    }
    td {
      border: none;
      padding: 5px 0;
      // text-align: center;
    }
    tr:hover > td {
      background: rgb(78, 77, 77);
    }
    tr.hover-row > td {
      background-color: rgb(78, 77, 77);
    }
    ::before {
      height: 0;
    }
    .greed {
      color: $green;
    }
    .red {
      color: $red;
    }
    .gray {
      color: #666;
    }
  }
  /deep/.el-table::before {
    height: 0;
  }
}
.allInfo {
  width: calc(100% - 397px);
}
</style>
