<template>
  <div class="send-code">
    <el-dialog
      theame
      title="输入验证码"
      :visible="sendCode"
      width="470px"
      top="15%"
      center
      custom-class="enter"
      :close-on-click-modal="false"
      @close="close"
    >
      <div class="code-content">
        <div class="step-title">已向手机号{{mobile}}发送验证码</div>
        <div class="input-area">
          <div class="code-ul" @click="focusCode">
            <div
              class="code-li"
              v-for="(val, i) in mobileCode"
              :key="i"
              :class="{flash: i === flash}"
            >{{val.key}}</div>
          </div>
          <input
            type="text"
            class="accept-code"
            maxlength="6"
            ref="acceptCode"
            @input="listenCode"
            @blur="flash = ''"
            @keyup.enter="login"
          />
        </div>
        <el-button class="action" hover @click="login">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "forget",
  components: {},
  data() {
    return {
      sendCode: false,
      mobileCode: [
        {
          key: ""
        },
        {
          key: ""
        },
        {
          key: ""
        },
        {
          key: ""
        },
        {
          key: ""
        },
        {
          key: ""
        }
      ],
      codeValue: "",
      flash: "",
      originMobile: ""
    };
  },
  watch: {
    codeValue(val) {
      this.mobileCode.forEach((a, b) => {
        a.key = val[b] || "";
      });
      this.setFlash();
    }
  },
  computed: {
    mobile() {
      let mobile = this.originMobile;
      let strMobile =
        mobile.substring(0, 3) + "*****" + mobile.substring(7, 11);
      return strMobile;
    }
  },
  mounted() {},

  methods: {
    setFlash() {
      for (let i = 0; i < this.mobileCode.length; i++) {
        if (!this.mobileCode[i].key) {
          this.flash = i;
          return false;
        } else {
          this.flash = "";
        }
      }
    },
    show(mobile) {
      this.sendCode = true;
      this.originMobile = mobile;
      this.codeValue = "";
      setTimeout(() => {
        this.focusCode();
      }, 500);
    },
    close(){
      this.sendCode = false;
    },
    listenCode(e) {
      let code = e.target.value;
      code = code.replace(/[^\.\d]/g, "").replace(/\./g, "");
      this.codeValue = code;
      this.$refs.acceptCode.value = code;
    },
    focusCode() {
      let codeValue = this.codeValue;
      this.$refs.acceptCode.value = "";
      this.$refs.acceptCode.focus();
      this.$refs.acceptCode.value = codeValue;
      this.setFlash();
    },
    login() {
      let code = this.codeValue;
      if (code.length === 6 && Number(code) > 0) {
        this.$emit("login", code);
      } else {
        this.$message({
          message: "请输入正确的验证码！",
          type: "error"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.send-code {
  /deep/ .el-dialog__wrapper {
    .el-dialog {
      margin-top: calc(15% + 107px) !important;
      .el-dialog__body {
        background: #2d3745;
        padding-bottom: 20px;
        .step-title {
          padding-top: 32px;
          text-align: center;
          color: $defaultColor;
          @include add-size($font_size_14);
        }
        .action {
          width: 230px;
          height: 30px;
          padding: 0;
          display: block;
          margin: 40px auto 0;
          @include add-size($font_size_14);
        }
        .code-ul {
          @include flex-center;
          width: 350px;
          margin: 19px auto 0;
          color: $white;
          @include add-size($font_size_16);
          .code-li {
            width: 50px;
            height: 40px;
            line-height: 40px;
            border-bottom: 1px solid #576373;
            margin-right: 10px;
            text-align: center;
            position: relative;
            &:last-child {
              margin-right: 0;
            }
            &.flash {
              &:after {
                content: "";
                position: absolute;
                width: 2px;
                height: 24px;
                left: 50%;
                top: 50%;
                margin-top: -12px;
                background-color: $white;
                animation: flash 1s linear infinite;
              }
              @keyframes flash {
                0% {
                  opacity: 1;
                }
                49% {
                  opacity: 1;
                }
                50% {
                  opacity: 0;
                }
                99% {
                  opacity: 0;
                }
                100% {
                  opacity: 1;
                }
              }
            }
          }
        }
        .accept-code {
          // width: 10px;
          // height: 0;
          // overflow: hidden;
          width: 1px;
          height: 1px;
          opacity: 0;
          padding: 0;
          border: none;
          position: absolute;
        }
      }
    }
  }

  // 修改入场动画
  /deep/.dialog-fade-enter-active {
    animation: anim-open 0.3s;
  }
  /deep/.dialog-fade-leave-active {
    animation: anim-close 0.3s;
  }
  @keyframes anim-open {
    0% {
      opacity: 0;
      transform: translate3d(0, 20px, 0);
    }
    100% {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }
  @keyframes anim-close {
    0% {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
    100% {
      transform: translate3d(0, 20px, 0);
      opacity: 0;
    }
  }
}
</style>
