<template>
  <div class="market">
    <div class="top-bar">
      <div class="index-list" v-if="step === 0">高级行情订阅</div>
      <div class="index-list" v-if="step === 1">
        <i class="el-icon-arrow-left" @click="step = 0"></i>
        {{ curData.name }}
      </div>
      <div class="buy-record" @click="recordVisible = true">购买记录</div>
    </div>
    <!-- 行情商城列表 -->
    <div class="market-index" v-if="step === 0">
      <div class="field-title">我的行情</div>
      <div class="card-ul" v-if="boughtList.length <= 0">
        <MarketCard :type="'none'" />
      </div>
      <div class="card-ul" v-if="boughtList.length > 0">
        <MarketCard
          v-for="(card, i) in boughtList"
          :type="'bought'"
          :liData="card"
          :key="i"
          @itemClick="toDetail"
          @gotoDel="gotoDel"
        />
      </div>
      <div class="field-title">行情商城</div>
      <div class="card-ul">
        <MarketCard
          v-for="(card, i) in cardList"
          :liData="card"
          :key="i"
          @itemClick="toDetail"
          @gotoDel="gotoDel"
        />
      </div>
    </div>
    <!-- 行情详细介绍 -->
    <div class="market-detail" v-if="step === 1">
      <div class="field-title">行情介绍</div>
      <el-button class="subscription" @click="dialogVisible = true" hover
        >立即订阅</el-button
      >
      <div class="vip-table">
        <el-table :data="tableData" border style="width: 690px;">
          <el-table-column
            prop="title"
            label="行情功能"
            align="center"
          ></el-table-column>
          <el-table-column prop="level0" label="基础功能" align="center">
            <template slot-scope="scope">
              <div v-html="scope.row.level0"></div>
            </template>
          </el-table-column>
          <el-table-column prop="level2" label="高级功能" align="center">
            <template slot-scope="scope">
              <div v-html="scope.row.level2"></div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="field-title">实时行情特色功能</div>
      <div class="feature-ul">
        <div class="feature-li">
          <img
            src="~@/assets/images/personal/market/icon1.png"
            alt
            class="li-icon"
          />
          <div class="li-name">报价速度</div>
          <div class="li-tips">报价实时刷新，毫秒级报价速度</div>
        </div>
        <div class="feature-li">
          <img
            src="~@/assets/images/personal/market/icon2.png"
            alt
            class="li-icon"
          />
          <div class="li-name">买卖盘口</div>
          <div class="li-tips">{{this.marketType != 4?'十': '一'}}档深度报价与挂单量</div>
        </div>
        <div class="feature-li">
          <img
            src="~@/assets/images/personal/market/icon3.png"
            alt
            class="li-icon"
          />
          <div class="li-name">成交明细</div>
          <div class="li-tips">多空双方的每一笔交易明细</div>
        </div>
        <div class="feature-li" v-if="this.marketType != 4">
          <img
            src="~@/assets/images/personal/market/icon4.png"
            alt
            class="li-icon"
          />
          <div class="li-name">经纪队列</div>
          <div class="li-tips">监察经纪席位实时买卖挂单</div>
        </div>
      </div>
    </div>
    <!-- 购买弹框 -->
    <el-dialog
      class="subscription-dialog"
      theame
      center
      title
      :visible="dialogVisible"
      width="800px"
      top="14%"
      @close="dialogVisible = false"
    >
      <div class="dia-content">
        <div class="tips">{{ tipsContent }}</div>
        <div class="months-ul">
          <div
            class="month-li"
            :class="{ active: month.select }"
            v-for="(month, i) in monthPrice"
            :key="i"
            @click="selectMonth(month)"
          >
            <div class="num">{{ month.value }}个月</div>
            <div class="price">
              ￥
              <span>{{ month.otherPrice }}</span>
            </div>
            <!-- <div class="origin">原价56</div> -->
          </div>
        </div>
        <div class="pay-bar">
          <div class="result-price">
            应付金额：
            <span>{{ mustPay }}</span
            >元
          </div>
          <div class="ali-icon">
            <!-- <div class="pay-btn"></div>
            <div class="pay-tips">点击图标支付</div>-->
            <el-button class="subscription" @click="goPay" hover
              >立即支付</el-button
            >
            <el-button v-if="showHas" class="subscription" @click="hasPay" hover
              >支付完成</el-button
            >
          </div>
        </div>
      </div>
    </el-dialog>
    <!-- 购买记录弹框 -->
    <el-dialog
      class="record-dialog"
      theame
      center
      title
      :visible="recordVisible"
      width="512px"
      top="14%"
      max-height="500"
      @close="recordVisible = false"
    >
      <div class="dia-content">
        <el-table :data="recordData" max-height="500">
          <el-table-column
            prop="name"
            label="行情类型"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="add_time"
            label="购买日期"
            align="center"
            width="210"
          ></el-table-column>
          <el-table-column prop="num" label="时长" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.value }}个月</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import MarketCard from './sub-lit/MarketCard'
import { getCustomerAcount } from '@/utils'
const shell = require('electron').shell
export default {
  components: {
    MarketCard
  },
  data() {
    return {
      step: 0,
      dialogVisible: false,
      recordVisible: false,

      monthPrice: [],
      recordData: [],
      boughtList: [],
      cardList: [],
      curData: {},
      mustPay: 0,
      couponId: '',
      showHas: false,
      tipsContent: '',
      marketType: 1
    }
  },
  computed: {
    tableData() {
      if (this.marketType != 4) {
        return [
          {
            title: '报价速度',
            level0: '延迟15分钟',
            level2: '实时行情'
          },
          {
            title: '盘口数据',
            level0: "<i class='none'></i>无",
            level2: "<i class='done'></i>10档"
          },
          {
            title: '成交明细',
            level0: "<i class='none'></i>无",
            level2: "<i class='done'></i>有"
          },
          {
            title: '经济队列',
            level0: "<i class='none'></i>无",
            level2: "<i class='done'></i>有"
          }
        ]
      } else {
        return [
          {
            title: '报价速度',
            level0: '延迟15分钟',
            level2: '实时行情'
          },
          {
            title: '盘口数据',
            level0: "<i class='none'></i>无",
            level2: "<i class='done'></i>1档"
          },
          {
            title: '成交明细',
            level0: "<i class='none'></i>无",
            level2: "<i class='done'></i>有"
          }
        ]
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.quotationInfo()
    })
  },
  activated() {
    this.step = 0
    this.myQuotation()
    this.subscribe()
  },
  methods: {
    // 获取行情商城数据
    quotationInfo() {
      this.cardList = []
      this.axios({
        url: '/customermarket/selectMarketStore',
        params: {},
        type: 'host3'
      }).then(res => {
        let dataset = res.data.ret
        let curIndex = Math.floor(res.data.ret.length / 3)
        dataset.map((value, index) => {
          if (index % 3 == 0) {
            this.cardList.push(value)
          }
        })
      })
    },
    // 获取我的行情数据
    myQuotation() {
      this.boughtList = []
      this.axios({
        url: '/customermarket/selectMarketSubscribe',
        params: {},
        type: 'host3'
      }).then(res => {
        let dataset = res.data.ret
        sessionStorage.setItem('myQuotation', JSON.stringify(dataset))
        dataset.map((value, index) => {
          value.type = 1
          this.boughtList.push(value)
        })
        // 更新账户vip信息
        getCustomerAcount(res => {
          this.$store.dispatch('updateIdentityData', res.data)
        })
      })
    },
    //获取购买记录数据
    subscribe() {
      this.recordData = []
      this.axios({
        url: '/customermarket/selectMarketSubscribeLog',
        params: {},
        type: 'host3'
      }).then(res => {
        this.recordData = res.data.ret
      })
    },
    //获取订单档位数据
    orederData(num) {
      this.monthPrice = []
      this.axios({
        url: '/customermarket/selectMarketStoreByMarketType',
        params: {
          marketType: num
        },
        type: 'host3'
      }).then(res => {
        let dataset = res.data.ret
        dataset.map((value, index) => {
          if (index == 0) {
            value.select = true
            this.mustPay = value.otherPrice
            this.couponId = value.id
          } else {
            value.select = false
          }
          this.monthPrice.push(value)
        })
      })
    },
    //跳转支付宝页面
    goPay() {
      this.showHas = true
      let mobile = this.$store.state.UserInfo.userInfo.mobile
      shell.openExternal(
        'http://h5.lc913.com/web/newWebPayment/indexPc.html?couponId=' +
          this.couponId +
          '&mobile=' +
          mobile
      )
    },
    //支付完成
    hasPay() {
      this.showHas = false
      this.dialogVisible = false
      this.step = 0
      this.myQuotation()
      this.subscribe()
    },
    selectMonth(e) {
      this.mustPay = e.otherPrice
      this.couponId = e.id
      this.monthPrice.forEach(a => {
        a.select = false
      })
      e.select = true
    },
    toDetail(li) {
      // this.step = 1;
      window.event.stopPropagation()
      this.curData = li
      if (li.marketType == '1') {
        this.tipsContent = '应港交所要求，仅限大陆手机用户使用'
      } else if (li.marketType == '2') {
        this.tipsContent = '应港交所要求，仅限中国大陆范围内使用'
      } else if (li.marketType == '3') {
        this.tipsContent = '应港交所要求，可在全球范围内使用港股实时行情'
      } else if (li.marketType == '4') {
        this.tipsContent = '可在全球范围内使用美股实时行情'
      }
      this.orederData(li.marketType)
      this.dialogVisible = true
    },
    gotoDel(li) {
      if (li.marketType == '1') {
        this.tipsContent = '应港交所要求，仅限大陆手机用户使用'
      } else if (li.marketType == '2') {
        this.tipsContent = '应港交所要求，仅限中国大陆范围内使用'
      } else if (li.marketType == '3') {
        this.tipsContent = '应港交所要求，可在全球范围内使用港股实时行情'
      } else if (li.marketType == '4') {
        this.tipsContent = '可在全球范围内使用美股实时行情'
      }
      this.marketType = li.marketType
      this.step = 1
      this.curData = li
      this.orederData(li.marketType)
    }
  }
}
</script>

<style lang="scss" scoped>
.market {
  height: 100%;
  width: 100%;
  .top-bar {
    height: 57px;
    @include flex-center;
    border-bottom: 1px solid #323840;
    padding-left: 20px;
    .index-list {
      @include add-size($font_size_18);
      color: $activeColor;
      i {
        color: $white;
        @include add-size($font_size_20);
        cursor: pointer;
        margin-right: 5px;
      }
    }
    .buy-record {
      @include add-size($font_size_14);
      color: $activeColor;
      cursor: pointer;
      margin-right: 115px;
      margin-left: auto;
    }
  }
  .field-title {
    margin-top: 27px;
    @include add-size($font_size_16);
    padding-left: 25px;
    line-height: 22px;
    position: relative;
    &::before {
      content: '';
      width: 6px;
      height: 22px;
      position: absolute;
      left: 0;
      top: 0;
      background-color: $activeColor;
    }
  }
  .market-index {
    padding-top: 20px;
    padding-left: 20px;
    .field-title {
      margin-bottom: 38px;
    }
    .card-ul {
      @include flex-box;
      flex-wrap: wrap;
      /deep/ .market-card:nth-child(n + 4) {
        margin-top: 30px;
      }
    }
  }
  .market-detail {
    position: relative;
    padding-top: 20px;
    padding-left: 20px;
    .subscription {
      position: absolute;
      right: 116px;
      top: 27px;
      width: 180px;
      height: 40px;
      color: #fff;
      @include linear;
      border: none;
      @include add-size($font_size_18);
      @include medium;
      padding: 0;
    }
    .vip-table {
      margin: 38px auto 0;
      /deep/.el-table {
        color: $white;
        margin: 0 auto;
        &::before,
        &::after {
          background-color: #767a92;
        }
        &.el-table--border,
        &.el-table--group {
          border-color: #767a92;
        }
        &.el-table--border::after,
        &.el-table--group::after {
          background-color: #767a92;
        }
        thead {
          color: $white;
        }
        .cell {
          line-height: 39px;
          div {
            @include flex-center;
            justify-content: center;
          }
          .none {
            display: inline-block;
            width: 14px;
            height: 14px;
            background: url(~@/assets/images/personal/market/none-icon.png) 0 0
              no-repeat;
            background-size: 14px 14px;
            margin-right: 6px;
          }
          .done {
            display: inline-block;
            width: 14px;
            height: 14px;
            background: url(~@/assets/images/personal/market/done-icon.png) 0 0
              no-repeat;
            background-size: 14px 14px;
            margin-right: 6px;
          }
        }
        .el-table--border {
          border-color: #767a92;
        }
        .el-table__body-wrapper {
          tr {
            background-color: #272b31;
            &:hover > td {
              background-color: #272b31;
            }
          }
          th,
          td {
            border-color: #767a92;
          }
        }
        .el-table__header-wrapper {
          tr,
          th {
            background-color: #7b634a;
            border-color: #767a92;
          }
        }
      }
      .header-bar {
        width: 100%;
        height: 100%;
        position: relative;
        left: 0;
        top: 0;
        overflow: visible;
        @include flex-box;
        .bar-t {
          width: 50%;
          padding-top: 13px;
          line-height: 1;
        }
        .bar-b {
          line-height: 1;
          width: 50%;
          padding-top: 32px;
        }
        &:after {
          width: 112%;
          content: '';
          position: absolute;
          left: -13px;
          top: 29px;
          height: 1px;
          background-color: #767a92;
          transform: rotate(14deg);
        }
      }
    }
    .feature-ul {
      width: 1200px;
      margin: 73px auto 0;
      @include flex-center;
      justify-content: space-between;
      .feature-li {
        text-align: center;
        .li-icon {
          width: 68px;
          height: 68px;
          border-radius: 50%;
          display: block;
          margin: 0 auto;
        }
        .li-name {
          @include add-size($font_size_16);
          color: $activeColor;
          margin-top: 22px;
        }
        .li-tips {
          @include add-size($font_size_14);
          margin-top: 22px;
          color: $white;
        }
      }
    }
  }
  .subscription-dialog {
    .dia-content {
      padding-bottom: 40px;
      .tips {
        @include add-size($font_size_18);
        text-align: center;
        margin-top: 29px;
        color: #fff;
      }
      .months-ul {
        @include flex-center;
        justify-content: space-around;
        margin-top: 30px;
        .month-li {
          width: 180px;
          height: 200px;
          border: 2px solid $defaultColor;
          border-radius: 6px;
          text-align: center;
          cursor: pointer;
          position: relative;
          .num {
            @include add-size($font_size_16);
            color: $white;
            margin-top: 40px;
          }
          .price {
            color: #d88020;
            @include add-size($font_size_16);
            margin-top: 27px;
            @include number-font;
            span {
              @include add-size(38px);
              @include number-font;
            }
          }
          .origin {
            @include add-size($font_size_18);
            color: #767a92;
            margin-top: 25px;
            text-align: center;
            text-decoration: line-through;
          }
          &.active {
            border: none;
            // @include linear;
            background: linear-gradient(-90deg, #dea567, #f6cfa5);
          }
          &:nth-child(2),
          &:nth-child(3) {
            &.active {
              &::before {
                left: 0;
                top: 0;
              }
            }
            &::before {
              content: '限时';
              position: absolute;
              left: -2px;
              top: -2px;
              width: 70px;
              height: 30px;
              background-color: #40a9ff;
              line-height: 30px;
              text-align: center;
              border-top-left-radius: 6px;
              border-bottom-right-radius: 6px;
              z-index: 10;
              color: $white;
              @include add-size(22px);
            }
          }
          &:nth-child(3) {
            &::before {
              content: '特惠';
              background-color: #ff3b3b;
            }
          }
        }
      }
      .pay-bar {
        @include flex-center;
        padding-left: 44px;
        margin-top: 60px;
      }
      .result-price {
        @include add-size($font_size_20);
        color: #fff;
        width: 451px;
        span {
          @include add-size(34px);
        }
      }
      .ali-icon {
        @include flex-box;
        padding: 0 30px;
      }
      .subscription {
        width: 180px;
        height: 40px;
        color: #fff;
        @include linear;
        border: none;
        @include add-size($font_size_18);
        @include medium;
        padding: 0;
      }
      .pay-btn {
        width: 98px;
        height: 98px;
        background: url(~@/assets/images/personal/market/alipay.png) 0 0
          no-repeat;
        background-size: 98px;
        cursor: pointer;
        margin: 0 auto;
      }
      .pay-tips {
        margin-top: 27px;
        text-align: center;
        @include add-size($font_size_18);
      }
    }
  }

  .record-dialog {
    .dia-content {
      /deep/ .el-table {
        color: $white;
        &::before {
          display: none;
        }
        thead {
          color: $defaultColor;
        }
        tr,
        td,
        th {
          background-color: $blockBgColor;
          border: none;
        }
        .el-table__body-wrapper {
          background-color: $blockBgColor;
          padding: 5px 0 20px;
        }
      }
    }
  }
}
</style>
