<template>
  <div class="main" id="login-drag">
    <div class="left-img">
      <div class="drag"></div>
      <img src="~@/assets/images/login/banner.png" alt />
    </div>
    <div class="right-form">
      <div class="window-act action-box">
        <div class="back" v-show="active != 0" @click="active = 0"></div>
        <!-- <div class="drag"></div> -->
        <div class="min" @click="windowAc('min')"></div>
        <div class="close" @click="windowAc('close')"></div>
      </div>
      <div class="right-login right-content" v-show="active == 0">
        <div class="right-title login-form">登录易昇证券</div>
        <div class="login-type action-box">
          <div
            class="type-account login-item"
            :class="{ active: loginType === 0 }"
            @click="loginType = 0"
          >
            账号登录
          </div>
          <div
            class="type-message login-item"
            :class="{ active: loginType === 1 }"
            @click="loginType = 1"
          >
            验证码登录
          </div>
        </div>
        <el-form :model="loginData" :rules="formRules" ref="loginData">
          <el-form-item
            class="mobile action-box"
            prop="mobile"
            :focus="focusTarget.mobile"
          >
            <el-select
              class="area-code"
              v-model="loginData.area"
              placeholder="区号"
            >
              <el-option
                :label="op.value"
                :value="op.value"
                v-for="(op, i) in areaData"
                :key="i"
              >
                <span>{{ op.label }}</span>
              </el-option>
            </el-select>
            <el-input
              class="mobile-value"
              v-model="loginData.mobile"
              placeholder="请输入手机号码"
              maxlength="11"
              @focus="focusStyle($event, 'mobile')"
              @blur="focusStyle($event, 'mobile')"
              @keyup.enter.native="loginSubmit"
            ></el-input>
            <i class="icon"></i>
          </el-form-item>
          <el-form-item
            label="密码"
            prop="password"
            class="password action-box"
            :focus="focusTarget.password"
            v-if="loginType === 0"
          >
            <el-input
              class="password-value"
              :type="showPassword"
              v-model="loginData.password"
              maxlength="20"
              placeholder="请输入登录密码"
              @focus="focusStyle($event, 'password')"
              @blur="focusStyle($event, 'password')"
              @keyup.enter.native="loginSubmit"
            ></el-input>
            <i
              class="icon"
              :class="{ password: showPassword === 'password' }"
              @click="
                showPassword === 'password'
                  ? (showPassword = 'text')
                  : (showPassword = 'password')
              "
            ></i>
          </el-form-item>
          <el-form-item
            label="验证码"
            prop="vcode"
            class="vcode action-box"
            :focus="focusTarget.vcode"
            v-if="loginType === 1"
          >
            <el-input
              class="vcode-value"
              v-model="loginData.vcode"
              placeholder="请输入验证码"
              maxlength="6"
              @focus="focusStyle($event, 'vcode')"
              @blur="focusStyle($event, 'vcode')"
              @keyup.enter.native="loginSubmit"
            ></el-input>
            <div
              class="get-vcode"
              :class="{ 'has-send': residueTime > 0 }"
              @click="getVCode('loginData', 8)"
            >
              {{ residueTime > 0 ? residueTime + "s后重新获取" : "获取验证码" }}
            </div>
          </el-form-item>
          <el-button
            class="act-btn login-submit action-box"
            hover
            @click="loginSubmit"
            >立即登录</el-button
          >
          <div class="register-forget action-box">
            <div class="register" @click="active = 1">注册账号</div>
            <div class="forget" @click="active = 3">忘记密码?</div>
          </div>
          <div class="login-tips action-box">
            <div class="tips-t">点击登录按钮，即表示同意</div>
            <div class="tips-protocol" @click="showMzsm(0)">
              《隐私协议与免责声明》
            </div>
          </div>
        </el-form>
      </div>
      <div class="right-register right-content" v-show="active == 1">
        <div class="right-title">注册账户</div>
        <el-form :model="registerData" :rules="formRules" ref="registerData">
          <el-form-item
            prop="mobile"
            class="mobile action-box"
            :focus="focusTarget.mobile"
          >
            <el-select
              class="area-code"
              v-model="registerData.area"
              placeholder="区号"
            >
              <el-option
                :label="op.value"
                :value="op.value"
                v-for="(op, i) in areaData"
                :key="i"
              >
                <span>{{ op.label }}</span>
              </el-option>
            </el-select>
            <el-input
              class="mobile-value"
              v-model="registerData.mobile"
              placeholder="请输入手机号码"
              maxlength="11"
              @focus="focusStyle($event, 'mobile')"
              @blur="focusStyle($event, 'mobile')"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="验证码"
            prop="vcode"
            class="vcode action-box"
            :focus="focusTarget.vcode"
          >
            <el-input
              class="vcode-value "
              v-model="registerData.vcode"
              placeholder="请输入验证码"
              maxlength="6"
              @focus="focusStyle($event, 'vcode')"
              @blur="focusStyle($event, 'vcode')"
            ></el-input>
            <div
              class="get-vcode"
              :class="{ 'has-send': residueTime > 0 }"
              @click="getVCode('registerData', 1)"
            >
              {{ residueTime > 0 ? residueTime + "s后重新获取" : "获取验证码" }}
            </div>
          </el-form-item>
          <el-form-item
            label="邀请码"
            prop="invite"
            class="invite action-box"
            :focus="focusTarget.invite"
          >
            <el-input
              class="invite-value  action-box"
              v-model="registerData.invite"
              placeholder="请输入邀请码(选填)"
              @focus="focusStyle($event, 'invite')"
              @blur="focusStyle($event, 'invite')"
            ></el-input>
          </el-form-item>
          <el-button
            class="act-btn login-submit action-box"
            hover
            @click="verifyCode('registerData', 1, 2)"
            >下一步</el-button
          >
          <div class="login-tips register-tips action-box">
            <div
              class="radio"
              :class="{ 'is-agree': isAgree }"
              @click="isAgree = !isAgree"
            ></div>
            <div class="tips-t">同意</div>
            <div class="tips-protocol" @click="showMzsm(1)">
              《港美股用户阅读协议免责声明》
            </div>
          </div>
        </el-form>
      </div>
      <div
        class="right-register set-password right-content"
        v-show="active == 2"
      >
        <div class="right-title">设置密码</div>
        <el-form :model="registerData" :rules="formRules" ref="registerData2">
          <el-form-item
            label="设置密码"
            prop="password1"
            class="new1 action-box"
            :focus="focusTarget.password"
          >
            <el-input
              class="password-value action-box"
              :type="showPassword"
              v-model="registerData.password1"
              placeholder="请输入8-20位密码"
              @focus="focusStyle($event, 'new1')"
              @blur="focusStyle($event, 'new1')"
            ></el-input>
            <i
              class="icon"
              :class="{ password: showPassword === 'password' }"
              @click="
                showPassword === 'password'
                  ? (showPassword = 'text')
                  : (showPassword = 'password')
              "
            ></i>
          </el-form-item>
          <el-form-item
            label="再次确认"
            prop="password2"
            class="new2 action-box"
            :focus="focusTarget.invite"
          >
            <el-input
              class="invite-value action-box"
              :type="showPassword"
              v-model="registerData.password2"
              placeholder="请确认您的密码"
              @focus="focusStyle($event, 'new2')"
              @blur="focusStyle($event, 'new1')"
            ></el-input>
            <i
              class="icon"
              :class="{ password: showPassword === 'password' }"
              @click="
                showPassword === 'password'
                  ? (showPassword = 'text')
                  : (showPassword = 'password')
              "
            ></i>
          </el-form-item>
          <el-button
            class="act-btn login-submit register-complete action-box"
            hover
            @click="registerUser"
            >完成注册</el-button
          >
        </el-form>
      </div>
      <div class="right-back right-content" v-show="active == 3">
        <div class="right-title">找回密码</div>
        <el-form :model="forgetData" :rules="formRules" ref="forgetData">
          <el-form-item
            prop="mobile"
            class="mobile action-box"
            :focus="focusTarget.mobile"
          >
            <el-select
              class="area-code"
              v-model="forgetData.area"
              placeholder="区号"
            >
              <el-option
                :label="op.value"
                :value="op.value"
                v-for="(op, i) in areaData"
                :key="i"
              >
                <span>{{ op.label }}</span>
              </el-option>
            </el-select>
            <el-input
              class="mobile-value"
              v-model="forgetData.mobile"
              placeholder="请输入手机号码"
              @focus="focusStyle($event, 'mobile')"
              @blur="focusStyle($event, 'mobile')"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="验证码"
            prop="vcode"
            class="vcode action-box"
            :focus="focusTarget.vcode"
          >
            <el-input
              class="vcode-value"
              v-model="forgetData.vcode"
              placeholder="请输入验证码"
              maxlength="6"
              @focus="focusStyle($event, 'vcode')"
              @blur="focusStyle($event, 'vcode')"
            ></el-input>
            <div
              class="get-vcode"
              :class="{ 'has-send': residueTime > 0 }"
              @click="getVCode('forgetData', 4)"
            >
              {{ residueTime > 0 ? residueTime + "s后重新获取" : "获取验证码" }}
            </div>
          </el-form-item>
          <el-button
            class="act-btn login-submit action-box"
            hover
            @click="verifyCode('forgetData', 2, 4)"
            >下一步</el-button
          >
        </el-form>
      </div>
      <div
        class="right-register new-password right-content"
        v-show="active == 4"
      >
        <div class="right-title">重置密码</div>
        <el-form :model="forgetData" :rules="formRules" ref="forgetData2">
          <el-form-item
            label="新密码"
            prop="password"
            class="vcode action-box"
            :focus="focusTarget.password"
          >
            <el-input
              class="password-value"
              :type="showPassword"
              v-model="forgetData.password"
              placeholder="请输入8-20位密码"
              @focus="focusStyle($event, 'new')"
              @blur="focusStyle($event, 'new')"
            ></el-input>
            <i
              class="icon"
              :class="{ password: showPassword === 'password' }"
              @click="
                showPassword === 'password'
                  ? (showPassword = 'text')
                  : (showPassword = 'password')
              "
            ></i>
          </el-form-item>

          <el-button
            class="act-btn login-submit action-box"
            hover
            @click="changePassword"
            >完成</el-button
          >
        </el-form>
      </div>
    </div>
    <Mzsm ref="mzsm" class="action-box" />
  </div>
</template>
<script>
const { ipcRenderer: ipc, remote } = require("electron");
import { setToken } from "@/utils";
import Mzsm from "@/components/login/mzsm";
export default {
  components: { Mzsm },
  data() {
    let validatePass = (rule, value, callback) => {
      if (value.length < 8) {
        callback(new Error("密码长度至少8位"));
      } else if (/[\u4e00-\u9fa5]/.test(value)) {
        callback(new Error("只能是英文字符"));
      } else if (/\s/.test(value)) {
        callback(new Error("密码不能有空格"));
      } else {
        callback();
      }
    };
    return {
      areaData: [
        {
          label: "中国大陆(+86)",
          value: "+86"
        },
        {
          label: "中国香港(+852)",
          value: "+852"
        },
        {
          label: "中国澳门(+853)",
          value: "+853"
        },
        {
          label: "中国台湾(+886)",
          value: "+886"
        }
      ],
      loginData: {
        /*    mobile: "13224926616",
        password: "Aa123456", */
        mobile: "",
        password: "",
        area: "+86",
        vcode: ""
      },
      showPassword: "password",
      focusTarget: {},
      active: 0,
      isAgree: true,
      registerData: {
        mobile: "",
        area: "+86",
        vcode: "",
        password1: "",
        password2: ""
      },
      forgetData: {
        mobile: "",
        area: "+86",
        vcode: "",
        password: ""
      },
      formRules: {
        mobile: [
          { required: true, message: "请输入正确的手机号", trigger: "blur" },
          {
            pattern: /^[1][3-9]\d{9}$|^([6|9])\d{7}$|^[0][9]\d{8}$|^[6]([8|6])\d{5}$/,
            message: "请输入正确的手机号",
            trigger: "blur"
          }
        ],
        vcode: [
          {
            required: true,
            message: "请输入验证码",
            trigger: "blur"
          }
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        password1: [{ validator: validatePass, trigger: "blur" }]
      },
      timer: {},
      residueTime: 0,
      loginType: 0
    };
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.initWindows();
        this.resize();
        this.initPage();
      }, 100);

      this.makeDraggableFallback(document.getElementById("login-drag"));
    });
  },
  watch: {
    loginType() {
      this.$refs.loginData.resetFields();
      this.initPage();
    }
  },
  methods: {
    makeDraggableFallback(el) {
      // 方案一
      // el.style['-webkit-app-region'] = 'drag';

      // 方案二
      let dragging = false;
      let mouseX = 0;
      let mouseY = 0;
      el.addEventListener("mousedown", e => {
        e.stopPropagation();
        dragging = true;
        const { pageX, pageY } = e;
        mouseX = pageX;
        mouseY = pageY;
      });
      el.addEventListener("mouseup", () => {
        dragging = false;
      });
      el.addEventListener("mouseleave", () => {
        dragging = false;
      });
      el.addEventListener("mousemove", e => {
        e.stopPropagation();
        if (dragging) {
          const { pageX, pageY } = e;
          const win = remote.getCurrentWindow();
          const pos = win.getPosition();
          pos[0] = pos[0] + pageX - mouseX;
          pos[1] = pos[1] + pageY - mouseY;
          win.setPosition(pos[0], pos[1], true);
        }
      });
    },
    stop(e) {
      e.stopPropagation();
    },
    initPage() {
      let mobile = localStorage.getItem("lastMobile") || "";
      this.loginData.mobile = mobile;
      let actionBoxs = document.getElementsByClassName("action-box");
      for (let i = 0; i < actionBoxs.length; i++) {
        actionBoxs[i].addEventListener("mousedown", e => {
          e.stopPropagation();
        });
      }
    },
    changePassword() {
      // 忘记密码 重新设置
      let target = this.forgetData;
      let flag = false;
      this.$refs["forgetData2"].validate(valid => {
        flag = valid;
      });
      if (!flag) {
        return false;
      }
      this.axios({
        url: "/app/forgetPassword.do",
        method: "post",
        params: {
          mobile: target.mobile,
          password: target.password
        }
      }).then(res => {
        this.$message({ type: "success", message: "修改成功！" });
        setTimeout(() => {
          this.active = 0;
        }, 1000);
      });
    },
    registerUser() {
      // 注册
      let target = this.registerData;
      let flag = false;
      this.$refs["registerData2"].validate(valid => {
        flag = valid;
      });
      if (!flag) {
        return false;
      }
      if (target.password1 === target.password2) {
        this.axios({
          url: "/app/web/register.do",
          method: "post",
          params: {
            mobile: target.mobile,
            invitationCode: target.invite,
            password: target.password1
          }
        }).then(res => {
          console.log(res);
          let token = res.headers.jwttoken;
          let customerId = res.data.body.customerId;
          localStorage.setItem("customerId", customerId);
          setToken(token);
          this.loginWindow();
        });
      } else {
        this.$message({
          showClose: true,
          message: "两次密码不一致，请重新输入",
          type: "error"
        });
      }
    },
    verifyCode(t, type, step) {
      // 验证手机号
      let flag = false;
      this.$refs[t].validate(valid => {
        flag = valid;
      });
      // type 为1时需要同意免责声明
      if (type == 1 && !this.isAgree) {
        this.$message({
          showClose: true,
          message: "请同意隐私协议与免责声明",
          type: "error"
        });
      }
      if (flag) {
        let target = this[t];
        this.axios({
          url: "/messageCode/checkModifyMobileCode.do",
          method: "post",
          params: {
            mobile: target.mobile,
            type: type,
            smsCode: target.vcode
          }
        })
          .then(res => {
            this.active = step;
          })
          .catch(err => {});
      }
    },
    getVCode(t, type) {
      // 发送手机验证码
      if (this.residueTime > 0) {
        return false;
      }
      let flag = false;
      this.$refs[t].validateField("mobile", valid => {
        flag = !valid;
      });
      if (flag) {
        let target = this[t];
        let mobile = target.mobile;
        this.axios({
          url: "/messageCode/getCode.do",
          params: {
            mobile: mobile,
            type: type
          }
        })
          .then(res => {
            this.$message({
              message: "发送成功！",
              type: "success"
            });
            this.startTimer();
          })
          .catch(err => {});
      }
    },
    startTimer() {
      // 发送验证码后倒计时
      this.residueTime = 60;
      this.timer = setInterval(() => {
        this.residueTime--;
        if (this.residueTime < 0) {
          this.residueTime = 0;
          clearInterval(this.timer);
        }
      }, 1000);
    },
    loginSubmit() {
      // 用户登录
      let target = this.loginData;
      // POST /app/loginBySmsCode.do     用户验证码登陆
      this.$refs["loginData"].validate(valid => {
        if (valid) {
          localStorage.setItem("lastMobile", target.mobile);
          let params = {};
          let url = "/app/login.do";
          if (this.loginType === 0) {
            params = {
              mobile: target.mobile,
              password: target.password
            };
          } else {
            url = "/app/loginBySmsCode.do";
            params = {
              mobile: target.mobile,
              smsCode: target.vcode
            };
          }
          this.axios({
            url: url,
            method: "post",
            params: params
          }).then(res => {
            let token = res.headers.jwttoken;
            let customerId = res.data.data.customerId;
            localStorage.setItem("customerId", customerId);
            setToken(token);
            this.$message({
              message: "登录成功！",
              type: "success"
            });
            this.isOnline(1);
            setTimeout(() => {
              this.loginWindow();
            }, 1000);
          });
        }
      });
    },
    isOnline(online) {
      this.axios({
        url: "/app/updateOnline.do",
        params: {
          isOnline: online
        }
      });
    },
    initWindows() {
      // 未登录或登录信息过期后跳转到登录界面  指定窗口大小
      remote.getCurrentWindow().setSize(700, 500);
      remote.getCurrentWindow().center();
      let body = document.getElementsByTagName("body")[0];
      body.setAttribute("class", "login");
      // 删除交易账户登录状态
      // this.$store.dispatch("updateAccountData", {});
    },
    resize() {
      let _this = this;
      document.addEventListener("visibilitychange", function() {
        if (!document.hidden && _this.$route.name === "login") {
          remote.getCurrentWindow().setSize(700, 500);
          remote.getCurrentWindow().center();
          console.log("login resize");
        }
      });
    },
    windowAc(ac) {
      ipc.send(ac);
    },
    loginWindow() {
      // 用户登录成功后打开首页且最大化
      let body = document.getElementsByTagName("body")[0];
      body.setAttribute("class", "");
      this.$router.push("/");
      this.windowAc("max");
    },
    focusStyle(e, target) {
      // 聚焦后底部边框变色
      if (e.type === "focus") {
        this.$set(this.focusTarget, target, true);
      } else {
        this.$set(this.focusTarget, target, false);
      }
    },
    showMzsm(type) {
      this.$refs.mzsm.show(type);
    }
  }
};
</script>

<style lang="scss" scoped>
$tColor: #2b2b2b;
$nColor: #767a92;
.main {
  height: 100%;
  @include flex-box;
  background-color: $white;
  .left-img {
    position: relative;
    width: 50%;
    height: 100%;
    .drag {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 11;
    }
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
    // &:after {
    //   position: absolute;
    //   content: "";
    //   width: 100%;
    //   height: 100%;
    //   left: 0;
    //   top: 0;
    //   z-index: 10;
    // }
  }
  .right-form {
    width: 50%;
    position: relative;
    padding-top: 32px;
    .window-act {
      @include flex-box;
      // -webkit-app-region: no-drag;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 100;
      div {
        width: 32px;
        height: 64px;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
        &.back {
          background-image: url("~@/assets/images/login/back.png");
          background-size: 8px 14px;
          background-position: center;
        }
        &.drag {
          width: 250px;
        }
        &.min {
          background-image: url("~@/assets/images/login/min-icon.png");
          background-size: 14px 2px;
          background-position: center 21px;
          margin-left: auto;
          &:hover {
            background-image: url("~@/assets/images/login/min-icon-b.png");
            background-size: 32px 32px;
            background-position: center 0;
          }
        }
        &.close {
          background-image: url("~@/assets/images/login/close-icon.png");
          background-size: 14px 14px;
            background-position: center 8px;
          &:hover {
            background-image: url("~@/assets/images/login/close-icon-b.png");
            background-size: 32px 32px;
            background-position: 0 0;
          }
        }
      }
    }
    .right-content {
      padding-left: 40px;
      padding-right: 40px;
      width: 350px;
      height: calc(100% - 32px);
      position: relative;
      .right-title {
        @include add-size($font_size_22);
        padding-top: 38px;
        color: $tColor;
        margin-bottom: 60px;
        letter-spacing: 2px;
        &.login-form {
          margin-bottom: 20px;
        }
      }
      .login-type {
        height: 30px;
        margin-bottom: 10px;
        @include flex-center;
        @include add-size($font_size_16);
        .login-item {
          cursor: pointer;
          padding-right: 10px;
          &.type-account {
            margin-right: 10px;
            border-right: 2px solid $borderColor;
          }
          &.active {
            color: $activeColor;
            @include medium;
          }
        }
      }
      /deep/ .el-form-item {
        border-bottom: 1px solid #e9e9e9;
        &[focus="true"] {
          border-bottom: 1px solid $activeColor;
        }
        @include flex-box;
        &.vcode {
          .el-form-item__error {
            left: -53px;
          }
        }
        &.password {
          .el-form-item__error {
            left: -39px;
          }
        }
      }
      /deep/ .el-input__inner {
        color: $tColor;
        font-family: "sourceFont";
        border: none;
        background-color: inherit;
        padding: 0;
        border-radius: 6px;
      }
      /deep/ .el-form-item__label {
        color: $tColor;
        &::before {
          display: none;
        }
      }

      .mobile {
        .area-code {
          width: 65px;
          /deep/ .el-input__suffix {
            right: 0;
          }
        }
        .mobile-value {
          /deep/ .el-input__inner {
            padding-left: 0;
          }
        }
        /deep/ .el-form-item__content {
          @include flex-box;
          .el-input__icon {
            color: $nColor;
            &:before {
              content: "\e78f";
            }
          }
        }
        .icon {
          position: absolute;
          width: 18px;
          height: 16px;
          background: url("~@/assets/images/login/user-icon.png") 0 0 no-repeat;
          background-size: 18px 16px;
          right: 2px;
          top: 50%;
          margin-top: -9px;
        }
      }
      .password {
        .password-value {
          width: 200px;
          padding-left: 14px;
        }
      }
      .icon {
        position: absolute;
        width: 18px;
        height: 11px;
        background: url("~@/assets/images/login/show-icon.png") 0 center
          no-repeat;
        background-size: 18px 11px;
        right: -10px;
        top: 50%;
        margin-top: -4px;
        cursor: pointer;
        &.password {
          background: url("~@/assets/images/login/hide-icon.png") 0 center
            no-repeat;
          background-size: 18px 8px;
          right: -8px;
        }
      }
      .act-btn {
        @include linear;
        border: none;
        width: 100%;
        height: 36px;
        color: $white;
        font-family: "sourceFont";
        @include add-size($font_size_16);
        letter-spacing: 4px;
        margin-top: 9px;
      }
      .register-forget {
        @include flex-box;
        justify-content: space-between;
        color: #40a9ff;
        @include add-size($font_size_14);
        margin-top: 11px;
        div {
          cursor: pointer;
        }
      }
      .login-tips {
        @include add-size($font_size_12);
        @include flex-box;
        position: absolute;
        width: 280px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 30px;
        .tips-t {
          color: $nColor;
        }
        .tips-protocol {
          color: $activeColor;
          cursor: pointer;
        }
        &.register-tips {
          width: 225px;
          .radio {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            border: 1px solid $nColor;
            margin-right: 7px;
            cursor: pointer;
            position: relative;
            &.is-agree {
              &:after {
                content: "";
                position: absolute;
                left: 50%;
                top: 50%;
                margin-left: -3px;
                margin-top: -3px;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: $activeColor;
              }
            }
          }
        }
      }
      .get-vcode {
        position: absolute;
        color: $activeColor;
        right: -24px;
        top: 0;
        cursor: pointer;
        &.has-send {
          color: $nColor;
          cursor: default;
        }
      }
      &.set-password {
        .register-complete {
          margin-top: 67px;
        }
      }
      &.new-password {
        .new {
          margin-top: 120px;
        }
      }
    }
  }
}
</style>
