<template>
  <div class="bank-card">
    <div class="no-card" v-show="status === 0">
      <img src="~@/assets/images/personal/no-card.png" alt class="no-card-icon" />
      <div class="no-card-t1">您还没添加任何银行卡</div>
      <div class="no-card-t2">需要点击下方按钮开始添加</div>
      <el-button class="action" hover @click="addCard">添加银行卡</el-button>
    </div>
    <div class="add-card" v-show="status === 1">
      <div class="form-title">添加银行卡</div>
      <div class="back" @click="status = bankData.length > 0 ? 2 : 0">取消</div>
      <el-form ref="addForm" :rules="addRule" :model="bindData">
        <el-form-item label="持卡人姓名" prop="name">
          <el-input v-model="bindData.name" placeholder="请输入持卡人姓名"></el-input>
        </el-form-item>
        <el-form-item label="银行卡种类">
          <el-input :value="'储蓄卡'" placeholder="请输入银行卡种类" readonly></el-input>
        </el-form-item>
        <el-form-item label="身份证号码" prop="idCardCode">
          <el-input v-model="bindData.idCardCode" placeholder="请输入身份证号码"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="bindData.bankPreMobile" placeholder="请输入银行预留手机号" readonly></el-input>
        </el-form-item>
        <el-form-item label="银行名称" prop="bankId">
          <el-select v-model="bindData.bankId" placeholder="请选择">
            <el-option
              v-for="item in bankList"
              :key="item.bankId"
              :label="item.bankName"
              :value="item.bankId"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="银行卡号" prop="accountNo">
          <el-input v-model="bindData.accountNo" placeholder="请输入银行卡号"></el-input>
        </el-form-item>

        <el-form-item label="开户支行" prop="bankAddress">
          <el-input v-model="bindData.bankAddress" placeholder="请输入开户支行"></el-input>
        </el-form-item>
        <el-form-item label="是否为内地卡">
          <el-select v-model="bindData.isHKCard" placeholder="请选择">
            <el-option
              v-for="item in cardLocation"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button class="action" hover @click="bindCard">{{bindData.isHKCard?'下一步':'确认提交'}}</el-button>
      </el-form>
    </div>
    <div class="card-list" v-show="status === 2">
      <el-button class="action add-card-btn" hover @click="addCard">添加银行卡</el-button>
      <div class="card-ul">
        <div
          class="card-li"
          v-for="(bank, i) in bankData"
          :key="i"
          :data-bg="bank.bankUrl"
          v-bind:style="{backgroundImage: 'url(' + bank.bankUrl + ')'}"
        >
          <i class="bank-icon" v-bind:style="{backgroundImage: 'url(' + bank.bankPicUrl + ')'}"></i>
          <div class="bank-name">{{bank.bankName}}</div>
          <div class="user-name">持卡人 {{bindData.name}}</div>
          <div class="bank-number">{{bank.accountNo | bankCardFilter}}</div>
          <i class="delete-icon" @click="deleteCard(bank)"></i>
        </div>
      </div>
    </div>
    <div class="card-img" v-show="status === 3">
      <div class="step-title">请上传银行卡正反面照片：</div>
      <div class="card-face">
        <label for="front-img" class="front">
          <img :src="bindData.cardImgZ || images.img_front" class="face-img" alt />
          <div class="card-text">请上传银行卡正面照</div>
          <imgUpload id="front-img" @change="uploadimg($event, 'cardImgZ')" />
        </label>
        <label for="back-img" class="back">
          <img :src="bindData.cardImgF || images.img_back" class="face-img" alt />
          <div class="card-text">请上传银行卡反面照</div>
          <imgUpload id="back-img" @change="uploadimg($event, 'cardImgF')" />
        </label>
      </div>
      <div class="tips">
        <div class="tips-title">拍照要求：</div>
        <div class="tips-text">1、银行卡为本人持有的银行卡；2、请确保银行卡边框完整,字体清晰,银行卡号码清晰可见。</div>
        <div class="img-demo">
          <div class="img-box" v-for="img in demoImg" :key="img.src">
            <img :src="img.src" alt />
            <div class="label">{{img.label}}</div>
          </div>
        </div>
      </div>
      <div class="action-btn">
        <el-button class="action prev" hover @click="status = 1">上一步</el-button>
        <el-button class="action" hover @click="submitWithImg">确认提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import imgUpload from "@/components/common/img-upload";
import img_front from "@/assets/images/personal/bank/front.png";
import img_back from "@/assets/images/personal/bank/back.png";
export default {
  components: { imgUpload },
  data() {
    return {
      images: {
        img_front,
        img_back
      },
      status: -1,
      options: [],
      addForm: {},
      bankId: "",
      bankData: [],
      bankList: [],
      value: "",
      cardType: [
        {
          label: "储蓄卡",
          value: 0
        },
        {
          label: "信用卡",
          value: 1
        }
      ],
      cardLocation: [
        {
          label: "是",
          value: 0
        },
        {
          label: "否",
          value: 1
        }
      ],
      bindData: {},
      demoImg: [
        {
          src: require("@/assets/images/personal/bank/right.png"),
          label: "正确示例"
        },
        {
          src: require("@/assets/images/personal/bank/wrong1.png"),
          label: "边角缺失"
        },
        {
          src: require("@/assets/images/personal/bank/wrong2.png"),
          label: "信息模糊"
        },
        {
          src: require("@/assets/images/personal/bank/wrong3.png"),
          label: "光线太暗"
        }
      ],
      addRule: {
        name: [
          {
            required: true,
            message: "请输入持卡人姓名",
            trigger: "change"
          }
        ],
        bankId: [
          {
            required: true,
            message: "请选择银行",
            trigger: "change"
          }
        ],
        accountNo: [
          {
            required: true,
            message: "请输入银卡卡号",
            trigger: "blur"
          }
        ],
        bankAddress: [
          {
            required: true,
            message: "请输入开户支行",
            trigger: "blur"
          }
        ],
        idCardCode: [
            {
            required: true,
            message: "请输入请输入身份证号码",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getBankList();
    });
  },
  activated() {
    this.getBankCardList();
    this.setBindData();
  },
  computed: {},
  methods: {
    uploadimg(e, target) {
      const fd = new FormData();
      fd.append("files", e.file);
      fd.append("type", 4);
      this.loadingInstance = this.$loading({
        text: "图片上传中...",
        fullscreen: true,
        background: "rgba(0,0,0,0.5)"
      });
      this.axios({
        url: "/appcustomer/picUpload.do",
        method: "post",
        headers: {
          "Content-Type": "multipart/form-data"
        },
        withCredentials: true,
        params: fd
      })
        .then(res => {
          this.bindData[target] = res.data.body;
        })
        .finally(() => {
          this.$loading().close();
        });
    },
    bindCard() {
      let bindData = this.bindData;
     
      let bankList = this.bankList;
      bankList.filter(a => {
        if (a.bankId == bindData.bankId) {
          bindData.bankName = a.bankName;
        }
      });
      let flag = false;
      this.$refs.addForm.validate(valid => {
        flag = valid;
      });
      if (!flag) {
        return false;
      }
      if (bindData.isHKCard == 0) {
        // POST /order/message/saveBankCarBind
        this.bindCardRequset(bindData);
      } else {
        // 非内地银行卡需提交图片
        this.status = 3;
      }
    },
    submitWithImg() {
      let bindData = this.bindData;
      if (bindData.cardImgZ && bindData.cardImgF) {
        this.bindCardRequset();
      } else {
        this.$message({
          message: "请先上传银行卡图片！",
          type: "warning"
        });
      }
    },
    bindCardRequset() {
      let bindData = this.bindData;
      this.$loading({
        text: "loading...",
        fullscreen: true,
        background: "transparent"
      });
      this.axios({
        url: "/order/message/saveBankCarBind",
        method: "post",
        params: bindData,
        type: "host2"
      })
        .then(res => {
          this.$message({
            message: "添加成功！",
            type: "success"
          });
          this.getBankCardList();
          this.status = 2;
        })
        .finally(() => {
          this.$loading().close();
        });
    },
    setBindData() {
      // console.log(this.$store.state.AccountLogin);
      let identityData = this.$store.state.system.accountInfo;
      let bindData = {
        name: identityData.name,
        accountNo: "",
        cardNo: 0,
        bankId: "",
        bankName: "",
        bankPreMobile: identityData.mobile,
        idCardCode: identityData.idCardCode,
        bankAddress: "",
        isHKCard: 0,
        cardImgZ: "",
        cardImgF: ""
      };
      this.bindData = bindData;
    },
    addCard() {
      let identityData = this.$store.state.system.accountInfo;
      if (identityData.ebokerUsername) {
        this.status = 1;
        return false;
      }
      this.$message({
        message: "未开户无法添加银行卡！",
        type: "error"
      });
    },
    deleteCard(card) {
      this.$confirm("确定解绑该银行卡？", "提示", {
        cancelButtonText: "取消"
      })
        .then(res => {
          // POST /order/message/updateBankCarCBind
          this.axios({
            url: "/order/message/updateBankCarCBind",
            method: "post",
            params: {
              accountNo: card.accountNo
            },
            type: "host2"
          }).then(res => {
            this.$message({
              message: "删除成功！",
              type: "success"
            });
            this.getBankCardList();
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    getBankCardList() {
      // GET /order/message/selectBankBind
      this.axios({
        url: "/order/message/selectBankBind",
        method: "get",
        params: {
          type: 1
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.length <= 0) {
          this.status = 0;
        } else {
          this.status = 2;
          this.bankData = res.data.ret;
        }
      });
    },
    getBankList() {
      this.axios({
        url: "/order/message/selectBankList",
        method: "get",
        params: {},
        type: "host2"
      }).then(res => {
        let data = res.data.ret;
        this.bankList = data;
      });
    }
  }
};
</script> 

<style lang="scss" scoped>
