      color: $white;
      @include add-size($font_size_18);
      margin-top: 43px;
    }
    .no-card-t2 {
      @include add-size($font_size_14);
      margin-top: 17px;
    }
  }
  .add-card {
    position: relative;
    padding-top: 57px;
    .form-title {
      @include add-size($font_size_18);
      color: $white;
      text-align: center;
    }
    .back {
      position: absolute;
      @include add-size($font_size_14);
      @include medium;
      right: 115px;
      top: 60px;
      cursor: pointer;
    }
    /deep/ .el-form {
      width: 1009px;
      margin: 50px auto 0;
      @include flex-box;
      flex-wrap: wrap;
      .el-form-item {
        width: 416px;
        &:nth-child(odd) {
          margin-right: 177px;
        }
      }
      .el-form-item__label {
        color: $white;
        @include add-size($font_size_14);
