        .el-input__icon {
          line-height: 30px;
          &::before {
            content: "\e78f";
          }
        }
      }
      .action {
        margin: 80px auto 0;
      }
    }
  }
  .card-list {
    position: relative;
    padding-top: 137px;

    .add-card-btn {
      position: absolute;
      top: 50px;
      right: 120px;
      margin: 0;
    }
    .card-ul {
      @include flex-box;
      flex-wrap: wrap;
      width: 1300px;
      margin: 0 auto;
      max-height: 721px;
      overflow-y: auto;
      .card-li {
