        @include add-size($font_size_14);
        white-space: nowrap;
      }
      .img-demo {
        @include flex-center;
        margin-top: 75px;
        .img-box {
          width: 103px;
          margin-right: 44px;
          text-align: center;
          &:last-child {
            margin-right: 0;
          }
          .label {
            margin-top: 10px;
          }
        }
      }
    }
    .action-btn {
      @include flex-center;
      justify-content: center;
      margin-top: 80px;
      .prev {
