<template>
  <div class="record">
    <el-dialog theame center title="认购记录" :visible="isShow" width="900px" top="12%" @close="close">
      <div class="record-content">
        <el-table :data="listData.record.list" empty-text=" " style="width: 100%" :height="500">
          <el-table-column prop="stockCode" label="股票名称" align="center">
            <template slot-scope="scope">
              <div class="cell">
                <span class="TipBg">HK</span>
                {{scope.row.stockCode}}
              </div>
              <div class="cell stock-name" style="margin-top: 4px;" :title="scope.row.stockName">{{scope.row.stockName}}</div>

            </template>
          </el-table-column>
          <!-- <el-table-column prop="stockName" label="股票名称" align="center">
            <template slot-scope="scope">
            </template>
          </el-table-column> -->
          <el-table-column prop="orderQty" label="认购数量/认购价格" align="center">
            <!-- <template slot="header" slot-scope="scope">

            </template>-->
            <template slot-scope="scope">
              <div class="cell">{{scope.row.orderQty}}</div>
              <div class="cell" style="margin-top: 4px;">{{scope.row.orderPrice}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="filledQty" label="中签数量/中签价" align="center">
            <template slot-scope="scope">
              <div class="cell">{{scope.row.filledQty}}</div>
              <div class="cell" style="margin-top: 4px;">{{scope.row.filledPrice}}</div>
            </template>
          </el-table-column>
          <!-- <el-table-column prop="orderPrice" label="认购价" align="center">
            <template slot-scope="scope">{{scope.row.orderPrice}} 港币</template>
          </el-table-column>-->
          <!-- <el-table-column prop="commissionAll" label="手续费" align="center">
            <template slot-scope="scope">{{scope.row.commissionAll}} 港币</template>
          </el-table-column>-->
          <!-- <el-table-column prop="money" label="认购总费用" align="center">
            <template slot-scope="scope">{{scope.row.money}} 港币</template>
          </el-table-column> -->
          <el-table-column prop="type" label="状态" align="center">
            <template slot-scope="scope">{{subscriptionStatus[scope.row.type]}}</template>
          </el-table-column>
          <el-table-column prop="finishTime" label="认购时间" align="center">
            <template slot-scope="scope">
              <div class="time-column">{{scope.row.finishTime}}</div>
            </template>
          </el-table-column>

          <template slot="append">
            <infinite-loading
              ref="infinite"
              @infinite="getRecordData"
              :distance="20"
              :identifier="listData.record.infiniteId"
              forceUseInfiniteWrapper=".el-table__body-wrapper"
            >
              <loading></loading>
              <span slot="no-more" class="no-more">—————— 暂无更多 ———————</span>
              <span slot="no-results" class="no-results">暂无数据</span>
            </infinite-loading>
          </template>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import infiniteLoading from "vue-infinite-loading";
export default {
  components: {
    infiniteLoading
  },
  data() {
    return {
      isShow: false,
      listData: {
        record: {
          page: 1,
          size: 10,
          total: 0,
          list: [],
          infiniteId: 0
        }
      },
      subscriptionStatus: {
        0: "待处理",
        1: "中签",
        2: "未中签",
        3: "已取消"
      }
    };
  },
  computed: {},
  watch: {},
  methods: {
    show() {
      this.isShow = true;
      this.refresh("record");
    },
    close() {
      this.isShow = false;
    },
    refresh(target) {
      this.listData[target].infiniteId++;
      this.listData[target].page = 1;
      this.listData[target].list = [];
    },
    getRecordData($state) {
      // GET /eipo/selectRecordList
      let data = this.listData.record;
      this.axios({
        url: "/eipo/selectRecordList",
        params: {
          page: data.page,
          size: data.size,
          applyWay: 0,
          // version: 1
        },
        type: "host3"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (data.page == 1) {
            data.list = res.data.ret.list;
          } else {
            data.list = data.list.concat(res.data.ret.list);
          }
          data.page++;
          data.total = res.data.ret.total;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang='scss' scoped>
.record-content {
  padding-top: 20px;
  padding-bottom: 20px;
  // /deep/ .el-table__append-wrapper{
  //   min-height: 250px;
  //   position: relative;
  // }
  // /deep/ .infinite-status-prompt{
