<template>
  <div class="stock-table">
    <el-table
      :data="stockTable"
      v-loading="loading"
      element-loading-text="loading"
      element-loading-background="rgba(0, 0, 0, 0)"
      :height="height || 190"
      :empty-text="emptyText"
      @row-contextmenu="clickHandler"
    >
      <el-table-column prop="StockCode" label="股票代码" align="center"></el-table-column>
      <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
      <el-table-column prop="OrderSide" label="方向" align="center">
        <template slot-scope="scope">
          <div
            :class="{fall: scope.row.OrderSide=='Buy',rise: scope.row.OrderSide=='Sell'}"
          >{{scope.row.OrderSide=='Buy'?'买入':'卖出'}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="AveragePrice" label="成交价" align="center">
        <template slot-scope="scope">
          <div>{{Number(scope.row.OrderPrice).toFixed(3)}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="FilledQty" label="成交量" align="center"></el-table-column>
      <el-table-column  label="成交时间" align="center" width="120">
         <template slot-scope="scope">
          <div>{{scope.row.DateTimes?scope.row.DateTimes.slice(10): scope.row.DateTime.slice(10)}}</div>
        </template>
      </el-table-column>
      <slot name="column"></slot>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    stockTable: Array,
    height: Number,
    type: String,
    loading: Boolean,
    emptyText: String
  },
  data() {
    return {};
  },
  methods: {
    clickHandler(row, column, event) {
      this.$emit("rowClick", row, column, event, this.type);
    }
  }
};
</script>

<style lang="scss" scoped>
.stock-table {
  /deep/ .el-table {
    width: 800px;
    margin-top: 12px;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $red;
      }
      .rise {
        color: $green;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
  }
}
</style>


