<template>
  <div class="statement-detail">
    <el-dialog
      class="detail-dialog"
      theame
      center
      title
      :visible="isShow"
      width="456px"
      :modal="false"
      top="15%"
      @close="close"
    >
      <div class="header" slot="title">
        <div class="warning" @click="showTips = true">
          <i class="el-icon-warning-outline"></i>温馨提示
        </div>
        <div class="header-text">结单详情</div>
      </div>
      <div class="detail-content" v-if="settleData.customerSettleAccountInfoVo">
        <div class="detail-title">
          <div
            class="title-l"
            v-if="settleData.lstCustomerAssetsSettleDay"
          >{{settleData.lstCustomerAssetsSettleDay[0].settleDate}} 日结单</div>
          <div
            class="title-l"
            v-if="settleData.lstCustomerSettleMonth"
          >{{settleData.lstCustomerSettleMonth[0].settleDate}} 月结单</div>
          <div class="title-r">币种：港币</div>
        </div>
        <div class="user-info">
          <div
            class="account-name"
          >{{settleData.customerSettleAccountInfoVo.customerName}}({{settleData.customerSettleAccountInfoVo.accountId}})</div>
          <div class="name-address">{{settleData.customerSettleAccountInfoVo.contractAddress}}</div>
        </div>
        <el-tabs v-model="activeName" :stretch="true">
          <el-tab-pane label="资产概况" name="first">
            <ul class="position-detail">
              <li class="position-li">
                <div class="position-li_name">初期资产净值</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].beginAssets | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].beginAssets | toFixed}}</div>
              </li>
              <li class="position-li">
                <div class="position-li_name">期末资产净值</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].endAssets | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].endAssets | toFixed}}</div>
              </li>
              <li class="position-li">
                <div class="position-li_name">持股总市值</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].marketValue | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].marketValue | toFixed}}</div>
              </li>
              <li class="position-li">
                <div class="position-li_name">可用资金</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].availableFunds | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].availableFunds | toFixed}}</div>
              </li>
              <li class="position-li">
                <div class="position-li_name">冻结资金</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].frozenFunds | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].frozenFunds | toFixed}}</div>
              </li>
              <li class="position-li">
                <div class="position-li_name">可取资金</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerAssetsSettleDay"
                >{{settleData.lstCustomerAssetsSettleDay[0].drawableFunds | toFixed}}</div>
                <div
                  class="position-li_value"
                  v-if="settleData.lstCustomerSettleMonth"
                >{{settleData.lstCustomerSettleMonth[0].drawableFunds | toFixed}}</div>
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="资金存取" name="second">
            <ul class="draw-ul">
              <li class="draw-li li-title">
                <div class="draw-li-column">类型</div>
                <div class="draw-li-column" align="right">币种</div>
                <div class="draw-li-column" align="right">金额</div>
                <div class="draw-li-column" align="right">时间</div>
              </li>
              <div class="scroll-area">
                <li
                  class="draw-li"
                  v-for="(fund, i) in settleData.lstCustomerSettleFoudDepositVo"
                  :key="i"
                >
                  <div class="draw-li-column">{{fund.recordType}}</div>
                  <div class="draw-li-column" align="right">{{fund.currency}}</div>
                  <div class="draw-li-column" align="right">{{Number(fund.depositMoney).toFixed(2)}}</div>
                  <div class="draw-li-column" align="right">{{fund.depositTime}}</div>
                </li>
                <div
                  class="nodata"
                  v-if="settleData.lstCustomerSettleFoudDepositVo.length <= 0"
                >暂无数据</div>
              </div>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="股票详情" name="third">
            <div class="table-block">
              <el-table :data="settleData.lstCustomerSettleHoldVo" height="220">
                <el-table-column prop="date" label="股票名称" width="120">
                  <template slot-scope="scope">
                    <div class="stock-name">{{scope.row.stockName}}</div>
                    <div class="stock-code">{{scope.row.stockCode}}</div>
                  </template>
                </el-table-column>
                <el-table-column prop="filledQty" label="持仓数量" align="right"></el-table-column>
                <el-table-column prop="lastPrice" label="昨收价" align="right">
                  <template slot-scope="scope">
                    <div>{{Number(scope.row.lastPrice).toFixed(3)}}</div>
                  </template>
                </el-table-column>
                <el-table-column prop="marketValue" label="市值" align="right"></el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
    <el-dialog
      class="detail-dialog"
      theame
      center
      title
      :visible="showTips"
      width="456px"
      :modal="false"
      top="15%"
      @close="showTips = false"
    >
      <div class="header" slot="title">
        <div class="warning" @click="showTips = false">
          <i class="el-icon-arrow-left"></i>
        </div>
        <div class="header-text">温馨提示</div>
      </div>
      <div class="warn-content">
        <p>1. 此結單為電腦編印，無需任可簽署。如七日內未有異議，此單內容將視為正確。</p>
        <p>2. 除非另有註明，所有交易由本公司以經紀名義透過香港交易及結算所進行。</p>
        <p>3.證券價格有時可能會非常波動。證券價格可升可跌，甚至變成毫無價值。買賣證券未必一定能夠賺取利潤，反而可能會致損失。</p>
        <p>4. 上列印花稅將經香港聯合交易所有限公司繳付。</p>
        <p>5.上列交易徵費乃代表香港聯合交易所所收將依例回繳香港聯合交易所。</p>
        <p>6. 假如參與者或證券合夥商號因為經營倒閉而令顧客遭受金錢上之損失時，賠償基金之責任只限於證券及期貨條例中所規定之有效索償。</p>
        <p>7. 經由參與者或證券合夥商號達成之每宗交易均須按交易所不時之規定繳納任何繳費。交易所已授權參與者或證券合夥商號根據交易所不時 之規定收取適當徵費。有關之交易所徵費規則可向交易所購閱。</p>
        <p>8. 所有協助或代表客戶之買賣均為不時修訂之憲法、交易所規則、規例、細則、交易所之習慣及用法，中央結算及交收系統規則及香港法律 之有關規定所管制，參與者或證券合夥商號及顧客同樣受制於上述規定。有關之交易所規則可向交易所購閱。</p>
        <p>9. 此結單亦作收據用途，並載有第571Q章證券及期貨(成交單據、戶口結單及收據)規則指明的資料。</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { hostEbroker } from "@/utils/config";

export default {
  data() {
    return {
      isShow: false,
      activeName: "first",
      fundDrawData: [
        {
          type: "资金存入",
          coin: "港币",
          price: "4259.23",
          date: "08-21 14:32"
        },
        {
          type: "资金存入",
          coin: "港币",
          price: "4259.23",
          date: "08-21 14:32"
        },
        {
          type: "资金存入",
          coin: "港币",
          price: "4259.23",
          date: "08-21 14:32"
        }
      ],
      stockData: [
        {
          StockName: "平安好医生八个字",
          StockCode: "01833",
          position: 1000,
          yes: 13.56,
          cur: 45645.21
        },
        {
          StockName: "平安好医生八个字",
          StockCode: "01833",
          position: 1000,
          yes: 13.56,
          cur: 45645.21
        },
        {
          StockName: "平安好医生八个字",
          StockCode: "01833",
          position: 1000,
          yes: 13.56,
          cur: 45645.21
        }
      ],
      requsetData: {
        month: {
          url: "/settle/getAssetsMonthSettleDetail",
          settleYM: "",
          deposityYM: "",
          market: "hk"
        },
        day: {
          url: "/settle/getAssetsDaySettleDetail",
          settleYMD: "",
          deposityYMD: "",
          market: "hk"
        }
      },
      liData: {},
      flag: "",
      settleData: {},
      showTips: false
    };
  },
  methods: {
    getDetailData() {
      let flag = this.flag;
      let data = this.requsetData[flag];
      let url = data.url;
      if (flag === "day") {
        data.settleYMD = this.liData.settleDate;
        data.deposityYMD = this.liData.settleDate;
      } else {
        data.settleYM = this.liData.settleDate;
        data.deposityYM = this.liData.settleDate;
      }
      this.axios({
        url: url,
        params: {
          ...data
        },
        type: "hostEbroker"
      }).then(res => {
        this.settleData = res.data.data;
      });
    },
    close() {
      this.isShow = false;
    },
    show(li, flag) {
      this.isShow = true;
      this.liData = li;
      this.flag = flag;
      this.getDetailData();
    }
  },
  filters: {
    toFixed(num) {
      return Number(num).toFixed(2);
    }
  }
};
</script>

<style lang="scss" scoped>
.statement-detail {
  @include add-size($font_size_14);
  .header {
    @include add-size($font_size_14);
    line-height: 32px;
    position: relative;
    .warning {
      cursor: pointer;
      position: absolute;
      left: 20px;
      top: 0;
      &:hover {
        color: $white;
      }
      i {
        margin-right: 10px;
      }
    }
  }
  .detail-content {
    color: $defaultColor;
    height: 367px;
    .detail-title {
      line-height: 28px;
      padding: 10px 20px 0;
      border-bottom: 1px solid #323840;
      @include flex-center;
      justify-content: space-between;
    }
    .user-info {
      padding: 10px 20px 21px;
      color: $white;
      .name-address {
        margin-top: 6px;
      }
    }
    .position-detail {
      .position-li {
        @include flex-center;
        line-height: 34px;
        padding: 0 20px;
        justify-content: space-between;
        .position-li_value {
          color: $white;
        }
      }
    }
    .draw-ul {
      .scroll-area {
        max-height: 220px;
        overflow-y: auto;
        .nodata {
          line-height: 60px;
          text-align: center;
        }
      }
      .draw-li {
        @include flex-center;
        color: $white;
        line-height: 34px;
        padding: 0 20px;
        justify-content: space-between;
        &.li-title {
          color: $defaultColor;
          line-height: 1;
          padding-top: 10px;
        }
        .draw-li-column {
          &[align="right"] {
            text-align: right;
          }
          &:nth-child(1) {
            width: 60px;
          }
          &:nth-child(2) {
            width: 30px;
          }
          &:nth-child(3) {
            width: 60px;
          }
          &:nth-child(4) {
            width: 160px;
          }
        }
      }
    }
    .table-block {
      padding: 10px 20px;
    }
    /deep/ .el-table {
      @include add-size($font_size_14);
      thead {
        color: $defaultColor;
      }
      .stock-code {
        position: relative;
        color: $defaultColor;
        padding-left: 20px;
        margin-top: 8px;
        @include add-size($font_size_12);
        &:after {
          content: "";
          position: absolute;
          width: 16px;
          height: 10px;
          background: url("~@/assets/images/base/hk-icon.png") 0 0 no-repeat;
          background-size: 16px 10px;
          left: 0;
          top: 1px;
        }
      }
      td {
        border: none;
        .cell {
          line-height: 1;
        }
      }
    }
    /deep/ .el-tabs {
      height: 100%;
      .el-tabs__item {
        color: $defaultColor;
        @include add-size($font_size_14);
        line-height: 32px;
        height: auto;
        &.is-active,
        &:hover {
          color: $white;
        }
      }
      .el-tabs__header {
        margin-bottom: 0;
      }
      .el-tabs__nav-wrap {
        &::after {
          background: #323840;
          height: 1px;
        }
        .el-tabs__active-bar {
          background-color: transparent;
          &::after {
            background-color: $activeColor;
            width: 36px;
            height: 4px;
            border-radius: 2px;
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: -1px;
            transform: translate(-50%, -50%);
          }
        }
      }
      .el-tabs__content {
        height: calc(100% - 80px);
        .news-ul {
          height: 100%;
          overflow-y: auto;
        }
      }
      .el-tab-pane {
        animation: show-tab 0.5s ease forwards;
        height: 100%;
      }
      @keyframes show-tab {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
    }
  }
  .warn-content {
    color: $defaultColor;
    padding: 20px;
    height: 367px;
    overflow-y: auto;
    p {
      line-height: 30px;
    }
  }
}
</style>


