<template>
  <div class="notice">
    <el-dialog
      theame
      center
      title="修改交易密码"
      :visible="dialogVisiblea"
      width="350px"
      top="16%"
      :modal="false"
      @close="dialogVisiblea = false"
    >
      <div class="change-type">
        <div class="common-account" :class="{active: changeType == 1}" @click="changeType = 1">标准账号</div>
        <div
          class="organization-account"
          :class="{active: changeType == 2}"
          @click="changeType = 2"
          v-if="hasOrganization"
        >机构账号</div>
      </div>
      <div class="email-sub">
        <div class="email-title">
          <span>旧密码</span>
        </div>
        <div class="email-inpt-number">
          <el-input
            :type="showPassword1"
            v-model="password0"
            maxlength="20"
            placeholder="请输入旧的交易密码"
          ></el-input>
          <i
            class="icon"
            :class="{password:showPassword1 ==='password'}"
            @click="changeShow('showPassword1')"
          ></i>
        </div>
        <div class="email-title">
          <span>新密码</span>
        </div>
        <div class="email-inpt-number">
          <el-input
            :type="showPassword2"
            v-model="password1"
            maxlength="20"
            placeholder="请输入8-20位新的交易密码"
          ></el-input>
          <i
            class="icon"
            :class="{password:showPassword2 ==='password'}"
            @click="changeShow('showPassword2')"
          ></i>
        </div>
        <div class="email-title">
          <span>再次确认</span>
        </div>
        <div class="email-inpt-number">
          <el-input
            :type="showPassword3"
            v-model="password2"
            maxlength="20"
            placeholder="请确认您的交易密码"
          ></el-input>
          <i
            class="icon"
            :class="{password:showPassword3 ==='password'}"
            @click="changeShow('showPassword3')"
          ></i>
        </div>
        <div class="tips">请设置8-20位字符的交易密码，首字母大写。</div>
        <div class="btn-sue-q">
          <el-button hover @click="changePassword" :class="{action: canWriteFlag}">确定修改</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "notice",
  components: {},
  data() {
    return {
      dialogVisiblea: false,
      changeType: 1,
      input: "",
      code: "",
      password0: "",
      password1: "",
      password2: "",
      canWriteFlag: false,
      showPassword1: "password",
      showPassword2: "password",
      showPassword3: "password"
    };
  },
  mounted() {},
  watch: {
    password0() {
      this.canWrite();
    },
    password1() {
      this.canWrite();
    },
    password2() {
      this.canWrite();
    }
  },
  computed: {
    identityData(){
      return this.$store.state.system.accountInfo;
    },
    hasOrganization() {
      return this.identityData.institutionalAccount;
    }
  },
  methods: {
    changeShow(tar) {
      let target = this[tar];
      target === "password" ? (target = "text") : (target = "password");
      this[tar] = target;
    },
    canWrite() {
      if (this.password0 && this.password1 && this.password2) {
        this.canWriteFlag = true;
      } else {
        this.canWriteFlag = false;
      }
    },
    show() {
      this.dialogVisiblea = true;
    },
    close() {
      this.dialogVisiblea = false;
    },
    changePassword() {
      try {
        if (!/^[A-Z]*$/.test(this.password1[0])) {
          throw new Error("首位需为大写字母");
        }
        if (/[\u4e00-\u9fa5]/.test(this.password1)) {
          throw new Error("只能是英文字符");
        }
        if (/\s/.test(this.password1)) {
          throw new Error("密码不能有空格");
        }
        if (this.password1.length < 8) {
          throw new Error("密码长度为8 - 20位");
        }
        if (this.password1 != this.password2) {
          throw new Error("两次密码不一致，请重新输入");
        }
        let params = {
          userNo: this.identityData.ebokerUsername,
          oldPassword: this.password0,
          newPassword: this.password2,
          channel: 0
        };
        let reqType = "";
        let url = "";
        if (this.changeType == 1) {
          // 标准账户
          reqType = "hostEbroker";
          url = "/changePassword.do";
        } else {
          // 机构账户
          // /appcustomer/changeTransactionPassword2.do
          url = "/changePassword.do";
          reqType = "hostEbroker";
          params.idCardCode = this.identityData.idCardCode;
          params.channel = 10;
          params.loginID = this.identityData.institutionalAccount;
          params.areaCode = this.identityData.areaCode || "86";
        }
        this.axios({
          url: url,
          method: "post",
          params: params,
          type: reqType
        }).then(res => {
          this.$message({
            message: "修改成功，请重新登录！",
            type: "success"
          });
          this.close();
          this.password0 = "";
          this.password1 = "";
          this.password2 = "";
          this.$store.dispatch("updateAccountData", {});
          this.$store.dispatch("closeDeal");
        });
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/.el-dialog__title {
  @include add-size($font_size_14);
}
/deep/.el-dialog {
  left: 58px;
}
.change-type {
  @include flex-center;
  div {
    margin-right: 10px;
    padding: 20px 0 0 10px;
    @include add-size($font_size_16);
    cursor: pointer;
    &.active {
      color: $activeColor;
    }
  }
}
.email-sub {
  .email-title {
    width: 100%;
    height: 40px;
    padding-top: 16px;
    padding-left: 17px;
    > span {
      color: $white;
      @include add-size($font_size_14);
    }
  }
  .email-inpt-number {
    width: 100%;
    height: 40px;
    padding-left: 17px;
    position: relative;
    /deep/.el-input--medium .el-input__inner {
      width: 314px;
      height: 35px;
      background-color: #1d242e;
      border: none;
      border-bottom: 1px solid #576373;
      padding-left: 0;
      border-radius: 0;
      color: $white;
      @include add-size($font_size_14);
    }
    /deep/.el-input__inner:hover {
      border: none;
      border-bottom: 1px solid #576373;
    }
  }
  .tips {
    padding-left: 17px;
    margin-top: 6px;
    color: $green;
  }
  .btn-sue-q {
    width: 100%;
    height: 78px;
    padding-top: 20px;
    /deep/.el-button {
      width: 250px;
      height: 30px;
      background-color: #9195ac;
      margin-left: 50px;
      border: none;
      border-bottom: 1px solid #1d242e;
      color: $white;
      border: none;
      @include medium;
      line-height: 30px;
      padding: 0;
    }
    .action {
      @include linear;
    }
  }
  .icon {
    position: absolute;
    width: 18px;
    height: 11px;
    background: url("~@/assets/images/login/show-icon.png") center no-repeat;
    background-size: 18px 11px;
    right: 30px;
    top: 50%;
    margin-top: -4px;
    cursor: pointer;
    &.password {
      background: url("~@/assets/images/login/hide-icon.png") center no-repeat;
      background-size: 18px 8px;
    }
  }
}
</style>