<template>
  <div class="activity-center">
    <div class="activity-ul">
      <div
        class="activity-li"
        v-for="(li,i) in activityList"
        :key="i"
        @click="linkActivity(li)"
        hover
      >
        <img src="~@/assets/images/personal/activity-banner.png" alt class="li-banner" />
        <div class="li-name">免费拿股票</div>
        <div class="li-tips">腾讯、美团等优质股票等你来领</div>
      </div>
    </div>
  </div>
</template>

<script>
const shell = require("electron").shell;
export default {
  components: {},
  data() {
    return {
      activityList: [
        // {
        //   url:
        //     "http://h5.lc913.com/web/webLottery/index.html?customerId="
        // }
      ]
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    linkActivity(li) {
      const customerId = localStorage.getItem("customerId");
      shell.openExternal(li.url + customerId);
    }
  }
};
</script> 

<style lang="scss" scoped>
.activity-center {
  height: 100%;
  width: 100%;
  padding: 50px;
  .activity-ul {
    @include flex-box;
    flex-wrap: wrap;
    .activity-li {
      width: 400px;
      height: 220px;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      margin-right: 90px;
      margin-bottom: 90px;
      background-color: $white;
      &:nth-child(3n) {
        margin-right: 0;
      }
      .li-banner {
        width: 400px;
        height: 160px;
        margin: 0;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
      .li-name {
        color: #2b2b2b;
        @include add-size($font_size_16);
        padding-left: 20px;
        @include medium;
        line-height: 34px;
      }
      .li-tips {
        padding-left: 20px;
        @include add-size($font_size_14);
