<template>
  <div class="self-select">
    <div
      class="self-div"
      :style="{ width: !hkOrUs ? '1659px' : '1526px' }"
      v-if="!noSelect"
    >
      <div
        class="self-select-content"
        :style="{ width: !hkOrUs ? '633px' : '500px' }"
        :class="{ allInfo: isShow }"
        v-if="!noSelect"
      >
        <div class="control" :class="{ controlImg: isShow }" @click="shouHide">
          <img src="@/assets/images/hq/right.png" alt />
        </div>
        <div class="table-content" ref="tableContent">
          <el-table
            ref="multipleTable"
            style="padding-right:9px;"
            :data="selectList"
            :row-class-name="tableRowClassName"
            :max-height="tableHeight"
            highlight-current-row
            @sort-change="changeSortable"
            @row-click="goDetail"
            @row-contextmenu="rowContextmenu"
          >
            <!-- fixed="left" -->
            <el-table-column
              class-name="order-num"
              label="序号"
              type="index"
              width="50"
              align="center"
            ></el-table-column>
            <el-table-column
              prop="name"
              label="股票名称"
              align="left"
              width="120"
            >
              <template slot-scope="scope">
                <div class="stock-name" :title="scope.row.name">
                  {{ scope.row.name }}
                </div>
                <div>
                  <span
                    v-if="scope.row.symbol.slice(0, 2) == 'hk'"
                    class="symbol"
                    :data-symbol="scope.row.symbol"
                    style="background: #FA541C;font-size:10px;padding:2px;"
                    >{{ scope.row.symbol.slice(0, 2).toUpperCase() }}</span
                  >
                  <span
                    v-else
                    class="symbol"
                    :data-symbol="scope.row.symbol"
                    style="background: #2186ff;font-size:10px;padding:2px;"
                    >{{ scope.row.symbol.slice(0, 2).toUpperCase() }}</span
                  >
                  <span>
                    {{ scope.row.symbol.slice(2) }}
                  </span>
                </div>
              </template>
            </el-table-column>
            <!-- <el-table-column prop="name" label align="center" width="150">
              <template slot-scope="scope">
                <sCharts v-if="scope.row.data" :sChartsData="scope.row.data" />
              </template>
            </el-table-column> -->
            <el-table-column
              prop="last"
              label="最新价"
              align="center"
              width="86"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.chg > 0" class="red"
                  >+{{ scope.row.last }}</span
                >
                <span v-else-if="scope.row.chg < 0" class="greed">
                  {{ scope.row.last }}
                </span>
                <span v-else class="gay">{{ scope.row.last }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="chg_pct"
              sortable
              label="涨跌幅"
              align="center"
              width="86"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.chg > 0" class="red"
                  >+{{ scope.row.chg_pct }}%</span
                >
                <span v-else-if="scope.row.chg < 0" class="greed"
                  >{{ scope.row.chg_pct }}%</span
                >
                <span v-else class="gay">{{ scope.row.chg_pct }}%</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="chg"
              label="涨跌额"
              align="center"
              width="96"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.chg > 0" class="red"
                  >+{{ scope.row.chg }}</span
                >
                <span v-else-if="scope.row.chg < 0" class="greed">{{
                  scope.row.chg
                }}</span>
                <span v-else class="gay">{{ scope.row.chg }}</span>
              </template>
            </el-table-column>
            <el-table-column
              v-if="isShow"
              prop="turnoverrate"
              label="换手率"
              align="center"
              width="96"
            ></el-table-column>
            <el-table-column
              v-if="isShow"
              prop="pe"
              label="市盈率"
              align="center"
              width="96"
            ></el-table-column>
            <el-table-column
              v-if="isShow"
              prop="bookvalueratio"
              label="市净率"
              align="center"
              width="96"
            ></el-table-column>
            <el-table-column
              v-if="isShow"
              prop="amplitude"
              label="日振幅"
              align="center"
              width="96"
            ></el-table-column>
            <el-table-column
              v-if="isShow"
              prop="volume"
              label="成交量"
              align="center"
              width="96"
            >
              <template slot-scope="scope">
                <span>{{ scope.row.volume | digitUnit }}</span>
              </template>
            </el-table-column>
            <el-table-column
              v-if="isShow"
              prop="amount"
              label="成交额"
              align="center"
              width="96"
            >
              <template slot-scope="scope">
                <span>{{ scope.row.amount | digitUnit }}</span>
              </template>
            </el-table-column>
            <el-table-column
              v-if="isShow"
              prop="ttm"
              label="市净率(动)"
              align="center"
              width="100"
            ></el-table-column>
            <el-table-column
              v-if="isShow"
              prop="flowmarketCap"
              label="流通市值"
              align="center"
              width="96"
            >
              <template slot-scope="scope">
                <span>{{ scope.row.flowmarketCap | digitUnit }}</span>
              </template>
            </el-table-column>
            <el-table-column
              v-if="isShow"
              prop="marketCap"
              show-overflow-tooltip
              label="总市值"
              align="center"
              width="120"
            >
              <template slot-scope="scope">
                <span>{{ scope.row.marketCap | digitUnit }}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="no-select" v-if="noSelect">
      <img class="no-select-icon" src="~@/assets/images/hq/no-select.png" alt />
      <div class="no-select-intr">您还没有添加任何股票</div>
      <div class="no-select-intr2">快去添加几只心仪的股票吧</div>
    </div>
    <!-- <rightMenu ref="rightMenu" :selfStockArr="selfStockArr" @setSelect="getSelectList" /> -->
  </div>
</template>

<script>
// import rightMenu from "@/components/popup/right-menu";

import { initArray, showDetailComponents } from "@/utils";
import Sortable from "sortablejs";
// import { selfSocket } from "@/utils/socket-instance";
import {
  initMOption,
  initKOption,
  init5MOption,
  initMainOption,
  init5WoniuMOption
} from "@/utils/k-line";
export default {
  components: {
    // rightMenu
  },
  data() {
    return {
      noSelect: false,
      selectList: [],
      isShow: false,
      loading: true,
      tableHeight: 901,
      showRight: false,
      show: true,
      showZs: false,
      hkMarket: 3,
      showLarger: false,
      stockQuery: {},
      isActive: 0,
      activeName: "first",
      stockData: {},
      dataset: {
        data: [],
        yestclose: 330
      },
      datasetFive: {
        data: [],
        yestclose: 330
      },
      moreBtn: "更多",
      tableIndex: 0,
      timer: "",
      listTimer: "",
      marketData: [
        "交易中",
        "午间休市",
        "停牌",
        "已收盘",
        "早盘竞价",
        "收盘竞价",
        "待上市",
        "未开盘"
      ],
      selfListTimer: "",
      needResetOrder: false, // 是否修改了自选列表顺序
      hkOrUs: true
    };
  },
  watch: {
    "$store.state.GlobalData.selfStatus"(val) {
      // console.log(val, "123木头人");
      if (this.$route.query.target === "selfSelect") {
        setTimeout(() => {
          this.getSelectList();
        }, 500);
      }
    },
    "$store.state.GlobalData.searchData"(val) {
      // 监听当前股票变动
      this.stockData = val;
      if (val.code.length > 7) {
        this.showZs = true;
      } else {
        this.showZs = false;
      }
      if (val.code.indexOf("hk") !== -1) {
        this.hkOrUs = true;
      } else if (val.code.indexOf("us") !== -1) {
        this.hkOrUs = false;
      }
      // this.codeLen = [...val.code].length;
      // this.companyProfile();
    },
    "$store.state.GlobalData.minutelyCharts.data.query"(val) {
      if (!this.showRight) {
        return false;
      }
      if (val.warrant && val.warrant.stock) {
        this.hkMarket = 4;
        this.woNiuData = val.warrant;
        this.woNiuStock = val.warrant.stock;
        setTimeout(() => {
          this.getChartData(0);
        }, 100);
      } else if (val.inline && val.inline.stock) {
        this.hkMarket = 4;
        this.woNiuData = val.inline;
        this.woNiuStock = val.inline.stock;
        setTimeout(() => {
          this.getChartData(0);
        }, 100);
      } else if (val.rights && val.rights.stock) {
        this.hkMarket = 4;
        this.woNiuData = val.rights;
        this.woNiuStock = val.rights.stock;
        setTimeout(() => {
          this.getChartData(0);
        }, 100);
      } else {
        this.hkMarket = 3;
      }
    }
  },
  computed: {
    rightType() {
      return this.$store.state.GlobalData.searchData.code.length > 7;
    },
    stockData1() {
      return this.$store.state.GlobalData.minutelyCharts.data.query;
    },
    selfStockArr() {
      let arr = [];
      this.selectList.forEach(a => {
        arr.push(a.symbol);
      });
      return arr;
    }
    // selfSelectList() {
    //   if (this.$store.state.GlobalData.selfStock.length > 0) {
    //     return this.$store.state.GlobalData.selfStock;
    //   } else {
    //     return [];
    //   }
    // }
  },
  activated() {
    this.showRight = true;
    this.showZs = false;
    this.needResetOrder = false;
    // this.selectList = [];
    this.isShow = false;
    this.getSelectList();
    // 初始化先显示缓存数据
    return;
    let cache = localStorage.getItem("selfStock");
    let selfStock;
    if (cache) {
      selfStock = JSON.parse(cache) || [];
    }

    if (selfStock.length > 0) {
      // let selfStock = JSON.parse(cache);
      this.$store.dispatch("SHOW_MINUTELY_CHARTS", {
        data: { minutely: selfStock[0].Data, query: selfStock[0].query },
        timestamp: Date.now()
      });
      if (selfStock[0].query.symbol.length > 7) {
        this.showZs = true;
      } else {
        this.showZs = false;
      }
      if (selfStock[0].query.symbol.indexOf("hk") !== -1) {
        this.hkOrUs = true;
      } else if (selfStock[0].query.symbol.indexOf("us") !== -1) {
        this.hkOrUs = false;
      }
      // if (this.$store.state.GlobalData.searchData.recover === false) {
      //   this.$store.dispatch("CHANGE_SEARCH_DATA", {
      //     code: selfStock[0].query.symbol
      //   });
      //   this.hasCache = true;
      // }
    } else {
      this.hasCache = false;
    }
  },
  deactivated() {
    this.showRight = false;
    clearTimeout(this.selfListTimer);
    if (this.needResetOrder) {
      this.resetOrder();
    }
    // selfSocket([]); // 关闭自选列表的订阅
  },
  mounted() {
    this.$nextTick(() => {
      this.getSelectList();
      this.initPage();
      let that = this;
      document.addEventListener("keyup", function(e) {
        if (!that.showRight) {
          return;
        }
        // 下40 上38
        // e.preventDefault()
        if (e.keyCode == 38) {
          if (that.tableIndex == 0) {
            return;
          } else {
            clearTimeout(that.timer);
            that.tableIndex -= 1;
            if (that.tableIndex > 7) {
              that.$refs.multipleTable.bodyWrapper.scrollTop -= 57;
            } else {
              that.$refs.multipleTable.bodyWrapper.scrollTop = 0;
            }
            that.$refs.multipleTable.setCurrentRow(
              that.selectList[that.tableIndex]
            );
            // that.timer = setTimeout(() => {
            //   that.$store.dispatch("CHANGE_SEARCH_DATA", {
            //     code: that.selectList[that.tableIndex].symbol,
            //     timestamp: new Date().getTime()
            //   });
            // }, 500);
          }
        } else if (e.keyCode == 40) {
          if (that.tableIndex < that.selectList.length - 1) {
            that.tableIndex += 1;
          } else {
            return;
          }
          clearTimeout(that.timer);
          if (that.tableIndex > 7) {
            that.$refs.multipleTable.bodyWrapper.scrollTop += 57;
          }
          that.$refs.multipleTable.setCurrentRow(
            that.selectList[that.tableIndex]
          );
          // that.timer = setTimeout(() => {
          //   that.$store.dispatch("CHANGE_SEARCH_DATA", {
          //     code: that.selectList[that.tableIndex].symbol,
          //     timestamp: new Date().getTime()
          //   });
          // }, 500);
        }
      });
    });
  },
  methods: {
    setIntervalList() {
      clearTimeout(this.selfListTimer);
      this.selfListTimer = setTimeout(() => {
        if (new Date().getHours() > 8 && new Date().getHours() < 17) {
          this.intervalListData();
        }
        this.setIntervalList();
      }, 20000);
    },
    menuSelect(act, target) {
      // if (act.value === 0) {
      //   let code = [];
      //   code.push(target.symbol);
      //   this.selectList.forEach(a => {
      //     if (a.symbol != target.symbol) {
      //       code.push(a.symbol);
      //     }
      //   });
      //   let data = {
      //     opt: 2, // 1增加，0删除， 2 批量操作
      //     codelist: code
      //   };
      //   this.deleteSelect(data);
      // } else if (act.value === 1) {
      //   let data = {
      //     code: target.symbol,
      //     opt: 0 // 1增加，0删除， 2 批量操作
      //   };
      //   this.deleteSelect(data);
      // }
    },
    // 拖动排序
    dragOrder() {
      // el-table__row
      // let table = this.$refs.multipleTable.$el;
      const tbody = document.querySelector(".el-table__body-wrapper tbody");
      let selectList = this.selectList;
      const _this = this;
      this.sortable = Sortable.create(tbody, {
        onStart() {
          _this.originArr = _this.sortable.toArray();
        },
        onEnd({ newIndex, oldIndex }) {
          _this.needResetOrder = true;
          // setTimeout(() => {
          //   _this.sortable.sort(_this.originArr);
          // }, 3000);
          let orders = document.querySelectorAll(".order-num div div");
          orders.forEach((el, index) => {
            el.innerHTML = index + 1;
          });
        }
      });
    },
    resetOrder() {
      let table = this.$refs.multipleTable.$el;
      let rows = table.getElementsByClassName("el-table__row");
      let codelist = [];
      for (let i = 0; i < rows.length; i++) {
        let code = rows[i]
          .getElementsByClassName("symbol")[0]
          .getAttribute("data-symbol");
        codelist.push(code);
      }
      let data = {
        opt: 2, // 1增加，0删除， 2 批量操作
        codelist
      };
      this.deleteSelect(data, "leave");
    },
    //自选请求接口
    deleteSelect(data, flag) {
      // 删除自选
      let url = "/quote-service/api/v1/selfstock";
      this.axios({
        url: url,
        method: "post",
        params: data,
        type: "HQ"
      }).then(res => {
        if (flag !== "leave") {
          selfSocket([]); // 关闭自选列表的订阅
          this.getSelectList();
        }
      });
    },
    rowContextmenu(row, column, event) {
      // 列表右键菜单操作
      row.x = event.clientX;
      row.y = event.clientY;
      // this.$refs.rightMenu.show(row);
      this.$store.dispatch("SET_RIGHT_MENU", {
        show: true,
        target: row
      });
    },
    initPage() {
      this.tableHeight = this.$refs.tableContent.offsetHeight || 890;
    },
    searchBarFocus() {
      let auto = document.getElementsByClassName("el-autocomplete")[0]
        .childNodes[0].childNodes[1];
      auto.focus();
    },
    goDetail(row, column, event) {
      if (row.symbol.indexOf("hk") !== -1) {
        this.hkOrUs = true;
      } else if (row.symbol.indexOf("us") !== -1) {
        this.hkOrUs = false;
      }
      clearTimeout(this.listTimer);
      this.tableIndex = row.index;
      if (event) {
        this.show = !this.show;
        this.isShow = false;
      }
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: row.symbol
      });
    },
    goDetailOther() {
      setTimeout(() => {
        this.$store.dispatch("CHANGE_SEARCH_DATA", {
          code: this.stockQuery.symbol,
          timestamp: new Date().getTime(),
          recover: true
        });
      }, 500);
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 0,
        timestamp: new Date().getTime()
      });
    },

    changeSortable(value) {
      console.log(value);
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (rowIndex % 2 === 0) {
        return "success-row";
      } else if (rowIndex % 2 === 1) {
        return "warning-row";
      }
      return "";
    },
    intervalListData(cb) {
      this.axios({
        url: "/quote-service/api/v1/selfstock",
        params: {},
        type: "HQ",
        hideMessage: true
      })
        .then(res => {
          localStorage.setItem("selfStock", JSON.stringify(res.data.Data));
          let data = res.data.Data || [];
          let list = [];
          data.map((value, index) => {
            list.push(value.query);
          });
          initArray(list);
          this.selectList = list;

          // if (this.$refs.multipleTable) {
          //   this.$refs.multipleTable.setCurrentRow(
          //     this.selectList[this.tableIndex]
          //   );
          // } else {
          //   setTimeout(() => {
          //     this.$refs.multipleTable.setCurrentRow(
          //       this.selectList[this.tableIndex]
          //     );
          //   }, 1000);
          // }

          if (typeof cb === "function") {
            cb();
          }
        })
        .catch(err => {});
    },
    getSelectList() {
      // this.selectList = []
      // this.loading = true;
      let _this = this;
      this.intervalListData(() => {
        // this.loading = false;
        // this.setIntervalList();
        // window.selectList = this.selectList;
        if (_this.selectList.length > 0) {
          _this.noSelect = false;
          if (_this.$store.state.GlobalData.currentStock.recover) {
            _this.$store.dispatch("SET_CURRENT_STOCK", {
              recover: false
            });
          } else {
            _this.$store.dispatch("SET_CURRENT_STOCK", {
              code: _this.selectList[0].symbol
            });
          }
          showDetailComponents({ show: true });
          // let codelist = [];
          // this.selectList.forEach(n => codelist.push(n.symbol));
          // selfSocket(codelist);
          // this.$store.dispatch("SET_SELF_STOCK", {
          //   all: true,
          //   data: this.selectList
          // });
          _this.$nextTick(() => {
            // 不加nextTick  首次无法加载，右侧组件未渲染完成
            _this.dragOrder();
            if (_this.hasCache != true) {
              _this.goDetail(this.selectList[0]);
            }
            _this.$refs.multipleTable.setCurrentRow(this.selectList[0]);
          });
        } else {
          this.noSelect = true;
        }
      });
    },
    shouHide() {
      this.isShow = !this.isShow;
      this.show = !this.show;
      this.$emit("getStatus", this.allInfo);
      if (!this.isShow) {
        this.$store.dispatch("SHOW_STOCK_KLINE", true);
        this.$store.dispatch("SHOW_COMPANY_INFO", true);
      } else {
        this.$store.dispatch("SHOW_STOCK_KLINE", false);
        this.$store.dispatch("SHOW_COMPANY_INFO", false);
      }
    },
    //获取窝轮牛熊证数据
    getChartData(num) {
      // let chart = this.$echarts.init(this.$refs.minDiv);
      let url = "/quote-service/api/v1/minutely";
      this.mcharts = this.$echarts.init(this.$refs.minDiv);
      let day;
      let dataset;
      if (num == 0) {
        day = 1;
        dataset = this.dataset;
      } else {
        day = 5;
        dataset = this.datasetFive;
      }
      let data = {
        code: this.woNiuStock,
        day: day,
        delay: 0
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(data => {
        this.stockQuery = data.data.query;
        let newData = data.data.Data;
        if (newData.length <= 0) return false;
        dataset.yestclose = newData[0].Hst_close;
        dataset.data = [];
        newData.map((value, index) => {
          var listAry = new Array();
          var t = new Date(value.Minute * 60 * 1000);
          listAry.push(t.getHours().toString() + t.getMinutes().toString());
          listAry.push(value.Price);
          listAry.push(value.Avg_price);
          listAry.push(value.Volume);
          dataset.data.push(listAry);
        });
        if (num == 0) {
          this.mcharts.setOption(initMainOption(dataset, "hk"));
          this.mcharts.getZr().on("dblclick", () => {
            this.goDetailOther();
          });
        } else {
          this.$echarts
            .init(this.$refs.minFiveDiv)
            .setOption(init5WoniuMOption(dataset, "hk"));
          this.$echarts
            .init(this.$refs.minFiveDiv)
            .getZr()
            .on("dblclick", () => {
              this.goDetailOther();
            });
        }
        // this.mcharts.setOption(initMainOption(dataset, "hk"));
      });
    },
    selectChart(num) {
      console.log(num);
      this.isActive = num;
      if (num == 0) {
        this.getChartData(num);
      } else if (num == 1) {
        this.getChartData(num);
      } else {
        this.klineData(num);
      }
    },
    klineData(type) {
      let cycleNum;
      if (type == 2) {
        cycleNum = "day";
        this.moreBtn = "更多";
      } else if (type == 3) {
        cycleNum = "week";
        this.moreBtn = "更多";
      } else if (type == 4) {
        cycleNum = "month";
        this.moreBtn = "更多";
      } else if (type == 5) {
        cycleNum = "year";
        this.moreBtn = "更多";
        console.log(this.moreBtn, 580000000);
      } else if (type == "minute1") {
        cycleNum = "minute1";
        this.moreBtn = "1分钟";
      } else if (type == "minute5") {
        cycleNum = "minute5";
        this.moreBtn = "5分钟";
      } else if (type == "minute15") {
        cycleNum = "minute15";
        this.moreBtn = "15分钟";
      } else if (type == "minute30") {
        cycleNum = "minute30";
        this.moreBtn = "30分钟";
      } else if (type == "minute60") {
        cycleNum = "minute60";
        this.moreBtn = "60分钟";
      }
      let kLineData = new Array();
      let kUrl = "/quote-service/api/v1/kline";
      let newKChart = this.$echarts.init(this.$refs.klineDiv);
      this.axios({
        url: kUrl,
        method: "get",
        params: {
          code: this.woNiuStock,
          cycle: cycleNum,
          page: 1
        },
        type: "HQ"
      })
        .then(data => {
          console.log(data);
          var dataK = data.data.Kline;
          dataK.map(function(value, index) {
            var KlineAry = new Array();
            var dateTime =
              value.date.slice(0, 4) +
              "-" +
              value.date.slice(4, 6) +
              "-" +
              value.date.slice(6, 8);
            // console.log(dateTime)
            KlineAry.push(dateTime);
            // KlineAry.push(value.open);
            KlineAry.push(Number(value.open));
            KlineAry.push(Number(value.close));
            KlineAry.push(Number(value.low));
            KlineAry.push(Number(value.hight));
            KlineAry.push(Number(value.volume));
            kLineData.push(KlineAry);
          });
          newKChart.setOption(initKOption(kLineData));
          newKChart.resize();
        })
        .catch(function(err) {
          console.log(err);
        });
      window.onresize = null;
      window.onresize = function() {
        newKChart.resize();
      };
    },
    handleCommand(command) {
      console.log(command);
      this.isActive = 6;
      // this.commandVal = command
      this.klineData(command);
    }
  }
};
</script>

