<template>
  <div class="personal-menu">
    <div class="user-info">
      <img class="user-pic" :src="userInfo.picUrl" alt />
      <div class="user-name">
        <p class="user-name-t">{{userInfo.customerName}}</p>
        <p class="user-name-m">{{userInfo.personSign || "什么都没有~"}}</p>
      </div>
      <i class="rewrite-icon" @click="rewriteInfo"></i>
    </div>
    <div class="menu-list">
      <div
        class="sub-menu"
        :class="{active: menu.active}"
        v-for="(menu, i) in menuList"
        :key="i"
        @click="changeMenu(menu)"
      >
        <img class="menu-icon" :src="menu.active?menu.iconSelect:menu.icon" />
        <div class="menu-name">{{menu.name}}</div>
        <i class="menu-arrow el-icon-arrow-right"></i>
      </div>
    </div>
    <personalInfo ref="personalInfo" />
  </div>
</template>

<script>
import personalInfo from "@/views/common/personal-info";

export default {
  components: {
    personalInfo
  },
  props: {},
  data() {
    return {
      menuList: [
        {
          name: "我的钱包",
          icon: require("@/assets/images/personal/menu-icon1-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon1-b.png"),
          active: true,
          url: "/mine"
        },
        {
          name: "我的行情",
          icon: require("@/assets/images/personal/menu-icon2-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon2-b.png"),
          active: false,
          url: "/market"
        },
        {
          name: "我的银行卡",
          icon: require("@/assets/images/personal/menu-icon3-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon3-b.png"),
          active: false,
          url: "/my-bank-card"
        },
        {
          name: "活动中心",
          icon: require("@/assets/images/personal/menu-icon4-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon4-b.png"),
          active: false,
          url: "/activity-center"
        },
        {
          name: "奖励中心",
          icon: require("@/assets/images/personal/menu-icon5-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon5-b.png"),
          active: false,
          url: "/reward-center"
        },
        {
          name: "邀请好友",
          icon: require("@/assets/images/personal/menu-icon6-a.png"),
          iconSelect: require("@/assets/images/personal/menu-icon6-b.png"),
          active: false,
          url: "/invite-friend"
        }
        // {
        //   name: "港美股学堂",
        //   icon: require("@/assets/images/personal/menu-icon7-a.png"),
        //   iconSelect: require("@/assets/images/personal/menu-icon7-b.png"),
        //   active: false,
        //   url: "/school"
        // },
        // {
        //   name: "易昇合伙人",
        //   icon: require("@/assets/images/personal/menu-icon8-a.png"),
        //   iconSelect: require("@/assets/images/personal/menu-icon8-b.png"),
        //   active: false,
        //   url: "/partner"
        // }
      ]
    };
  },
  computed: {
    userInfo() {
      return this.$store.state.system.userInfo;
    }
  },
  activated() {
    this.$nextTick(() => {});
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        if (this.$route.query.target === "myBankCard") {
          this.changeMenu(this.menuList[2]);
        } else if (this.$route.query.target === "market") {
          // 行情
          this.changeMenu(this.menuList[1]);
        } else {
        }
      }, 100);
    });
  },
  methods: {
    getUserInfo() {},
    changeMenu(menu) {
      this.$router.push(menu.url);
      this.menuList.forEach(a => (a.active = false));
      menu.active = true;
      // console.log(this.menuList, menu);
    },
    rewriteInfo() {
      this.$refs.personalInfo.show();
    }
  },
  watch: {
    "$store.state.UserInfo.targetMenu"(e) {
      this.changeMenu(this.menuList[e.target]);
    },
    $route(e) {
      setTimeout(() => {
        if (this.$route.query.target === "myBankCard") {
          this.changeMenu(this.menuList[2]);
        } else if (this.$route.query.target === "market") {
          // 行情
          this.changeMenu(this.menuList[1]);
        }
      }, 100);
      if (e.path == "/wd") {
        this.menuList.forEach(a => (a.active = false));
        this.menuList[0].active = true;
      }
      // console.log(e);
    }
  }
};
</script> 

<style lang="scss" scoped>
.personal-menu {
  height: 100%;
  width: 300px;
  background-color: $blockBgColor;
  border-radius: 4px;
  margin-right: 4px;
  .user-info {
    padding: 25px 0 46px 30px;
    @include flex-box;
    align-items: center;
    .user-pic {
      width: 46px;
      height: 46px;
      border-radius: 50%;
      margin-right: 20px;
    }
    .user-name {
      .user-name-t {
        color: $white;
        @include add-size($font_size_16);
      }
      .user-name-m {
        @include add-size($font_size_12);
        margin-top: 10px;
        @include single-line-ellipsis;
        max-width: 120px;
        line-height: 14px;
      }
    }
    .rewrite-icon {
      width: 17px;
      height: 17px;
      margin-right: 35px;
      margin-left: auto;
      cursor: pointer;
      background: url(~@/assets/images/personal/write-icon.png) 0 0 no-repeat;
      background-size: 17px 17px;
    }
  }
  .menu-list {
    background-color: $subBlockColor;
    .sub-menu {
      @include flex-center;
      cursor: pointer;
      align-items: center;
      padding: 0 33px;
      height: 60px;
      .menu-icon {
        width: 26px;
        // height: 26px;
        margin-right: 16px;
      }
      .menu-name {
        @include add-size($font_size_14);
        color: $defaultColor;
      }
      .menu-arrow {
        margin-left: auto;
      }
      &:hover,
      &.active {
        background-color: $activeColor;
        .menu-name {
          color: $white;
        }
        .menu-arrow {
          color: $white;
        }
      }
    }
  }
}
</style>
