<template>
  <div class="companyInfo">
    <div
      class="introduce"
      v-if="companyData.recordsList && companyData.recordsList[0]"
    >
      <div class="introduceTit">
        <div @click="introduceNum = 0" :class="{ active: introduceNum == 0 }">
          公司简介
          <span></span>
        </div>
        <div @click="introduceNum = 1" :class="{ active: introduceNum == 1 }">
          股本变动
          <span></span>
        </div>
        <div @click="introduceNum = 2" :class="{ active: introduceNum == 2 }">
          股权变动
          <span></span>
        </div>
        <div @click="introduceNum = 3" :class="{ active: introduceNum == 3 }">
          分红信息
          <span></span>
        </div>
      </div>
      <div v-show="introduceNum == 0" class="introduceCon">
        <div v-if="companyData.data && companyData.data.f007V">
          <span>公司名称</span>
          <span>{{ companyData.data.f007V }}</span>
        </div>
        <div>
          <span>证券代码</span>
          <span>{{ companyData.recordsList[0].seccode }}</span>
        </div>
        <div>
          <span>股票名称</span>
          <span>{{ companyData.recordsList[0].secname }}</span>
        </div>
        <div>
          <span>所属行业</span>
          <span>{{ companyData.recordsList[0].f018v }}</span>
        </div>
        <div>
          <span>公司网址</span>
          <span>{{ companyData.recordsList[0].f013v }}</span>
        </div>
        <div v-if="companyData.data && companyData.data.f004D">
          <span>上市日期</span>
          <span>{{
            Number(companyData.data.f004D) | dateFormat("yyyy-MM-dd")
          }}</span>
        </div>
      </div>
      <div v-show="introduceNum == 1" class="stockChange">
        <ul class="gb-ul">
          <li class="gb-li">
            <div>
              <span>实收股本</span>
              <span>公告日期</span>
            </div>
          </li>
          <li class="gb-li" v-for="(gb, i) in stockCapitalData.list" :key="i">
            <div>
              <span>{{ gb.f006N }}（约{{ gb.f006N | gbCount }}）</span>
              <span>{{ gb.f010D | dateFormat("yyyy-MM-dd") }}</span>
            </div>
          </li>
          <infinite-loading
            slot="append"
            @infinite="stockCapital"
            force-use-infinite-wrapper=".stockChange"
            spinner="bubbles"
            class="loading"
            :identifier="stockCapitalData.infiniteId"
            ref="infiniteLoading"
          >
            <span slot="no-more" class="no-more">—————— 加载完成 ———————</span>
            <span slot="no-results" class="no-result">暂无数据</span>
          </infinite-loading>
        </ul>
      </div>
      <div v-show="introduceNum == 2" class="equityChange">
        <div>
          <span>股东名称</span>
          <span>变动数量(万)</span>
          <span>公告日期</span>
        </div>
        <div v-for="(gd, i) in stockShareholderData.list" :key="i">
          <span>{{ gd.f003V }}</span>
          <span>{{ gd.f005N | wan }}</span>
          <span>{{ gd.declaredate | dateFormat("yyyy-MM-dd") }}</span>
        </div>
      </div>
      <div v-show="introduceNum == 3" class="dividendPayout equityChange">
        <div>
          <span>公告日期</span>
          <span>派息金额</span>
          <span>除净日</span>
        </div>
        <div v-for="(fh, i) in distributeDividendsData.list" :key="i">
          <span>{{ fh.f001D | dateFormat("yyyy-MM-dd") }}</span>
          <span>{{ fh.f010N }}</span>
          <span>{{ fh.f002D | dateFormat("yyyy-MM-dd") }}</span>
        </div>
      </div>
    </div>
    <div class="nodate" v-else>暂无数据</div>
  </div>
</template>
<script>
import InfiniteLoading from "vue-infinite-loading";

export default {
  components: {
    InfiniteLoading
  },
  data() {
    return {
      introduceNum: 0,
      companyData: {},
      stockCode: "",
      stockCapitalData: {
        infiniteId: 0,
        page: 1,
        size: 10,
        list: []
      }, // 股本变动
      stockShareholderData: {
        infiniteId: 0,
        page: 1,
        size: 10,
        limit: 10,
        list: []
      },
      distributeDividendsData: {
        infiniteId: 0,
        page: 1,
        size: 10,
        limit: 10,
        list: []
      }
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.companyProfile();
      this.stockCapitalData.list = [];
      this.stockCapitalData.page = 1;
      this.stockCapitalData.infiniteId++;
    },
    //公司简介
    companyProfile() {
      let stockCode = this.$store.state.GlobalData.currentStock.code
        .replace("hk", "")
        .replace("us", "");
      this.stockCode = stockCode;
      let url = "/companyProfile";
      let data = {
        stockCode: stockCode
      };
      this.axios({
        url: url,
        params: data,
        type: "hostNews",
        hideMessage: true
      })
        .then(res => {
          // console.log(res, "379aa");
          this.companyData = res.data.ret;
          this.stockShareholder();
          this.distributeDividends();
        })
        .catch(err => {
          this.companyData = {};
        });
    },
    /* 
    GET /stockCapital 股本变动
    GET /distributeDividends 分红派息
    GET /stockShareholder  股权变动
    */
    stockCapital($state) {
      let stockCode = this.stockCode;
      // GET /stockCapitalPage
      let stockCapitalData = this.stockCapitalData;
      let url = "/stockCapitalPage";
      let data = {
        stockcode: stockCode,
        page: stockCapitalData.page,
        pageSize: stockCapitalData.size
      };
      this.axios({
        url: url,
        params: data,
        type: "hostNews",
        hideMessage: true
      }).then(res => {
        if (res.data.ret.capital.length > 0) {
          if (data.page == 1) {
            stockCapitalData.list = res.data.ret.capital;
          } else {
            stockCapitalData.list = stockCapitalData.list.concat(
              res.data.ret.capital
            );
          }
          stockCapitalData.page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    },
    stockShareholder() {
      let stockCode = this.stockCode;
      let url = "/stockShareholder";
      let stockShareholderData = this.stockShareholderData;
      let data = {
        stockcode: stockCode,
        limit: stockShareholderData.limit
      };
      this.axios({
        url: url,
        params: data,
        type: "hostNews",
        hideMessage: true
      }).then(res => {
        // console.log(res, "379aa");
        this.stockShareholderData.list = res.data.ret.data.records;
      });
    },
    distributeDividends() {
      let stockCode = this.stockCode;
      let url = "/distributeDividends";
      let distributeDividendsData = this.distributeDividendsData;
      let data = {
        stockcode: stockCode,
        limit: distributeDividendsData.limit
      };
      this.axios({
        url: url,
        params: data,
        type: "hostNews",
        hideMessage: true
      }).then(res => {
        // console.log(res, "379aa");
        this.distributeDividendsData.list = res.data.ret.data.records;
      });
    }
  },
  watch: {
    "$store.state.GlobalData.searchData"(val) {
      // 监听当前股票变动
      // this.stockData = val;
      let stockCode = val.code.replace("hk", "").replace("us", "");
      if (this.stockCode != stockCode) {
        this.stockCode = stockCode;
        this.getData();
      }
    },
    introduceNum(val) {
      if (val === 1) {
        if (this.stockCapitalData.list.length <= 0) {
          this.stockCapital();
        }
      }
    }
  },
  filters: {
    gbCount(val) {
      return (val / 10000).toFixed(3) + "万";
    },
    wan(val) {
      return (val / 10000).toFixed(2);
    }
  }
};
</script>

<style lang="scss" scoped>
.introduce {
  height: 100%;
  overflow: auto;
  .introduceTit {
    @include flex-box;
    justify-content: space-between;
    width: 474px;
    padding: 10px 0;
    > div {
      padding: 0 20px;
      cursor: pointer;
      @include add-size($font_size_14);
      color: #fff;
      > span {
        height: 3px;
        display: block;
        margin-top: 4px;
      }
    }
    .active {
      color: $activeColor;
      > span {
        background: $activeColor;
      }
    }
  }
  .introduceCon {
    // width: 474px;
    > div {
      @include add-size($font_size_14);
      @include flex-box;
      justify-content: flex-start;
      color: #fff;
      padding: 10px 20px;
      &:nth-child(odd) {
        background: #252e3b;
      }
      > span {
        width: 30%;
        text-align: left;
        &:last-child {
          padding-left: 60px;
        }
      }
    }
  }
  .stockChange {
    max-height: 200px;
    overflow-y: auto;
    .gb-ul {
      .gb-li {
        &:nth-child(odd) {
          background: #252e3b;
        }
        > div {
          @include add-size($font_size_14);
          @include flex-box;
          justify-content: flex-start;
          color: #fff;
          padding: 10px 20px;

          &:first-child {
            color: $defaultColor;
          }
          > span {
            width: 20%;
            text-align: left;
            &:last-child {
              padding-left: 60px;
            }
          }
        }
      }
    }
  }
  .equityChange {
    max-height: 200px;
    overflow-y: auto;
    > div {
      @include add-size($font_size_14);
      @include flex-box;
      justify-content: flex-start;
      color: #fff;
      padding: 10px 20px;
      &:nth-child(odd) {
        background: #252e3b;
      }
      &:first-child {
        color: $defaultColor;
      }
      > span {
        width: 20%;
        text-align: left;
      }
    }
  }
}
.companyInfo {
  position: relative;
  height: 100%;
  .nodate {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>
