<template>
  <div class="help-center">
    <el-dialog
      class="help-center-dialog"
      theame
      center
      title
      :visible="remindMask"
      width="400px"
      top="8%"
      :modal="false"
      :show-close="false"
      @close="closeRemind"
    >
      <div class="title" slot="title">提醒设置</div>
      <div class="hepl-content" v-if="stockData">
        <div class="stock-info">
          <div class="stoName">
            <div class="name-t">{{ stockData.name }}</div>

            <span v-if="stockData.symbol" class="stock-code">
              <span class="hk-icon">HK</span>
              {{ stockData.symbol.slice(2) }}
            </span>
          </div>
          <div
            class="stoPrice"
            :class="
              stockData.chg > 0
                ? 'upColor'
                : stockData.chg < 0
                ? 'downColor'
                : 'drayColor'
            "
          >
            <span>{{ stockData.last }}</span>
            <div v-if="stockData.chg > 0">
              <span>
                <img src="@/assets/images/hq/up.png" alt />
              </span>
              <span class="price">+{{ stockData.chg }}</span>
              <span class="price">+{{ stockData.chg_pct }}%</span>
            </div>
            <div v-else-if="stockData.chg < 0">
              <span>
                <img src="@/assets/images/hq/down.png" alt />
              </span>
              <span class="price">{{ stockData.chg }}</span>
              <span class="price">{{ stockData.chg_pct }}%</span>
            </div>
            <div v-else>
              <span></span>
              <span class="price">+{{ stockData.chg }}</span>
              <span class="price">+{{ stockData.chg_pct }}%</span>
            </div>
          </div>
        </div>
        <div class="setting-form">
          <el-form label-width="110px" label-position="left">
            <el-form-item
              :label="item.label"
              v-for="(item, i) in settimgForm"
              :key="i"
            >
              <div class="label" slot="label">
                <div
                  class="label-radio"
                  :class="{ active: item.active }"
                  @click="item.active = !item.active"
                ></div>
                <div class="label-text">{{ item.label }}</div>
              </div>
              <el-input
                type="number"
                :value="item.value"
                v-model="item.value"
                @blur="inputBlur"
                @change="getValue($event, item)"
              ></el-input>
              {{ item.unit }}
            </el-form-item>

            <div class="frequency-t">提醒频率</div>
            <div class="frequency-ul">
              <div class="frequency-li" @click="changeNotifyType(0)">
                <div
                  class="label-radio"
                  :class="{ active: notifyType == 0 }"
                ></div>
                只提醒一次
              </div>
              <div class="frequency-li" @click="changeNotifyType(1)">
                <div
                  class="label-radio"
                  :class="{ active: notifyType == 1 }"
                ></div>
                每日提醒一次
              </div>
              <div class="frequency-li" @click="changeNotifyType(2)">
                <div
                  class="label-radio"
                  :class="{ active: notifyType == 2 }"
                ></div>
                每次都提醒
              </div>
            </div>
            <div class="action-btn">
              <el-button hover class="cancel" @click="close">取消</el-button>
              <el-button hover class="confirm" @click="addNotice"
                >确定</el-button
              >
            </div>
          </el-form>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      remindMask: false,
      settimgForm: [
        {
          active: false,
          label: "股价涨到",
          unit: "港元",
          type: 0,
          value: ""
        },
        {
          active: false,
          label: "股价跌到",
          unit: "港元",
          type: 1,
          value: ""
        },
        {
          active: false,
          label: "日涨超幅",
          unit: "%",
          type: 2,
          value: ""
        },
        {
          active: false,
          label: "日涨跌幅",
          unit: "%",
          type: 3,
          value: ""
        },
        {
          active: false,
          label: "5分钟涨幅超",
          unit: "%",
          type: 4,
          value: ""
        },
        {
          active: false,
          label: "5分钟跌幅超",
          unit: "%",
          type: 5,
          value: ""
        },
        {
          active: false,
          label: "成交量超",
          unit: "万股",
          type: 6,
          value: ""
        }
      ],
      notifyType: 0,
      oldValue: ["", "", "", "", "", "", ""],
      stockData: {}
    };
  },
  props: {
    // stockData: Object
  },
  watch: {
    settimgForm: {
      deep: true,
      handler(v, m) {
        this.linkOldValue();
      },
    },
    "$store.state.system.remindData.show"(val) {
      if (val) {
        this.stockData = this.$store.state.system.remindData.stock;
        this.show();
      } else {
        this.close();
      }
    }
  },
  methods: {
    closeRemind() {
      this.$store.dispatch("SET_REMIND_DATA", { show: false });
    },
    getValue(e, item) {
      // console.log(e)
      item.value = e;
    },
    inputBlur() {
      // console.log(this.settimgForm);
      this.settimgForm.forEach((a, b) => {
        if (a.value > 0 && a.type < 2) {
          a.value = Number(a.value).toFixed(3);
        }
        if (a.value > 0 && a.type >= 2 && a.type < 5) {
          a.value = Number(a.value).toFixed(2);
        }
      });
    },
    initData() {
      this.settimgForm.forEach(a => {
        a.value = "";
        a.active = false;
      });
      this.getNotice();
    },
    changeNotifyType(val) {
      this.notifyType = val;
    },
    linkOldValue() {
      this.settimgForm.forEach((a, b) => {
        if (a.value == "") {
          a.active = false;
        } else {
          a.active = true;
        }
      });
    },
    addNotice() {
      //
      let stockData = this.stockData;
      let stockCode = this.stockData.symbol;
      let notifyType = this.notifyType;
      let condition = [];
      this.settimgForm.forEach(a => {
        if (a.active) {
          let value = Number(a.value);
          let o = {
            condition_type: a.type,
            condition_value: value
          };
          condition.push(o);
        }
      });
      let con = 0;
      condition.forEach(b => {
        if (b.condition_type == 0) {
          if (b.condition_value <= Number(stockData.last)) {
            this.$message({
              message: "涨额要大于当前价格",
              type: "error"
            });
            con++;
          }
        }
        if (b.condition_type == 1) {
          if (b.condition_value >= Number(stockData.last)) {
            this.$message({
              message: "跌额要小于当前价格",
              type: "error"
            });
            con++;
          }
        }
      });
      console.log(condition, "条件");
      if (con > 0) {
        return false;
      }
      if (condition.length <= 0) {
        // this.$message({
        //   message: "请设置提醒条件",
        //   type: "error"
        // });
        // return false;
      }
      this.axios({
        url: "/quote-service/api/v1/stocknotify",
        method: "post",
        params: {
          stock_code: stockCode,
          notify_type: notifyType,
          condition: condition
        },
        type: "HQ"
      }).then(res => {
        this.$message({
          message: "设置成功！",
          type: "success"
        });
        this.close();
      });
    },
    getNotice() {
      let stockCode = this.stockData.symbol;
      this.axios({
        url: "/quote-service/api/v1/stocknotify",
        params: {
          stock_code: stockCode
        },
        type: "HQ"
      }).then(res => {
        let data = res.data.Data;
        this.notifyType = data.notify_type;
        this.settimgForm.forEach(a => {
          if (data.condition) {
            data.condition.forEach(b => {
              if (a.type == b.condition_type) {
                if (String(b.condition_value).indexOf(".") != -1) {
                  a.value = Number(b.condition_value).toFixed(3);
                } else {
                  a.value = b.condition_value;
                }
                a.active = true;
              }
            });
          }
        });
      });
    },
    close(e) {
      this.remindMask = false;
    },
    show() {
      this.remindMask = true;
      this.initData();
    }
  }
};
</script>

<style lang="scss" scoped>
.help-center {
  .help-center-dialog {
    /deep/ .el-dialog {
      border-radius: 4px;
      overflow: hidden;
      .el-dialog__header {
        background-color: inherit;
        height: 56px;
      }
      .title {
        @include add-size($font_size_16);
        color: $white;
        line-height: 56px;
        text-align: center;
      }
      .hepl-content {
        padding: 0 30px;
        .stock-info {
          @include flex-center;
        }
        .stoName {
          width: 96px;
          .name-t {
            color: #fff;
            margin-bottom: 7px;
            @include add-size($font_size_14);
          }
          .stock-code {
            @include add-size($font_size_12);
            color: $defaultColor;
            .hk-icon {
              background: #fa541c;
              padding-right: 2px;
              color: #fff;
            }
          }
        }
        .stoPrice {
          padding-bottom: 12px;
          @include flex-center;
          width: 242px;
          > span {
            @include add-size($font_size_20);
            padding-left: 0;
            font-weight: bold;
          }
          > div {
            @include flex-center;
            > span {
              @include add-size($font_size_16);
              padding: 0 3px;
              &:first-child {
                display: inline-block;
                > img {
                  width: 12px;
                }
              }
            }
            .price {
              width: 68px;
              display: inline-block;
              text-align: right;
            }
          }
        }
        .upColor {
          color: $red;
        }
        .downColor {
          color: $green;
        }
        .drayColor {
          color: #eee;
        }
        .setting-form {
          margin-top: 10px;
          /deep/ .el-form {
            color: $defaultColor;
            .label {
              color: $defaultColor;
              @include flex-center;
            }
            .label-radio {
              width: 12px;
              height: 12px;
              margin-right: 8px;
              border: 1px solid #767a92;
              border-radius: 4px;
              cursor: pointer;
              position: relative;
              &.active {
                border: 1px solid $activeColor;
                &::before {
                  content: "";
                  position: absolute;
                  width: 6px;
                  height: 6px;
                  left: 50%;
                  top: 50%;
                  margin-left: -3px;
                  margin-top: -3px;
                  border-radius: 2px;
                  background-color: $activeColor;
                }
              }
            }
            .el-form-item {
              margin-bottom: 6px;
            }
            .el-input {
              width: 190px;
              .el-input__inner {
                background-color: inherit;
                border: none;
                border-bottom: 1px solid #576373;
                border-radius: 0;
                height: 24px;
                line-height: 24px;
                color: $white;
              }
            }
            .notice-t {
              @include flex-center;
              margin-bottom: 24px;
              margin-top: 14px;
            }
            .frequency-t {
              margin-bottom: 24px;
            }
            .frequency-ul {
              @include flex-center;
              justify-content: space-between;
              .frequency-li {
                cursor: pointer;
                @include flex-center;
                &:hover {
                  color: $activeColor;
                }
              }
            }
            .action-btn {
              @include flex-center;
              margin-top: 20px;
              justify-content: flex-end;
              height: 45px;
              border-top: 1px solid #767a92;
              .el-button {
                width: 58px;
                height: 24px;
                line-height: 24px;
                border-radius: 4px;
                border: none;
                padding: 0;
                @include add-size($font_size_14);
              }
              .confirm {
                color: $white;
                @include linear;
              }
              .cancel {
                border: 1px solid #767a92;
                color: $defaultColor;
                background-color: inherit;
              }
            }
          }
        }
      }
    }
  }
}
</style>
