<template>
  <div class="personal-info">
    <el-dialog
      class="personal-info-dialog"
      theame
      center
      title
      :visible="infoMask"
      width="500px"
      top="15%"
      :close-on-click-modal="false"
      :before-close="saveInfo"
    >
      <div class="info-content">
        <div class="dialog-title">个人资料</div>
        <label for="photo" class="photo-box">
          <el-image class="photo" :src="infoForm.src || infoForm.picUrl"></el-image>
          <div class="hover-mask">修改头像</div>
          <imgUpload :id="'photo'" @change="getPhoto" v-if="infoMask" />
        </label>
        <el-form :model="infoForm">
          <el-form-item label="昵称">
            <el-input v-model="infoForm.customerName" maxlength="20" placeholder="还没有设置签名"></el-input>
          </el-form-item>
          <el-form-item label="个人简介">
            <el-input v-model="infoForm.personSign" placeholder="一句话介绍自己" maxlength="40"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import imgUpload from "@/components/common/img-upload";
import { uploadImg, getPersonalData } from "@/utils";

export default {
  name: "personal-info",
  components: { imgUpload },
  data() {
    return {
      infoMask: false,
      infoForm: {},
      hasChanged: false
    };
  },
  mounted() {},
  computed: {},
  watch: {
    infoForm: {
      // 深度监听infoForm数据是否有进行修改
      handler(val, old) {
        this.hasChanged = true;
      },
      deep: true
    }
  },
  methods: {
    initMask() {
      let info = this.$store.state.system.userInfo;
      let infoForm = {
        customerName: info.customerName,
        personSign: info.personSign,
        picUrl: info.picUrl,
        src: ""
      };
      this.infoForm = infoForm;
      setTimeout(() => {
        this.hasChanged = false;
      });
      this.originData = this.$store.state.system.userInfo;
    },
    getPhoto(e) {
      this.infoForm.file = e.file;
      this.infoForm.src = e.src;
    },
    saveInfo(e) {
      // 保存本次修改
      if (this.hasChanged) {
        let infoForm = this.infoForm;
        let _this = this;
        if (infoForm.src) {
          uploadImg({
            file: infoForm.file,
            type: 0,
            cb(res) {
              _this.editInfo(res.data.body);
            }
          });
        } else {
          this.editInfo();
        }
      } else {
        console.log("未做任何修改");
        this.close();
      }
    },
    editInfo(imgUrl) {
      let infoForm = this.infoForm;
      let originData = this.originData;
      let params = {};
      if (infoForm.customerName != originData.customerName) {
        params.customerName = infoForm.customerName;
      }
      if (infoForm.personSign != originData.personSign) {
        params.personSign = infoForm.personSign;
      }
      if (imgUrl) {
        params.picUrl = imgUrl;
      }
      this.axios({
        url: "/app/editCustomer.do",
        method: "post",
        params: params
      }).then(res => {
        this.close();
        // reLogin();
        getPersonalData();
      });
    },
    show() {
      this.infoMask = true;
      this.initMask();
    },
    close() {
      this.infoMask = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.personal-info {
  .personal-info-dialog {
    /deep/ .el-dialog {
      height: 420px;
      .info-content {
        .dialog-title {
          @include add-size($font_size_18);
          color: $white;
          text-align: center;
          line-height: 78px;
        }
        .photo-box {
          width: 80px;
          height: 80px;
          margin: 0 auto;
          position: relative;
          border-radius: 50%;
          cursor: pointer;
          display: block;
          .photo {
            width: 80px;
            height: 80px;
            border-radius: 50%;
          }
          &:hover {
            .hover-mask {
              opacity: 1;
            }
          }
          .hover-mask {
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            left: 0;
            top: 0;
            opacity: 0;
            transition: all 0.3s ease;
            background-color: rgba(0, 0, 0, 0.3);
            color: $white;
            text-align: center;
            line-height: 80px;
          }
        }
        .el-form {
          margin-top: 30px;
        }
        .el-form-item {
          @include flex-center;
          justify-content: center;
          margin-bottom: 30px;
          .el-form-item__label {
            padding-right: 27px;
            width: 90px;
            text-align: right;
            color: $defaultColor;
            line-height: 1;
          }
        }
        .el-form-item--medium .el-form-item__content,
        .el-form-item--medium .el-form-item__label {
          line-height: 1;
        }
        .el-input__inner {
          border: 1px solid $defaultColor;
          background-color: inherit;
          width: 240px;
          height: 30px;
          padding: 0 10px;
          border-radius: 6px;
          color: $white;
        }
      }
    }
  }
}
</style>
