<template>
  <div class="cur-deal">
    <div class="deal-title">今日成交（{{historyList.length}}笔）</div>
    <el-table
      :data="historyList"
      element-loading-text="loading"
      element-loading-background="rgba(0, 0, 0, 0)"
      :height="170"
      :empty-text="'暂无成交记录'"
      @row-contextmenu="selectRow"
    >
      <el-table-column prop="StockCode" label="股票代码" align="center">
        <template slot-scope="scope">
          <div>
            <span class="TipBg">HK</span>
            {{scope.row.StockCode}}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
      <el-table-column prop="OrderSide" label="方向" align="center">
        <template slot-scope="scope">
          <div
            :class="{fall: scope.row.OrderSide=='Buy',rise: scope.row.OrderSide=='Sell'}"
          >{{scope.row.OrderSide=='Buy'?'买入':'卖出'}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="AveragePrice" label="成交价" align="center">
        <template slot-scope="scope">
          <div>{{Number(scope.row.OrderPrice).toFixed(3)}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="FilledQty" label="成交量" align="center"></el-table-column>
      <el-table-column label="成交时间" align="center">
        <template slot-scope="scope">
          <div>{{(scope.row.DateTimes || scope.row.DateTime) | dateTimeString}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" width="820"></el-table-column>
    </el-table>
    <Contextmenu ref="Contextmenu" :requestConfig="requestConfig" />
  </div>
</template>

<script>
import stockTable from "@/components/deal/stock-table";
import Contextmenu from "@/components/deal/contextmenu";
export default {
  components: {
    stockTable,
    Contextmenu
  },
  props: {
    requestConfig: Object
  },
  data() {
    return {
      historyList: []
    };
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.system.dealBar.currentType;
    }
  },
  watch: {
    currentType() {
      this.getHistoryList();
    }
  },
  methods: {
    getHistoryList() {
      // let accountNo = this.$store.state.AccountLogin.accountData.LoginID;
      // console.log(this.requestConfig, "132木头人");
      // let now = new Date().Format("yyyy/MM/dd");
      // let start = now;
      // let end = now;
      let params = {};
      // if (this.currentType == 0) {
      //   params = {
      //     accountNo: this.requestConfig.account[this.currentType]
      //     // startDate: start,
      //     // endDate: end
      //   };
      // } else {
        params = {
          market: "hk",
          startDate: new Date().Format("yyyy-MM-dd"),
          endDate: new Date().Format("yyyy-MM-dd"),
          pageStart: 1,
          pageSize: 100
        };
      // }
      this.axios({
        url: this.requestConfig.curHistory[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.historyList = res.data.data || [];
      });
    },
    selectRow(row, column, event) {
      let distanceX = event.clientX;
      let distanceY = event.clientY;
      this.$refs.Contextmenu.show({
        left: distanceX,
        top: distanceY,
        type: "deal",
        row: row
      });
      this.closeContextmenu();
    },
    closeContextmenu() {
      let html = document.getElementsByTagName("html")[0];
      let _this = this;
      html.onclick = function() {
        if (_this.$refs.Contextmenu) {
          _this.$refs.Contextmenu.close();
        }
      };
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {
    this.getHistoryList();
  }
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类

.cur-deal {
  padding: 20px;

  /deep/ .el-table {
    margin-top: 12px;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $red;
      }
      .rise {
        color: $green;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
  }
}
</style>