<template>
  <div class="indexBody">
    <div class="hqRight">
      <div class="SdTop">
        <div class="SdTopOne">
          <div class="stoLeft">
            <div class="stoName">
              <span>HK</span>
              <span v-if="stockData.symbol">{{
                stockData.symbol.slice(2)
              }}</span>
              <span>{{ stockData.name }}</span>
            </div>
            <div
              class="stoPrice"
              :class="
                stockData.chg > 0
                  ? 'upColor'
                  : stockData.chg < 0
                  ? 'downColor'
                  : 'drayColor'
              "
            >
              <span>{{ stockData.last }}</span>
              <div v-if="stockData.chg > 0">
                <span>
                  <img src="@/assets/images/hq/up.png" alt />
                </span>
                <span class="price">+{{ stockData.chg }}</span>
                <span class="price">+{{ stockData.chg_pct }}%</span>
              </div>
              <div v-else-if="stockData.chg < 0">
                <span>
                  <img src="@/assets/images/hq/down.png" alt />
                </span>
                <span class="price">{{ stockData.chg }}</span>
                <span class="price">{{ stockData.chg_pct }}%</span>
              </div>
              <div v-else>
                <span></span>
                <span class="price">+{{ stockData.chg }}</span>
                <span class="price">+{{ stockData.chg_pct }}%</span>
              </div>
            </div>
            <div class="stoTime">
              {{ marketData[stockData.marketStatus] }}
              {{
                stockData.mkt_time.slice(4, 6) +
                  "." +
                  stockData.mkt_time.slice(6, 8)
              }}
              {{
                stockData.mkt_time.slice(8, 10) +
                  ":" +
                  stockData.mkt_time.slice(10, 12) +
                  ":" +
                  stockData.mkt_time.slice(12, 14)
              }}（北京时间）
            </div>
          </div>
        </div>
      </div>
      <div class="indexMark">
        <div class="sellTit">
          <div @click="trade">
            <img src="@/assets/images/hq/jy.png" alt />
            <span>交易</span>
          </div>
          <div>
            <span @click="linkMarket">
              <img src="@/assets/images/hq/hq.png" alt />
            </span>
            <span @click="showRemind">
              <img src="@/assets/images/hq/tx.png" alt />
            </span>
            <span @click="selfSelect">
              <img v-if="selfStock" src="@/assets/images/hq/szx.png" alt />
              <img v-else src="@/assets/images/hq/jzx.png" alt />
            </span>
          </div>
        </div>
        <div class="queryCon">
          <div>
            <div>
              <span>今开</span>
              <span>{{ stockData.open }}</span>
            </div>
            <div>
              <span>最高</span>
              <span style="color:#ff3b3b">{{ stockData.high }}</span>
            </div>
            <div>
              <span>成交额</span>
              <span>{{ stockData.amount | digitUnit }}</span>
            </div>
          </div>
          <div>
            <div>
              <span>昨收</span>
              <span>{{ stockData.hst_close }}</span>
            </div>
            <div>
              <span>最底</span>
              <span style="color:#00c360">{{ stockData.low }}</span>
            </div>
            <div>
              <span>成交量</span>
              <span>{{ stockData.volume | digitUnit }}</span>
            </div>
          </div>
        </div>
        <div class="chengfen">成分股</div>
        <div class="queryTab" ref="tableArea">
          <el-table
            :data="tableData"
            :row-class-name="tableRowClassName"
            :max-height="maxHeight"
            empty-text=" "
            @sort-change="changeSortable"
            @row-click="goDetail"
            @row-contextmenu="rowContextmenu"
          >
            <!-- fixed="left" -->
            <!-- <el-table-column label="序号" type="index" width="50" align="center"></el-table-column> -->
            <el-table-column
              prop="name"
              label="股票名称"
              align="left"
              width="80"
            >
              <template slot-scope="scope">
                <div class="code-name">
                  <div>{{ scope.row.name }}</div>
                  <div>
                    <span
                      style="background: #FA541C;font-size:10px;padding:2px;"
                      >HK</span
                    >
                    <span style="color:#9195AC;">
                      {{ scope.row.symbol.slice(2) }}
                    </span>
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="name" label align="center" width="100">
              <template slot-scope="scope">
                <!-- <div :class="'smallCharts' + scope.$index" style="height:40px;"></div> -->
                <sCharts v-if="scope.row.data" :sChartsData="scope.row.data" />
              </template>
            </el-table-column>
            <el-table-column
              prop="last"
              label="最新价"
              align="center"
              width="64"
            ></el-table-column>
            <el-table-column
              prop="chg_pct"
              sortable="custom"
              label="涨跌幅"
              align="center"
              width="86"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.chg > 0" class="red"
                  >+{{ scope.row.chg_pct }}%</span
                >
                <span v-else-if="scope.row.chg < 0" class="greed"
                  >{{ scope.row.chg_pct }}%</span
                >
                <span v-else class="gay">{{ scope.row.chg_pct }}%</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="chg"
              label="涨跌额"
              align="center"
              width="60"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.chg > 0" class="red"
                  >+{{ scope.row.chg }}</span
                >
                <span v-else-if="scope.row.chg < 0" class="greed">{{
                  scope.row.chg
                }}</span>
                <span v-else class="gay">{{ scope.row.chg }}</span>
              </template>
            </el-table-column>

            <infinite-loading
              v-if="loading"
              slot="append"
              @infinite="appendTable"
              :identifier="infiniteId"
              force-use-infinite-wrapper=".el-table__body-wrapper"
              spinner="bubbles"
            >
              <span slot="no-more" class="no-more"
                >—————— 加载完成 ———————</span
              >
              <span slot="no-results" class="no-result">暂无数据</span>
            </infinite-loading>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InfiniteLoading from "vue-infinite-loading";
import sCharts from "@/components/common/sCharts";

import {
  initMOption,
  initKOption,
  init5MOption,
  initMainOption
} from "@/utils/k-line";
export default {
  name: "index",
  components: {
    InfiniteLoading,
    sCharts
  },
  data() {
    return {
      show: false,
      showLarger: false,
      tableData: [],
      maxHeight: 800,
      loading: true,
      count: 0,
      type: 0,
      direction: 1,
      index: 1,
      infiniteId: 0,
      selfStock: false,
      marketData: [
        "交易中",
        "午间休市",
        "停牌",
        "已收盘",
        "早盘竞价",
        "收盘竞价",
        "待上市",
        "未开盘"
      ]
    };
  },
  computed: {
    stockData() {
      return this.$store.state.GlobalData.stockData.query;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.maxHeight = this.$refs.tableArea.offsetHeight;
      // this.getStockQuery(this.$store.state.GlobalData.currentStock.code);
      this.$store.dispatch("SHOW_STOCK_KLINE", true);
    });
  },
  activated() {
    this.$store.dispatch("SHOW_STOCK_KLINE", true);
    this.$store.dispatch("SHOW_INDEX_STOCK", true);
    this.$store.dispatch("SHOW_COMPANY_INFO", false);
    setTimeout(() => {
      console.log(this.$store.state.system.showCompanyInfo)
    }, 5000)
  },
  watch: {
    "$store.state.GlobalData.currentStock"(val) {
      if (this.$route.name === "indexStock") {
        this.infiniteId++;
        this.tableData = [];
        this.index = 1;
      }
    }
  },
  methods: {
    rowContextmenu(row, column, event) {
      // 列表右键菜单操作
      row.x = event.clientX;
      row.y = event.clientY;
      this.$refs.rightMenu.show(row);
    },
    // 获取快照数据
    getStockQuery(stockCode) {
      let url = "/quote-service/api/v1/stockquery";
      let data = {
        codelist: stockCode,
        delay: 0
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        this.selfStock = res.data.Data[0].selfStock;
        this.$store.dispatch("SET_STOCK_DATA", {
          query: res.data.Data[0]
        });
      });
    },
    //获取成分股数据
    appendTable(state) {
      let url = "/quote-service/api/v1/hk/componenstocksrange";
      let data = {
        code: this.$store.state.GlobalData.currentStock.code,
        count: 20,
        direction: this.direction,
        index: this.index,
        type: this.type,
        delay: 0
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        let data = res.data.Data || [];
        if (data.length > 0) {
          data.map((value, index) => {
            let dataset = {
              data: [],
              yestclose: 330
            };
            if (value.Data.length != 0) {
              dataset.yestclose = value.Data[0].Hst_close;
            }
            value.Data.map((val, i) => {
              let listAry = new Array();
              let t = new Date(val.Minute * 60 * 1000);
              listAry.push(t.getHours().toString() + t.getMinutes().toString());
              listAry.push(val.Price);
              listAry.push(val.Avg_price);
              listAry.push(val.Volume);
              dataset.data.push(listAry);
            });
            value.query.data = dataset;
            this.tableData.push(value.query);
          });
          state.loaded();
          this.index += 20;
        } else {
          state.complete();
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return "success-row";
      } else if (rowIndex % 2 === 1) {
        return "warning-row";
      }
      return "";
    },
    changeSortable(value) {
      console.log(value);
      if (value.order == null) {
        // 默认排序
        this.type = 0;
        this.direction = 1;
      } else if (value.order == "ascending") {
        //升序
        this.type = 0;
        this.direction = 0;
      } else {
        //降序
        this.type = 0;
        this.direction = 1;
      }
      this.tableData = [];
      this.infiniteId++;
      this.index = 1;
    },
    goDetail(row, column, event) {
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: row.symbol,
        recover: true
      });
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 0,
        timestamp: new Date().getTime()
      });
    },
    //交易
    trade() {
      let dealBar = this.$store.state.system.dealBar;
      if (dealBar.commonAccount || dealBar.organization) {
        this.$store.dispatch("SET_DEAL_BAR", { barShow: true });
      } else {
        this.$store.dispatch("SET_DEAL_BAR", { loginShow: true });
      }
    },
    linkMarket() {
      this.$router.push({ path: "wd", query: { target: "market" } });
    },
    showRemind() {
      this.$store.dispatch("SET_REMIND_DATA", {
        show: true,
        stock: this.stockData
      });
    },
    selfSelect() {
      // this.showImg = !this.showImg;
      let time = new Date().getTime();
      if (!this.selfStock) {
        this.selfData(1);
      } else {
        this.selfData(0);
      }
      this.$store.dispatch("CHANGE_SelfStatus_DATA", { timestamp: time });
    },
    //自选请求接口
    selfData(num) {
      let code = this.$store.state.GlobalData.currentStock.code
      let url = "/quote-service/api/v1/selfstock";
      let data = {
        code: code,
        opt: num // 1增加，0删除， 2 批量操作
      };
      this.axios({
        url: url,
        method: "post",
        params: data,
        type: "HQ"
      }).then(res => {
        this.getStockQuery(code);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.indexBody {
  @include flex-box;
  justify-content: space-between;
  position: relative;
  height: 100%;
  width: 100%;
  .zsList {
    width: calc(100% - 394px);
    height: 100%;
    // padding-right: 4px;
    position: relative;
  }
  .hqRight {
    width: 390px;
    height: 1002px;
    position: absolute;
    right: 0;
    top: -70px;
    z-index: 20;
    background: #1d242e;
    .SdTop {
      .SdTopOne {
        // @include flex-box;
        // justify-content: space-between;
        padding: 12px 10px 10px 10px;
        .stoLeft {
          // width: 300px;
          .stoName {
            padding: 12px 0;
            > span {
              @include add-size($font_size_20);
              padding: 0 8px;
              color: #fff;
              &:first-child {
                @include add-size($font_size_14);
                background: #fa541c;
                padding: 2px;
                position: relative;
                top: -2px;
              }
              &:nth-child(2) {
                color: #fff;
                @include add-size($font_size_16);
              }
            }
          }
          .stoPrice {
            padding-bottom: 12px;
            > span {
              @include add-size($font_size_20);
              padding-left: 0;
              font-weight: bold;
            }
            > div {
              display: -webkit-inline-box;
              > span {
                @include add-size($font_size_16);
                padding: 0 3px;
                &:first-child {
                  width: 12px;
                  display: inline-block;
                  > img {
                    width: 100%;
                  }
                }
              }
              .price {
                position: relative;
                top: -1px;
              }
            }
          }
          .upColor {
            color: $red;
          }
          .downColor {
            color: $green;
          }
          .drayColor {
            color: #eee;
          }
          .stoTime {
            @include add-size($font_size_12);
            color: #fff;
          }
        }
      }
      .SdTopTwo {
        @include flex-box;
        justify-content: space-between;
        > div {
          @include add-size($font_size_14);
          width: 150px;
          height: 30px;
          text-align: center;
          border-radius: 4px;
          cursor: pointer;
          > span {
            color: #fff;
          }
          > img {
            width: 15%;
            position: relative;
            top: 3px;
          }
          &:first-child {
            background: $red;
          }
          &:nth-child(2) {
            background: $green;
          }
          &:last-child {
            background: $activeColor;
          }
        }
        /deep/.el-dropdown {
          > span {
            width: 150px;
            height: 30px;
            display: inline-block;
            color: #fff;
            > i {
              > img {
                width: 15%;
                position: relative;
                top: 3px;
              }
            }
          }
        }
      }
    }
    .indexMark {
      .sellTit {
        @include flex-box;
        justify-content: flex-start;
        padding: 0 10px;
        > div {
          &:first-child {
            @include add-size($font_size_14);
            background: $activeColor;
            width: 60px;
            height: 20px;
            line-height: 20px;
            text-align: center;
            border-radius: 4px;
            cursor: pointer;
            > span {
              color: #fff;
            }
            > img {
              width: 25%;
              position: relative;
              top: 1px;
            }
          }
          &:last-child {
            padding-left: 16px;
            > span {
              padding: 0 5px;
              cursor: pointer;

              > img {
                width: 13%;
              }
            }
          }
        }
      }
      .queryCon {
        padding: 5px 10px;
        > div {
          @include flex-box;
          justify-content: space-between;
          > div {
            width: calc(100% / 3);
            @include flex-box;
            justify-content: space-between;
            padding: 5px 15px;
          }
        }
      }
      .chengfen {
        @include add-size($font_size_14);
        color: $activeColor;
        padding-left: 10px;
        font-weight: bold;
      }
      .queryTab {
        height: 800px;
        /deep/.el-table {
          @include add-size($font_size_12);
          background: #1d242e;
          .cell {
            white-space: nowrap;
            color: #fff;
            padding: 0;
          }
          .el-table__empty-block {
            background: #1d242e;
          }
          .warning-row {
            background: #1d242e;
          }
          .success-row {
            background: #252e3b;
          }
          th {
            background: #1d242e;
            border: none;
            // text-align: center;
          }
          td {
            border: none;
            padding: 5px 0;
            // text-align: center;
          }
          tr:hover > td {
            background: rgb(78, 77, 77);
          }
          tr.hover-row > td {
            background-color: rgb(78, 77, 77);
          }
          ::before {
            height: 0;
          }
          .greed {
            color: $green;
          }
          .red {
            color: $red;
          }
          .gray {
            color: #666;
          }
          .code-name {
            padding-left: 10px;
          }
        }
        /deep/.el-table::before {
          height: 0;
        }
      }
    }
  }
}
</style>
