<template>
  <div class="main-sy">
    <card-box class="index" :headType="true">
      <!-- <template v-slot:header>
        <span
          :class="{ active: point.active }"
          v-for="point in exponentData"
          :key="point.id"
          @click="changePoint(point)"
        >{{ point.name }}</span>
      </template>-->
      <template v-slot:content v-if="exponentData[0].data">
        <div class="zsIndex">
          <div class="exponent" ref="exponent"></div>
          <div class="indexContent">
            <div class="conTxt">
              <div v-for="(item, index) in exponentData" :key="index">
                <div
                  v-show="item.active"
                  class="indexCon"
                  :class="
                    item.data.chg > 0
                      ? 'upColor'
                      : item.data.chg < 0
                      ? 'downColor'
                      : 'color'
                  "
                >
                  <span>{{ Math.floor(item.data.last * 100) / 100 }}</span>
                  <div class="indexTop">
                    <span v-if="item.data.chg > 0"
                      >+{{ Math.floor(item.data.chg * 100) / 100 }}</span
                    >
                    <span v-else>{{
                      Math.floor(item.data.chg * 100) / 100
                    }}</span>
                    <span v-if="item.data.chg > 0"
                      >+{{ item.data.chg_pct }}%</span
                    >
                    <span v-else>{{ item.data.chg_pct }}%</span>
                  </div>
                </div>
              </div>
              <div class="indexBtn">
                <span
                  :class="{ active: point.active }"
                  v-for="point in exponentData"
                  :key="point.id"
                  @click="changePoint(point)"
                  >{{ point.name }}</span
                >
              </div>
            </div>
          </div>
        </div>

        <!-- <canvas width="578" height="246"></canvas> -->
      </template>
    </card-box>
    <card-box
      :more="true"
      :listData="selectList"
      :type="2"
      :cardType="'selfStock'"
      @more="subBlock(0)"
      @rowClick="linkSelfSelect($event, 0)"
      @contextmenu="contextmenu"
    >
      <template v-slot:header>
        <div class="label">自选</div>
        <!-- <div class="add-self" @click="subBlock(0)">
          <i class="el-icon-circle-plus-outline"></i> 添加自选
        </div>-->
      </template>
      <template v-slot:content v-if="selectList.length <= 0 && isReqest">
        <div class="add-self">
          <!-- <i class="el-icon-circle-plus-outline"></i> 添加自选 -->
          <img
            class="no-select-icon"
            src="~@/assets/images/hq/no-select.png"
            alt
          />
          <div class="no-select-intr">暂无美股自选股票</div>
          <div class="no-select-intr2">快去添加几只心仪的股票吧</div>
        </div>
      </template>
    </card-box>
    <card-box
      :more="true"
      label="全部美股"
      :listData="listData2"
      :type="2"
      @more="linkMore(0)"
      @rowClick="rowClick($event, 0)"
    ></card-box>

    <card-box
      :more="true"
      label="ETF"
      :listData="listData4"
      :type="2"
      @more="linkMore(1)"
      @rowClick="rowClick($event, 1)"
      @contextmenu="contextmenu"
    ></card-box>
    <card-box
      :more="true"
      label="中概股"
      :listData="listData5"
      :type="2"
      @more="linkMore(2)"
      @rowClick="rowClick($event, 2)"
      @contextmenu="contextmenu"
    ></card-box>
    <card-box
      :more="true"
      label="热门行业"
      :listData="listData3"
      :type="3"
      @more="linkMore(3, 'industry')"
      @rowClick="rowClick($event, 3, 'industry')"
    ></card-box>
    <card-box
      :more="true"
      :listData="listData6"
      :type="2"
      @more="linkMore(4, 'five')"
      @rowClick="rowClick($event, 4, 'five')"
      @contextmenu="contextmenu"
    >
      <template v-slot:header>
        <span
          :class="{ active: sort.active }"
          v-for="sort in sortBank"
          :key="sort.id"
          @click="changeSort(sort)"
          >{{ sort.name }}</span
        >
      </template>
    </card-box>
    <card-box
      :more="true"
      :listData="listData7"
      :type="2"
      @more="sortMarketMore"
      @rowClick="sortMarketRow($event)"
      @contextmenu="contextmenu"
    >
      <template v-slot:header>
        <span
          :class="{ active: sort.active }"
          v-for="sort in sortMarket"
          :key="sort.target"
          @click="sortMarketSelect(sort)"
          >{{ sort.name }}</span
        >
      </template>
    </card-box>
    <!-- <card-box
      :more="true"
      label="盘后榜"
      :listData="listData1"
      :type="2"
      @more="linkMore(6)"
      @rowClick="rowClick($event, 6)"
      @contextmenu="contextmenu"
    ></card-box>-->
    <!-- <rightMenu ref="rightMenu" @setSelect="addSelf" /> -->
  </div>
</template>

<script>
import cardBox from "./card-box_us";
import { initIndexMOption } from "@/utils/k-line";
import { initSocket } from "@/utils/index.js";
// import rightMenu from "@/components/popup/right-menu";

export default {
  components: {
    cardBox
    // rightMenu
  },
  data() {
    return {
      exponentData: [
        {
          name: "道琼斯ETF",
          data: "",
          active: true
        },
        {
          name: "标普500ETF",
          data: "",
          active: false
        },
        {
          name: "纳指100ETF",
          data: "",
          active: false
        }
      ],
      sortBank: [
        {
          name: "五分钟涨幅榜",
          active: true,
          type: 0,
          direction: 1,
          value: "five00"
        },
        {
          name: "五分钟跌幅榜",
          active: false,
          type: 0,
          direction: 0,
          value: "five01"
        },
        {
          name: "五分钟振幅榜",
          active: false,
          type: 1,
          direction: 1,
          value: "five10"
        }
      ],
      sortMarket: [
        {
          name: "盘前榜",
          active: true,
          type: 0,
          target: 5,
          value: "premarket"
        },
        {
          name: "盘后榜",
          active: false,
          type: 0,
          target: 6,
          value: "aftmarket"
        }
      ],
      listData1: [],
      listData2: [],
      listData3: [],
      listData4: [],
      listData5: [],
      listData6: [],
      listData7: [],
      listData8: [],
      selectList: [], // 自选列表
      hqList: 0,
      index: 1,
      stockCode: "usDIA",
      indexType: "us", // hk 港股 us 美股  sz 沪深
      dataset: {
        data: [],
        yestclose: 330
      },
      mChart: "",
      isReqest: false, // 请求后再显示结果
      requestTime: 0
    };
  },
  mounted() {
    this.$nextTick(() => {
      // setTimeout(() => {
      this.getIndex("usDIA,usIVV,usQQQ");
      // this.drawChart(0)
      this.repeatRequest();
      // }, 500);
      // setTimeout(() => {
      //   this.exponentData.map((value, index) => {
      //     if (value.active) {
      //       this.changePoint(value);
      //     }
      //   });
      // }, 1000);
    });
  },
  activated() {
    if (this.listData2.length > 0) {
      this.getIndex("usDIA,usIVV,usQQQ");
      this.exponentData.map((value, index) => {
        if (value.active) {
          this.changePoint(value);
        }
      });
    }
    this.isAlive = true;
  },
  deactivated() {
    this.isAlive = false;
    delete window.uChart;
  },
  methods: {
    sortMarketRow(e) {
      let sort = this.sortMarket.filter(a => {
        return a.active;
      })[0];
      this.rowClick(e, sort.target);
    },
    sortMarketMore(e) {
      let sort = this.sortMarket.filter(a => {
        return a.active;
      })[0];
      this.linkMore(sort.target);
    },
    sortMarketSelect(sort) {
      this.sortMarket.forEach(a => {
        a.active = false;
      });
      sort.active = true;
      this.appendTable(
        {
          instrument: sort.value //盘前榜
        },
        res => {
          let data = res.data.Data;
          if (data && data.length > 0) {
            this.listData7 = this.resetCanvasList(data);
          } else {
            this.listData7 = [];
          }
        }
      );
    },
    contextmenu(e, li) {
      // console.log(e, li)
      li.x = e.x;
      li.y = e.y;
      // this.$refs.rightMenu.show(li);
      this.$store.dispatch("SET_RIGHT_MENU", {
        show: true,
        target: li
      });
    },
    linkSelfSelect(li) {
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 0,
        timestamp: new Date().getTime()
      });
      let code = li.StockCode || li.symbol || li.query.symbol;
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: code,
        recover: true
      });
    },
    rowClick(li, b, c) {
      let code = li.StockCode || li.symbol || li.query.symbol;
      if (code.length > 7) {
        // item.query = '90'
        this.$router.push({
          path: "zs"
        });
        setTimeout(() => {
          this.$store.dispatch("SET_CURRENT_STOCK", {
            code: code,
            recover: true
          });
          // 延迟是为了确保要监听股票代码改变的组件加载完成
        }, 500);
        return;
      }
      let sort = c;
      if (c == "five") {
        this.sortBank.forEach(m => {
          if (m.active) {
            sort = m.value;
          }
        });
      } else if (c == "industry") {
        sort = li.IndustryId;
        // console.log(li);
      }
      this.linkMore(b, sort, 1);

      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: code,
        recover: true
      });
    },
    repeatRequest() {
      let timer = setTimeout(() => {
        if (!this.exponentData[0].data) {
          this.getIndex("usDIA,usIVV,usQQQ");
          this.repeatRequest();
        }
      }, 8000);
    },
    changePoint(p) {
      this.exponentData.forEach(a => (a.active = false));
      p.active = true;
      this.stockCode = p.data.symbol;
      this.drawChart();
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: p.data.symbol
      });
      // this.getIndex(p.stockCode)
    },
    changeSort(s) {
      this.sortBank.forEach(a => (a.active = false));
      s.active = true;
      this.fiveMinuteList(s);
    },
    // 获取指数数据
    getIndex(code) {
      let url = "/quote-service/api/us/v1/stockquery";
      let data = {
        codelist: code
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        // console.log(res);
        this.exponentData.map((value, index) => {
          value.data = res.data.Data[index];
        });
        this.$nextTick(() => {
          this.drawChart();
        });
        // console.log(this.exponentData);
        this.getListQueue();
        this.addSelf();
        this.exponentData.map((value, index) => {
          if (value.active) {
            this.changePoint(value);
          }
        });
      });
    },
    drawMinute() {
      //订阅事件a
      let dataset = this.dataset;
      // console.log(data, '订阅者模式');
      // var time = data.Minute
      let dataQuery = this.$store.state.GlobalData.minutelyCharts.data.query;
      this.exponentData.map((value, index) => {
        if (value.active && value.data.symbol === dataQuery.symbol) {
          value.data = dataQuery;
        }
      });

      let minutely =
        this.$store.state.GlobalData.minutelyCharts.data.minutely || [];

      minutely.forEach(data => {
        var t = new Date(data.Minute * 60 * 1000);
        var time = t.getHours().toString() + t.getMinutes().toString();
        // console.log(dataset)
        // console.log(dataset.data[dataset.data.length - 1][0])
        if (
          dataset.data.length > 0 &&
          time == dataset.data[dataset.data.length - 1][0]
        ) {
          // console.log("时间相等");
          dataset.data[dataset.data.length - 1][1] = data.Price;
          dataset.data[dataset.data.length - 1][2] = data.Avg_price;
          dataset.data[dataset.data.length - 1][3] = data.Volume;
          // console.log(dataset);
        } else {
          var curData = new Array();
          curData.push(time);
          curData.push(data.Price);
          curData.push(data.Avg_price);
          curData.push(data.Volume);
          dataset.data.push(curData);
        }
      });
      window.uChart.setOption(initIndexMOption(dataset, "us"));
    },
    // 绘制图表
    drawChart() {
      this.dataset.data = [];
      let dataset = this.dataset;
      let url = "/quote-service/api/us/v1/minutely";

      window.uChart = this.$echarts.init(this.$refs.exponent);

      this.axios({
        url: url,
        method: "get",
        params: {
          code: this.stockCode,
          day: 1,
          delay: 0
        },
        type: "HQ"
      })
        .then(data => {
          // console.log(data, 116);
          let stockData = data.data.query;
          let newData = data.data.Data;
          if (newData.length <= 0) {
            dataset.yestclose = stockData.hst_close;
            window.uChart.setOption(initIndexMOption(dataset, "us"));
            return false;
          }
          dataset.yestclose = newData[0].Hst_close;
          newData.map(function(value, index) {
            var listAry = new Array();
            var t = new Date(value.Minute * 60 * 1000);
            listAry.push(t.getHours().toString() + t.getMinutes().toString());
            listAry.push(value.Price);
            listAry.push(value.Avg_price);
            listAry.push(value.Volume);
            dataset.data.push(listAry);
          });
          stockData.data = dataset;
          // bus.$emit('data', stockData)
          window.uChart.setOption(initIndexMOption(dataset, "us"));
        })
        .catch(function(err) {
          console.log(err);
        });
      // this.initSocket(dataset, mChart);
      window.onresize = null;
      window.onresize = () => {
        setTimeout(() => {
          window.uChart.resize();
        }, 1000);
      };
    },
    linkMore(b, c, sec) {
      // a: 菜单下标  b: a 菜单下子类下标 c: b菜单子类的value
      // 根据flag不同跳转到对应页面
      // console.log(flag);
      // console.log(this.$route);
      let a = 2;
      let second = sec ? sec * 1000 : 1000 * 11;
      let sort = c;
      if (c == "five") {
        this.sortBank.forEach(m => {
          if (m.active) {
            sort = m.value;
          }
        });
      }
      if (c == "industry") {
        sort = this.listData3[0].IndustryId;
      }
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a,
        b,
        c: sort,
        timestamp: new Date().getTime() - second
      });
    },
    subBlock(a) {
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a,
        timestamp: new Date().getTime() - 1000 * 11
      });
    },
    addSelf() {
      // 添加自选
      this.isReqest = false;
      this.axios({
        url: "/quote-service/api/v1/selfstock",
        params: {},
        type: "HQ"
      }).then(res => {
        this.isReqest = true;
        let data = res.data.Data || [];
        let da = data.filter(a => {
          return a.query.symbol.indexOf("us") !== -1;
        });
        this.selectList = this.resetCanvasList(da).slice(0, 4);
      });
    },
    resetCanvasList(data) {
      let list = [];
      data.map((value, index) => {
        let dataset = {
          data: [],
          yestclose: 330
        };
        if (value.Data.length != 0) {
          dataset.yestclose = value.query.hst_close;
        }
        value.Data.map((val, i) => {
          let listAry = new Array();
          let t = new Date(val.Minute * 60 * 1000);
          listAry.push(t.getHours().toString() + t.getMinutes().toString());
          listAry.push(val.Price);
          listAry.push(val.Avg_price);
          listAry.push(val.Volume);
          dataset.data.push(listAry);
        });
        value.query.data = dataset;
        list.push(value.query);
      });
      return list;
    },
    industryList() {
      this.axios({
        url: "/quote-service/api/us/v1/industry",
        method: "get",
        params: {
          count: 7
        },
        type: "HQ"
      }).then(res => {
        // console.log(res);
        let data = res.data.Data;
        if (data.length > 0) {
          this.listData3 = data;
        }
      });
    },
    fiveMinuteList(query) {
      // 五分钟榜单
      let url = "/quote-service/api/us/v1/stockyidong";
      let data = {
        direction: query.direction, //0 是 5分钟涨跌幅， 1是 5分钟振幅， type 为2 获取首页异动数据
        type: query.type, //type为0按涨跌幅排序，为1按价格排序
        count: 4 //一次获取股票的数量
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        // console.log(res);
        let data = res.data.Data;
        if (data.length > 0) {
          this.listData6 = this.resetCanvasList(data);
        }
      });
    },
    getListQueue() {
      this.appendTable(
        {
          instrument: "allus" //全部美股
        },
        res => {
          let data = res.data.Data;
          if (data.length > 0) {
            this.listData2 = this.resetCanvasList(data);
          }
        }
      );
      this.appendTable(
        {
          instrument: "premarket" //盘后榜
        },
        res => {
          let data = res.data.Data;
          if (data.length > 0) {
            this.listData7 = this.resetCanvasList(data);
          }
        }
      );

      this.appendTable(
        {
          instrument: "etf" //ETF
        },
        res => {
          let data = res.data.Data;
          if (data.length > 0) {
            this.listData4 = this.resetCanvasList(data);
            localStorage.setItem(
              "firstStock",
              JSON.stringify(res.data.Data[0].query)
            );
          }
        }
      );
      this.appendTable(
        {
          instrument: "china" //中概股
        },
        res => {
          let data = res.data.Data;
          if (data.length > 0) {
            this.listData5 = this.resetCanvasList(data);
          }
        }
      );
      this.changeSort(this.sortBank[0]);
      this.industryList();
    },
    appendTable(query, cb) {
      let url = "/quote-service/api/us/v1/range";
      let data = {
        direction: 1, //1涨幅排行，0跌幅排行 或价格大小排序
        index: 1, //当前最后一只股票索引+1
        type: 0, //type为0按涨跌幅排序，为1按价格排序
        count: 4, //一次获取股票的数量
        ...query
      };
      // if (this.hqList == 0) {
      //   data.instrument = "EQTY"; //全部
      // } else if (this.hqList == 1) {
      //   data.market = "MAIN"; //主板
      //   console.log(data);
      // } else if (this.hqList == 2) {
      //   data.market = "GEM"; //创业板
      // }
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        // console.log(res);
        cb(res);
      });
    }
  },
  watch: {
    $route(route) {
      this.indexType = route.query.index;
    },
    "$store.state.GlobalData.stockData.minutely"(val) {
      if (this.dataset.data.length > 0 && this.isAlive) {
        this.drawMinute();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.main-sy {
  width: 100%;
  height: 100%;
  background-color: $blockBgColor;
  padding: 27px 0 0 20px;
  /deep/ .card-box {
    // margin-right: 20px;
    margin-bottom: 20px;
    // &:nth-child(3n){
    //   margin-right: 0;
    // }
    &.index {
      .card-title {
        background-color: inherit;
      }
      .card-content {
        .zsIndex {
          @include flex-box;
          justify-content: space-between;
          .exponent {
            width: 80%;
            height: 278px;
            // top: 10px;
          }
          .indexContent {
            width: 20%;
            background: $subBlockColor;
            position: relative;
            .conTxt {
              @include position-center;
              .indexCon {
                padding: 0 10px;
                > span {
                  @include add-size($font_size_16);
                  padding: 7px 0;
                  display: block;
                  text-align: center;
                }
                > .indexTop {
                  @include flex-box;
                  justify-content: space-between;
                  @include add-size($font_size_12);
                  padding: 7px 0;
                  > span {
                    width: 50%;
                    &:first-child {
                      padding-right: 5px;
                    }
                  }
                }
              }
              .upColor {
                color: $red;
              }
              .downColor {
                color: $green;
              }
              .indexBtn {
                text-align: center;
                .active {
                  background: $activeColor;
                  color: $white;
                }
                > span {
                  width: 80px;
                  cursor: pointer;
                  display: inline-block;
                  border: 1px solid $activeColor;
                  padding: 5px 9px;
                  margin: 5px 0;
                  border-radius: 4px;
                  color: $activeColor;
                }
              }
            }
          }
        }
      }
    }
    .card-title {
      span {
        margin-right: 33px;
        cursor: pointer;
        &.active {
          color: $activeColor;
        }
      }
    }
    .add-self {
      // cursor: pointer;
      // @include flex-center;
      padding-top: 50px;
      // justify-content: center;
      line-height: 24px;
      text-align: center;
      @include add-size($font_size_16);
      .no-select-icon {
        width: 180px;
      }
      // &:hover {
      //   color: $activeColor;
      // }
      i {
        margin-right: 5px;
      }
    }
  }
  @include flex-box;
  flex-wrap: wrap;
}
</style>
