<template>
  <div class="cur-entrust">
    <div class="deal-title entrust">今日委托（{{curEntrust.length}}笔）</div>
    <!-- @row-contextmenu="selectRow" -->
    <el-table :data="curEntrust" empty-text="暂无委托记录" :height="170">
      <el-table-column prop="StockCode" label="股票代码" align="center">
        <template slot-scope="scope">
          <div>
            <span class="TipBg">HK</span>
            <span>{{scope.row.StockCode}}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
      <el-table-column prop="OrderSide" label="方向" align="center">
        <template slot-scope="scope">
          <div
            :class="{fall: scope.row.OrderSide=='Buy',rise: scope.row.OrderSide=='Sell'}"
          >{{scope.row.OrderSide=='Buy'?'买入':'卖出'}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="OrderPrice" label="委托价" align="center">
        <template slot-scope="scope">
          <div>{{Number(scope.row.OrderPrice).toFixed(3)}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="OrderQty" label="委托量" align="center"></el-table-column>
      <el-table-column prop="DateTimes" label="委托时间" align="center">
        <template slot-scope="scope">
          <div>{{(scope.row.DateTimes ||  scope.row.DateTime) | dateTimeString}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="OrderStatus" label="状态" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.OrderStatus | OrderStatus}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" width="820"></el-table-column>
    </el-table>
    <Contextmenu ref="Contextmenu" @cancelOrder="cancelOrder" :requestConfig="requestConfig" />
  </div>
</template>

<script>
import Contextmenu from "@/components/deal/contextmenu";
export default {
  components: {
    Contextmenu
  },
  props: {
    requestConfig: Object
  },
  data() {
    return {
      curEntrust: []
    };
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.system.dealBar.currentType;
    }
  },
  watch: {
    currentType() {
      this.queryCurrEntrust();
    }
  },
  methods: {
    queryCurrEntrust() {
      // 获取当天委托
      // / queryCurrEntrust.do  curEntrust
      let params = {};
      // if (this.currentType == 0) {
      //   params = {
      //     accountNo: this.requestConfig.account[this.currentType]
      //   };
      // } else {
        params = {
          market: "hk",
          startDate: new Date().Format("yyyy-MM-dd"),
          endDate: new Date().Format("yyyy-MM-dd"),
          pageStart: 1,
          pageSize: 100
        };
      // }
      this.axios({
        url: this.requestConfig.curEntrust[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.curEntrust = res.data.data || [];
      });
    },
    selectRow(row, column, event, flag) {
      let distanceX = event.clientX;
      let distanceY = event.clientY;
      this.$refs.Contextmenu.show({
        left: distanceX,
        top: distanceY,
        type: flag || "entrust",
        row: row
      });
      this.closeContextmenu();
    },
    closeContextmenu() {
      let html = document.getElementsByTagName("html")[0];
      let _this = this;
      html.onclick = function() {
        if (_this.$refs.Contextmenu) {
          _this.$refs.Contextmenu.close();
        }
      };
    },
    cancelOrder() {
      this.$emit("cancelOrder");
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {
    this.queryCurrEntrust();
  },
  filters: {
    OrderStatus(val) {
      const status = {
        Filled: "已成交",
        Waiting: "待成交",
        Reject: "已拒绝",
        Cancel: "已撤单",
        "Partially Filled": "已成交",
        "Host Reg": "已成交",
        "In Market": "待成交",
        "Reject New": "已拒绝",
        "Waiting Cancel": "撤单中"
      };
      return status[val] || "待成交";
    }
  }
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类

.cur-entrust {
  padding: 20px;
  // width: 1200px;
 
  /deep/ .el-table {
    margin-top: 12px;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $red;
      }
      .rise {
        color: $green;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
  }
}
</style>