<template>
  <div class="forget">
    <el-dialog
      theame
      title="提示：需登录交易账户，才能进行交易操作"
      :visible="showForget"
      width="470px"
      height="350px"
      top="15%"
      class="account-dialog"
      :modal="false"
      :close-on-click-modal="false"
      @close="close"
    >
      <div class="header" slot="title">
        <i class="el-icon-arrow-left back" @click="close"></i>
      </div>
      <div class="forget-content" v-if="type === 'forget'">
        <!--         <div class="step-title">忘记交易密码</div>
        <p
          class="text-content"
        >亲爱的易昇证券用户，为了保障您的账号安全，修改交易密码需要验证您的个人身份证信息。如您记得您的交易密码，请登录易昇证券交易账号之后，点击“更多-修改交易密码-手机号验证” 进行密码修改。如果您忘记了您的交易密码，请您准备好您的相关账户与身份证资料以及绑定的银行卡信息，联系我们的客服为您进行人工解绑。如有疑问，请拨打 400-1166-913 联系客服。</p>-->
        <el-form :model="forgetData" :rules="formRules" ref="forgetData">
          <el-form-item label="账号" class="account" prop="account" v-show="stepNum === 0">
            <el-select
              placeholder="请先开户"
              v-model="forgetData.account"
              @change="forgetData.password = ''"
            >
              <el-option
                :label="op.value"
                :value="op.value"
                v-for="op in accountSelectData"
                :key="op.value"
              >
                <span class="option-value">{{ op.value }}</span>
                <span class="option-label">{{ op.label }}</span>
              </el-option>
            </el-select>

            <!-- <i class="icon"></i> -->
          </el-form-item>
          <el-form-item label="手机号" prop="mobile" class="password" v-show="stepNum === 0">
            <el-input class="password-value" v-model="forgetData.mobile" readonly></el-input>
          </el-form-item>
          <el-form-item label="验证码" prop="vcode" class="password" v-show="stepNum === 0">
            <el-input
              class="password-value"
              v-model="forgetData.vcode"
              placeholder="请输入验证码"
              maxlength="6"
            ></el-input>
            <div
              class="get-vcode"
              :class="{'has-send': residueTime>0}"
              @click="getVCode('forgetData', 3)"
            >{{residueTime>0?residueTime+'s后重新获取':'获取验证码'}}</div>
          </el-form-item>
          <el-form-item label="密码" prop="password" class="password" v-show="stepNum === 1">
            <el-input
              class="password-value"
              :type="showPassword1"
              v-model="forgetData.password1"
              maxlength="20"
              placeholder="请输入登录密码"
              @keyup.enter.native="forgetSubmit"
            ></el-input>
            <i
              class="icon"
              :class="{password:showPassword1 ==='password'}"
              @click="showPassword1 === 'password'?showPassword1 = 'text':showPassword1 ='password'"
            ></i>
          </el-form-item>
          <el-form-item label="密码" prop="password" class="password" v-show="stepNum === 1">
            <el-input
              class="password-value"
              :type="showPassword2"
              v-model="forgetData.password2"
              maxlength="20"
              placeholder="再次输入"
              @keyup.enter.native="forgetSubmit"
            ></el-input>
            <i
              class="icon"
              :class="{password:showPassword2 ==='password'}"
              @click="showPassword2 === 'password'?showPassword2 = 'text':showPassword2 ='password'"
            ></i>
          </el-form-item>
          <el-button class="action login-submit" hover @click="nextStep" v-show="stepNum === 0">下一步</el-button>
          <el-button
            class="action login-submit"
            hover
            @click="forgetSubmit"
            v-show="stepNum === 1"
          >提交</el-button>
          <el-button class="action login-submit" hover @click="lastStep" v-show="stepNum === 1">上一步</el-button>
        </el-form>
      </div>
      <div class="forget-content" v-if="type === 'statement'">
        <div class="step-title">交易免责声明</div>
        <p class="text-content" v-html="content"></p>
      </div>
      <div class="forget-content" v-if="type === 'ewm'">
        <div class="step-title">请扫描下方二维码进行开户</div>
        <div class="ewm-img">
          <!-- <img src="http://h5.lc913.com/web/openAccount/ewm.png" alt /> -->
          <img src="http://www.easy913.com/image/erweima118.png" alt />
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "forget",
  components: {},
  data() {
    return {
      showForget: false,
      stepNum: 0,
      type: "forget",
      forgetData: {
        account: "",
        password1: "",
        password2: "",
        mobile: "",
        vcode: "",
        areaCode: ""
      },
      showPassword1: "password",
      showPassword2: "password",
      accountSelectData: [],
      formRules: {
        mobile: [
          { required: false, message: "请输入正确的手机号", trigger: "blur" },
          {
            pattern: /^[1][3-9]\d{9}$|^([6|9])\d{7}$|^[0][9]\d{8}$|^[6]([8|6])\d{5}$/,
            message: "请输入正确的手机号",
            trigger: "blur"
          }
        ],
        vcode: [
          {
            required: true,
            message: "请输入验证码",
            trigger: "blur"
          }
        ],
        password: [{ required: false, message: "请输入密码", trigger: "blur" }]
      },
      residueTime: 0,
      content: `<p>本声明是根据香港《个人资料(私隐)条例》(“条例”)的要求而提供予易昇证券有限公司 (“本公司”)的个人客户。</p>
                <p>1. 释义</p>
                <p>“账户”指当前或今后根据本协议以客户名义在本公司开立的任何一个或多个交易账户；</p>
                <p>“开户表格”指客户与本公司开立户口时填妥及签署的指定文件；</p>
                <p>“协议”指原先已签署或随后不时修订或增补后的本协议文本，包括开户表格及附属于本协议的各种附表；</p>
                <p>“公司”指与客户签定协议的法人；</p>
                <p>“业务代理”指代表本公司在香港或其他地方，提供行政、电讯、计算机、付款、收贷或证券结算、保管、核数、银行、融资、保险、风险管理、业务咨询、外判、客户关系管理、市场或其他提供予本集团营运的业务的代理人，合约承包商或第三方服务提供商。</p>
                <p>“客户”其名称、地址和相关数据列于开户表格中；</p>
                <p>“指示”指由客户或其授权人士向本公司发出的任何指示或买卖盘；</p>
                <p>2. 披露义务</p>
                <p>除特别声明外，客户必须按开户表格上的要求，将个人资料提供给本公司。假如客户不提供此等数据，本公司将没有足够数据来为客户开设及管理账户。</p>
                <p>3.个人资料之使用</p>
                <p>3.1 使用者</p>
                <p>有关客户的所有个人资料(不论是由客户所提供，还是由其他人士所提供，及不论这些数据是在客户收到客户协议之前，还是之后)将可被任何下列之公司或人士使用：</p>
                <p>(i) 本公司的关联公司</p>
                <p>(ii) 本公司的任何董事、高级职员、雇员或代理人；</p>
                <p>(iii) 执行客户指示和/或从事本公司业务而由本公司授权的任何人士(例如律师、顾问、代名人、托管人等)；</p>
                <p>(iv) 本公司持有与客户相关的任何权利和义务的任何实际或建议的承让人；</p>
                <p>(v) 任何政府机构、监管机构或其他团体或机构(不论是法例或是任何公司成员适用的规例所要求) ；</p>
                <p>(vi) 任何业务代理。</p>
                <p>3.2 目的</p>
                <p>客户的所有个人资料可被任何使用者用于下列目的；</p>
                <p>(i) 处理客户的开户申请；</p>
                <p>(ii) 执行新的或现有顾客的查核及信用调查程序，以及协助其他金融机构从事此类工作；</p>
                <p>(iii) 持续账目管理，包括收取欠款，强制执行担保、抵押或其他权利和利益；</p>
                <p>(iv) 设计提供予客户之新产品和服务，或向客户推广本公司的产品；</p>
                <p>(v) 将此等资料转移到香港以外的任何地方；</p>
                <p>(vi) 为了下列目的而进行客户个人资料的比较(不论收集此等数据的目的及来源，及不论此等数据是向用户或任何其他人士所收集的)：(a)信用调查；(b)资料核实；和/或(c)编制或核实资料，以便采取使用者或任何其他人士认为合适的行动(包括可能与客户或任何其他人的权利、义务或权益有关的行动)；</p>
                <p>(vii) 用于与客户有关的任何其他协议和服务之条款所规定之目的；</p>
                <p>(viii) 有关遵守任何法律、规例、法院判决或其他任何监管机构之判决的任何目的；</p>
                <p>(ix) 调查可疑交易；</p>
                <p>(x) 任何有关于执行客户指示或与本公司业务或交易有关连的目的。</p>
                <p>3.3 使用数据作直接促销</p>
                <p>本公司可使用及/或转送客户的数据给本公司关联公司作直接促销，而本公司须为此目的取得客户同意(其包括客户不反对之表示)。因此，请注意以下两点：</p>
                <p>(i) 客户的姓名、联络详情、投资组合数据、交易模式及财务背景可被用于直接促销本公司的投资及有关财务产品及服务；</p>
                <p>(ii) 若客户不愿意本公司使用及/或转送个人资料作直接促销，客户可行使其不同意此安排的权利。</p>
                <p>3.4 使用数据的时期</p>
                <p>本公司将会储存客户数据不多于有关监管机构的条例、规定及法例的要求。</p>
                <p>4. 客户的权利</p>
                <p>根据条例之规定，客户有权查阅和修正客户的个人资料。一般来说(除某些豁免外) 客户赋予的权利；</p>
                <p>(i) 询问本公司是否持有与客户有关的个人资料；</p>
                <p>(ii) 在合理的时间内，客户可查阅其个人资料；公司将以合理的方式及清楚易明的格式回复客户，但须收取合理的费用。</p>
                <p>(iii) 要求修正客户的个人资料；</p>
                <p>(iv) 如客户要求查阅或修正个人资料被拒绝，客户有权要求说明被拒绝的理由及反对任何该等拒绝</p>
                <p>(v) 反对使用的数据基于以上条款3.2，然而，反对本公司对以上任何一项的使用将妨碍公司管理账户，因此，反对本公司使用数据将被视为要求关闭账户。</p>
                <h3>免责声明</h3>
                <p>1、易昇证券有限公司 (以下简称「易昇证券」 ) 已作出最大努力维护本网站数据的准确性和可靠性。但是，易昇证券并不保证这些数据的准确性或可靠性。易昇证券并不承担任因依赖本网站的全部或部分内容，或因依赖该等信息或建议而引致的经济或其它损失的责任(不论是民事侵权行为或合约或其他)。</p>
                <p>2、本网站可能会受干扰、信息传送可能会延迟、暂停或中断、或受到黑客的攻击，数据在传送过程亦可 能发生错误。鉴于互联网不是一个完全可靠的通讯设施，而该等风险亦超出易昇证券所能控制。此外， 由于错误或延迟接获及传送的下单指令及其它信息，可能导致下单指令执行失败或延误，及/或交易价 格不同于下单的价格。这等风险由使用者承担。</p>
                <p>3、易昇证券、其信息供货商或其持牌代表并不会提出或担保或承担任何有关本网站或相关的信息的准确 性、可靠性、完整性及/或一致性的责任。</p>
                <p>4、本网站超链接的任何网站仅供参考之用，易昇证券不会对连结网站内容的准确性或其提供的任何其它 表述负任何责任。提供超链接至易昇证券网站以外的网站或网页，并不视为易昇证券同意、推荐、认 可、保证或推介任何第三方或在其网站所提供的服务/产品，亦不可视为易昇证券与该等第三方及/或其 网站有任何形式的合作，除非易昇证券已明确声明有合作关系。如阁下因使用此等网站而产生或引致 由阁下承担或蒙受的任何损害或损失，易昇证券概不负责。使用者须自行承担因使用或进入此等网站 的风险，并受此等网站内条约及条款的约束。</p>
                <p>5、任何因使用本网站及/或下载本网站提供任何软件所产生的计算机病毒或类似的损害，易昇证券概不承 担该等损失或损害 (不论怎样及是否直接或间接）。</p>
                <p>6、易昇证券有权终止或更改信息、产品或本网站所提供的服务，而无需事先通知使用者。</p>
                <p>7、任何本网站上显示的信息只供参考，而不得解释为提请或邀请购买和销售任何投资、证券或产品，或 在任何司法管辖区从事投资、 证券或产品的交易。</p>
                <p>8、倘若本网站所载的信息及数据受到某些国家的适用法律所禁止，则不能在该等国家浏览或使用有关信 息及数据。阁下须自行识别、留意及遵守该等国家的适用法律所施加的限制。</p>
                <p>9、投资附带风险，投资产品价格可跌亦可升。阁下应确保有关投资产品适合阁下之需要。如有怀疑，阁 下应咨询持牌或注册投资顾问的意见，并要求其确认有关投资产品适合并符合阁下之投资目标。投资 产品的过往业绩并不表示将来的回报。投资产品的价值可能会非常波动，并可能于短时间内大幅下跌。 阁下亦可能损失全数投资金额。在投资前应参阅有关投资产品的认购章程。</p>
                <p>10、投资者需自行评估是否适合作出有关投资及能否承受相关风险，且于作出任何投资前，应按阁下需要 咨询阁下的法律、财务、税务、会计及其他专业顾问。</p>
                <p>11、使用网上交易的风险披露 - 透过互联网进行交易可能因传送故障、干扰、通讯挤拥引致之延误传送、互联网公用性质所致之不正 确数据传送或任何其它不受易昇证券控制的原因，而导致有关指示之传送、接收或执行产生失误或延 误，包括于未执行已有效发出的修改、于取消指示时已执行原本或未经修改的指示、交易指示未能执 行或延误以致成交价格有别于指示发出时之价位，及/或未经授权第三方可能获得通讯及个人数据。易 昇证券对于你或任何人士因该等风险所产生之损失或费用概不承担责任。 - 你的登入代码或登录密码存在被滥用或未经授权的人利用的风险。请勿将个人登入户口或密码提供予 第三者，且定期更换密码。如果有任何未经授权使用你的交易或登录密码，你应立即通知易昇证券及 你同意承担因任何未经授权使用你的交易或登录密码所带来之所有风险，并会应要求赔偿易昇证券因 任何未经授权使用你的交易或登录密码而可能遭受的任何亏损、成本、损失、费用或索赔。</p>`
    };
  },
  mounted() {},
  watch: {
    stepNum(val) {
      if (val === 0) {
        this.forgetData.vcode = "";
      }
      if (val === 1) {
        this.forgetData.password1 = "";
        this.forgetData.password2 = "";
      }
    }
  },
  methods: {
    forgetSubmit() {
      try {
        let forgetData = this.forgetData;
        // if (!forgetData.password1) {
        //   throw new Error("请输入密码");
        // }
        if (forgetData.password1.length < 8) {
          throw new Error("密码长度至少8位");
        }
        if (!/^[A-Z]*$/.test(forgetData.password1[0])) {
          throw new Error("首位需为大写字母");
        }
        if (/[\u4e00-\u9fa5]/.test(forgetData.password1)) {
          throw new Error("只能是英文字符");
        }
        if (/\s/.test(forgetData.password1)) {
          throw new Error("密码不能有空格");
        }
        if (forgetData.password1 != forgetData.password2) {
          throw new Error("两次输入密码不一致，请重新输入");
        }
        let channel = 0;
        this.accountSelectData.forEach(a => {
          if (a.value === forgetData.account) {
            if (a.label === "机构账户") {
              channel = 10;
            }
          }
        });
        let url = "/appcustomer/changeTransactionPassword.do";
        // if(channel === 0){

        // }
        this.axios({
          url: url,
          method: "post",
          params: {
            channel: channel, //0 标准账户  10 机构账户
            type: 3,
            newPassword: forgetData.password1
          }
        })
          .then(res => {
            this.$message({
              message: "重置成功！",
              type: "success"
            });
            this.showForget = false;
            this.forgetData = {
              account: "",
              password1: "",
              password2: "",
              mobile: "",
              vcode: "",
              areaCode: ""
            };
            this.stepNum = 0;
          })
          .catch(err => {});
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    lastStep() {
      this.$confirm("返回上一步需重新发送验证码，是否继续？", "提示", {
        cancelButtonText: "取消"
      })
        .then(res => {
          this.stepNum = 0;
        })
        .catch(err => {
          console.log(err);
        });
    },
    startTimer() {
      // 发送验证码后倒计时
      this.residueTime = 60;
      this.timer = setInterval(() => {
        this.residueTime--;
        if (this.residueTime < 0) {
          this.residueTime = 0;
          clearInterval(this.timer);
        }
      }, 1000);
    },
    getVCode(t, type) {
      // 发送手机验证码
      if (this.residueTime > 0) {
        return false;
      }
      let flag = false;
      this.$refs[t].validateField("mobile", valid => {
        flag = !valid;
      });
      if (flag) {
        let target = this[t];
        let mobile = target.mobile;
        this.startTimer();
        this.axios({
          url: "/messageCode/getCode.do",
          params: {
            mobile: mobile,
            type: type
          }
        })
          .then(res => {
            this.$message({
              message: "发送成功！",
              type: "success"
            });
          })
          .catch(err => {});
      }
    },
    nextStep() {
      // POST /appcustomer/confirmIdentification.do
      let forgetData = this.forgetData;
      let channel = 0;
      this.accountSelectData.forEach(a => {
        if (a.value === forgetData.account) {
          if (a.label === "机构账户") {
            channel = 10;
          }
        }
      });
      let flag = false;
      this.$refs.forgetData.validateField("vcode", valid => {
        flag = !valid;
      });
      if (flag) {
        this.axios({
          url: "/appcustomer/confirmIdentification.do",
          method: "post",
          params: {
            areaCode: forgetData.areaCode,
            channel: channel, //0 标准账户  10 机构账户
            mobile: forgetData.mobile,
            smsCode: forgetData.vcode,
            type: 3
          }
        })
          .then(res => {
            if (channel === 0) {
              this.resetPassword();
            } else {
              this.stepNum = 1;
            }
          })
          .catch(err => {});
      }
    },
    resetPassword() {
      let identityData = this.$store.state.AccountLogin.identityData;
      this.$confirm(
        `重置后的密码将会发送到您的开户邮箱${identityData.email}，确认需要重置密码？`,
        "提示",
        {}
      ).then(() => {
        this.axios({
          url: "/resetPassword",
          method: "post",
          params: {
            userNo: identityData.ebokerUsername
          },
          type: "hostEbroker"
        }).then(res => {
          this.stepNum = 0;
          this.$message({
            message: "重置成功!",
            type: "success"
          });
        });
      });
    },
    setAccount() {
      // institutionalAccount 机构账号  ebokerUsername 个人账号
      let identityData = this.$store.state.AccountLogin.identityData;
      console.log(identityData);
      if (identityData.idCardCode) {
        this.accountSelectData = [];
        this.forgetData.mobile = identityData.mobile;
        this.forgetData.areaCode = identityData.areaCode;
        this.forgetData.account =
          identityData.ebokerUsername || identityData.institutionalAccount;
        if (identityData.ebokerUsername) {
          this.accountSelectData.push({
            value: identityData.ebokerUsername,
            label: "标准账户"
          });
        }
        if (identityData.institutionalAccount) {
          this.accountSelectData.push({
            value: identityData.institutionalAccount,
            label: "机构账户"
          });
        }
      } else {
        // 无交易账户，提示在app上申请
        this.$message({
          message: "未开通交易账户，请在APP上申请！",
          type: "error"
        });
      }
    },
    close() {
      this.showForget = false;
      this.forgetData = {
        account: "",
        password1: "",
        password2: "",
        mobile: "",
        vcode: "",
        areaCode: ""
      };
      this.stepNum = 0;
    },
    show(type) {
      this.showForget = true;
      this.type = type;
      if (type === "forget") {
        this.setAccount();
      }
    }
  }
};
</script>

