<template>
  <div class="invite-friends">
    <div class="top-info">
      <div class="invite-code">
        我的邀请码：
        <span class="white">{{ inviteData.invitationCode }}</span>
      </div>
      <div class="invite-info">
        <div class="link">
          <div class="super-link">
            我的邀请链接：
            <span
              class="white link-url"
              :title="inviteData.invitationLink"
              @click="copyLink"
              >{{ inviteData.invitationLink }}</span
            >
          </div>
          <div class="t">
            我的邀请二维码
            <span class="check">
              点击查看
              <div class="invite-qr">
                <!-- <div id="qrcode" ref="qrcode"></div> -->
                <vue-q-art
                  :config="ewmConfig"
                  :downloadButton="false"
                ></vue-q-art>
              </div>
            </span>
          </div>
        </div>
        <div class="invite-num">
          <div class="white">
            <span class="num">{{ customerList.length }}</span
            >(人)
          </div>
          <div class="t">邀请人数</div>
        </div>
        <!-- <div class="invite-award">
          <div class="white">
            <span class="num">{{0}}</span>(元)
          </div>
          <div class="t" @click="openExternal">邀请奖励</div>
        </div> -->
      </div>
    </div>
    <div class="invite-list">
      <div class="list-title">我的邀请</div>
      <table-block>
        <el-table class="list-table" :data="customerList" height="600px">
          <el-table-column
            label="手机号"
            prop="mobile"
            width="120"
          ></el-table-column>
          <el-table-column
            label="注册时间"
            prop="regTime"
            width="200"
            align="right"
            class="is-arrow"
          >
            <template slot="header">
              <span>
                注册时间
                <i class="el-icon-d-caret"></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column
            label="账户状态"
            prop="countState"
            width="120"
            align="right"
          ></el-table-column>
          <el-table-column
            label="月交易次数"
            prop="tradTimes"
            width="140"
            align="right"
            class="is-arrow"
          >
            <template slot="header">
              <span>
                月交易次数
                <i class="el-icon-d-caret"></i>
              </span>
            </template>
          </el-table-column>
        </el-table>
      </table-block>
    </div>
  </div>
</template>

<script>
import tableBlock from "@/components/deal/table-block";
const shell = require("electron").shell;
const clipboard = require("electron").clipboard;
import VueQArt from "vue-qart";
import logo from "@/assets/logo.png";
export default {
  components: { tableBlock, VueQArt },
  data() {
    return {
      customerList: [],
      inviteData: {},
      qrImg: "",
      ewmConfig: {
        value: "",
        imagePath: logo,
        filter: "#fff"
      }
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.init();
    });
  },
  activated() {
    // this.init();
  },
  methods: {
    init() {
      this.getInviteData();
      this.getCustomerList();
    },
    getInviteData() {
      this.axios({
        // GET /app/getMyInvitation.do
        url: "/app/getMyInvitation.do",
        method: "get",
        params: {}
      }).then(res => {
        this.inviteData = res.data.data;
        this.ewmConfig.value = this.inviteData.invitationLink;
        console.log(this.ewmConfig);
      });
    },
    getCustomerList() {
      // GET /appcustomer/getMyTotalCustomerList.do
      this.axios({
        url: "/appcustomer/getMyTotalCustomerList.do",
        method: "get",
        params: {}
      }).then(res => {
        this.customerList = res.data.data;
      });
    },
    copyLink() {
      let link = this.inviteData.invitationLink;
      clipboard.writeText(link);
      this.$message({
        message: "复制成功！",
        type: "success"
      });
    },
    openExternal() {
      // shell.openExternal("http://www.baidu.com");
    }
  }
};
</script>

<style lang="scss" scoped>
.invite-friends {
  height: 100%;
  width: 100%;
  .top-info {
    height: 192px;
    border-bottom: 4px solid $baseBg;
    padding-left: 20px;
    @include add-size($font_size_16);
    .white {
      color: $white;
    }
    .link-url {
      display: inline-block;
      max-width: 500px;
      @include single-line-ellipsis;
      cursor: pointer;
    }
    .super-link {
      @include flex-center;
    }
    .invite-code {
      padding: 30px 0;
    }
    .invite-info {
      @include flex-box;
      .t {
        margin-top: 28px;
      }
      .link {
        width: 660px;
      }
      .check {
        color: $activeColor;
        margin-left: 40px;
        cursor: pointer;
        position: relative;
        &:hover {
          .invite-qr {
            opacity: 1;
            height: 140px;
          }
        }
        .invite-qr {
          background-color: $white;
          // padding: 10px;
          position: absolute;
          left: 0;
          top: 30px;
          transition: all ease 0.3s;
          height: 0;
          opacity: 0;
          width: 140px;
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
          border-radius: 2px;
          overflow: hidden;
          z-index: 999;
          div {
            width: 140px;
            height: 140px;
            margin: 0;
            /deep/ canvas {
              width: 140px;
              height: 140px;
            }
          }
        }
      }
      .invite-num {
        width: 127px;
        @include add-size($font_size_14);
      }
      .invite-award {
        @include add-size($font_size_14);
      }
      .num {
        @include add-size(22px);
        margin-right: 4px;
      }
    }
  }
  .invite-list {
    padding-left: 20px;
    padding-top: 30px;
    .list-title {
      @include add-size($font_size_16);
      padding-left: 26px;
      position: relative;
      line-height: 20px;
      &::before {
        content: "";
        width: 6px;
        height: 20px;
        background-color: $activeColor;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    /deep/.el-table {
      color: $white;
      margin-top: 32px;
      padding-left: 26px;
      @include add-size($font_size_14);
      .el-table__body-wrapper {
        margin-top: 10px;
        .el-table_1_column_2,
        .el-table_1_column_4 {
          padding-right: 20px;
        }
      }
      thead {
        color: $defaultColor;
      }
      .el-table__row {
        .cell {
          line-height: 36px;
        }
      }
    }
  }
}
</style>
