<template>
  <div class="market-card" :type="type" @click="gotoDel">
    <div class="card-title" v-if="type !== 'none'">
      <i class="card-icon" :type="liData.marketType"></i>
      <div class="card-title-t">{{liData.name}}</div>
    </div>
    <div class="card-tips" v-if="!type && liData.marketType !== 4">提供香港交易所实时行情、10档盘口和逐笔成交明细</div>
    <div class="card-tips" v-if="!type && liData.marketType == 4">提供全美综合实时行情、最佳买卖档和逐笔成交明细</div>
    <div class="card-tips" v-if="type=== 'bought'">有效期：{{liData.endTime}}</div>
    <div class="price-btn" v-if="type !== 'none'">
      <div class="card-price" v-if="!type">￥{{liData.otherPrice}}/月</div>
      <div class="card-price" v-if="type=== 'bought'">剩余{{liData.remainDays}}天</div>
      <div class="act-btn" hover @click="itemClick">{{type?'继续订阅':'立即订阅'}}</div>
    </div>
    <div class="none" v-if="type === 'none'">暂未订阅高级行情，点击下方选择订阅</div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  props: {
    liData: Object,
    type: String
  },
  computed: {},
  watch: {},
  methods: {
    itemClick() {
      this.$emit("itemClick", this.liData);
    },
    gotoDel() {
      this.$emit("gotoDel", this.liData);
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang='scss' scoped>
$color: #370704;
.market-card {
  cursor: pointer;
  width: 374px;
  height: 160px;
  background: url(~@/assets/images/personal/market/bg-b.png) 0 0 no-repeat;
  background-size: 374px 160px;
  &[type="none"] {
    background: url(~@/assets/images/personal/market/bg-a.png) 0 0 no-repeat;
    background-size: 374px 160px;
  }
  padding: 20px 12px;
  margin-right: 79px;
  margin-bottom: 20px;
  .card-title {
    @include flex-center;
    .card-icon {
      width: 30px;
      height: 30px;
      margin-right: 11px;
      &[type="1"] {
        background: url(~@/assets/images/personal/market/hk-icon.png) 0 0
          no-repeat;
        background-size: 30px 30px;
      }
      &[type="2"] {
        background: url(~@/assets/images/personal/market/hk-icon.png) 0 0
          no-repeat;
        background-size: 30px 30px;
      }
      &[type="3"] {
        background: url(~@/assets/images/personal/market/hk-icon.png) 0 0
          no-repeat;
        background-size: 30px 30px;
      }
      &[type="4"] {
        background: url(~@/assets/images/personal/market/us-icon.png) 0 0
          no-repeat;
        background-size: 30px 30px;
      }
    }
    .card-title-t {
      color: $color;
      @include add-size($font_size_18);
    }
  }
  .card-tips {
    padding-left: 41px;
    padding-right: 70px;
    line-height: 18px;
    color: $color;
    @include add-size($font_size_14);
    height: 36px;
  }
  .price-btn {
    padding-left: 41px;
    @include flex-box;
    margin-top: 20px;
    .card-price {
      color: #b03200;
      @include add-size($font_size_20);
      padding-top: 12px;
    }
    .act-btn {
      @include add-size($font_size_16);
      color: $white;
      width: 90px;
      height: 34px;
      background-color: $color;
      border-radius: 17px;
      margin-left: auto;
      line-height: 34px;
      text-align: center;
      cursor: pointer;
    }
  }
  .none {
    @include add-size($font_size_18);
    color: $white;
    @include flex-center;
    text-align: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    // line-height: 120px;
  }
}
</style>