<template>
  <div
    class="contextmenu"
    :style="{ left: left + 'px', top: top + 'px' }"
    v-if="visible"
    @click.stop="stop"
  >
    <ul class="menu-ul">
      <li v-for="(li, i) in menu" :key="i" @click="menuAction(li)">
        {{ li.label }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    // menu: Array,
    // distance: { type: [Number, String], default: 0 }
  },
  data() {
    return {
      left: 0,
      top: 0,
      visible: false,
      target: {},
      distance: 80,
      menu: [],
      menuType1: [
        {
          label: "交易",
          value: 0
        },
        {
          label: "提醒",
          value: 1
        },
        {
          label: "加自选",
          value: 2
        }
      ],
      menuType2: [
        {
          label: "交易",
          value: 0
        },
        {
          label: "提醒",
          value: 1
        },
        {
          label: "删除",
          value: 3
        }
      ]
    };
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.AccountLogin.currentType;
    },
    isLogin() {
      return (
        this.$store.state.system.dealBar.commonAccount ||
        this.$store.state.system.dealBar.organization
      );
    }
  },
  mounted() {
    let _this = this;
    document.addEventListener("click", function() {
      _this.close();
    });
  },
  methods: {
    show(query) {
      if (!query.chg) {
        query.chg = query.yd_chg;
      }
      if (!query.symbol) {
        query.symbol = query.StockCode;
      }
      this.stockData = query;
      if (query.selfStock) {
        this.menu = this.menuType2;
      } else {
        this.menu = this.menuType1;
      }
      if (this.selfStockArr && this.selfStockArr.length > 0) {
        this.menu[3] = {
          label: "置顶",
          value: 4
        };
      }
      this.showMenu({
        left: query.x,
        top: query.y,
        row: query
      });
      if (!query.hand) {
        this.getStockQuery(query.symbol);
      }
    },
    getStockQuery(stockCode) {
      let url = "/quote-service/api/v1/stockquery";
      if (stockCode.indexOf("hk") === -1) {
        url = "/quote-service/api/us/v1/stockquery";
      }
      let data = {
        codelist: stockCode,
        delay: 0
      };
      this.axios({
        url: url,
        params: data,
        type: "HQ"
      })
        .then(res => {
          this.stockQuery = res.data.Data[0];
          this.stockData = { ...this.stockData, ...this.stockQuery };
        })
        .catch(err => {});
    },
    menuSelect(act, row) {
      if (act.value === 2) {
        // 加自选
        let data = {
          opt: 1, // 1增加，0删除， 2 批量操作
          code: row.symbol
        };
        this.setSelect(data);
      } else if (act.value === 3) {
        // 删自选
        let data = {
          code: row.symbol,
          opt: 0 // 1增加，0删除， 2 批量操作
        };
        this.setSelect(data);
      } else if (act.value === 4) {
        // 置顶
        let codelist = this.selfStockArr.filter(m => {
          return m != row.symbol;
        });
        codelist.unshift(row.symbol);
        let data = {
          codelist: codelist,
          opt: 2 // 1增加，0删除， 2 批量操作
        };
        this.setSelect(data);
      } else if (act.value === 1) {
        // 提醒
        // this.$refs.Remind.show();
        this.$store.dispatch("SET_REMIND_DATA", { show: true, stock: row });
      } else if (act.value === 0) {
        // 交易
        if (this.isLogin) {
          if (row.hand) {
            this.$store.dispatch("SET_RIGHT_TARGET", row);
          } else {
            this.$store.dispatch("SET_RIGHT_TARGET", this.stockQuery);
          }
          this.$store.dispatch("SET_DEAL_BAR", { barShow: true });
          setTimeout(() => {
            this.$store.dispatch(
              "SHOW_TARGET_DEAL",
              {
                target: 0,
                timestamp: new Date().getTime()
              },
              200
            );
          });
        } else {
          this.$store.dispatch("SET_DEAL_BAR", { loginShow: true });
        }
      }
    },
    setSelect(data) {
      let url = "/quote-service/api/v1/selfstock";
      this.axios({
        url: url,
        method: "post",
        params: data,
        type: "HQ"
      }).then(res => {
        if (data.opt === 0) {
          this.$message({
            message: "删除成功！",
            type: "success"
          });
          if (this.$route.fullPath.indexOf("selfSelect") === -1) {
            // 在自选页面不置反
            this.stockData.selfStock = false;
          }
        } else if (data.opt === 1) {
          this.$message({
            message: "添加成功！",
            type: "success"
          });
          if (this.$route.fullPath.indexOf("selfSelect") === -1) {
            this.stockData.selfStock = true;
          }
        }
        this.$store.dispatch("SET_RIGHT_MENU", {
          update: Date.now()
        });
      });
    },
    showMenu(target) {
      this.left = target.left;
      this.top = target.top;
      this.type = target.type;
      this.visible = true;
      this.target = target.row;
      if (window.innerHeight - this.top < this.distance) {
        this.top -= this.distance;
      }
    },
    menuAction(menu) {
      // this.$emit("menuSelect", menu, this.target);
      this.menuSelect(menu, this.target);
      this.close();
    },
    close() {
      // this.visible = false;
      this.$store.dispatch("SET_RIGHT_MENU", {
        show: false
      });
    },
    stop() {}
  },
  watch: {
    "$store.state.system.rightMenu.show"(val) {
      if (val) {
        this.visible = true;
        this.show(this.$store.state.system.rightMenu.target);
      } else {
        this.visible = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.contextmenu {
  position: fixed;
  left: 100px;
  top: 100px;
  border: 1px solid $borderColor;
  background-color: $blockBgColor;
  width: 100px;
  z-index: 999999;
  .menu-ul {
    li {
      line-height: 30px;
      cursor: pointer;
      @include add-size($font_size_14);
      text-align: center;
      &:hover {
        color: $white;
        background-color: $activeColor;
      }
    }
  }
}
</style>
