import {
    createHeader
} from './header'

/*
@ params.openParams 正常的请求参数
@ params.heartParams 心跳包请求参数
@ params.heartBeatTime 心跳包间隔 默认10000ms
*/

class Socket {
    constructor(params = {}) {
        this.params = {
            ...params
        };
        // this.url = params.url || "";
        this.header = {
            ...createHeader('ws')
        };
        this.initWs();
        this.reconnection = 0;
    }
    initWs() {
        if (!this.ws) {
            this.ws = new WebSocket(this.params.url);
            this.onopen();

        } else {
            this.ws = new WebSocket(this.params.url);
            console.log("重连")
            this.onopen(() => {
                this.onmessage(this.onmessageCallback);
            });
        }
        // this.onclose();
        this.onerror();

    }

    onopen(cb) {
        this.ws.onopen = () => {
            let sendData = {
                Header: {
                    ...this.header,
                    "Command": 1
                },
                Params: this.params.openParams
            };
            // console.log(sendData)
            this.send(sendData);
            this.heartBeat();
            if (typeof cb === "function") {
                cb();
            }
        }
    }

    send(sendData) {
        if (this.ws.readyState === 1) {
            this.ws.send(JSON.stringify(sendData));
        }
    }

    resend(params = {}) {
        this.send({
            Header: {
                ...this.header,
                "Command": 3
            },
            Params: {}
        });
        setTimeout(() => {
            this.params = params;
            let sendData = {
                Header: {
                    ...this.header,
                    "Command": 1
                },
                Params: this.params.openParams
            };
            this.send(sendData);
        }, 300)

    }

    onmessage(cb) {
        this.onmessageCallback = cb
        this.ws.onmessage = (re) => {
            cb(re)
        }

    }

    heartBeat() {
        // 心跳包
        let sendData = {
            Header: {
                ...this.header,
                "Command": 4
            },
            Params: {}
        };
        clearInterval(this.timer)
        this.timer = setInterval(() => {
            if (this.ws.readyState != this.ws.OPEN) {
                this.initWs();
            } else {
                this.ws.send(JSON.stringify(sendData));
            }
        }, this.params.heartBeatTime || 30000)
    }

    closeHeartBeat() {
        clearInterval(this.timer);
        this.timer2 = setTimeout(() => {
            console.log(this)
            if (this.reconnection <= 10) {
                // 尝试重连两次
                this.reconnection++;
                this.initWs();
            }
        }, 1000)
    }

    onclose() {
        console.log('关闭socket')
        this.ws.close()
        clearInterval(this.timer);
        clearTimeout(this.timer2);
    }

    onerror() {
        this.ws.onerror = () => {
            console.log("@error");
            this.onclose()
            this.closeHeartBeat();
        }
    }
}


export {
    Socket
}