<template>
  <div class="menu">
    <div class="menu-list">
      <div
        class="menu-list-item"
        :class="{ isChoose: item.active || item.starState }"
        v-for="(item, index) in menuList"
        :key="index"
        @click="changeRouter(item)"
      >
        <img
          class="icon"
          :src="item.active || item.starState ? item.iconSelect : item.icon"
          alt
        />
        <label
          class="select-label"
          :for="'a' + index"
          v-if="item.select"
          @click.stop="selectTarget"
        >
          <i
            class="el-icon-caret-bottom arrow"
            :class="{ active: item.showSelect }"
          ></i>
          <el-cascader
            class="menu-select"
            v-model="item.currentVal"
            :options="item.select"
            :props="{ expandTrigger: 'hover' }"
            @visible-change="visibleChange($event, item)"
            @change="changeCurrent(item)"
            ref="elCascader"
          ></el-cascader>
        </label>
        <div class="item-name">{{ item.name }}</div>
      </div>
    </div>
    <!-- <div class="back" @click="historyBack">后退</div> -->
    <div class="search-bar" :class="{ shrink: shrink }">
      <search />
    </div>
  </div>
</template>

<script>
import search from "./search.vue";
import { showDetailComponents } from "@/utils";
export default {
  components: { search },
  data() {
    return {
      menuList: [
        {
          name: "自选",
          icon: require("@/assets/images/menu/icon1-a.png"),
          iconSelect: require("@/assets/images/menu/icon1-b.png"),
          url: "self",
          active: false,
          currentVal: "",
          query: {
            target: "selfSelect"
          }
        },
        {
          name: "港股",
          icon: require("@/assets/images/menu/icon2-a.png"),
          iconSelect: require("@/assets/images/menu/icon2-b.png"),
          url: "/",
          active: true,
          currentVal: "",
          showSelect: false,
          select: [
            {
              label: "全部",
              value: 1,
              type: 1,
              code: 456,
              url: "/hk"
            },
            {
              label: "主板",
              value: 2,
              type: 1,
              code: 45674,
              url: "/hk"
            },
            {
              label: "创业板",
              value: 3,
              type: 1,
              code: 45674,
              url: "/hk"
            },
            {
              label: "热门行业",
              value: 6,
              type: 1,
              code: 45674,
              children: [],
              url: "/hk"
            },
            {
              label: "异动榜单",
              value: 7,
              type: 1,
              code: 45674,
              url: "/hk",
              children: [
                {
                  label: "五分钟涨幅榜",
                  value: "five00",
                  code: 5
                },
                {
                  label: "五分钟跌幅榜",
                  value: "five01",
                  code: 5
                },
                {
                  label: "五分钟振幅榜",
                  value: "five10",
                  code: 5
                }
              ]
            },
            {
              label: "窝轮",
              value: 4,
              type: 1,
              code: 45674,
              url: "/hk"
            },
            {
              label: "牛熊证",
              value: 5,
              type: 1,
              code: 45674,
              url: "/hk"
            },
            {
              label: "界内证",
              value: 8,
              type: 1,
              code: 45674,
              url: "/hk"
            }
          ]
        },
        {
          name: "美股",
          icon: require("@/assets/images/menu/icon3-a.png"),
          iconSelect: require("@/assets/images/menu/icon3-b.png"),
          url: "usIndex",
          active: false,
          currentVal: "",
          showSelect: false,
          query: {
          },
          select: [
            {
              label: "全部",
              value: "us1",
              type: 1,
              code: 456
            },
            {
              label: "ETF",
              value: "us2",
              type: 1,
              code: 45674
            },
            {
              label: "中概股",
              value: "us3",
              type: 1,
              code: 45674
            },
            {
              label: "热门行业",
              value: "us4",
              type: 1,
              code: 45674,
              children: []
            },
            {
              label: "异动榜单",
              value: "us5",
              type: 1,
              code: 45674,
              children: [
                {
                  label: "五分钟涨幅榜",
                  value: "five00",
                  code: "us5"
                },
                {
                  label: "五分钟跌幅榜",
                  value: "five01",
                  code: "us5"
                },
                {
                  label: "五分钟振幅榜",
                  value: "five10",
                  code: "us5"
                }
              ]
            },
            {
              label: "盘前榜单",
              value: "us6",
              type: 1,
              code: 45674
            },
            {
              label: "盘后榜单",
              value: "us7",
              type: 1,
              code: 45674
            }
          ]
        },
        {
          name: "沪深",
          icon: require("@/assets/images/menu/icon4-a.png"),
          iconSelect: require("@/assets/images/menu/icon4-b.png"),
          url: "hs",
          active: false,
          showSelect: false,
          disable: true
        },
        {
          name: "新股",
          icon: require("@/assets/images/menu/icon5-a.png"),
          iconSelect: require("@/assets/images/menu/icon5-b.png"),
          url: "newStock",
          active: false
        },
        {
          name: "窝轮",
          icon: require("@/assets/images/menu/icon6-a.png"),
          iconSelect: require("@/assets/images/menu/icon6-b.png"),
          url: "wl",
          active: false,
          query: {
            label: "全部",
            value: 4,
            type: 1,
            code: 456
          }
        },
        {
          name: "牛熊证",
          icon: require("@/assets/images/menu/icon7-a.png"),
          iconSelect: require("@/assets/images/menu/icon7-b.png"),
          url: "nxz",
          active: false,
          query: {
            label: "主板",
            value: 5,
            type: 1,
            code: 45674
          }
        },
        {
          name: "界内证",
          icon: require("@/assets/images/menu/icon11-a.png"),
          iconSelect: require("@/assets/images/menu/icon11-b.png"),
          url: "jnz",
          active: false,
          query: {
            label: "主板",
            value: 8,
            type: 1,
            code: 45674
          }
        },
        {
          name: "交易",
          icon: require("@/assets/images/menu/icon8-a.png"),
          iconSelect: require("@/assets/images/menu/icon8-b.png"),
          url: "deal",
          active: false,
          starState: false,
          type: "business"
        },
        {
          name: "资讯",
          icon: require("@/assets/images/menu/icon9-a.png"),
          iconSelect: require("@/assets/images/menu/icon9-b.png"),
          url: "news",
          active: false
        },
        {
          name: "我的",
          icon: require("@/assets/images/menu/icon10-a.png"),
          iconSelect: require("@/assets/images/menu/icon10-b.png"),
          url: "mine",
          active: false
        }
      ],

      value: 0,
      noticeIsShow: false,
      noticeIsShows: false,
      searchKey: "",
      shrink: false,
      currentVal: "",
      starStateTimer: {}
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.setInputId();
      this.getIndustry();
      this.getUsIndustry();
      this.checkIndustry();
    });
  },
  computed: {},
  methods: {
    checkIndustry() {
      setTimeout(() => {
        if (this.menuList[1].select[3].children.length <= 0) {
          this.getIndustry();
          this.getUsIndustry();
          this.checkIndustry();
        }
      }, 8000);
    },
    historyBack() {
      if (this.$route.pathname == "/") {
      } else {
        return false;
        this.$router.back(-1);
      }
    },
    clearElCascader() {
      let elCascaders = this.$refs.elCascader;
      // console.log(elCascaders);
    },
    setInputId() {
      // select-label
      let selectLabels = document.getElementsByClassName("select-label");
      for (let i = 0; i < selectLabels.length; i++) {
        let id = selectLabels[i].getAttribute("for");
        selectLabels[i].childNodes[1].childNodes[0].childNodes[1].setAttribute(
          "id",
          id
        );
      }
    },
    routerTarget(val) {
      // js 控制跳转路由
      // val.a   第一级菜单
      // val.b   下拉框一级菜单
      // val.c   下拉框二级菜单value值 需根据value遍历
      let menuList = this.menuList;
      if (typeof val.c != "undefined") {
        menuList[val.a].currentVal = [
          menuList[val.a].select[val.b].value,
          val.c
        ];
      } else if (typeof val.b != "undefined") {
        menuList[val.a].currentVal = [menuList[val.a].select[val.b].value];
      } else {
        this.changeRouter(menuList[val.a]);
        return false;
      }
      this.changeCurrent(menuList[val.a]);
    },
    visibleChange(a, b) {
      b.showSelect = a;
    },
    //获取热门行业二级菜单
    getIndustry() {
      let url = "/quote-service/api/v1/industry";
      this.axios({
        url: url,
        method: "get",
        params: {},
        type: "HQ",
        store: "hk_industry.ADD_HK_INDUSTRY"
      }).then(res => {
        let data = res.data.Data;
        data.map((val, index) => {
          let obj = {
            label: val.IndustryName,
            value: val.IndustryId,
            code: 4
          };
          this.menuList[1].select[3].children.push(obj);
        });
      });
    },
    //获取美股热门行业二级菜单
    getUsIndustry() {
      let url = "/quote-service/api/us/v1/industry";
      this.axios({
        url: url,
        method: "get",
        params: {},
        type: "HQ"
      }).then(res => {
        let data = res.data.Data;
        data.map((val, index) => {
          let obj = {
            label: val.IndustryName,
            value: val.IndustryId,
            code: "us4"
          };
          this.menuList[2].select[3].children.push(obj);
        });
      });
    },
    changeCurrent(item) {
      // debugger;
      let o = {};
      let currentVal = item.currentVal;
      if (currentVal.length == 1) {
        // 一级菜单
        item.select.forEach(a => {
          if (a.value === currentVal[0]) {
            o = a;
          }
        });
      } else {
        // 二级菜单
        item.select.forEach(a => {
          if (a.value === currentVal[0]) {
            a.children.forEach(b => {
              if (b.value === currentVal[1]) {
                o = b;
              }
            });
          }
        });
      }
      console.log(item, o, 420420);
      this.changeRouter(item, o);
    },
    selectTarget() {},
    setFontsize(size) {
      window.document.documentElement.setAttribute("data-size", size);
    },
    changeRouter(item, option) {
      if (item.disable) {
        return false;
      }
      //交易
      if (item.type === "business") {
        if (
          this.$store.state.system.dealBar.commonAccount ||
          this.$store.state.system.dealBar.organization
        ) {
          this.$store.dispatch("SET_DEAL_BAR", { barShow: true });
        } else {
          this.$store.dispatch("SET_DEAL_BAR", { loginShow: true });
        }
        // if (this.$route.name != "HQ") {
        //   option = this.menuList[1].select[0];
        // } else {
        // }
        // 点击交易  高亮2秒后变暗
        this.menuList[7].starState = true;
        clearTimeout(this.starStateTimer);
        this.starStateTimer = setTimeout(() => {
          this.menuList[7].starState = false;
        }, 2000);

        return false;
      }
      // 重置下拉菜单
      let q = item.currentVal || [];
      let cur = JSON.parse(JSON.stringify(q));
      this.menuList.forEach(a => {
        a.active = false;
        a.currentVal = [];
      });
      item.currentVal = option ? cur : [];
      item.active = true;

      // 跳转路由
      if (item.url) {
        this.navigateTo(item, option);
      }
    },
    goHome() {
      this.changeRouter(this.menuList[0]);
    },
    navigateTo(e, option) {
      // console.log(e, option, "123木头人");
      let url = option ? option.url : e.url;
      this.$router.replace({ path: url, query: { ...e.query, ...option } });
      this.clearElCascader();
    }
  },
  watch: {
    $route(e) {
      // console.log(e, "router")
      if (e.query.target === "myBankCard") {
        // 此条件为交易入金选择银行卡时跳转到个人中心模块添加银行卡
        this.menuList.forEach(a => {
          a.active = false;
          if (a.url === "wd") {
            a.active = true;
          }
        });
      }
      // 搜索框
      // console.log(e, "123木头人");
      if (
        e.fullPath.indexOf("hk") > 0 ||
        e.fullPath.indexOf("self") > 0 ||
        e.fullPath.indexOf("indexStock") > 0 ||
        e.fullPath.indexOf("stockDetail") > 0
      ) {
        this.shrink = true;
      } else {
        this.shrink = false;
      }
      if (e.name != "HQ") {
        // 离开港股行情后 清除下拉选中记录
        this.menuList[1].currentVal = "";
      }
      if (e.fullPath.indexOf("hk") > 0 || e.fullPath.indexOf("self") > 0) {
        // this.$store.dispatch("SHOW_STOCK_QUERY", true);
        // this.$store.dispatch("SHOW_STOCK_KLINE", true);
      } else {
        showDetailComponents({ show: false });
      }
    },
    "$store.state.system.routerTarget"(val) {
      // debugger
      this.routerTarget(val);
    }
  }
};
</script>

<style lang="scss" scoped>
.menu {
  height: 74px;
  width: 100%;
  // background-color: $baseBg;
  position: fixed;
  left: 0;
  top: 32px;
  padding-left: 20px;
  @include flex-center;
  z-index: 10;

  .menu-list {
    @include add-size($font_size_14);
    @include flex-center;
    height: 100%;
    .menu-list-item {
      cursor: pointer;
      margin-right: 30px;
      text-align: center;
      position: relative;
      &.isChoose,
      &:hover {
        background-color: $baseBg;
        color: $activeColor;
      }
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        display: block;
        margin: 0 auto;
      }
      .item-name {
        margin-top: 6px;
        cursor: pointer;
      }
      .menu-select {
        width: 0;
        height: 0;
        visibility: hidden;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        left: -20px;
        top: 20px;
      }
      .select-label {
        position: absolute;
        right: -24px;
        top: 9px;
        width: 18px;
        height: 14px;
        cursor: pointer;
      }
      .arrow {
        position: absolute;
        right: 0;
        top: 0;
        padding: 4px;
        width: 18px;
        height: 14px;
        transition: all 0.3s ease;
        transform-origin: 50% 50%;
        transform: rotate(0);
        &.active {
          transform: rotate(180deg);
        }
      }
    }
  }

  .search-bar {
    margin-left: auto;
    margin-right: 20px;
    transition: all 0.3s ease;
    &.shrink {
      margin-right: 397px;
    }
    /deep/ .el-input {
      .el-input__inner {
        background-color: $blockBgColor;
        border: none;
        color: $white;
        width: 380px;
      }
      .el-input__suffix {
        @include add-size($font_size_18);
        cursor: pointer;
      }
    }
  }

  .refresh {
    @include add-size($font_size_20);
    text-align: center;
    line-height: 30px;
    cursor: pointer;
    margin: 0 auto;
    display: block;
    &:hover {
      color: $activeColor;
    }
  }
}
</style>
