const state = {
  currentStock: {
    code: "",
    recover: false
  }, // 当前股票代码
  stockData: {
    // 当前股票数据
    asklist: [],
    bidlist: [],
    brokerSell: {},
    brokerBuy: {},
    query: {},
    trade: [],
    minutely: []
  },
  selfStock: [], // 自选列表
  indexStockData: {} // 指数数据
  ,dealBuySell: {} // 交易时买卖十档的订阅
};

const mutations = {
  setCurrentStock(state, e) {
    state.currentStock = {...state.currentStock, ...e};
  },
  setStockData(state, e) {
    state.stockData = { ...state.stockData, ...e };
  },
  pushStockTrade(state, e){
    state.stockData.trade.push(e);
  },
  setSelfStock(state, e) {
    state.selfStock = e;
  },
  setIndexStockData(state, e) {
    state.indexStockData = { ...state.indexStockData, ...e };
  },
  setDealBuySell(state, e) {
    state.dealBuySell = { ...state.dealBuySell, ...e };
  },
};

const actions = {
  SET_CURRENT_STOCK({ commit }, e) {
    commit("setCurrentStock", e);
  },
  SET_STOCK_DATA({ commit }, e) {
    commit("setStockData", e);
  },
  SET_SELF_STOCK({ commit }, e) {
    commit("setSelfStock", e);
  },
  SET_INDEX_STOCK_DATA({ commit }, e) {
    commit("setIndexStockData", e);
  },
  PUSH_STOCK_TRADE({ commit }, e){
    commit("pushStockTrade", e);
  },
  SET_DEAL_BUY_SELL({ commit }, e) {
    commit('setDealBuySell', e)
  },
};

export default {
  state,
  mutations,
  actions
};
