<template>
  <div class="field-main">
    <div class="sort-bar">
      <div
        class="sort-li"
        :class="{active: sort.active}"
        v-for="sort in sortData"
        :key="sort.label"
        @click="setNearDate(sort)"
      >{{sort.label}}</div>
      <div class="date-picker">
        <el-date-picker
          class="picker"
          id="datePicker"
          v-model="pickerData"
          type="daterange"
          align="right"
          unlink-panels
          value-format="yyyy/MM/dd"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          @change="changeDate"
        ></el-date-picker>
      </div>
    </div>
    <el-table
      :data="stockData"
      v-loading="loading"
      element-loading-text="loading"
      element-loading-background="rgba(0, 0, 0, 0)"
      :height="170"
      :empty-text="emptyText"
      @row-contextmenu="clickHandler"
    >
      <el-table-column prop="StockCode" label="股票代码" align="center">
        <template slot-scope="scope">
          <div>
            <span class="TipBg">HK</span>
            {{scope.row.StockCode}}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
      <el-table-column prop="OrderSide" label="方向" align="center">
        <template slot-scope="scope">
          <div
            :class="{fall: scope.row.OrderSide=='Buy',rise: scope.row.OrderSide=='Sell'}"
          >{{scope.row.OrderSide=='Buy'?'买入':'卖出'}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="AveragePrice" label="委托价" align="center">
        <template slot-scope="scope">
          <div>{{Number(scope.row.OrderPrice).toFixed(3)}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="OrderQty" label="委托量" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.OrderQty}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DateTimes" label="委托时间" align="center"></el-table-column>
      <el-table-column prop="OrderStatus" label="状态" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.OrderStatus | OrderStatus}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" width="820"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import stockTable from "@/components/deal/stock-table";

export default {
  components: {
    stockTable
  },
  props: {
    requestConfig: Object
  },
  data() {
    return {
      sortData: [
        {
          label: "近一周",
          value: 7,
          active: true
        },
        {
          label: "近一月",
          value: 30,
          active: false
        },
        {
          label: "近三月",
          value: 90,
          active: false
        }
      ],
      stockData: [],
      pickerData: [],
      startDate: "",
      endDate: "",
      loading: true,
      emptyText: ""
    };
  },
  activated() {
    if (!this.startDate) {
      this.nearDate(7);
    }
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.system.dealBar.currentType;
    }
  },
  watch: {
    currentType() {
      // 当前选择的账户
      // this.nearDate(7);
      this.getStockData();
    }
  },
  methods: {
    clickHandler() {},
    changeDate(e) {
      this.startDate = e[0];
      this.endDate = e[1];
      this.getStockData();
      this.sortData.forEach(s => {
        s.active = false;
      });
    },
    setNearDate(sort) {
      this.sortData.forEach(s => {
        s.active = false;
      });
      sort.active = true;
      this.nearDate(sort.value);
      this.pickerData = [];
    },
    nearDate(day) {
      let now = new Date();
      // if (this.currentType == 0) {
      //   this.endDate = now.Format("yyyy/MM/dd");
      //   let start = now.getTime() - day * 24 * 60 * 60 * 1000;
      //   this.startDate = new Date(start).Format("yyyy/MM/dd");
      // } else {
        this.endDate = now.Format("yyyy-MM-dd");
        let start = now.getTime() - day * 24 * 60 * 60 * 1000;
        this.startDate = new Date(start).Format("yyyy-MM-dd");
      // }
      this.getStockData();
    },
    getStockData() {
      let accountID = this.$store.state.system.accountInfo.LoginID;
      this.loading = true;
      let params = {};
      if (this.currentType == 0) {
        params = {
          startDate: this.startDate,
          endDate: this.endDate,
          accountID: accountID
        };
      } else {
        params = {
          startDate: this.startDate,
          endDate: this.endDate,
          market: "hk"
        };
      }

      this.axios({
        url: this.requestConfig.entrust[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      })
        .then(res => {
          this.loading = false;
          this.stockData = res.data.data || [];
        })
        .catch(err => {
          this.loading = false;
        });
    }
  },
  filters: {
    OrderStatus(val) {
      const status = {
        Filled: "已成交",
        Waiting: "待成交",
        Reject: "已拒绝",
        Cancel: "已撤单",
        "Partially Filled": "已成交",
        "Host Reg": "已成交",
        "In Market": "未成交",
        "Reject New": "已拒绝",
        "Waiting Cancel": "撤单中"
      };
      return status[val] || "未成交";
    }
  }
};
</script>

<style scoped lang="scss">
.field-main {
  padding: 10px;
  .sort-bar {
    @include flex-center;
    @include add-size($font_size_14);
    height: 41px;
    line-height: 40px;
    border-bottom-color: 1px solid transparent;
    .sort-li {
      margin-right: 44px;
      cursor: pointer;
      position: relative;
      &.active,
      &:hover {
        color: $activeColor;
      }
      &.active {
        &:after {
          content: "";
          width: 30px;
          height: 2px;
          border-radius: 1px;
          background-color: $activeColor;
          position: absolute;
          left: 50%;
          margin-left: -15px;
          bottom: 0;
        }
      }
    }

    .date-picker {
      /deep/ .el-date-editor {
        background-color: inherit;
        border: none;
        position: relative;
        font-family: element-icons;
        &::before,
        &::after {
          content: "\E78E";
          @include add-size($font_size_14);
          position: absolute;
          left: 22px;
          top: -3px;
        }
        &::after {
          left: 177px;
        }
        .el-range-input {
          background-color: inherit;
          color: $white;
          font-family: "sourceFont", "Microsoft Yahei", sans-serif;
          border: 1px solid $defaultColor;
          border-radius: 4px;
          width: 100px;
          height: 22px;
          line-height: 22px;
          padding-left: 20px;
          @include add-size($font_size_12);
        }
        .el-range-separator {
          color: $white;
          margin: 0 5px;
          width: auto;
          font-family: "sourceFont", "Microsoft Yahei", sans-serif;
        }
        .el-range__icon {
          display: none;
        }
      }
    }
  }
  /deep/ .el-table {
    margin-top: 12px;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $red;
      }
      .rise {
        color: $green;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
  }
}
</style>