<template>
  <div class="foundation">
    <div class="fund-data">
      <div class="fund-data-li">
        <span>可用金额(港币)</span>
        <span>{{accountData.availCapital | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>待收本金(港币)</span>
        <span>{{accountData.money | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>待收收益(港币)</span>
        <span>{{accountData.expectProfit | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>累计收益(港币)</span>
        <span>{{accountData.cumulative | moneyFormat}}</span>
      </div>
      <div class="tips-btn">
        <span @click="riskNotice = true">风险提示</span>
        <span @click="commonProblem = true">常见问题</span>
      </div>
    </div>
    <div class="item-type">
      <div class="item-type-li" :class="{active: currentType===0}" @click="currentType = 0">项目列表</div>
      <div class="item-type-li" :class="{active: currentType===1}" @click="currentType = 1">我的项目</div>
    </div>
    <el-table v-show="currentType===0" :data="listData.list" height="160" empty-text=" ">
      <el-table-column prop="fundName" label="项目名称"></el-table-column>
      <el-table-column prop="fundProfitRate" label="期望年化收益" align="center">
        <template slot-scope="scope">
          <div class="rise">+{{scope.row.fundProfitRate}}%</div>
        </template>
      </el-table-column>
      <el-table-column prop="raiseMoney" label="募资总额" align="center"></el-table-column>
      <el-table-column label="剩余可投" align="center">
        <template slot-scope="scope">{{scope.row.raiseMoney - scope.row.surplusRaiseMoney}}</template>
      </el-table-column>
      <el-table-column prop="_residueTime" label="募资开始/截止时间" width="140px" align="center">
        <!-- <template slot-scope="scope">{{scope.row.raiseEndTime }}</template> -->
      </el-table-column>
      <el-table-column prop="lockDays" label="锁定期限" align="center"></el-table-column>
      <el-table-column prop="interestRaisingInfo" label="还款方式" align="center"></el-table-column>
      <el-table-column prop="startMoney" label="起投金额" align="center"></el-table-column>
      <el-table-column prop="payBackInfo" label="起息时间" align="center"></el-table-column>
      <el-table-column prop="raiseEndTime" label="到期时间" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.reachingInterestTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="serviceCharge" label="服务费" align="center"></el-table-column>
      <el-table-column prop="fundUseExplain" label="募资用途" align="center">
        <template slot-scope="scope">
          <div class="use-for" :title="scope.row.fundUseExplain">{{scope.row.fundUseExplain}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <div
            class="action-btn"
            :class="{'no-residue': !scope.row._canBuy}"
            @click="buyIn(scope.row)"
            hover
          >买入</div>
        </template>
      </el-table-column>

      <template slot="append">
        <infinite-loading
          ref="infinite"
          @infinite="getList"
          :identifier="listData.identityId"
          :distance="20"
          forceUseInfiniteWrapper=".el-table__body-wrapper"
        >
          <loading></loading>
          <span slot="no-results">—————— 暂无数据 ———————</span>
          <span slot="no-more">—————— 暂无更多 ———————</span>
        </infinite-loading>
      </template>
    </el-table>
    <el-table v-show="currentType===1" :data="selfListData.list" :height="150" empty-text=" ">
      <el-table-column prop="fundName" label="项目名称"></el-table-column>
      <el-table-column prop="fundProfitRate" label="预期年化收益" align="center">
        <template slot-scope="scope">
          <div class="rise">+{{scope.row.fundProfitRate}}%</div>
        </template>
      </el-table-column>
      <el-table-column prop="money" label="投资金额(港币)" align="center"></el-table-column>
      <el-table-column prop="expectProfit" label="预期回报(港币)" align="center"></el-table-column>
      <el-table-column prop label="项目状态" align="center">
        <template slot-scope="scope">
          <div class="state" :data-status="scope.row.state">{{scope.row.state | stateName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="expectStartTime" label="起息时间" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.expectStartTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="endTime" label="到期时间" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.endTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="buyTime" label="交易时间" align="center"></el-table-column>
      <el-table-column prop="fundOrderNo" label="交易单号" align="center"></el-table-column>

      <template slot="append">
        <infinite-loading
          ref="infinite"
          @infinite="getSelfList"
          :identifier="selfListData.identityId"
          :distance="20"
          forceUseInfiniteWrapper=".el-table__body-wrapper"
        >
          <loading></loading>
          <span slot="no-results">—————— 暂无数据 ———————</span>
          <span slot="no-more">—————— 暂无更多 ———————</span>
        </infinite-loading>
      </template>
    </el-table>
    <el-dialog
      class="fund-info-dialog"
      theame
      center
      :title="currentData.fundName"
      :modal="false"
      :visible="buyInShow"
      width="456px"
      top="15%"
      @close="buyInShow = false"
    >
      <div class="info-content">
        <div class="money-input">
          <div class="add-tip">
            <span class="add-tip-t">加入金额(港币)</span>
            <p
              class="per"
            >最低加入金额{{currentData.startMoney > (currentData.raiseMoney - currentData.surplusRaiseMoney) ? (currentData.raiseMoney - currentData.surplusRaiseMoney) : currentData.startMoney}}港币 {{currentData.startMultiple}}港币递增</p>
          </div>
          <el-input
            maxlength="11"
            v-model="buyInMoney"
            class="num-input"
            type="number"
            @blur="formatMoney"
            placeholder="请输入金额"
          ></el-input>
          <div class="available-money">可用金额：{{accountData.availCapital | moneyFormat}}</div>
        </div>
        <div class="buy-in-btn" @click="buyInInput" hover>买入</div>
      </div>
    </el-dialog>
    <el-dialog
      class="tips-info-dialog"
      theame
      center
      title="常见问题"
      :modal="false"
      :visible="commonProblem"
      width="350px"
      top="15%"
      @close="commonProblem = false"
    >
      <div class="info-content">
        <p class="problem">1.可用金额如何充值?</p>
        <p class="answer">
          可用金额是您股票账户上的可用金额。可前往“交易-入金”
          充值入金。
        </p>
        <p class="problem">2.买入后什么时候开始计息?</p>
        <p class="answer">
          根据项目详情页面-起息时间确定投资起息时间。
          起息时间分为买入后次日起息、买入后T+1起息、项目满标
          后次日起息。
        </p>
        <p class="problem">3.买入如何退出?</p>
        <p class="answer">
          目前只支持项目到期自动退出，当投项目到期后，系统将自
          动为您退出。
        </p>
        <p class="problem">4.买入为什么会失败?</p>
        <p class="answer">4.1买入金额小于最低认购金额或不满足投资递增倍数。</p>
        <p class="answer">4.2买入金额大于项目剩余可投金额。</p>
        <p class="answer">4.3买入金额大于账户可用金额。</p>
        <p class="answer">4.4其他情况可联系易昇证券客服400-116-6913</p>
        <p class="problem">5.本金与利息怎么发放?</p>
        <p class="answer">项目到期后T+1日内本金与收益发放到您的交易账户。</p>
      </div>
    </el-dialog>
    <el-dialog
      class="tips-info-dialog"
      theame
      center
      title="风险提示"
      :modal="false"
      :visible="riskNotice"
      width="430px"
      top="15%"
      @close="riskNotice = false"
    >
      <div class="info-content">
        <p class="problem">操作风险</p>
        <p class="answer">
          不可控制或预测的通信故障、系统故障、设备故障、电力故障等突发事故，
          有可能给出借人造成一定的损失。由于通信故障、电力故障、系统设备故
          障等不可抗力因素造成的影响，可能使出借人无法及时做出合理决策或者
          决策无法执行，对出借人造成一定损失。市场有风险，投资需谨慎。请您
          在出借前，仔细阅读《出借风险说明和禁止性行为》。
        </p>
        <p class="problem">免责声明</p>
        <p class="answer">
          易昇证券作为交易服务平台进行信息发布，不对任何投资人及/或任何交易
          提供任何担保，无论是明示、默示或法定的。易昇证券提供的各种信息及
          资料仅供参考，投资人应依其独立判断做出决策。投资人据此进行投资交
          易的，产生的投资风险由投资人自行承担，易昇证券不承担任何责任。
        </p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import infiniteLoading from "vue-infinite-loading";

export default {
  components: {
    infiniteLoading
  },
  data() {
    return {
      currentType: 0,
      foundationData: [],
      accountData: {},
      listData: {
        list: [],
        identityId: 0,
        page: 1,
        size: 10
      },
      selfListData: {
        list: [],
        identityId: 0,
        page: 1,
        size: 10
      },
      timer: {},
      currentData: {},
      buyInShow: false,
      buyInMoney: "",
      riskNotice: false,
      commonProblem: false
    };
  },
  computed: {
    compareMoney() {
      if (this.currentData.startMoney) {
        let money =
          this.currentData.startMoney >
          this.currentData.raiseMoney - this.currentData.surplusRaiseMoney
            ? this.currentData.raiseMoney - this.currentData.surplusRaiseMoney
            : this.currentData.startMoney;
        return money;
      } else {
        return 0;
      }
    }
  },
  watch: {
    currentType(val) {
      if (val === 1 && this.selfListData.list.length <= 0) {
        this.selfListData.list = [];
        this.selfListData.identityId++;
        this.selfListData.page = 1;
      } else if (val === 0 && this.listData.list.length <= 0) {
        this.listData.list = [];
        this.listData.identityId++;
        this.listData.page = 1;
      }
    }
  },
  activated() {
    this.getAccountData();
    this.currentType = 0;
    this.listData.list = [];
    this.listData.identityId++;
    this.listData.page = 1;
  },
  deactivated() {
    this.selfListData.list = [];
    this.listData.list = [];
  },
  methods: {
    formatMoney() {
      // if (!money) {
      //   this.$message({
      //     message: "输入金额必须是加入金额的整数倍",
      //     type: "error"
      //   });
      // }
    },
    buyInInput() {
      try {
        let inputMoney = Number(this.buyInMoney);
        if(inputMoney <= 0)
          throw new Error("请输入买入金额");
        // console.log(this.compareMoney);
        if (inputMoney < Number(this.compareMoney))
          throw new Error("买入金额不能低于起投金额");

        let moneySec = inputMoney - this.currentData.startMoney;
        let moneySecRes =
          Math.floor(moneySec / this.currentData.startMultiple) *
          this.currentData.startMultiple;

        inputMoney = Number(this.currentData.startMoney) + moneySecRes;
        if (inputMoney > this.accountData.availCapital)
          throw new Error("当前可用余额不足");

        this.axios({
          url: "/fund/project/saveBuyFund",
          method: "post",
          params: {
            buyMoney: inputMoney, // 输入金额
            id: this.currentData.id // 基金主键id
          },
          type: "host2"
        }).then(res => {
          this.$message({
            message: "购买成功",
            type: "success"
          });
          this.buyInMoney = "";
          this.buyInShow = false;
          this.currentType = 1;
          this.selfListData.list = [];
          this.getAccountData();

          this.listData.list = [];
        });
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    buyIn(row) {
      if (row.raiseMoney - row.surplusRaiseMoney <= 0) {
        this.$message({
          message: "募集金额已满！",
          type: "error"
        });
        return;
      }
      if (!row._canBuy) {
        this.$message({
          message: "募资暂未开始",
          type: "error"
        });
        return false;
      }
      this.currentData = row;
      this.buyInShow = true;
      this.buyInMoney = "";
    },
    getAccountData() {
      // host2
      this.axios({
        url: "/fund/project/selectAvailCapital",
        params: {},
        type: "host2"
      }).then(res => {
        this.accountData = res.data.ret || {};
      });
    },
    getList($state) {
      // /fund/project/selectListInfo?page=1&size=10
      let listData = this.listData;
      this.axios({
        url: "/fund/project/selectListInfoPC",
        params: {
          page: listData.page,
          size: listData.size
        },
        type: "host2"
      }).then(res => {
        let list = res.data.ret.list || [];
        if (list.length > 0) {
          list.forEach(i => ((i._residueTime = 0), (i._canBuy = true)));
          if (listData.page == 1) {
            listData.list = list;
          } else {
            listData.list = listData.list.concat(list);
          }
          listData.page++;
          this.setDataTime();
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    getSelfList($state) {
      // /fund/project/selectListInfo?page=1&size=10
      // fund/project/selectProjectList
      let selfListData = this.selfListData;
      this.axios({
        url: "/fund/project/selectProjectList",
        params: {
          page: selfListData.page,
          size: selfListData.size
        },
        type: "host2"
      }).then(res => {
        let list = res.data.ret.list || [];
        if (list.length > 0) {
          if (selfListData.page == 1) {
            selfListData.list = list;
          } else {
            selfListData.list = selfListData.list.concat(list);
          }
          selfListData.page++;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    setDataTime() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        regTime();
        this.setDataTime();
      }, 1000);
      let _this = this;
      regTime();
      function regTime() {
        _this.listData.list.forEach(a => {
          // 判断是否可以买入
          if (a.raiseMoney - a.surplusRaiseMoney <= 0) {
            a._canBuy = false;
          }
          let beginTime = a.raiseBeginTime.replace(/-/g, "/");
          let endTime = a.raiseEndTime.replace(/-/g, "/");
          let now = new Date().getTime();
          let end = new Date(endTime).getTime();
          let begin = new Date(beginTime).getTime();
          let res = "",
            resText = "";
          if (begin > now) {
            res = begin - now;
            resText = "开始";
            a._canBuy = false;
          } else if (end > now) {
            res = end - now;
            resText = "结束";
          } else {
            a._residueTime = "已截止";
            a._canBuy = false;
            return;
          }
          let day = Math.floor(res / (24 * 60 * 60 * 1000));
          let level1 = res % (24 * 60 * 60 * 1000);
          let hh = Math.floor(level1 / (60 * 60 * 1000));
          let level2 = level1 % (60 * 60 * 1000);
          let mm = Math.floor(level2 / 60 / 1000);
          let level3 = level2 % (60 * 1000);
          let ss = Math.floor(level3 / 1000);
          a._residueTime = `${day}天${hh}小时${mm}分${ss}秒${resText}`;
        });
      }
    }
  },
  filters: {
    // residueTime(val) {
    //   let now = new Date().getTime();
    //   let end = new Date(val).getTime();
    //   let resi = new Date(end - now).Format("dd天hh小时mm分ss秒");
    //   return resi;
    // }
    stateName(val) {
      const state = {
        "-1": "已逾期",
        "1": "还款中",
        "2": "已结清",
        "3": "提前还款",
        "4": "逾期还款",
        "5": "等待起息"
      };
      return state[val] || "异常";
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {}
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
$borColor: #323840;
.foundation {
  padding-left: 20px;
  padding-right: 20px;
  .fund-data {
    @include flex-center;
    padding: 20px 0;
    color: $white;
    position: relative;
    @include add-size($font_size_14);
    .fund-data-li {
      margin-right: 72px;
      span:first-child {
        margin-right: 4px;
      }
    }
    .tips-btn {
      position: absolute;
      right: 0;
      top: 20px;
      span {
        cursor: pointer;
        color: #178de7;
        margin-left: 24px;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
  .item-type {
    @include flex-center;
    .item-type-li {
      padding-bottom: 10px;
      margin-right: 53px;
      position: relative;
      cursor: pointer;
      &.active {
        color: $activeColor;
        &::after {
          content: "";
          position: absolute;
          width: 30px;
          height: 2px;
          left: 50%;
          bottom: 0;
          margin-left: -15px;
          background-color: $activeColor;
        }
      }
    }
  }
  /deep/ .el-table {
    .action-btn {
      width: 48px;
      height: 22px;
      border-radius: 6px;
      line-height: 22px;
      margin: 0 auto;
      color: $white;
      @include linear;
      cursor: pointer;
      &.no-residue {
        background: #999;
      }
    }
    .use-for {
      @include single-line-ellipsis;
    }
    .state {
      &[data-status="1"] {
        color: $red;
      }
      &[data-status="5"] {
        color: #178de7;
      }
    }
  }
  .fund-info-dialog {
    .info-content {
      color: $defaultColor;
      padding: 36px 38px;
      .money-input {
        border: 1px solid $borColor;
        border-radius: 6px;
        padding: 30px;
        @include add-size($font_size_12);
        .add-tip {
          line-height: 18px;
          .add-tip-t {
            @include add-size($font_size_18);
            color: $white;
          }
          .per {
            line-height: 24px;
          }
        }
        /deep/.num-input {
          margin-top: 10px;
          input {
            padding-left: 70px;
            background-color: inherit;
            border: none;
            border-radius: 0;
            border-bottom: 1px solid $borColor;
            width: 270px;
            @include add-size($font_size_16);
            color: $white;
          }
          &:before {
            content: "HK$";
            color: $white;
            @include add-size(30px);
            position: absolute;
            left: 0;
            top: 0;
          }
        }
        .available-money {
          padding-top: 10px;
        }
      }
      .buy-in-btn {
        width: 140px;
        height: 30px;
        cursor: pointer;
        @include add-size($font_size_14);
        @include linear;
        color: $white;
        line-height: 30px;
        text-align: center;
        border-radius: 6px;
        margin: 49px auto 36px;
      }
    }
  }
  .tips-info-dialog {
    .info-content {
      @include add-size($font_size_12);
      color: $white;
      padding: 4px 20px 20px;
      line-height: 16px;
      p {
        margin-bottom: 6px;
      }
      .problem {
        margin-top: 16px;
      }
    }
  }
}
</style>