<template>
  <div
    class="newsContent"
    ref="newsContent"
    v-if="stockData.code"
    tabindex="1"
    @keyup.up="keyUp"
    @keyup.down="keyDown"
  >
    <div class="list-content">
      <div
        class="contentRow"
        v-for="news in newsReq.list"
        :key="news.newsOwnId"
        @click="showDetail(news)"
      >
        <span :title="news.newsTitle">{{news.newsTitle}}</span>
        <span>{{news.newsPubTime}}</span>
      </div>
    </div>
    <infinite-loading
      slot="append"
      @infinite="companyNews"
      force-use-infinite-wrapper=".newsContent"
      spinner="bubbles"
      class="loading"
      :identifier="infiniteId"
      ref="infiniteLoading"
    >
      <span slot="no-more" class="no-more">—————— 加载完成 ———————</span>
      <span slot="no-results" class="no-result">暂无数据</span>
    </infinite-loading>
    <newsDetail :newsList="newsReq.list" ref="newsDetail" />
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading";
import newsDetail from "@/components/common/news-detail";

export default {
  name: "newsList",
  components: {
    InfiniteLoading,
    newsDetail
  },
  data() {
    return {
      newsReq: {
        page: 1,
        size: 10,
        list: []
      },
      showList: false,
      stockCode: "",
      infiniteId: 0
    };
  },
  props: {
    stockData: Object,
    activeName: String
  },
  mounted() {},
  watch: {
    "stockData.code"(v) {
      this.newsReq.page = 1;
      this.newsReq.list = [];
      this.infiniteId++;
    },
    activeName(val) {
      if (val === "first") {
        if (
          this.stockData.code &&
          this.stockCode !=
            this.stockData.code.replace("hk", "").replace("us", "")
        ) {
          this.newsReq.page = 1;
          this.newsReq.list = [];
          this.infiniteId++;
        }
      }
    }
  },
  methods: {
    keyUp(e) {
      this.$refs.newsDetail.prev();
      e.stopPropagation();
    },

    keyDown(e) {
      this.$refs.newsDetail.next();
      e.stopPropagation();
    },
    showDetail(news) {
      this.$refs.newsDetail.show(news);
    },
    resetList() {
      this.showList = false;
      this.$nextTick(() => {
        this.showList = true;
      });
    },
    companyNews($state) {
      //  GET /selectSymPolOption
      setTimeout(() => {
        let url = "/selectSymPolOption";
        let stockCode = this.stockData.code.replace("hk", "").replace("us", "");
        this.stockCode = stockCode;
        let newsReq = this.newsReq;
        let data = {
          stockCode: stockCode,
          page: newsReq.page,
          size: newsReq.size
        };
        this.axios({
          url: url,
          params: data,
          type: "hostNews"
        })
          .then(res => {
            // console.log(res, "379aa");
            if (res.data.ret.list.length > 0) {
              if (newsReq.page == 1) {
                newsReq.list = res.data.ret.list;
                // $state.reset();
              } else {
                newsReq.list = newsReq.list.concat(res.data.ret.list);
              }
              newsReq.page++;
              if ($state) $state.loaded();
            } else {
              if ($state) $state.complete();
            }
          })
          .catch(err => {
            if ($state) $state.complete();
          });
      }, 0);
    }
  }
};
</script>

<style lang="scss" scoped>
.newsContent {
  @include add-size($font_size_16);
  height: 100%;
  position: relative;
  overflow: auto;
  outline: none;
  .list-content{
    min-height: calc(100% - 50px);
  }
  .contentRow {
    @include flex-box;
    cursor: pointer;
    justify-content: space-between;
    padding: 17px 0;
    border-bottom: 1px solid #323840;
    color: #fff;
    > span {
      max-width: 800px;
      @include single-line-ellipsis;

      &:last-child {
        padding-right: 15px;
      }
    }
    &:hover {
      color: $activeColor;
    }
  }
  /deep/ .loading {
    .no-more,
    .no-result {
      line-height: 36px;
      @include add-size($font_size_12);
    }
  }
}
</style>