<template>
  <div class="newsContent" v-if="stockData.code">
    <div class="list-content">
      <div
        class="contentRow"
        v-for="news in newsReq.list"
        :key="news.newsOwnId"
        @click="showDetail(news)"
      >
        <span>{{news.f002V}}</span>
        <span>{{Number(news.f001D) | dateFormat("yyyy-MM-dd")}}</span>
      </div>
    </div>

    <infinite-loading
      slot="append"
      @infinite="companyNotice"
      force-use-infinite-wrapper=".newsContent"
      spinner="bubbles"
      :identifier="infiniteId"
      class="loading"
      ref="infiniteLoading"
    >
      <span slot="no-more" class="no-more">—————— 加载完成 ———————</span>
      <span slot="no-results" class="no-result">暂无数据</span>
    </infinite-loading>
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading";
const shell = require("electron").shell;

export default {
  name: "newsList",
  components: {
    InfiniteLoading
  },
  data() {
    return {
      newsReq: {
        page: 1,
        size: 10,
        list: []
      },
      showList: false,
      stockCode: "",
      infiniteId: 0
    };
  },
  props: {
    stockData: Object,
    activeName: String
  },
  mounted() {
    this.getData();
  },
  watch: {
    stockData() {
      this.newsReq.page = 1;
      this.newsReq.list = [];
      this.infiniteId++;
    },
    activeName(val) {
      if (val === "second") {
        if (
          this.stockData.code &&
          this.stockCode !=
            this.stockData.code.replace("hk", "").replace("us", "")
        ) {
          this.getData();
        }
      }
    }
  },
  activated() {},
  methods: {
    getData() {
      this.newsReq.page = 1;
      this.newsReq.list = [];
      this.infiniteId++;
    },
    showDetail(news) {
      // f003V
      shell.openExternal(news.f003V);
    },
    resetList() {
      this.showList = false;
      this.$nextTick(() => {
        this.showList = true;
      });
    },
    companyNotice($state) {
      //  GET /selectSymPol
      setTimeout(() => {
        let url = "/selectSymPol";
        let stockCode = this.stockData.code.replace("hk", "").replace("us", "");
        this.stockCode = stockCode;
        let newsReq = this.newsReq;
        let data = {
          stockcode: stockCode,
          page: newsReq.page,
          pageSize: newsReq.size
        };
        this.axios({
          url: url,
          params: data,
          type: "hostNews",
          hideMessage: true
        })
          .then(res => {
            // console.log(res, "379aa");
            if (
              res.data.ret.announcement &&
              res.data.ret.announcement.length > 0
            ) {
              if (newsReq.page == 1) {
                newsReq.list = res.data.ret.announcement;
              } else {
                newsReq.list = newsReq.list.concat(res.data.ret.announcement);
              }
              newsReq.page++;
              if ($state) $state.loaded();
            } else {
              if ($state) $state.complete();
            }
          })
          .catch(err => {
            if ($state) $state.complete();
          });
      }, 300);
    }
  }
};
</script>

<style lang="scss" scoped>
.newsContent {
  @include add-size($font_size_16);
  height: 100%;

  overflow: auto;
  .list-content{
    min-height: calc(100% - 50px);
  }
  .contentRow {
    @include flex-box;
    cursor: pointer;
    justify-content: space-between;
    padding: 22px 0;
    border-bottom: 1px solid #323840;
    color: #fff;
    > span {
      &:last-child {
        padding-right: 15px;
      }
    }
    &:hover {
      color: $activeColor;
    }
  }
  /deep/ .loading {
    .no-more,
    .no-result {
      line-height: 36px;
      @include add-size($font_size_12);
    }
  }
}
</style>