<template>
  <div class="update">
    <el-button @click="checkUpdate">更新</el-button>
  </div>
</template>

<script>
const { ipcRenderer: ipc } = require("electron");

export default {
  components: {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    checkUpdate() {
      this.axios({
        url: "https://www.yisita123.com/v1/common/updateInfo",
        method: "get",
        params: {}
      }).then(res => {
        console.log(res);
        ipc.send("update", res.data.data);
      });
    }
  },
  created() {},

  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang="scss" scoped>
//@import url(); 引入公共css类
.update {
  position: absolute;
  left: 30px;
  bottom: 60px;
  z-index: 12;
}
</style>
