<template>
  <div class="deal-lock">
    <el-dialog
      theame
      title="解锁交易"
      :visible="dealLock"
      @close="dealLock = false"
      width="300px"
      :modal="false"
      height="250px"
      top="15%"
    >
      <div class="title" slot="title">解锁交易</div>
      <div class="lock-content">
        <div class="account">
          <span>账号：</span>
          <span>{{ebokerUsername.id}}</span>
        </div>
        <div class="password">
          <span>密码：</span>
          <el-input v-model="password" maxlength="20" placeholder="请输入交易密码" type="password"></el-input>
        </div>
        <el-button class="confirm" @click="confirm">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      dealLock: false,
      password: ""
    };
  },
  computed: {
    ebokerUsername() {
      let ebokerUsername = {
        id: this.$store.state.system.accountInfo.ebokerUsername,
        name: this.$store.state.system.accountInfo.enName
      };
      return ebokerUsername;
    }
    // orgUsername() {
    //   let orgUsername = {
    //     id: this.$store.state.AccountLogin.organizationData.APPUSERNAME,
    //     name: this.$store.state.AccountLogin.organizationData.NAME
    //   };
    //   return orgUsername;
    // },
    // currentType() {
    //   return this.$store.state.AccountLogin.currentType;
    // }
  },
  watch: {},
  methods: {
    confirm() {
      if (!this.password) {
        this.$message({ message: "请输入密码", type: "error" });
        return false;
      }
      this.axios({
        url: "/validatePassword",
        method: "post",
        params: {
          userNo: this.ebokerUsername.id,
          password: this.password
        },
        type: "hostEbroker"
      }).then(res => {
        this.dealLock = false;
        this.password = "";
        localStorage.setItem("lockTime", Date.now());
        this.$emit("verify", res);
      });
    },
    show(){
      this.dealLock = true;
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.deal-lock {
  .title {
    text-align: center;
    @include add-size($font_size_16);
    line-height: 30px;
  }
  .lock-content {
    color: $defaultColor;
    // text-align: center;
    line-height: 30px;
    padding: 20px 30px;
    .password {
      margin-top: 10px;
      @include flex-center;
      /deep/ .el-input {
        width: 150px;
        .el-input__inner {
          background-color: inherit;
          border-radius: 0;
          line-height: 30px;
          height: 30px;
          border-color: $defaultColor;
        }
      }
    }
    .confirm {
      @include linear;
      border: none;
      line-height: 24px;
      height: 24px;
      color: $white;
      padding: 0;
      width: 44px;
      margin: 20px auto 0;
      display: block;
    }
  }
}
</style>