<template>
  <div class="vip-buy" :min="min">
    <div class="vip-btn"  @click="linkMarket">
      <div class="text">您尚未开启LV{{level || 2}}行情</div>
      <div class="btn-text">立即开启</div>
    </div>
    <p class="vip-tips">应港交所要求，非高级订阅用户行情延迟至少15分钟 。</p>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  props: {
    min: Boolean,
    level: Number
  },
  computed: {},
  watch: {},
  methods: {
    linkMarket() {
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 10,
        timestamp: new Date().getTime()
      });
      setTimeout(() => {
        this.$router.push({ path: "wd", query: { target: "market" } });
      }, 100);
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.vip-buy {
  width: 100%;

  .vip-btn {
    margin: 30px auto;
    width: 80%;
    // width: 278px;
    height: 124px;
    background: url("~@/assets/images/hq/vip-bg.png") center no-repeat;
    background-size: contain;
    cursor: pointer;
    .text {
      color: #370704;
      text-align: center;
      @include add-size($font_size_16);
      padding-top: 30px;
    }
    .btn-text {
      text-align: center;
      @include add-size($font_size_14);
      background-color: #370704;
      color: $white;
      width: 100px;
      height: 30px;
      line-height: 30px;
      margin: 20px auto;
      border-radius: 8px;
    }
  }
  .vip-tips {
    text-align: center;
  }
  &[min="true"] {
    .vip-btn {
      width: 95%;

      .text {
        @include add-size($font_size_12);
      }
      .btn-text {
        @include add-size($font_size_12);
        width: 60px;
        height: 24px;
        line-height: 24px;
        border-radius: 4px;
      }
    }
  }
}
</style>