<template>
  <div class="search">
    <el-autocomplete
      class="my-autocomplete"
      v-model="state"
      clearable
      :fetch-suggestions="querySearch"
      placeholder="请输入股票名称/代码"
      :trigger-on-focus="false"
      :debounce="500"
      @select="handleSelect"
    >
      <template slot-scope="{ item }">
        <div class="name" v-show="item.code.slice(0, 2) == 'hk'" :title="item.value">
          <span class="hkSpan">
            <span>HK</span>
          </span>
          <span>{{ item.code.slice(2) }}</span>
          <span>{{ item.value }}</span>
        </div>
        <div class="name" v-show="item.code.slice(0, 2) == 'us'" :title="item.value">
          <span class="usSpan">
            <span>US</span>
          </span>
          <span>{{ item.code.slice(2) }}</span>
          <span>{{ item.value }}</span>
        </div>
      </template>
    </el-autocomplete>
  </div>
</template>
<script>
export default {
  name: "search",
  props: {
    currentStock: Object
  },
  data() {
    return {
      restaurants: [],
      state: ""
    };
  },
  watch:{
    currentStock(val){
      this.state = val.name
    }
  },
  methods: {
    querySearch(queryString, cb) {
      console.log(queryString);
      this.loadAll(queryString, cb);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    handleSelect(item) {
      this.$emit("getMessage", item);
    },
    loadAll(queryString, cb) {
      // console.log(queryString);
      this.restaurants = [];
      if (queryString == "") {
        return;
      }
      let url = "/quote-service/api/v1/stocksearch";
      let data = {
        condition: queryString
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        console.log(res, 167);
        let data = res.data.Data;
        data.map((value, index) => {
          let dataAry = {};
          dataAry.value = value.StockInfo.name;
          dataAry.code = value.StockInfo.code;
          this.restaurants.push(dataAry);
        });
        // let restaurants = this.restaurants;
        // let results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // console.log(results)
        // 调用 callback 返回建议列表的数据
        cb(this.restaurants);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.search {
  width: 220px;
  /deep/.el-autocomplete {
    display: block;
    .el-input__inner {
      background-color: $blockBgColor;
      border: 1px solid $blockBgColor;
    }
   
  }
}
</style>