<template>
  <div class="new-stock" v-if="!hidePage">
    <div class="left-content">
      <!-- <div class="warrant-btn">
      </div>-->
      <div class="table-list" ref="tableList">
        <div class="table-block">
          <div class="table-title">
            <div class="left-type">已递表（{{ listData.ydb.total }}）</div>
            <i class="el-icon-refresh refresh" @click="refresh('ydb')"></i>
            <div class="right-slide" @click="stretch(0)">
              {{ slideData[0].label }}
            </div>
          </div>
          <el-table
            :data="listData.ydb.list"
            empty-text=" "
            :height="slideData[0].show ? height : 0"
          >
            <el-table-column
              prop="f001v"
              class-name="left_20"
              label="公司名称"
              align="left"
              width="420"
              ref="tableRef1"
            >
              <template slot-scope="scope">
                <div>{{ scope.row.f001v }}</div>
              </template>
            </el-table-column>

            <el-table-column prop="f007v" label="当前状态" align="center">
              <template slot="header" slot-scope="scope">
                <label for="status-to" class="board-to" :data-i="scope">
                  当前状态
                  <i
                    class="el-icon-caret-bottom"
                    :class="{ active: listData.ydb.focus2 }"
                  ></i>
                </label>
                <el-select
                  id="status-to"
                  class="table-select"
                  v-model="listData.ydb.F007V"
                  @focus="listData.ydb.focus2 = true"
                  @blur="listData.ydb.focus2 = false"
                >
                  <el-option
                    :label="st.label"
                    :value="st.value"
                    v-for="st in listData.ydb.F007VSelect"
                    :key="st.value"
                  ></el-option>
                </el-select>
              </template>
              <template slot-scope="scope">
                <div>{{ scope.row.f007v | boardText }}</div>
              </template>
            </el-table-column>
            <el-table-column prop="f002d" label="申请日期" align="center">
              <template slot-scope="scope">
                <div>{{ scope.row.f002d }}</div>
              </template>
            </el-table-column>
            <el-table-column prop="f005v" label="所属板块" align="center">
              <template slot="header" slot-scope="scope">
                <label for="board-to" class="board-to" :data-i="scope">
                  所属板块
                  <i
                    class="el-icon-caret-bottom"
                    :class="{ active: listData.ydb.focus1 }"
                  ></i>
                </label>
                <el-select
                  id="board-to"
                  class="table-select"
                  v-model="listData.ydb.F005V"
                  @focus="listData.ydb.focus1 = true"
                  @blur="listData.ydb.focus1 = false"
                >
                  <el-option
                    :label="op.label"
                    :value="op.value"
                    v-for="op in listData.ydb.F005VSelect"
                    :key="op.value"
                  ></el-option>
                </el-select>
              </template>
              <template slot-scope="scope">
                <div>{{ scope.row.f005v | boardText }}</div>
              </template>
            </el-table-column>
            <el-table-column label="递表文件" align="center">
              <template slot-scope="scope">
                <span
                  class="check-prospectus"
                  @click="checkProspectus(scope.row)"
                  >点击查看递表文件</span
                >
              </template>
            </el-table-column>
            <template slot="append">
              <infinite-loading
                ref="infinite"
                @infinite="getYDBListData"
                :identifier="listData.ydb.infiniteId"
                :distance="20"
                forceUseInfiniteWrapper=".table1"
              >
                <loading></loading>
                <span slot="no-more" class="no-more"
                  >—————— 暂无更多 ———————</span
                >
                <span slot="no-results" class="no-results">暂无数据</span>
              </infinite-loading>
            </template>
          </el-table>
        </div>
        <div class="table-block">
          <div class="table-title">
            <div class="left-type">可认购（{{ listData.rgz.total }}）</div>
            <i class="el-icon-refresh refresh" @click="refresh('rgz')"></i>
            <div class="right-slide" @click="stretch(3)">
              {{ slideData[3].label }}
            </div>
          </div>
          <el-table
            :data="listData.rgz.list"
            empty-text=" "
            :height="slideData[3].show ? height : 0"
            @row-click="goWaitDetail($event, 1)"
            ref="tableRef2"
          >
            <el-table-column
              prop="stockCode"
              label="股票代码"
              class-name="icon-cell"
              align="center"
            >
              <template slot-scope="scope">
                <div class="cell">
                  <span class="TipBg">HK</span>
                  <span>{{ scope.row.productCode }}</span>
                  <div class="icon-cut">
                    <img
                      src="@/assets/images/new-stock/icon3.png"
                      alt
                      title="现金认购"
                    />
                    <img
                      src="@/assets/images/new-stock/icon1.png"
                      alt
                      title="融资比例"
                      v-if="scope.row.leverMultiple > 0"
                    />
                    <img
                      src="@/assets/images/new-stock/icon2.png"
                      alt
                      title="国际配售"
                      v-if="scope.row.international == 0"
                    />
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="stockName" label="股票名称" align="center">
              <template slot-scope="scope">
                <div class="cell stock-name" :title="scope.row.nameGb">
                  {{ scope.row.nameGb }}
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="issuePriceRange"
              label="招股价"
              align="center"
            >
              <template slot-scope="scope">
                <div class="cell stock-name">
                  {{ scope.row.issuePriceRange }} 港币
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="qty" label="每手股数" align="center">
              <template slot-scope="scope">
                <div class="cell stock-name">{{ scope.row.qty }} 股</div>
              </template>
            </el-table-column>
            <el-table-column
              prop="priceAllo"
              label="最低认购金额"
              align="center"
            >
              <template slot-scope="scope"
                >{{ scope.row.priceAllo | point }} 港币</template
              >
            </el-table-column>
            <el-table-column
              prop="issuedShares"
              label="发行数量"
              align="center"
            >
              <template slot-scope="scope"
                >{{ scope.row.issuedShares | digitUnit }} 股</template
              >
            </el-table-column>
            <el-table-column
              prop="closeTime"
              label="认购截止时间"
              align="center"
            >
              <!-- <template slot-scope="scope">{{scope.row.issuedShares | digitUnit}} 股</template> -->
            </el-table-column>
            <!-- <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <div
                  class="act-btn"
                  v-if="scope.row._isBuy"
                  @click.stop="showIPOMask(scope.row)"
                >点击认购</div>
                <div class="act-btn is-end" v-else>认购结束</div>
              </template>
            </el-table-column>-->

            <template slot="append">
              <infinite-loading
                ref="infinite"
                @infinite="getListData($event, 'rgz')"
                :distance="20"
                :identifier="listData.rgz.infiniteId"
                forceUseInfiniteWrapper=".table2"
              >
                <loading></loading>
                <span slot="no-more" class="no-more"
                  >—————— 暂无更多 ———————</span
                >
                <span slot="no-results" class="no-results">暂无数据</span>
              </infinite-loading>
            </template>
          </el-table>
        </div>
        <div class="table-block">
          <div class="table-title">
            <div class="left-type">待上市（{{ listData.dss.total }}）</div>
            <i class="el-icon-refresh refresh" @click="refresh('dss')"></i>
            <div class="right-slide" @click="stretch(1)">
              {{ slideData[1].label }}
            </div>
          </div>
          <el-table
            :data="listData.dss.list"
            empty-text=" "
            :height="slideData[1].show ? height : 0"
            @row-click="goWaitDetail"
            ref="tableRef3"
          >
            <el-table-column prop="productCode" label="股票代码" align="center">
              <template slot-scope="scope">
                <div class="cell">
                  <span class="TipBg">HK</span>
                  {{ scope.row.productCode }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="nameGb" label="股票名称" align="center">
              <template slot-scope="scope">
                <div class="cell stock-name" :title="scope.row.nameGb">
                  {{ scope.row.nameGb }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="allotPrice" label="发行价" align="center">
              <template slot-scope="scope">{{
                scope.row.allotPrice ? scope.row.allotPrice + "港币" : "- -"
              }}</template>
            </el-table-column>
            <el-table-column
              prop="subscribeMultiple"
              label="认购倍数"
              align="center"
            >
              <template slot-scope="scope">
                <div>{{ scope.row.subscribeMultiple | point }}</div>
              </template>
            </el-table-column>
            <el-table-column
              prop="successRate"
              label="一手中签率"
              align="center"
            >
              <template slot-scope="scope">{{
                Number(scope.row.successRate)
                  ? Number(scope.row.successRate).toFixed(2) + "%"
                  : "- -"
              }}</template>
            </el-table-column>
            <el-table-column
              prop="allotDate"
              label="暗盘交易时间"
              align="center"
            >
              <template slot-scope="scope">{{ scope.row.allotDate }}</template>
            </el-table-column>
            <!-- <el-table-column prop="qty" label="每手股数" align="center"></el-table-column>
            <el-table-column prop="startTime" label="申购日期" align="center"></el-table-column>-->
            <el-table-column
              prop="listingDate"
              label="上市日期"
              align="center"
            ></el-table-column>
            <!-- <el-table-column prop="inDustry" label="一手中签率"  align="center"></el-table-column> -->
            <!-- <el-table-column label="招股书" align="center">
              <template slot-scope="scope">
                <span class="check-prospectus" @click="checkProspectus(scope.row)">查看</span>
              </template>
            </el-table-column>-->
            <template slot="append">
              <infinite-loading
                ref="infinite"
                @infinite="getListData($event, 'dss')"
                :distance="20"
                :identifier="listData.dss.infiniteId"
                forceUseInfiniteWrapper=".table3"
              >
                <loading></loading>
                <span slot="no-more" class="no-more"
                  >—————— 暂无更多 ———————</span
                >
                <span slot="no-results" class="no-results">暂无数据</span>
              </infinite-loading>
            </template>
          </el-table>
        </div>
        <!-- 已上市 -->
        <div class="table-block">
          <div class="table-title">
            <div class="left-type">已上市（{{ listData.yss.total }}）</div>
            <i class="el-icon-refresh refresh" @click="refresh('yss')"></i>
            <div class="right-slide" @click="stretch(2)">
              {{ slideData[2].label }}
            </div>
          </div>
          <el-table
            :data="listData.yss.list"
            empty-text=" "
            :height="slideData[2].show ? height : 0"
            @row-click="goDetail"
            ref="tableRef4"
          >
            <el-table-column prop="productCode" label="股票代码" align="center">
              <template slot-scope="scope">
                <div class="cell">
                  <span class="TipBg">HK</span>
                  {{ scope.row.productCode.slice(2) }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="股票名称" align="center">
              <template slot-scope="scope">
                <div class="cell stock-name" :title="scope.row.name">
                  {{ scope.row.name }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="allotPrice" label="发行价" align="center">
              <template slot-scope="scope"
                >{{ Number(scope.row.allotPrice).toFixed(3) }} 港币</template
              >
            </el-table-column>
            <el-table-column prop="last" label="最新价" align="center">
              <template slot-scope="scope">
                <div
                  :class="{
                    fall: Number(scope.row.last) < Number(scope.row.allotPrice),
                    rise: Number(scope.row.last) > Number(scope.row.allotPrice)
                  }"
                >
                  {{ scope.row.last }} 港币
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="firstDayFain"
              label="首日涨幅"
              align="center"
            >
              <template slot-scope="scope">
                <div
                  :class="{
                    rise: scope.row.firstDayFain > 0,
                    fall: scope.row.firstDayFain < 0
                  }"
                >
                  {{ scope.row.firstDayFain }}%
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="cumulativeIncrease"
              label="累积涨幅"
              align="center"
            >
              <template slot-scope="scope">
                <div
                  :class="{
                    rise: scope.row.cumulativeIncrease > 0,
                    fall: scope.row.cumulativeIncrease < 0
                  }"
                >
                  {{ scope.row.cumulativeIncrease }}%
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="listingDate"
              label="上市日期"
              align="center"
            ></el-table-column>
            <template slot="append">
              <infinite-loading
                ref="infinite"
                @infinite="getYSSListData"
                :distance="20"
                :identifier="listData.yss.infiniteId"
                forceUseInfiniteWrapper="table4"
              >
                <loading></loading>
                <span slot="no-more" class="no-more"
                  >—————— 暂无更多 ———————</span
                >
                <span slot="no-results" class="no-results">暂无数据</span>
              </infinite-loading>
            </template>
          </el-table>
        </div>
      </div>
    </div>
    <div class="right-content">
      <div class="news-block">
        <div class="news-type-title right-bolck-title">
          <div
            class="zt"
            :class="{ active: rightNewsData.current === 'sf' }"
            @click="rightNewsData.current = 'sf'"
          >
            专题资讯
          </div>
          <div
            class="dx"
            :class="{ active: rightNewsData.current === 'nl' }"
            @click="rightNewsData.current = 'nl'"
          >
            打新课堂
          </div>
          <div class="act-btn" @click="showRecord">
            <i class="act-icon"></i>
            认购记录
          </div>
          <div class="act-btn" @click="showWarrant">
            <i class="act-icon"></i>
            认购流程
          </div>
        </div>
        <div class="news-list">
          <div
            class="news-list-content"
            v-show="rightNewsData.current === 'sf'"
          >
            <div
              class="news-li"
              v-for="(news, index) in rightNewsData.sf.list"
              :key="index"
              @click="newsDetail(news)"
            >
              <div class="news-li-title" :title="news.newsTitle">
                {{ news.newsTitle }}
              </div>
              <div class="news-li-date">
                {{ news.newsPubTime.slice(0, 10) }}
              </div>
            </div>
          </div>
          <div
            class="news-list-content"
            v-show="rightNewsData.current === 'nl'"
          >
            <div
              class="news-li"
              v-for="(news, index) in rightNewsData.nl.list"
              :key="index"
              @click="newsDetail2(news)"
            >
              <div class="news-li-title" :title="news.newsTitle">
                {{ news.newsTitle }}
              </div>
              <div class="news-li-date">
                {{ news.newsPubTime.slice(0, 10) }}
              </div>
            </div>
          </div>
          <infinite-loading
            ref="infinite"
            @infinite="rightNews"
            :identifier="rightNewsData[rightNewsData.current].infiniteId"
            :distance="20"
            forceUseInfiniteWrapper=".news-list"
            v-if="!rightNewsData.change"
          >
            <loading></loading>
            <span slot="no-more" class="no-more">—————— 暂无更多 ———————</span>
            <span slot="no-results" class="no-results">暂无数据</span>
          </infinite-loading>
        </div>
      </div>
      <div class="market-general">
        <div class="right-bolck-title">
          <div>市场概况</div>
        </div>
        <div class="market-table">
          <div
            class="market-table-row"
            v-for="(row, i) in marketTable"
            :key="i"
          >
            <div class="label">{{ row.label }}</div>
            <div class="value" v-for="(val, m) in row.value" :key="m">
              <div v-if="i == 0">{{ val }}</div>
              <div v-if="i == 1">{{ Number(val) }}</div>
              <div
                :class="{
                  rise: i > 3 && Number(val) > 0,
                  fall: i > 3 && Number(val) < 0
                }"
                v-if="i > 1"
              >
                {{ val | digitUnit }}{{ row.unit }}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="industry-statistics">
        <div class="right-bolck-title">
          <div class="right-bolck-title_text">行业统计</div>
          <div
            class="data-part-li"
            :class="{ active: industryFlag === 'month' }"
            @click="industryFlag = 'month'"
          >
            近一个月
          </div>
          <div
            class="data-part-li"
            :class="{ active: industryFlag === 'mar' }"
            @click="industryFlag = 'mar'"
          >
            近三个月
          </div>
          <div
            class="data-part-li"
            :class="{ active: industryFlag === 'year' }"
            @click="industryFlag = 'year'"
          >
            近一年
          </div>
        </div>
        <div class="industry-data">
          <div class="circle-ring">
            <div class="ring" ref="ring"></div>
          </div>
          <div class="num-data">
            <ul class="num-data-ul">
              <li class="num-data-li num-data-li-title">
                <div class="li-name">行业名称</div>
                <div class="li-new">新股占比</div>
                <div class="li-first">首日占比</div>
              </li>
              <li class="num-data-li" v-for="(li, m) in industryList" :key="m">
                <div class="li-name cir-icon">{{ li.sector }}</div>
                <div class="li-new">
                  {{ Number(li.newShareRate).toFixed(2) }}%
                </div>
                <div class="li-first">
                  {{ Number(li.firstRate).toFixed(2) }}%
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <NewsDetail
      ref="NewsDetail"
      :newsList="
        rightNewsData.current === 'sf'
          ? rightNewsData.sf.list
          : rightNewsData.nl.list
      "
    />
    <Warrant ref="warrant" />
    <IPOMask ref="IPOMask" />
    <Record ref="Record" />
    <NewsMask ref="NewsDetail2" />
  </div>
</template>

<script>
import Warrant from "./Warrant";
import IPOMask from "./IPO-mask";
import Record from "./Record";
import NewsMask from "./newsMask";
import NewsDetail from "@/components/common/news-detail";
import infiniteLoading from "vue-infinite-loading";
const shell = require("electron").shell;

export default {
  components: {
    Warrant,
    IPOMask,
    NewsDetail,
    Record,
    NewsMask,
    infiniteLoading
  },
  data() {
    return {
      hidePage: false,
      slideData: [
        {
          show: true,
          label: "收起"
        },
        {
          show: true,
          label: "收起"
        },
        {
          show: true,
          label: "收起"
        },
        {
          show: true,
          label: "收起"
        }
      ],
      height: 162,
      tableData: [],
      value: 0,
      F007VSelect: [
        {
          label: "正在处理",
          value: 0
        },
        {
          label: "通过聆讯",
          value: 1
        }
      ],
      listData: {
        ydb: {
          page: 1,
          size: 25,
          total: 0,
          F005V: "", //上市板块 MAIN-主板 GEM-创业板
          F007V: "", // 状态 0-正在处理 1-通过聆讯2-已上市 3-被发回
          list: [],
          infiniteId: 100,
          focus1: false,
          focus2: false,
          F005VSelect: [
            {
              label: "全部板块",
              value: ""
            },
            {
              label: "主板",
              value: "MAIN"
            },
            {
              label: "创业板",
              value: "GEM"
            }
          ],
          F007VSelect: [
            {
              label: "全部",
              value: ""
            },
            {
              label: "正在处理",
              value: 0
            },
            {
              label: "通过聆讯",
              value: 1
            }
          ]
        },
        dss: {
          page: 1,
          size: 25,
          type: 1,
          total: 0,
          list: [],
          infiniteId: 0
        },
        yss: {
          page: 1,
          size: 25,
          total: 0,
          list: [],
          infiniteId: 0
        },
        rgz: {
          page: 1,
          size: 25,
          type: 0,
          total: 0,
          list: [],
          infiniteId: 0
        }
      },
      subscriptionStatus: {
        0: "待处理",
        1: "中签",
        2: "未中签"
      },
      marketTable: [
        {
          label: "时间",
          unit: "",
          value: ["近一个月", "近三个月", "近一年"]
        },
        {
          label: "上市新股数量",
          key: "count",
          unit: "",
          value: ["10", "10", "10"]
        },
        {
          label: "募资额",
          key: "raiseFund",
          unit: "",
          value: []
        },
        {
          label: "首日上涨率",
          key: "averagingUp",
          unit: "%",
          value: []
        },
        {
          label: "首日平均涨幅",
          key: "everyAverage",
          unit: "%",
          value: []
        },
        {
          label: "每手平均首日收益",
          key: "averageRate",
          unit: "",
          value: []
        },
        {
          label: "累计一手利润",
          key: "retainedProfits",
          unit: "",
          value: []
        }
      ],
      industryFlag: "month",
      industryData: {},
      rightNewsData: {
        current: "sf",
        change: false,
        nl: {
          url: "/eipo/selectFightNew",
          page: 1,
          size: 10,
          type: "host3",
          list: [],
          infiniteId: 0
        },
        sf: {
          url: "/news/title/selectNewList",
          page: 1,
          size: 10,
          list: [],
          type: "hostNews",
          infiniteId: 0
        }
      }
    };
  },
  activated() {
    if (this.$refs.ring.offsetHeight <= 0) {
      return false;
    }
    for (let key in this.listData) {
      if (this.listData[key].list.length <= 0) {
        this.listData[key].infiniteId++;
      }
    }
    this.marketStatistics();
    this.industryStatistics();
  },
  computed: {
    // showLoginBtn() {
    //   return this.$store.state.AccountLogin.accountData.SessionKey;
    // },
    industryList() {
      let originList = this.industryData[this.industryFlag];
      if (originList) {
        let list = [];
        let total = 0;
        let rate = 0;
        originList.forEach((a, i) => {
          if (i < 7) {
            list[i] = a;
          }
          if (i >= 7) {
            total += Number(a.newShareRate);
            rate += Number(a.firstRate);
            list[7] = {
              firstRate: rate,
              name: "其他",
              newShareRate: total,
              sector: "其他",
              value: total.toFixed(2)
            };
          }
        });
        return list;
      } else {
        return [];
      }
    },
    isLogin() {
      let isLogin =
        this.$store.state.system.dealBar.commonAccount ||
        this.$store.state.system.dealBar.organization;
      return isLogin;
    }
  },
  watch: {
    "listData.ydb.F005V"(val) {
      this.listData.ydb.infiniteId++;
      this.listData.ydb.page = 1;
      this.listData.ydb.list = [];
    },
    "listData.ydb.F007V"(val) {
      this.listData.ydb.infiniteId++;
      this.listData.ydb.page = 1;
      this.listData.ydb.list = [];
    },
    industryFlag() {
      this.drawRing();
    },
    "$store.state.GlobalData.needRelogin"(val) {
      if (!val && this.$route.name === "newStock") {
        this.reload();
        this.$store.dispatch("SET_NEED_RELOGIN", true);
      }
    },
    "rightNewsData.current"(val) {
      // this.rightNewsData[val]
      this.rightNewsData.change = true;
      this.$nextTick(() => {
        this.rightNewsData.change = false;
      });
    },
    isLogin() {
      this.initTableHeight();
      let heigth = this.$refs.tableList.offsetHeight;
      this.tableHeight = heigth / 4 - 34 - 8;
      let len = this.slideData.filter(a => {
        return a.show === true;
      });
      this.height =
        this.tableHeight + (this.tableHeight * (4 - len.length)) / len.length;
    }
  },
  mounted() {
    this.$nextTick(() => {
      // this.$refs.tableRef1.$parent.bodyWrapper.classList.add('table1');
      this.initTableHeight();
      // this.getListData("", "rgz");
      // this.getListData("", "dss");
      // this.getYSSListData();
    });
  },
  methods: {
    showRecord() {
      // 认购记录
      this.$refs.Record.show();
    },
    newsDetail2(news) {
      this.$refs.NewsDetail2.show(news);
    },
    newsDetail(news) {
      this.$refs.NewsDetail.show(news);
    },
    rightNews($state) {
      // 打新课堂 GET /eipo/selectFightNew
      let rightNewsData = this.rightNewsData[this.rightNewsData.current];
      this.axios({
        url: rightNewsData.url,
        params: {
          page: rightNewsData.page,
          size: rightNewsData.size
        },
        type: rightNewsData.type
      }).then(res => {
        let data = res.data.ret;
        if (data.list.length > 0) {
          if (this.rightNewsData.current === "nl") {
            data.list.forEach(a => {
              a.newsId = a.fighNewId;
            });
          }
          let list = data.list;
          if (rightNewsData.page == 1) {
            rightNewsData.list = list;
          } else {
            rightNewsData.list = rightNewsData.list.concat(list);
          }
          rightNewsData.page++;
          $state.loaded();
        } else {
          if (rightNewsData.list.length > 0) {
            $state.loaded();
          }
          $state.complete();
        }
      });
    },
    reload() {
      this.hidePage = true;
      this.$nextTick(() => {
        this.hidePage = false;
        for (let key in this.listData) {
          this.listData[key].infiniteId++;
          this.listData[key].list = [];
          this.listData[key].page = 1;
        }
        this.rightNewsData.sf.page = 1;
        this.rightNewsData.sf.infiniteId++;
        this.rightNewsData.nl.page = 1;
        this.rightNewsData.nl.infiniteId++;
        this.marketStatistics();
        this.industryStatistics();
      });
    },
    industryStatistics() {
      // 行业统计 GET /eipo/selectIndustry
      this.axios({
        url: "/eipo/selectIndustry",
        params: {},
        type: "host3"
      }).then(res => {
        let data = res.data.ret;
        for (let key in data) {
          data[key].forEach(a => {
            a.value = Number(a.newShareRate).toFixed(2);
            a.name = a.sector;
          });
        }
        this.industryData = data;
        setTimeout(() => {
          this.drawRing();
        }, 0);
      });
    },
    marketStatistics() {
      // 市场概况 GET /eipo/selectMarketProfile
      let marketTable = this.marketTable;
      this.axios({
        url: "/eipo/selectMarketProfile",
        params: {},
        type: "host3"
      }).then(res => {
        let data = res.data.ret;
        marketTable.forEach((a, b) => {
          if (b > 0) {
            a.value[0] = Number(data.month[a.key]).toFixed(2);
            a.value[1] = Number(data.mar[a.key]).toFixed(2);
            a.value[2] = Number(data.year[a.key]).toFixed(2);
          }
        });
      });
    },
    drawRing() {
      let ring = this.$refs.ring;
      let ringChart = this.$echarts.init(ring);
      let data = this.industryList;
      ringChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c}% "
        },
        color: [
          "#f5222d",
          "#FA8C16",
          "#FADB14",
          "#A0D911",
          "#13C2C2",
          "#1890FF",
          "#722ED1",
          "#d5d5d5"
        ],
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                formatter: "{d}%",
                borderBottomWidth: 1,
                color: "#fff"
              }
            },
            labelLine: {
              normal: {
                show: false,
                length2: 30
              }
            },
            data: data
          }
        ]
      });
    },
    initTableHeight() {
      let heigth = this.$refs.tableList.offsetHeight;
      this.tableHeight = heigth / 4 - 34 - 8;
      this.height = heigth / 4 - 34 - 8;
      // console.log(this.height);
    },
    refresh(target) {
      this.listData[target].infiniteId++;
      this.listData[target].page = 1;
      this.listData[target].list = [];
    },
    goDetail(row) {
      console.log(row);
      setTimeout(() => {
        this.$store.dispatch("CHANGE_SEARCH_DATA", {
          code: row.productCode,
          timestamp: new Date().getTime(),
          recover: true
        });
        // 延迟是为了确保要监听股票代码改变的组件加载完成
      }, 500);
      // 港股
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 0,
        timestamp: new Date().getTime()
      });
    },
    goWaitDetail(row, type) {
      console.log(row, type, 8455);
      let code = "hk" + row.productCode;
      setTimeout(() => {
        this.$store.dispatch("CHANGE_SEARCH_DATA", {
          code: code,
          timestamp: new Date().getTime(),
          recover: false
        });
      }, 300);
      this.$router.replace({
        path: "stockDetail",
        query: { data: row.productCode, type: type }
      });
    },
    checkProspectus(row) {
      if (row.otherInfoUrl || row.f008v) {
        shell.openExternal(row.otherInfoUrl || row.f008v);
      } else {
        this.$message({
          message: "内容不存在！",
          type: "error"
        });
      }
    },
    showLoginAccount() {
      this.$store.dispatch("showAccountLogin");
    },
    getListData($state, target) {
      // GET /eipo/selectListInfo
      /* 
        type: 0认购中 1待上市
      */
      let listData = this.listData;
      this.axios({
        url: "/eipo/selectListInfo",
        params: {
          page: listData[target].page,
          size: listData[target].size,
          type: listData[target].type,
          channel: 4,
          version: 4
        },
        type: "host3"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (target === "rgz") {
            // 可认购  增加是否认购结束字段
            res.data.ret.list.forEach(a => {
              if (
                new Date(a.newCloseTime.replace(/\-/g, "/")).getTime() >
                Date.now()
              ) {
                a._isBuy = true;
              } else {
                a._isBuy = false;
              }
            });
          }
          if (listData[target].page == 1) {
            listData[target].list = res.data.ret.list;
          } else {
            listData[target].list = listData[target].list.concat(
              res.data.ret.list
            );
          }
          listData[target].total = res.data.ret.total;
          listData[target].page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    },
    getYDBListData($state) {
      // 已递表 GET /eipo/selectPassedList
      let data = this.listData.ydb;
      this.axios({
        url: "/eipo/selectPassedList",
        params: {
          page: data.page,
          size: data.size,
          F005V: data.F005V, //上市板块 MAIN-主板 GEM-创业板
          F007V: data.F007V // 状态 0-正在处理 1-通过聆讯2-已上市 3-被发回
        },
        type: "host3"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          let list = res.data.ret.list;
          if (data.page == 1) {
            data.list = list;
          } else {
            data.list = data.list.concat(list);
          }
          data.page++;
          data.total = res.data.ret.total;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    getYSSListData($state) {
      // GET /eipo/selectListedListInfo
      /* 
        已上市
      */
      let data = this.listData.yss;
      this.axios({
        url: "/eipo/selectListedListInfo",
        params: {
          page: data.page,
          size: data.size
        },
        type: "host3"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          let list = res.data.ret.list.filter(a => {
            return a && a.productCode;
          });
          if (data.page == 1) {
            data.list = list;
          } else {
            data.list = data.list.concat(list);
          }
          data.page++;
          data.total = res.data.ret.total;
          // this.goDetail(data.list[0]);
          if($state) $state.loaded();
        } else {
          if($state) $state.complete();
        }
      });
    },
    getRecordData($state) {
      // GET /eipo/selectRecordList
      let data = this.listData.record;
      this.axios({
        url: "/eipo/selectRecordList",
        params: {
          page: data.page,
          size: data.size
        },
        type: "host3"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (data.page == 1) {
            data.list = res.data.ret.list;
          } else {
            data.list = data.list.concat(res.data.ret.list);
          }
          data.page++;
          data.total = res.data.ret.total;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    showIPOMask(row) {
      if (!this.showLoginBtn) {
        this.showLoginAccount();
      } else {
        this.$refs.IPOMask.show(row);
      }
    },
    stretch(index) {
      this.slideData[index].show = !this.slideData[index].show;
      this.slideData[index].label = this.slideData[index].show
        ? "收起"
        : "展开";
      let len = this.slideData.filter(a => {
        return a.show === true;
      });
      this.height =
        this.tableHeight + (this.tableHeight * (4 - len.length)) / len.length;
    },
    showWarrant() {
      this.$refs.warrant.show();
    }
  },
  filters: {
    billion(num) {
      let n = Number(num);
      return (n / 100000000).toFixed(2);
    },
    point(num) {
      if (Number(num) == 0) {
        return "- -";
      } else {
        return Number(num).toFixed(2);
      }
    },
    boardText(val) {
      const extendText = {
        MAIN: "主板",
        GEM: "创业板",
        0: "正在处理",
        1: "通过聆讯"
      };
      return extendText[val];
    }
  }
};
</script>

<style lang="scss" scoped>
