<template>
  <div class="stock-deal">
    <stockDeal
      :accountInfo="accountInfo"
      :currentStock="currentStock"
      :curPosition="curPosition"
      :requestConfig="requestConfig"
      :currentType="currentType"
      :orderPrice="orderPrice"
      :showFlag="acrossMenuSingle.extra"
      @getStockOrder="getStockOrder"
      @stockQuery="getStockQuery"
    />
    <div class="deal-five">
      <div
        class="deal-li"
        v-for="(sale, index) in saleList"
        :key="index"
        @click="setOrderPrice(sale)"
        hover
      >
        <div class="li-name" v-if="saleList.length > 1">卖{{ capital[4 - index] }}</div>
        <div class="li-name" v-if="saleList.length == 1">卖一</div>
        <div
          class="li-price"
          :class="{rise: sale.price > hstClose, fall: sale.price < hstClose}"
        >{{Number(sale.price).toFixed(3)}}</div>
        <div class="li-num">
          {{ sale.qty | bidask }}
          <span v-if="sale.num">({{ sale.num }})</span>
        </div>
      </div>
      <div class="deal-li peecent">
        <div class="percent-l" :style="{width: widthPercent+'%'}">
          <span>{{widthPercent}}%</span>
        </div>
        <div class="percent-r" :style="{width: (100 - widthPercent)+'%'}">
          <span>{{100 - widthPercent}}%</span>
        </div>
      </div>
      <div
        class="deal-li buy"
        v-for="(buy, io) in buyList"
        :key="io+'a'"
        @click="setOrderPrice(buy)"
        hover
      >
        <div class="li-name">买{{ capital[io] }}</div>
        <div
          class="li-price"
          :class="{rise: buy.price > hstClose, fall: buy.price < hstClose}"
        >{{Number(buy.price).toFixed(3)}}</div>
        <div class="li-num">
          {{ buy.qty | bidask }}
          <span v-if="buy.num">({{ buy.num }})</span>
        </div>
      </div>
    </div>

    <div class="today-deal">
      <div class="postion-stock">
        <div class="position-t">我的持仓</div>
        <el-table
          class="stock-table"
          :data="comuptedPosition"
          @row-contextmenu="selectRow(arguments, 'deal')"
          height="90"
        >
          <el-table-column prop="StockCode" label="股票代码" align="left">
            <template slot-scope="scope">
              <div class="stock-name">
                <i></i>
                {{scope.row.StockCode}}
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
          <el-table-column prop="StockQty" label="持仓" align="center"></el-table-column>
          <el-table-column prop="sellNum" label="可用" align="center"></el-table-column>
          <el-table-column prop="Last" label="现价" align="center">
            <template slot-scope="scope">
              <div>{{Number(scope.row.Last * scope.row.StockQty).toFixed(3)}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="CostNum" label="成本" align="center">
            <template slot-scope="scope">
              <div>{{Number(scope.row.CostNum).toFixed(3)}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="CurProfitLoss" label="浮动盈亏" align="center">
            <template slot-scope="scope">
              <div
                :class="{rise: scope.row.CurProfitLoss > 0, fall: scope.row.CurProfitLoss < 0}"
              >{{scope.row.CurProfitLoss >= 0 ?'+' + scope.row.CurProfitLoss : scope.row.CurProfitLoss}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="ProfitLossRatio" label="盈亏率" align="center">
            <template slot-scope="scope">
              <div
                :class="{rise: scope.row.CurProfitLoss > 0, fall: scope.row.CurProfitLoss < 0}"
              >{{scope.row.ProfitLossRatio}}%</div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="cur-entrust-stock">
        <div class="entrust-t">当前委托</div>

        <el-table
          :data="curEntrust"
          empty-text="暂无委托记录"
          height="90"
          @row-contextmenu="selectRow(arguments, 'entrust')"
        >
          <el-table-column prop="StockCode" label="股票代码" align="left">
            <template slot-scope="scope">
              <div class="stock-name">
                <i></i>
                {{scope.row.StockCode}}
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
          <el-table-column prop="OrderSide" label="方向" align="center">
            <template slot-scope="scope">
              <div
                :class="{fall: scope.row.OrderSide=='Buy',rise: scope.row.OrderSide=='Sell'}"
              >{{scope.row.OrderSide=='Buy'?'买入':'卖出'}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="OrderPrice" label="委托价" align="center">
            <template slot-scope="scope">
              <div>{{Number(scope.row.OrderPrice).toFixed(3)}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="OrderQty" label="委托量" align="center"></el-table-column>
          <el-table-column prop="FilledQty" label="成交量" align="center"></el-table-column>
          <el-table-column prop="DateTimes" label="委托时间" align="center">
            <template slot-scope="scope">
              <div>{{(scope.row.DateTimes|| scope.row.DateTime) | dateTimeString}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="OrderStatus" label="状态" align="center">
            <template slot-scope="scope">
              <div>{{scope.row.OrderStatus | OrderStatus}}</div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <Contextmenu ref="Contextmenu" @cancelOrder="cancelOrder" :requestConfig="requestConfig" />
  </div>
</template>

<script>
import stockTable from "@/components/deal/stock-table";
import Contextmenu from "@/components/common/contextmenu";
import stockDeal from "@/components/deal/stock-deal";
import vipBtn from "@/components/common/vip-btn";

import { buySellSocket } from "@/utils";
export default {
  props: {
    accountInfo: Object,
    curEntrust: Array,
    curPosition: Array,
    requestConfig: Object,
    acrossMenuSingle: Object
  },
  components: {
    stockTable,
    Contextmenu,
    stockDeal,
    vipBtn
  },
  data() {
    return {
      dealForm: {
        name: ""
      },
      value: "",
      historyList: [],
      linkStatus: false,
      currentStock: {},
      // curEntrust: [],
      orderPrice: "",
      baseDataLabel: {
        totalAssets: "账户总资产（港币）",
        availableAmount: "当前可用",
        withDrawCashBalance: "当前可提",
        frozenAmount: "冻结金额",
        holdMarketValue: "持仓市值",
        totalProfitLoss: "持仓盈亏",
        curProfitLoss: "今日盈亏"
      },
      stockType: "pos",
      hstClose: "", // 昨收价
      capital: ["一", "二", "三", "四", "五"]
    };
  },
  mounted() {
    this.$nextTick(() => {
      // this.getHistoryList();
    });
  },
  activated() {
    // this.getHistoryList();
    // this.queryCurrEntrust();
  },
  computed: {
    baseData() {
      let baseData = {};
      for (let item in this.baseDataLabel) {
        baseData[item] = this.accountInfo[item];
      }
      return baseData;
    },
    comuptedPosition() {
      let position = this.curPosition;
      let entrust = this.curEntrust;
      position.forEach(a => {
        a.freezeNum = 0;
        a.sellNum = Number(a.StockQty);
        entrust.forEach(b => {
          if (a.StockCode === b.StockCode && b.OrderSide != "0") {
            a.freezeNum += Number(b.OrderQty);
            a.sellNum -= Number(b.OrderQty);
          }
        });
      });
      return position;
    },
    saleList() {
      // 卖五档
      let symbol = this.stockCode;
      if (
        !this.isVIP ||
        (this.$store.state.GlobalData.stockData.asklist.length <= 0 &&
          !this.$store.state.GlobalData.dealBuySell[symbol])
      ) {
        let arr = [];
        for (let i = 0; i < 5; i++) {
          arr.push({
            num: "--",
            price: "0",
            qty: "--"
          });
        }
        return arr;
      }
      if (this.linkStatus) {
        // return this.$store.state.DealBar.BUY_SELL_LIST.slice(5);
        let asklist = this.$store.state.GlobalData.dealBuySell[symbol].data
          .asklist;
        if (asklist.length > 5) {
          return asklist.slice(5);
        } else {
          return asklist;
        }
      } else {
        let asklist = this.$store.state.GlobalData.stockData.asklist.filter(
          a => {
            return a;
          }
        );
        if (asklist.length > 5) {
          return asklist.reverse().slice(5);
        } else {
          return asklist.reverse();
        }
      }
    },
    buyList() {
      // 买五档
      let symbol = this.stockCode;
      if (
        !this.isVIP ||
        (this.$store.state.GlobalData.stockData.bidlist.length <= 0 &&
          !this.$store.state.GlobalData.dealBuySell[symbol])
      ) {
        let arr = [];
        for (let i = 0; i < 5; i++) {
          arr.push({
            num: "--",
            price: "0",
            qty: "--"
          });
        }
        return arr;
      }
      if (this.linkStatus) {
        // return this.$store.state.DealBar.BUY_SELL_LIST.slice(0, 5);
        return this.$store.state.GlobalData.dealBuySell[
          symbol
        ].data.bidlist.slice(0, 5);
      } else {
        return this.$store.state.GlobalData.stockData.bidlist.slice(
          0,
          5
        );
      }
    },
    widthPercent() {
      // 买卖占比
      let bidNum = 0;
      let askNum = 0;
      this.saleList.map((value, index) => {
        bidNum = bidNum + Number(value.qty);
      });
      this.buyList.map((value, index) => {
        askNum = askNum + Number(value.qty);
      });
      let percent = (
        Number((askNum / (bidNum + askNum)).toFixed(2)) * 100
      ).toFixed(0);
      if (bidNum >= 0) {
        return percent;
      } else {
        return "50";
      }
    },
    currentType() {
      // 当前选择的账户
      return this.$store.state.system.dealBar.currentType;
    },
    identityData(){
      return this.$store.state.system.accountInfo
    },
    isVIP() {
      // 港股行情VIP
      // return false;
      return this.identityData.hasHkValue || false;
    }
  },
  watch: {
    "$store.state.system.rightTarget"() {
      if (this.$store.state.system.rightTarget.symbol) {
        // 右键菜单进行交易时
        this.currentStock = this.$store.state.system.rightTarget;
        this.getStockQuery(this.currentStock);
        this.getStockOrder(this.currentStock.symbol);
        console.log(this.currentStock)
        setTimeout(() => {
          this.$store.dispatch("SET_RIGHT_TARGET", {});
        }, 1000)
      }
    },
    "$store.state.system.dealBar.barShow"(val) {
      if (val) {
        if (this.$store.state.system.rightTarget.symbol){
          return false;
        }
        this.linkStatus = false;
        let stock = JSON.parse(
          JSON.stringify(this.$store.state.GlobalData.stockData.query)
        );
        if (stock.symbol.length <= 7) {
          this.currentStock = stock;
        } else {
          this.currentStock = JSON.parse(localStorage.getItem("firstStock"));
        }
        this.getStockQuery(this.currentStock);
        this.getStockOrder(this.currentStock.symbol);
      }
    },
    "$store.state.GlobalData.stockData.query"(val) {
      // console.log(val);
      if (this.currentStock.symbol !== val.symbol && val.symbol.length <= 7) {
        this.linkStatus = false;
        this.currentStock = JSON.parse(
          JSON.stringify(this.$store.state.GlobalData.stockData.query)
        );
        this.getStockQuery(this.currentStock);
      }
    },
    currentType() {
      // this.getHistoryList();
      // this.queryCurrEntrust();
    },
    "$store.state.DealBar.isAction"() {
      // this.getHistoryList();
      // this.queryCurrEntrust();
    }
  },
  methods: {
    getStockQuery(query) {
      this.hstClose = Number(query.hst_close);
    },
    setOrderPrice(li) {
      // 点击买卖十档获取对应价格
      this.orderPrice = li.price;
    },
    queryCurrEntrust() {
      // 获取当天委托
      // / queryCurrEntrust.do  curEntrust
      let params = {};
      if (this.currentType == 0) {
        params = {
          accountNo: this.requestConfig.account[this.currentType]
        };
      } else {
        params = {
          market: "hk",
          startDate: new Date().Format("yyyy-MM-dd"),
          endDate: new Date().Format("yyyy-MM-dd"),
          pageStart: 1,
          pageSize: 100
        };
      }
      this.axios({
        url: this.requestConfig.curEntrust[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType],
        hideMessage: true
      }).then(res => {
        let list = res.data.data.filter(a => {
          return a.OrderStatus === "Waiting" || a.OrderStatus === "In Market";
        });
        this.curEntrust = list;
      });
    },
    getHistoryList() {
      // let accountNo = this.$store.state.AccountLogin.accountData.LoginID;
      // console.log(this.requestConfig, "132木头人");
      let now = new Date().Format("yyyy/MM/dd");
      let start = now;
      let end = now;
      let params = {};
      if (this.currentType == 0) {
        params = {
          accountNo: this.requestConfig.account[this.currentType]
          // startDate: start,
          // endDate: end
        };
      } else {
        params = {
          market: "hk",
          startDate: new Date().Format("yyyy-MM-dd"),
          endDate: new Date().Format("yyyy-MM-dd"),
          pageStart: 1,
          pageSize: 100
        };
      }
      this.axios({
        url: this.requestConfig.curHistory[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.historyList = res.data.data || [];
      });
    },
    //买卖十档
    getStockOrder(code) {
      let url = "/quote-service/api/v1/order";
      if (code.indexOf("us") != -1) {
        url = "/quote-service/api/us/v1/order";
      }
      let data = {
        code: code
      };
      this.stockCode = code;
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        this.linkStatus = true;
        console.log(res);
        if (!res.data.Data) return false;
        let bidList = res.data.Data[0].bidlist; //买十档
        let askList = res.data.Data[0].asklist.reverse(); //卖十档
        let bidNum = 0;
        let askNum = 0;
        bidList.map((value, index) => {
          bidNum = bidNum + Number(value.qty);
        });
        askList.map((value, index) => {
          askNum = askNum + Number(value.qty);
        });
        // 同步交易窗口买卖五档数据
        let ul = askList.slice(5).concat(bidList.slice(0, 5));
        // this.$store.dispatch("UPDATE_BUY_SELL", ul);
        // 同步websocket数据
        let val = {};
        val[code] = {
          data: res.data.Data[0],
          timestamp: new Date().getTime()
        };
        this.$store.dispatch("SET_DEAL_BUY_SELL", val);
        buySellSocket(code);
      });
    },
    selectRow(arg, flag) {
      let distanceX = arg[2].clientX;
      let distanceY = arg[2].clientY;
      this.$refs.Contextmenu.show({
        left: distanceX,
        top: distanceY,
        type: flag || "entrust",
        row: arg[0]
      });
      this.closeContextmenu();
    },
    closeContextmenu() {
      let html = document.getElementsByTagName("html")[0];
      let _this = this;
      html.onclick = function() {
        if (_this.$refs.Contextmenu) {
          _this.$refs.Contextmenu.close();
        }
      };
    },
    cancelOrder() {
      // this.$emit("cancelOrder");
      setTimeout(() => {
        // this.queryCurrEntrust();
      }, 1000);
    }
  },
  filters: {
    OrderStatus(val) {
      const status = {
        Filled: "已成交",
        Waiting: "待成交",
        Reject: "已拒绝",
        Cancel: "已取消",
        "In Market": "待成交",
        "Partially Filled": "部分成交",
        "Host Reg": "已记录"
      };
      return status[val];
    }
  }
};
</script> 

<style lang="scss" scoped>
$borColor: #576373;
.stock-deal {
  // width: 100%;
  @include flex-box;
  // max-width: 379px;
  /deep/ .el-form {
    border-right: 1px solid $borColor;
    margin-right: 18px;
  }
  .deal-five {
    width: 200px;
    margin-top: 10px;
    // @include add-size($font_size_14);
    .deal-li {
      @include flex-center;
      padding: 0 10px;
      justify-content: space-between;
      line-height: 21px;
      color: $white;
      background: rgba(255, 169, 64, 0.2);
      @include add-size($font_size_14);
      .li-name {
        width: 50px;
        white-space: nowrap;
      }
      .li-price {
        width: 120px;
        white-space: nowrap;
        text-align: center;
        &.rise {
          color: $red;
        }
        &.fall {
          color: $green;
        }
      }
      .li-num {
        width: 130px;
        white-space: nowrap;
        text-align: right;
      }
      &:nth-child(5) {
        background: rgba(255, 169, 64, 0.5);
      }
      &:nth-child(7) {
        background: rgba(105, 192, 255, 0.5);
      }
      &:nth-child(7 + n) {
        background: rgba(105, 192, 255, 0.2);
      }
      &:last-child {
        padding-bottom: 8px;
      }
      &.peecent {
        padding: 0;
        .percent-l {
          // padding-left: 20px;
          background: $red;
          position: relative;
          height: 20px;
          span {
            position: absolute;
            left: 10px;
            z-index: 1;
            top: -2px;
          }
        }
        .percent-r {
          // padding-right: 20px;
          background: $green;
          text-align: right;
          position: relative;
          height: 20px;
          span {
            position: absolute;
            right: 10px;
            top: -2px;
            z-index: 1;
          }
        }
      }
    }
  }
  .today-deal {
    margin-top: 10px;
    margin-left: 10px;
    padding-left: 18px;
    border-left: 1px solid $borColor;
    .base-info {
      border-bottom: 1px solid $borColor;
      padding-bottom: 20px;
      @include flex-center;
      justify-content: space-between;
      .info-li {
        // width: 146px;
        @include flex-center;

        .li-name {
          @include add-size($font_size_12);
        }
        .li-value {
          color: $white;
          @include add-size($font_size_16);
          margin-right: 40px;
          margin-left: 10px;
        }
      }
    }
    .stock-type {
      @include flex-box;
      div {
        line-height: 36px;
        @include add-size($font_size_14);
      }
    }
  }
  /deep/ .el-table {
    width: 1000px;
    margin-top: 6px;
    color: $defaultColor;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $green;
      }
      .rise {
        color: $red;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
    .stock-name {
      @include flex-center;
      // justify-content: center;
      i {
        display: inline-block;
        margin-right: 2px;
        width: 18px;
        height: 12px;
        background: url("~@/assets/images/hq/HK.png") 0 center no-repeat;
        background-size: 18px 12px;
      }
    }
  }
}
</style>
