<template>
  <div id="app" :class="{ 'is-login': isLogin }">
    <TopBar v-show="logining" />
    <Menu v-show="logining" />
    <Update />
    <img src="@/assets/images/test/timg.jpg" style="position: absolute; left: 10px; top:10px; width: 300px;height: 200px;z-index:11;" alt="">
    <router-view></router-view>
  </div>
</template>

<script>
// const { ipcRenderer: ipc } = require("electron");
import Update from "@/views/common/update"
import TopBar from "@/views/common/top-bar";
import Menu from "@/views/common/menu";
export default {
  name: "ysb",
  components: { TopBar, Menu, Update },
  data() {
    return {};
  },

  mounted() {
    this.init();
    // this.resetRouter();
  },
  computed: {
    logining() {
      if (this.$route.name === "login") {
        document.getElementsByTagName("body")[0].style.paddingTop = 0;
      } else {
        document.getElementsByTagName("body")[0].style.paddingTop = "106px";
      }
      return this.$route.name !== "login";
    },
    isLogin() {
      return (
        this.$store.state.system.dealBar.commonAccount ||
        this.$store.state.system.dealBar.organization
      );
    }
  },
  watch: {},
  methods: {
    init() {
      // if (this.$route.path !== "/login" && window.innerWidth === 1400) {
      //   ipc.send("max");
      // }
      let _this = this;
      document.addEventListener("visibilitychange", function() {
        let isHidden = document.hidden;
        if (isHidden) {
          // console.log("隐藏");
          if (localStorage.getItem("token")) {
            _this.isOnline(0);
          }
        } else {
          // console.log("显示");
          if (localStorage.getItem("token")) {
            _this.isOnline(1);
          }
          // _this.$store.dispatch("CHANGE_SEARCH_DATA", {
          //   timestamp: new Date().getTime()
          // });
        }
      });
      if (localStorage.getItem("token")) {
        _this.isOnline(1);
      }
    },
    isOnline(online) {
      this.axios({
        url: "/app/updateOnline.do",
        params: {
          isOnline: online
        }
      });
    },
    listenUserLeave() {
      // 用户指定时长内不再操作界面，锁定应用
      // let lockData = localStorage.getItem("lockData");
      // if (lockData) {
      //   lockData = JSON.parse(localStorage.getItem("lockData"));
      // } else {
      //   lockData = {
      //     lockSwitch: true,
      //     lockTime: 30
      //   };
      // }
      // if (Boolean(lockData.lockSwitch)) {
      //   clearTimeout(this.timer);
      //   this.timer = setTimeout(() => {
      //     lock();
      //   }, Number(lockData.lockTime) * 60 * 1000);
      // } else {
      //   clearTimeout(this.timer);
      // }
      // clearIconFlash
      ipc.send("clearIconFlash");
    },
    initRouter() {
      if (this.$route.path === "/login" || this.$route.path === "/lock") {
        this.showTopBar = false;
      } else {
        this.showTopBar = true;
      }
      // console.log(this.$route);
      // if (this.$route.name != "HQ") {
      //   this.$store.dispatch("closeDeal");
      // }
    },
    relogin() {
      // localStorage.removeItem("token");
      // this.$store.dispatch("updateAccountData", {});
      this.$store.dispatch("closeAccountLogin");
      this.$store.dispatch("closeDeal");
    },
    resetRouter() {
      if (window.history.length >= 9) {
        window.history.go(-(window.history.length - 1));
        location.reload();
      }
    },
    needRelogin() {}
  },
  created() {}
};
</script>

