const state = {
  reqCount: 1, // 请求次数
  showStockQuery: false, // 是否显示股票详情
  showStockKline: false, // 是否显示股票K线图
  showIndexStock: false, // 是否显示底部指数
  showCompanyInfo: false, // 是否显示股票新闻模块
  userInfo: {}, // 外围账户信息
  accountInfo: {}, //  交易账户信息
  rightMenu: {
    // 右键菜单数据
    show: false,
    target: {},
    update: 0
  },
  rightTarget: {}, // 右键交易对象
  targetDeal: {
    // 交易模块  右键菜单跳转
    target: 0, // 0 买入 1 卖出
    timestamp: 0
  },
  remindData: {
    // 股票提醒
    show: false,
    stock: {}
  },
  routerTarget: {
    a: "", // 一级菜单 （自选、港股、美股等...）
    b: "", // 二级菜单 （全部、主板、创业板等...）
    timestamp: 0
  },
  dealBar: {
    loginShow: false, // 交易弹框显示
    barShow: false, // 交易栏显示
    commonAccount: false, // 是否登录交易账户
    organization: false, // 是否登录机构账户
    currentType: 0 // 当前账户类型
  }
};

const mutations = {
  setReqCount(state) {
    state.reqCount++;
  },
  showStockQuery(state, e) {
    state.showStockQuery = e || false;
  },
  showStockKline(state, e) {
    state.showStockKline = e || false;
  },
  showIndexStock(state, e) {
    state.showIndexStock = e || false;
  },
  showCompanyInfo(state, e) {
    state.showCompanyInfo = e || false;
  },
  setUserInfo(state, e) {
    state.userInfo = e;
  },
  setAccountInfo(state, e) {
    state.accountInfo = e;
  },
  setRightMenu(state, e) {
    state.rightMenu = { ...state.rightMenu, ...e };
  },
  setRemindData(state, e) {
    state.remindData = { ...state.remindData, ...e };
  },
  changeRouterTarget(state, e) {
    state.routerTarget = e;
  },
  setDealBar(state, e) {
    state.dealBar = { ...state.dealBar, ...e };
  },
  setRightTarget(state, e) {
    state.rightTarget = e;
  },
  showTargetDeal(state, e) {
    state.targetDeal = e;
  }
};

const actions = {
  ADD_REQ_COUNT({ commit }) {
    commit("setReqCount");
  },
  SHOW_STOCK_QUERY({ commit }, e) {
    commit("showStockQuery", e);
  },
  SHOW_STOCK_KLINE({ commit }, e) {
    commit("showStockKline", e);
  },
  SHOW_INDEX_STOCK({ commit }, e) {
    commit("showIndexStock", e);
  },
  SHOW_COMPANY_INFO({ commit }, e) {
    commit("showCompanyInfo", e);
  },
  SET_USER_INFO({ commit }, e) {
    commit("setUserInfo", e);
  },
  SET_ACCOUNT_INFO({ commit }, e) {
    commit("setAccountInfo", e);
  },
  SET_RIGHT_MENU({ commit }, e) {
    commit("setRightMenu", e);
  },
  SET_REMIND_DATA({ commit }, e) {
    commit("setRemindData", e);
  },
  CHANGE_ROUTER_TARGET({ commit }, e) {
    commit("changeRouterTarget", e);
  },
  SET_DEAL_BAR({ commit }, e) {
    commit("setDealBar", e);
  },
  SET_RIGHT_TARGET({ commit }, e) {
    commit("setRightTarget", e);
  },
  SHOW_TARGET_DEAL({ commit }, e) {
    commit("showTargetDeal", e);
  }
};

export default {
  state,
  mutations,
  actions
};
