<template>
  <div class="record">
    <el-dialog theame center title="详情" :visible="isShow" width="990px" top="4%" @close="close">
      <div class="news-content">
        <webview class="webview" id="foo" :src="newsUrl" style="display:inline-flex;" v-if="isShow"></webview>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  components: {
  },
  data() {
    return {
      isShow: false,
      newsUrl: ""
    };
  },
  computed: {},
  watch: {},
  methods: {
    show(news) {
      this.isShow = true;
      this.newsUrl = news.newsContent;
    },
    close() {
      this.isShow = false;
    },
    
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
  activated() {}
};
</script>
<style lang='scss' scoped>
.news-content{
  padding: 0 30px;
  .webview {
    width: 100%;
    height: 800px;
  }
}
</style>