<template>
  <div class="feedback">
    <el-dialog
      class="feedback-dialog"
      theame
      center
      title
      :visible="showMask"
      width="600px"
      top="10%"
      @close="showMask = false"
    >
      <div class="feedback-content">
        <div class="content-title">意见反馈</div>
        <div class="part-item">
          <div class="part-title">问题分类</div>
          <div class="flag-ul">
            <div
              class="flag-li"
              :class="{active: flag._check}"
              v-for="(flag, i) in problemFlag"
              :key="i"
              @click="checkType(flag)"
            >{{flag.adviceTypeName}}</div>
          </div>
        </div>
        <div class="part-item">
          <div class="part-title">提个意见</div>
          <div class="part-content">
            <div class="img-list">
              <div class="img-box" v-for="img in imgList" :key="img.imgSrc">
                <img :src="img.imgSrc" alt />
                <i class="delete-icon" @click="deleteImg(img)"></i>
              </div>
              <label for="feedback-img" class="add-img" v-show="imgList.length < 3">
                <imgUpload id="feedback-img" :multiple="true" @change="imgArr" />
              </label>
            </div>

            <textarea
              class="feedback-text"
              cols="30"
              rows="10"
              v-model="adviceContent"
              placeholder="请详细描述您的意见，如果能配上想要的图，将有助于我们找到解决方案"
            ></textarea>
          </div>
        </div>
        <el-button class="submit" hover @click="saveProblem">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import imgUpload from "@/components/common/img-upload";
import { uploadImg } from "@/utils";
export default {
  props: [""],
  components: {
    imgUpload
  },
  data() {
    return {
      showMask: false,
      problemFlag: [],
      imgList: [],
      adviceContent: "",
      adviceTypeId: -1
    };
  },
  methods: {
    checkType(e) {
      this.problemFlag.forEach(a => {
        a._check = false;
      });
      e._check = true;
      this.adviceTypeId = e.adviceTypeId;
    },
    deleteImg(img) {
      this.imgList = this.imgList.filter((i, m) => {
        return i.imgSrc !== img.imgSrc;
      });
    },
    imgArr(files) {
      console.log(files);
      try {
        if (this.imgList.length + files.length > 3) {
          throw new Error("最多上传3张图片！");
        }

        this.imgList = this.imgList.concat(files);
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    getType() {
      // GET /app/advice/select
      this.axios({
        url: "/app/advice/select",
        method: "get",
        params: {},
        type: "host3"
      }).then(res => {
        let ret = res.data.ret;
        ret.forEach(a => {
          a._check = false;
        });
        this.problemFlag = ret;
        this.getType = function() {};
      });
    },
    saveProblem() {
      try {
        let adviceTypeId = this.adviceTypeId;
        let adviceContent = this.adviceContent;
        this.problemFlag.forEach(a => {
          if (a._check) {
            adviceTypeId = a.adviceTypeId;
          }
        });
        if (adviceTypeId == -1) {
          throw new Error("请选择问题类型！");
        }
        if (!adviceContent) {
          throw new Error("请描述您的意见！");
        }
        if (this.imgList.length > 0) {
          this.uploadImgArr(0, () => {
            this.postData();
          });
        } else {
          this.postData();
        }
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    postData() {
      let imgList = this.imgList;
      this.axios({
        url: "/app/advice/saveAdvice",
        method: "post",
        params: {
          advicePicFour: "",
          advicePicOne: imgList[0] ? imgList[0].webUrl : "",
          advicePicThree: imgList[1] ? imgList[1].webUrl : "",
          advicePicTwo: imgList[2] ? imgList[2].webUrl : "",
          adviceTypeId: this.adviceTypeId,
          adviceContent: this.adviceContent
        },
        type: "host3"
      }).then(res => {
        this.$message({
          message: "我们已收到您的建议，感谢您的支持！",
          type: "success"
        });
        setTimeout(() => {
          this.resetMask();
        }, 1000)
      });
    },
    resetMask() {
      this.problemFlag.forEach(a => (a._check = false));
      this.imgList = [];
      this.adviceContent = "";
      this.adviceTypeId = -1;
      this.showMask = false;
    },
    uploadImgArr(index, cb) {
      let imgArr = this.imgList;
      let _this = this;
      // 依次上传图片
      uploadImg({
        file: imgArr[index].file,
        type: 4,
        cb(res) {
          imgArr[index].webUrl = res.data.body;
          index++;
          if (index <= imgArr.length - 1) {
            _this.uploadImgArr(index, cb);
          } else {
            cb();
          }
        }
      });
    },
    init() {
      this.getType();
    },
    close(e) {},
    show() {
      this.init();
      this.showMask = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.feedback {
  .feedback-dialog {
    .feedback-content {
      padding: 0 31px 40px;
      .content-title {
        @include add-size($font_size_18);
        color: $white;
        text-align: center;
        line-height: 78px;
      }
      .part-item {
        .part-title {
          @include add-size($font_size_16);
          color: $white;
          position: relative;
          padding-left: 16px;
          line-height: 20px;
          margin-bottom: 20px;
          &::before {
            content: "";
            position: absolute;
            width: 6px;
            height: 20px;
            background-color: $activeColor;
            left: 0;
            top: 0;
          }
        }
        .flag-ul {
          @include flex-box;
          flex-wrap: wrap;
          margin-bottom: 20px;
          .flag-li {
            width: 120px;
            height: 40px;
            border: 1px solid #767a92;
            border-radius: 6px;
            text-align: center;
            line-height: 38px;
            margin-right: 80px;
            margin-bottom: 20px;
            cursor: pointer;
            &.active,
            &:hover {
              color: $activeColor;
              border-color: $activeColor;
            }
            &:nth-child(3n) {
              margin-right: 0;
            }
          }
        }
        .part-content {
          width: 522px;
          height: 206px;
          border: 1px solid #767a92;
          border-radius: 6px;
          margin-top: 20px;
          padding: 39px 52px;
          .img-list {
            @include flex-center;
            .img-box {
              position: relative;
              width: 62px;
              height: 62px;
              margin-right: 10px;
              img {
                max-width: 62px;
                min-width: 62px;
                min-height: 62px;
                border-radius: 2px;
                position: absolute;
                transform: translate(-50%, -50%);
                left: 50%;
                top: 50%;
              }
              .delete-icon {
                position: absolute;
                right: -9px;
                top: -9px;
                width: 18px;
                height: 18px;
                cursor: pointer;
                background: url(~@/assets/images/personal/bank/delete-icon.png) 0
                  0 no-repeat;
                &:hover {
                  animation-name: bigger;
                  animation-duration: 0.3s;
                  animation-timing-function: ease-in-out;
                  animation-iteration-count: forwards;
                  animation-play-state: running;
                  transform-origin: center center;
                }
                @keyframes bigger {
                  from {
                    transform: scale(1);
                  }
                  to {
                    transform: scale(1.1);
                  }
                }
              }
            }
          }
          .add-img {
            width: 62px;
            height: 62px;
            cursor: pointer;
            display: block;
            border: 2px dashed #767a92;
            position: relative;
            &:before,
            &:after {
              content: "";
              position: absolute;
              width: 33px;
              height: 2px;
              background-color: #767a92;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
            &:after {
              width: 2px;
              height: 33px;
            }
          }
          .feedback-text {
            width: 440px;
            height: 60px;
            margin-top: 30px;
            background-color: inherit;
            border: none;
            resize: none;
            outline: none;
            color: $white;
          }
        }
      }
      .submit {
        @include linear;
        width: 120px;
        height: 40px;
        margin-left: auto;
        margin-top: 30px;
        color: $white;
        @include add-size($font_size_16);
        display: block;
        border: none;
      }
    }
  }
}
</style>


