<template>
  <div class="notice">
    <el-dialog
      theame
      center
      title="修改邮箱账号"
      :visible="dialogVisiblea"
      width="350px"
      top="18%"
      :modal="false"
      @close="dialogVisiblea = false"
    >
      <div class="email-sub">
        <div class="email-title">
          <span>邮箱</span>
        </div>
        <div class="email-inpt-number">
          <el-input v-model="email" placeholder="请输入新的邮箱账号"></el-input>
        </div>
        <div class="email-title">
          <span>验证码</span>
        </div>
        <div class="email-inpt-number">
          <div class="email-cod">
            <el-input v-model="code" maxlength="6" placeholder="请输入下发到邮箱的验证码"></el-input>
            <el-button @click="sendEmailCode">{{residueTime>0?residueTime+'s后重新获取':'获取验证码'}}</el-button>
          </div>
        </div>
        <div class="btn-sue-q">
          <el-button hover @click="changeEmail">确定邮箱</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getPersonalData } from "@/utils";

export default {
  name: "notice",
  components: {},
  data() {
    return {
      dialogVisiblea: false,
      email: "",
      code: "",
      timer: {},
      residueTime: 0
    };
  },
  mounted() {},

  methods: {
    changeEmail() {
      this.checkCode(res => {
        // POST /app/editCustomerEmail.do
        this.axios({
          url: "/app/editCustomerEmail.do",
          method: "post",
          params: {
            email: this.email
          }
        }).then(res => {
          this.$message({
            message: "修改成功！",
            type: "success",
            showClose: true
          });
          this.close();
          this.email = "";
          this.code = "";
          this.getPersonalData();
        });
      });
    },
    getPersonalData() {
      getPersonalData();
    },
    checkCode(cb) {
      // GET /messageCode/verifyCode.do
      this.axios({
        url: "/messageCode/verifyCode.do",
        method: "get",
        params: {
          checkCode: this.code
        }
      }).then(res => {
        if (typeof cb === "function") {
          cb(res);
        }
      });
    },
    startTimer() {
      // 发送验证码后倒计时
      this.residueTime = 60;
      this.timer = setInterval(() => {
        this.residueTime--;
        if (this.residueTime < 0) {
          this.residueTime = 0;
          clearInterval(this.timer);
        }
      }, 1000);
    },
    sendEmailCode() {
      // GET /messageCode/getEmailCode.do
      if (this.residueTime > 0) {
        return false;
      }
      let email = this.email;
      this.axios({
        url: "/messageCode/getEmailCode.do",
        method: "get",
        params: {
          email: email
        }
      }).then(res => {
        this.startTimer();
        this.$message({
          message: "验证码发送成功，请查收~",
          type: "success",
          showClose: true
        });
      });
    },
    close() {
      this.dialogVisiblea = false;
    },
    show() {
      this.dialogVisiblea = true;
      this.code = "";
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/.el-dialog__title {
  @include add-size($font_size_14);
}
/deep/.el-dialog {
  // left: 58px;
}
.email-sub {
  .email-title {
    width: 100%;
    height: 45px;
    padding-top: 28px;
    padding-left: 17px;
    > span {
      color: $white;
      @include add-size($font_size_14);
    }
  }
  .email-inpt-number {
    width: 100%;
    height: 50px;
    padding-left: 17px;
    padding-top: 10px;
    // border-bottom: 1px solid red;
    /deep/.el-input--medium .el-input__inner {
      width: 314px;
      height: 35px;
      background-color: #1d242e;
      border-top: 1px solid #1d242e;
      border-left: 1px solid #1d242e;
      border-right: 1px solid #1d242e;
      border-bottom: 1px solid #576373;
      padding-left: 0;
      border-radius: 0;
      color: #9195ac;
      @include add-size($font_size_14);
    }
    /deep/.el-input__inner:hover {
      border-top: 1px solid #1d242e;
      border-left: 1px solid #1d242e;
      border-right: 1px solid #1d242e;
      border-bottom: 1px solid #576373;
    }
    .email-cod {
      width: 314px;
      height: 35px;
      border-bottom: 1px solid #576373;
      @include flex-box;
      /deep/.el-input--medium .el-input__inner {
        width: 190px;
        height: 30px;
        background-color: #1d242e;
        border-top: 1px solid #1d242e;
        border-left: 1px solid #1d242e;
        border-right: 1px solid #1d242e;
        border-bottom: 1px solid #1d242e;
        padding-left: 0;
        border-radius: 0;
        color: #9195ac;
        @include add-size($font_size_14);
      }
      /deep/.el-input__inner:hover {
        border-top: 1px solid #1d242e;
        border-left: 1px solid #1d242e;
        border-right: 1px solid #1d242e;
        border-bottom: 1px solid #1d242e;
      }
      /deep/.el-button {
        width: 85px;
        padding-left: 0;
        padding-top: 5px;
        color: $white;
        height: 30px;
        @include add-size($font_size_14);
        background-color: #1d242e;
        border-top: 1px solid #1d242e;
        border-left: 1px solid #1d242e;
        border-right: 1px solid #1d242e;
        border-bottom: 1px solid #1d242e;
        @include medium;
      }
    }
  }
  .btn-sue-q {
    width: 100%;
    height: 78px;
    padding-top: 20px;
    /deep/.el-button {
      width: 250px;
      height: 30px;
      @include linear;
      margin-left: 50px;
      border-top: 1px solid #1d242e;
      border-left: 1px solid #1d242e;
      border-right: 1px solid #1d242e;
      border-bottom: 1px solid #1d242e;
      color: $white;
      border: none;
      @include medium;
    }
  }
}
</style>