<template>
  <div class="top-bar">
    <div class="draw-area">
      <img class="logo" src="~@/assets/images/base/logo2.png" alt />
      <div class="title">易昇证券</div>
    </div>
    <!-- <div class="back system-btn" @click="back">后退</div> -->
    <!-- <div class="refresh system-btn" @click="refresh">
      <i class="el-icon-refresh"></i> 刷新
    </div> -->
    <div class="bar-action">
      <div class="message system-btn" @click="notice">消息</div>
      <div class="setting system-btn" @click="setting">设置</div>
      <div class="min ac-i" @click="windowAc('min')"></div>
      <div class="max ac-i" @click="windowAc('max')"></div>
      <div class="close ac-i btn" @click="windowClose"></div>
    </div>
    <Setting ref="setting" />
    <Notice ref="notice" />
  </div>
</template>

<script>
const { ipcRenderer: ipc } = require("electron");
import Setting from "./setting";
import Notice from "./notice";

export default {
  components: { Setting, Notice },
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(() => {});
    // setTimeout(() => {
    //   ipc.send("iconFlash");
    // }, 5000)
  },
  methods: {
    back() {
      if (this.$route.fullPath !== "/hq?index=hk") {
        this.$router.go(-1);
      }
    },
    refresh() {},
    windowClose() {
      this.windowAc("close");
    },
    windowAc(ac) {
      ipc.send(ac);
    },
    notice() {
      this.$refs.notice.show();
    },
    setting() {
      this.$refs.setting.show();
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
  z-index: 1999;
  @include flex-box;
  align-items: center;
  color: $defaultColor;
  background-color: $blockBgColor;
  @include add-size($font_size_14);
  .draw-area {
    @include flex-center;
    -webkit-app-region: drag;
    justify-content: center;
    align-items: center;
    width: 1300px;
    height: 100%;
    margin: 0 auto;
    .logo {
      width: 22px;
      height: 22px;
      border-radius: 50%;
      margin-right: 10px;
    }
    .title {
      @include add-size($font_size_12);
      color: #f5f9ff;
    }
  }
  .bar-action{
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
    @include flex-center;
  }
  .system-btn {
    cursor: pointer;
    padding-left: 26px;
    &:hover {
      color: $activeColor;
      &.message {
        background-image: url(~@/assets/images/base/notice-b.png);
      }
      &.setting {
        background-image: url(~@/assets/images/base/setting-b.png);
      }
    }
    &.message {
      margin-right: 20px;
      background: url(~@/assets/images/base/notice-a.png) 0 0 no-repeat;
      background-size: 18px 14px;
    }
    &.setting {
      margin-right: 40px;
      background: url(~@/assets/images/base/setting-a.png) 6px 0 no-repeat;
      background-size: 14px 14px;
    }
    &.refresh {
      padding-left: 20px;
      margin-right: 20px;
    }
  }

  .ac-i {
    cursor: pointer;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: center;
    &.min {
      right: 64px;
      background-image: url("~@/assets/images/base/min-icon-a.png");
      background-size: 13px 2px;
      background-position: center 16px;
      &:hover {
        background-image: url("~@/assets/images/base/min-icon-b.png");
        background-size: 32px 32px;
        background-position: center -6px;
        background-color: #455163;
      }
    }
    &.max {
      right: 32px;
      background-image: url("~@/assets/images/base/max-icon-a.png");
      background-size: 14px 14px;
      &:hover {
        background-image: url("~@/assets/images/base/max-icon-b.png");
        background-size: 32px 32px;
      }
    }
    &.close {
      right: 0;
    }
  }
}
</style>
