        <span>总储备</span>
        <span>营业额/(银行经营收入)</span>
        <span>经营溢利</span>
        <span>非经营性项目收益</span>
        <span>融资成本/(财务费用)</span>
        <span>除税前经营溢利(亏损)</span>
        <span>除税后经营溢利(亏损)</span>
        <span>税项</span>
        <span>股息总额</span>
        <span>现金流量净额</span>

        <span>毛利率</span>
        <span>税前利润率</span>
        <span>销售净利率</span>
        <span>产权比率(负债权益比)</span>
        <span>资产报酬率</span>
        <span>权益报酬率</span>
        <span>流动比率</span>
        <span>速动比率</span>
        <span>现金与流动资产比率</span>
        <span>资产负债率</span>
        <span>长期负债比率</span>
        <span>毛利润</span>
        <span>业务利润</span>
        <span>股东权益合计</span>
      </div>
      <div class="financeRight">
        <ul class="slide-ul clear">
          <li class="slide-li" v-for="(li, i) in financialIndicesData.list" :key="i">
            <div>{{li.f001V}}</div>
            <div>{{li.declaredate | dateFormat('yyyy-MM-dd')}}</div>
            <div>{{li.startdate | dateFormat('yyyy-MM-dd')}}</div>
            <div>{{li.enddate | dateFormat('yyyy-MM-dd')}}</div>
            <div>{{li.f003V}}</div>
            <div>{{li.f004N}}</div>
            <div>{{li.f005N}}</div>
            <div>{{li.f006N}}</div>

            <div>{{li.f007N | unit}}</div>
            <div>{{li.f008N | unit}}</div>
            <div>{{li.f009N | unit}}</div>
            <div>{{li.f010N | unit}}</div>
            <div>{{li.f011N | unit}}</div>
            <div>{{li.f012N | unit}}</div>
            <div>{{li.f013N | unit}}</div>
            <div>{{li.f014N | unit}}</div>
            <!-- 总储备 -->
            <div>{{li.f016N | unit}}</div>

            <div>{{li.f017N | unit}}</div>
            <div>{{li.f018N | unit}}</div>
            <div>{{li.f019N | unit}}</div>
            <div>{{li.f020N | unit}}</div>
            <!--  -->
            <div>{{li.f021N | unit}}</div>
            <div>{{li.f022N | unit}}</div>
            <div>{{li.f023N | unit}}</div>
            <div>{{li.f024N | unit}}</div>
            <div>{{li.f025N | unit}}</div>
            <!-- 毛利率 -->
            <div>{{li.f037N}}</div>
            <div>{{li.f038N}}</div>
            <div>{{li.f039N}}</div>
            <div>{{li.f040N}}</div>
            <div>{{li.f041N}}</div>
            <div>{{li.f042N}}</div>
            <div>{{li.f043N}}</div>
            <div>{{li.f044N}}</div>
            <div>{{li.f045N}}</div>
            <div>{{li.f046N}}</div>
            <div>{{li.f047N}}</div>
            <div>{{li.f048N}}</div>
            <div>{{li.f049N}}</div>
            <div>{{li.f050N}}</div>
          </li>
        </ul>
      </div>
    </div>
    <div class="nodata" v-if="financialIndicesData.list.length <= 0">暂无数据</div>
  </div>
</template>

<script>
import { initArray } from "@/utils";
export default {
  components: {},
  data() {
    return {
      financialIndicesData: {
        infiniteId: 0,
        limit: 1000,
        list: []
      },
      scrollLeft: 0
    };
  },
  props: {
    stockData: Object
  },
  computed: {},
  watch: {
    "$store.state.GlobalData.searchData"(val) {
      // 监听当前股票变动
      // this.stockData = val;
      let stockCode = val.code.replace("hk", "").replace("us", "");
      if (this.stockCode != stockCode) {
        this.stockData = val;
        this.stockCode = stockCode;
        this.financialIndices();
      }
    }
  },
  mounted() {
    this.financialIndices();
  },
  methods: {
    financialIndices() {
      // GET /financialIndices
      let stockCode = this.stockData.code.replace("hk", "").replace("us", "");
      let url = "/financialIndices";
      let financialIndicesData = this.financialIndicesData;
      let data = {
        stockcode: stockCode,
        limit: financialIndicesData.limit
      };
      this.axios({
        url: url,
        params: data,
        type: "hostNews",
        hideMessage: true
      }).then(res => {
        initArray(res.data.ret.data.records);
        this.financialIndicesData.list = res.data.ret.data.records;
      }).catch(err => {
        this.financialIndicesData.list = [];
      });
    },
    scroll(e) {
      this.scrollLeft = e.target.scrollLeft;
    }
  },
  filters: {
    unit(val) {
      if (val > 10000000) {
        return (val / 100000000).toFixed(2) + "亿";
      } else {
        return val;
      }
    }
  }
};
</script>
<style lang='scss' scoped>
.finance {
  @include flex-box;
  justify-content: space-between;
  height: 100%;
  overflow: auto;
  position: relative;
  .financeLeft {
    width: 190px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: $blockBgColor;
    box-shadow: 0 0 40px rgba($color: #000000, $alpha: 1);
    > span {
      @include add-size($font_size_14);
      display: block;
      color: #fff;
      line-height: 34px;
      padding: 0 20px;
    }
  }
  .financeRight {
    width: 80%;
    // overflow-x: auto;
    margin-left: 200px;
    .slide-ul {
      white-space: nowrap;
      // height: 100%;
      .slide-li {
        display: inline-block;
        height: 100%;
        width: 120px;
        vertical-align: middle;
        div {
          @include add-size($font_size_14);
          color: #fff;
          // padding: 10px 20px;
          // height: 34px;
          line-height: 34px;
        }
      }
    }
  }
}
.wrap-content {
  position: relative;
  height: 100%;
  .nodata {
    padding: 20px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 60%;
    transform: translateX(-50%);
    text-align: center;
  }
}
</style>      