function setHtml() {
  // if (router && router.history.current.fullPath === "/hq?index=hk") {
  //   console.log("首页")
  // }
  var devInnerWidth = 1920; // 开发时的InnerHeight
  if (router && router.history.current.path === "/login") {
    devInnerWidth = 700;
  }
  var devDevicePixelRatio = 1.0; // 开发时的devicepixelratio
  var devScaleFactor = 1; // 开发时的ScaleFactor
  var scaleFactor = remote.screen.getPrimaryDisplay().scaleFactor;
  var zoomFactor =
    (window.innerWidth / devInnerWidth) *
    (window.devicePixelRatio / devDevicePixelRatio) *
    (devScaleFactor / scaleFactor);
  zoomFactor = Number(zoomFactor.toFixed(2));
  // console.log(zoomFactor)
  webFrame.setZoomFactor(zoomFactor);
}

setTimeout(() => {
  setHtml();
}, 300)
window.addEventListener("resize", setHtml);

// 对Date的扩展，将 Date 转化为指定格式的String
// 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
// 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
// 例子：
// (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
// (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
Date.prototype.Format = function(fmt) {
  var o = {
    "M+": this.getMonth() + 1, //月份
    "d+": this.getDate(), //日
    "h+": this.getHours(), //小时
    "m+": this.getMinutes(), //分
    "s+": this.getSeconds(), //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
    S: this.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};

//订阅者模式
function Public() {
  this.handlers = {};
}
Public.prototype = {
  // 订阅事件
  on: function(eventType, handler) {
    var self = this;
    if (!(eventType in self.handlers)) {
      self.handlers[eventType] = [];
    }
    self.handlers[eventType].push(handler);
    return this;
  },
  // 触发事件(发布事件)
  emit: function(eventType) {
    var self = this;
    var handlerArgs = Array.prototype.slice.call(arguments, 1);
    for (var i = 0; i < self.handlers[eventType].length; i++) {
      self.handlers[eventType][i].apply(self, handlerArgs);
    }
    return self;
  },
  // 删除订阅事件
  off: function(eventType, handler) {
    var currentEvent = this.handlers[eventType];
    var len = 0;
    if (currentEvent) {
      len = currentEvent.length;
      for (var i = len - 1; i >= 0; i--) {
        if (currentEvent[i] === handler) {
          currentEvent.splice(i, 1);
        }
      }
    }
    return this;
  }
};

