let notifyNum = 0;
function setNotification(msg) {
  if (notifyNum < 3) {
    // 最多显示4条消息
    notifyNum++;
    if (document.hidden) {
      ipc.send("iconFlash");
      ipc.send("flashFrame", true);
    }
    Vue.prototype.$notify({
      title: "提示",
      message: msg,
      duration: 0,
      position: "bottom-right",
      onClick() {
        let reg = /[^(][a-zA-Z0-9]+(?=\))/g;
        let target = msg.match(reg);
        let a = 1,
          code = "";
        if (!isNaN(target[0])) {
          code = "hk" + target[0];
        } else {
          code = "us" + target[0];
          a = 2;
        }
        store.dispatch("CHANGE_ROUTER_TARGET", {
          a: a,
          b: 0,
          timestamp: new Date().getTime()
        });
        setTimeout(() => {
          store.dispatch("CHANGE_SEARCH_DATA", {
            code: code,
            timestamp: new Date().getTime()
          });
        }, 500);

        notifyNum--;
        this.close();
      },
      onClose() {
        notifyNum--;
      }
    });
  }
}

