// 定义一个深拷贝函数  接收目标target参数
function deepClone(target) {
  // 定义一个变量
  let result;
  // 如果当前需要深拷贝的是一个对象的话
  if (typeof target === "object") {
    // 如果是一个数组的话
    if (Array.isArray(target)) {
      result = []; // 将result赋值为一个数组，并且执行遍历
      for (let i in target) {
        // 递归克隆数组中的每一项
        result.push(deepClone(target[i]));
      }
      // 判断如果当前的值是null的话；直接赋值为null
    } else if (target === null) {
      result = null;
      // 判断如果当前的值是一个RegExp对象的话，直接赋值
    } else if (target.constructor === RegExp) {
      result = target;
    } else {
      // 否则是普通对象，直接for in循环，递归赋值对象的所有值
      result = {};
      for (let i in target) {
        result[i] = deepClone(target[i]);
      }
    }
    // 如果不是对象的话，就是基本数据类型，那么直接赋值
  } else {
    result = target;
  }
  // 返回最终结果
  return result;
}

function initArray(arr, tar) {
  // tar  传入的指定key
  if (Array.isArray(arr)) {
    arr.forEach(a => {
      if (tar) {
        for (let key in a[tar]) {
          if (
            a[tar][key] === "" ||
            a[tar][key] === undefined ||
            a[tar][key] === null
          ) {
            a[tar][key] = "— —";
          }
        }
      } else {
        for (let key in a) {
          if (a[key] === "" || a[key] === undefined || a[key] === null) {
            a[key] = "— —";
          }
        }
      }
    });
  } else {
    console.error("传入参数必须是数组");
  }
}

let BUY_SELL_SOCKET = null;
let BUY_SELL_STOCK_CODE = [];
function clearCurStock() {
  // 关闭交易窗口后清除已订阅股票的买卖十档
  BUY_SELL_STOCK_CODE = [];
  buySellSocket("");
}
function buySellSocket(code) {
  // 交易弹框中的买卖十档订阅
  let Socket = require("@/utils/socket").Socket;
  let topic = [];
  if (BUY_SELL_STOCK_CODE.indexOf(code) == -1) {
    BUY_SELL_STOCK_CODE.push(code);
  }
  BUY_SELL_STOCK_CODE.forEach(q => {
    topic.push("order." + q);
  });
  if (BUY_SELL_SOCKET) {
    BUY_SELL_SOCKET.resend({
      url: "ws://quote.lc913.com:8080/ws",
      openParams: {
        Topic: topic,
        Delay: 0
      },
      heartParams: {
        command: 1
      }
    });
    return false;
  }
  BUY_SELL_SOCKET = new Socket({
    url: "ws://quote.lc913.com:8080/ws",
    openParams: {
      Topic: topic,
      Delay: 0
    },
    heartParams: {
      command: 1
    }
  });
  let delay = 0; // 最小间隔更新一次数据
  store.dispatch("SET_DEAL_BUY_SELL", {});
  BUY_SELL_SOCKET.onmessage(e => {
    if (!store.state.system.dealBar.barShow) {
      // 交易弹框关闭不再接收数据
      return false;
    }
    let data = JSON.parse(e.data);
    let realData = JSON.parse(data.Result.Data[0]);
    let timestamp = new Date().getTime();
    if (data.Result.Topic.indexOf("order.") != -1) {
      //买卖十档
      let target = store.state.GlobalData.dealBuySell[realData.symbol] || {};
      let lastTime = target.timestamp || 0;
      if (timestamp - lastTime > delay) {
        let val = {};
        realData.asklist.reverse();
        val[realData.symbol] = {
          data: realData,
          timestamp
        };
        store.dispatch("SET_DEAL_BUY_SELL", val);
      }
    }
  });
}

// 指数数据 长连接
function indexFutures() {
  // 交易弹框中的买卖十档订阅
  let Socket = require("@/utils/socket").Socket;
  let topic = [];
  let codeList = [
    "hk0000100",
    "hk0001400",
    "hk0001500",
    "usDIA",
    "usIVV",
    "usQQQ"
  ];
  codeList.forEach(q => {
    topic.push("snapshot." + q);
  });
  let loginImei = "LoginRepeatNotice$" + createHeader().Imei;
  topic.push(loginImei); // 阻止设备同时登陆

  let INDEX_FUTURES_WS = new Socket({
    url: "ws://quote.lc913.com:8080/ws",
    openParams: {
      Topic: topic,
      Delay: 0
    },
    heartParams: {
      command: 1
    }
  });
  let delay = 1000; // 最小间隔更新一次数据
  let tem = {};
  INDEX_FUTURES_WS.onmessage(e => {
    let data = JSON.parse(e.data);
    if (data.Result.Topic === loginImei) {
      // Vue.prototype.$notify
      Vue.prototype
        .$confirm(data.Result.Data[0], "提示", {
          showCancelButton: false
        })
        .then(() => {
          reLogin();
        })
        .catch(() => {
          reLogin();
        });
      return false;
    }
    let realData = JSON.parse(data.Result.Data[0]);

    let name = data.Result.Topic.split(".")[1];
    tem[name] = realData.query;
    // SET_INDEX_STOCK_DATA
    store.dispatch("SET_INDEX_STOCK_DATA", tem);
  });
}

// 判读股票是窝轮、牛熊、界内证还是普通股票
function sortStock(stock) {
  if (stock.inline && stock.inline.stock) {
    return 4; //界内证
  } else if (stock.rights && stock.rights.stock) {
    return 1; //牛熊
  } else if (stock.warrant && stock.warrant.stock) {
    return 2; // 窝轮
  } else {
    return 0; //普通股票
  }
}

