<template>
  <div class="fund-record">
    <div class="sort-bar">
      <div class="label">类型：</div>
      <el-select v-model="requestData.recordType" placeholder="请选择">
        <el-option v-for="item in option" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </div>
    <!-- <table-block>
    </table-block> -->
      <el-table :data="fundRecord" :height="170" empty-text=" ">
        <el-table-column prop="money" align="center" label="金额" width="120">
          <template slot-scope="scope">
            <div
              :class="{fall: scope.row.recordType != 0, rise: scope.row.recordType == 0}"
            >{{(scope.row.recordType !=0?'-':'+') + (Number(scope.row.money).toFixed(2))}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="recordType" label="类型" align="center" width="80">
          <template slot-scope="scope">
            <div>{{scope.row.recordType == 0?'入金':'出金'}}</div>
          </template>
        </el-table-column>
        <!-- 0待审核 1通过 2驳回 -->
        <el-table-column prop="state" label="状态" align="center" width="100">
          <template slot-scope="scope">
            <div>{{stateName[scope.row.state]}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="提交时间" align="center" width="255"></el-table-column>
        <el-table-column label="操作" align="center" v-if="currentType == 0">
          <template slot-scope="scope">
            <div class="check" v-if="scope.row.recordType == 0" @click="showDetail(scope.row)">查看凭证</div>
          </template>
        </el-table-column>
        <el-table-column align="center" ></el-table-column>

        <template slot="append">
          <infinite-loading
            ref="infinite"
            @infinite="getRecordList"
            :identifier="identityId"
            :distance="20"
            forceUseInfiniteWrapper=".el-table__body-wrapper"
          >
            <loading></loading>
            <span slot="no-more">—————— 暂无更多 ———————</span>
            <span slot="no-results">—————— 暂无数据 ———————</span>
          </infinite-loading>
        </template>
      </el-table>
    <el-image-viewer v-if="showViewer" :on-close="closeViewer" :url-list="imgUrls" />
  </div>
</template>

<script>
import tableBlock from "@/components/deal/table-block";
import infiniteLoading from "vue-infinite-loading";
import ElImageViewer from "@/components/element-ui/image/image-viewer";

export default {
  components: {
    tableBlock,
    infiniteLoading,
    ElImageViewer
  },
  data() {
    return {
      recordType: "",
      option: [
        {
          label: "全部",
          value: ""
        },
        {
          label: "出金",
          value: "1"
        },
        {
          label: "入金",
          value: "0"
        }
      ],
      stateName: ["待审核", "通过", "驳回"],
      fundRecord: [],
      requestData: {
        page: 1,
        size: 10,
        recordType: "",
        channel: 0
      },
      identityId: 0,
      showViewer: false,
      imgUrls: []
    };
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.system.dealBar.currentType;
    }
  },
  activated() {
    this.$nextTick(() => {
      // this.getRecordList();
      this.identityId++;
      this.requestData.page = 1;
      this.fundRecord = [];
    });
  },
  watch: {
    "requestData.recordType"() {
      this.identityId++;
      this.fundRecord = [];
      this.requestData.page = 1;
      // this.getRecordList();
    },
    currentType() {
      this.identityId++;
      this.fundRecord = [];
      this.requestData.page = 1;
    }
  },
  methods: {
    showDetail(row) {
      console.log(row);
      if (row.recordType != 0) {
        return false;
      }
      let id = row.id;
      // GET /order/message/selectFundInfo
      this.axios({
        url: "/order/message/selectFundInfo",
        params: {
          id
        },
        type: "host2"
      }).then(res => {
        console.log(res);
        let imgs = res.data.ret.voucher.split(",");
        if (imgs.length > 0) {
          this.imgUrls = imgs;
          this.showViewer = true;
        } else {
          this.$message({
            message: "该记录没有上传交易凭证！",
            type: "error"
          });
        }
      });
    },
    closeViewer() {
      this.showViewer = false;
    },
    getRecordList($state) {
      // GET /order/message/selectFundList
      this.requestData.channel = this.currentType;
      this.axios({
        url: "/order/message/selectFundList",
        params: {
          ...this.requestData
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (this.requestData.page == 1) {
            this.fundRecord = res.data.ret.list;
          } else {
            this.fundRecord = this.fundRecord.concat(res.data.ret.list);
          }
          this.requestData.page++;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    infiniteHandler($state) {
      setTimeout(() => {
        let arr = JSON.parse(JSON.stringify(this.list));
        this.stockData = this.stockData.concat(arr);
        $state.loaded();
      }, 2000);
      // ajax("https://www.example.com/api/news").then(res => {
      //   if (res.data.length) {
      //     $state.loaded();
      //   } else {
      //     $state.complete();
      //   }
      // });
    }
  }
};
</script>

<style lang="scss" scoped>
.fund-record {
  padding-left: 20px;
  .sort-bar {
    @include add-size($font_size_12);
    @include flex-center;
    /deep/ .el-select {
      .el-input__inner {
        background: inherit;
        border: none;
        @include add-size($font_size_12);
        color: $white;
        width: 90px;
      }
      .el-input__icon {
        color: $defaultColor;
        &::before {
          content: "\E78F";
        }
      }
    }
  }
  /deep/ .table-block {
    width: calc(100% - 30px);
    .el-table {
      margin-top: 8px;
      td {
        .cell {
          line-height: 34px;
          // @include add-size($font_size_14);
        }
      }
      .fall {
        color: $green;
      }
      .rise {
        color: $red;
      }
      .check {
        color: $activeColor;
        cursor: pointer;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
}
</style>