
const ip = require('ip');
import store from "@/store";
import md5 from 'js-md5';
// import getMac, {isMAC} from "getmac";
import {machineIdSync} from 'node-machine-id';
// 设备标识
let imei = machineIdSync();

const createHeader = function (flag) {
    // 时间戳
    let timeStamp = Math.floor(new Date().getTime() / 1000);
    // 当前版本
    let version = 1.0;
    // 频道
    let channel = 3;
    // 请求数量
    let Counter = store.state.system.reqCount;
    store.dispatch("ADD_REQ_COUNT");
    // 用户、游客id
    let userId = localStorage.getItem("customerId") || "";

    // 用户ip
    let iPAddress = ip.address();
    // 登录凭证
    // timeStamp = 1565234857;
    // imei = "12342143265"
    let token = md5(`${timeStamp}#yszq#${imei}`);
    let jwttoken = localStorage.getItem("token") || "";
    if(flag){
        // webSocket header
        return {
            Token: token,
            Timestamp: timeStamp,
            Version: '1.0.0',
            Channel: 2,
            ReqNo: Counter,
            Imei: imei,
            Userid: userId,
            Ip: iPAddress
        }
    } else {
        // ajax header
        return {
            "Content-Type": "application/json;charset=UTF-8",
            Authorization: `Bearer ${token}`,
            Timestamp: timeStamp,
            Version: version,
            Channel: channel,
            ReqNo: Counter,
            Imei: imei,
            Userid: userId,
            Ip: iPAddress,
            JwtToken: jwttoken
        }
    }
}
export {
    createHeader
}