const state = {
  hk_industry: {
    timestamp: 0,
    value: []
  }
};

const mutations = {
  SET_HK_INDUSTRY(state, e) {
    state.hk_industry = {
      timestamp: Date.now(),
      value: e
    };
  }
};

const actions = {
  ADD_HK_INDUSTRY({ commit }, e) {
    commit("SET_HK_INDUSTRY", e);
  }
};

export default {
  state,
  mutations,
  actions
};
