<template>
  <div class="upload-box">
    <input type="file" ref="inputFile" :id="id" mini @change="imgChoose" multiple="multiple" v-if="multiple" />
    <input type="file" ref="inputFile" :id="id" mini @change="imgChoose" v-else />
  </div>
</template>

<script>
export default {
  props: {
    id: String,
    multiple: Boolean
  },
  data() {
    return {};
  },
  methods: {
    imgChoose(e) {
      try {
        // console.log(e);
        if (!this.multiple) {
          let file = e.target.files[0];
          if (!file) {
            return false;
          }
          let imgSrc = window.URL.createObjectURL(file);

          if (file.size > 5 * 1024 * 1024) {
            throw new Error("图片大小不能超过5M！");
          }
          if (file.type.indexOf("image") < 0) {
            throw new Error("只能上传图片！");
          }
          let o = {
            src: imgSrc,
            file: file
          };
          this.$emit("change", o);
        } else {
          let files = e.target.files;
          let filesArr = new Array;
          for (let i = 0; i < files.length; i++) {
            if (files[i].size > 5 * 1024 * 1024) {
              throw new Error("图片大小不能超过5M！");
            }
            if (files[i].type.indexOf("image") < 0) {
              throw new Error("只能上传图片！");
            }
            let o = {};
            o.file = files[i];
            o.imgSrc = window.URL.createObjectURL(files[i]);
            filesArr.push(o);
          }

          this.$emit("change", filesArr);
        }
        this.$refs.inputFile.value = "";
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.upload-box {
  input[mini] {
    width: 0;
    height: 0;
    padding: 0;
    border: none;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
  }
}
</style>


