<template>
  <div
    class="deal-cover"
    :class="{ active: dealVisible }"
    :data-mini="isLogin && !dealVisible"
  >
    <div class="bar-top" @click="showMaxDeal">
      <div
        class="account-btn personal-account"
        :class="{ active: currentType == 0 }"
        v-if="showErbroker"
        @click="changeCurrentType(0)"
      >
        用户：{{ ebokerUsername.id }}——{{ ebokerUsername.name }}
      </div>
      <div
        class="account-btn personal-account"
        :class="{ active: currentType == 1 }"
        v-if="showOrganization"
        @click="changeCurrentType(1)"
      >
        用户：{{ orgUsername.id }}——{{ orgUsername.name }}
      </div>
      <div
        class="account-btn organization-account"
        @click="loginMore"
        v-if="showMoreLogin && privateFlag"
      >
        <i class="el-icon-circle-plus-outline"></i>
        登录更多交易账户
      </div>
      <div class="account-btn widgets" @click="showTrading">多窗口交易</div>
      <div class="account-btn widgets-close" @click="closeAll">
        关闭所有交易窗口
      </div>
      <!-- <el-switch
        class="widgets-close"
        v-model="lockSwitch"
        @change="changeLockData"
        active-color="#DEA567"
        inactive-color="#9195AC"
      ></el-switch>-->
      <div class="set-min btn" @click.stop="closeDeal">
        {{ dealVisible ? "最小化" : "最大化" }}
      </div>
      <div class="logout btn" @click.stop="logOut">退出</div>
    </div>
    <div class="bar-content">
      <div class="left-menu">
        <div
          class="menu-li"
          :class="{ active: menu.show, index: currentBlock === menu.block }"
          v-for="menu in menuData"
          :key="menu.id"
          :style="{ height: menu.show ? menu.height + 'px' : '26px' }"
        >
          <div class="menu-label" @click="isShow(menu)">
            {{ menu.label }}
            <i
              class="el-icon-arrow-down arrow"
              v-if="menu.subMenu.length > 0"
            ></i>
          </div>
          <div class="sub-menu" v-if="menu.subMenu.length > 0">
            <div
              class="sub-menu-li"
              v-for="(me, i) in menu.subMenu"
              :key="i"
              :class="{ active: me.active, hide: me.show === false }"
              @click="subMenu(me)"
            >
              {{ me.label }}
            </div>
          </div>
        </div>
      </div>
      <div class="right-block">
        <div class="across-menu">
          <div
            class="across-menu-li"
            v-for="(ac, i) in acrossMenu"
            :key="i"
            :data-show="ac.show"
            :class="{ active: ac.active }"
            @click="chooseAcross(ac)"
          >
            {{ ac.label }}
          </div>
        </div>
        <keep-alive>
          <transition name="component-fade" mode="out-in">
            <keep-alive>
              <component
                v-if="dealVisible"
                :accountInfo="accountInfo"
                :curPosition="curPosition"
                :curEntrust="curEntrust"
                :is="currentBlock"
                :requestConfig="requestConfig"
                :acrossMenuSingle="acrossMenuSingle"
                @cancelOrder="getBaseData"
              ></component>
            </keep-alive>
          </transition>
        </keep-alive>
      </div>
      <trading
        :accountInfo="accountInfo"
        :curPosition="curPosition"
        :curEntrust="curEntrust"
        :requestConfig="requestConfig"
        ref="trading"
      />
    </div>
  </div>
</template>

<script>
import indexField from "./deal-fields/index-field";
import historyField from "./deal-fields/history-field";
import fundField from "./deal-fields/fund-field";
import entrustField from "./deal-fields/entrust-field";
import statementField from "./deal-fields/statement-field";
import fundRecordField from "./deal-fields/fund-record-field";
import fundSaveField from "./deal-fields/fund-save-field";
import fundDrawField from "./deal-fields/fund-draw-field";
import curDealField from "./deal-fields/cur-deal-field";
import curEntrustField from "./deal-fields/cur-entrust-field";
import foundationField from "./deal-fields/foundation-field";
import trading from "@/components/deal/drag-cover";
const shell = require("electron").shell;
import { clearCurStock } from "@/utils";
import config from "@/utils/config";
const requestConfig = {
  position: [
    // 当前持仓、账户信息
    "/getAccountPosition.do",
    "/getAccountPosition.do"
  ],
  curHistory: ["/getQryHistoryFilledOrder.do", "/getQryHistoryFilledOrder.do"],
  history: [
    // 成交历史记录
    "/getQryHistoryFilledOrder.do",
    "/getQryHistoryFilledOrder.do"
  ],
  curEntrust: [
    // 当天委托
    "/getQryHistoryWTOrder.do",
    "/getQryHistoryWTOrder.do"
  ],
  entrust: ["/getQryHistoryWTOrder.do", "/getQryHistoryWTOrder.do"],
  addOrder: ["/addOrder.do", "/addOrder.do"],
  cancelOrder: ["/cancelOrder.do", "/cancelOrder.do"],
  type: ["hostEbroker", "organization"],
  account: []
};

export default {
  components: {
    indexField,
    historyField,
    fundField,
    entrustField,
    statementField,
    fundRecordField,
    fundSaveField,
    fundDrawField,
    trading,
    curDealField,
    curEntrustField,
    foundationField
  },
  data() {
    return {
      menuData: [
        {
          label: "交易",
          id: 1,
          show: false,
          height: 0,
          subMenu: [],
          block: "indexField"
        },
        {
          label: "查询",
          id: 2,
          height: 0,
          show: true,
          subMenu: [
            {
              label: "资金股份",
              block: "fundField",
              active: false
            },
            {
              label: "今日成交",
              block: "curDealField",
              active: false
            },
            {
              label: "今日委托",
              block: "curEntrustField",
              active: false
            },
            {
              label: "历史成交",
              block: "historyField",
              active: false
            },
            {
              label: "历史委托",
              block: "entrustField",
              active: false
            },
            {
              label: "我的结单",
              block: "statementField",
              active: false,
              show: true
            },
            {
              label: "资金记录",
              block: "fundRecordField",
              active: false
            }
          ]
        },
        {
          label: "操作",
          id: 3,
          show: true,
          height: 0,
          subMenu: [
            {
              label: "资金存入",
              block: "fundSaveField",
              active: false,
              extra: "bwHide"
            },
            {
              label: "资金提取",
              block: "fundDrawField",
              active: false
            },
            {
              label: "基金理财",
              block: "foundationField",
              active: false,
              show: true
            }
            /* {
              label: "转入股票",
              block: "transferField",
              active: false
            } */
          ]
        }
      ],
      acrossMenu: [
        {
          label: "买入",
          value: "indexField",
          extra: "buy",
          active: true
        },
        {
          label: "卖出",
          value: "indexField",
          extra: "sale",
          active: false
        },
        {
          label: "资金股份",
          value: "fundField",
          active: false
        },
        {
          label: "今日成交",
          value: "curDealField",
          active: false
        },
        {
          label: "今日委托",
          value: "curEntrustField",
          active: false
        },
        {
          label: "历史成交",
          value: "historyField",
          active: false
        },
        {
          label: "历史委托",
          value: "entrustField",
          active: false
        },
        {
          label: "资金存入",
          value: "fundSaveField",
          active: false
        },
        {
          label: "资金提取",
          value: "fundDrawField",
          active: false
        },
        {
          label: "我的结单",
          value: "statementField",
          active: false,
          show: "true"
        },
        {
          label: "资金记录",
          value: "fundRecordField",
          active: false
        },
        {
          label: "基金理财",
          value: "foundationField",
          active: false
        }
      ],
      currentId: 1,
      currentBlock: "indexField",
      lockSwitch: true,
      accountInfo: {},
      curPosition: [],
      curEntrust: [],
      requestConfig: requestConfig,
      privateFlag: config.privateFlag, // 是否显示多个账户登录 （内部使用）
      acrossMenuSingle: {
        label: "买入",
        value: "indexField",
        extra: "buy",
        active: true
      }
    };
  },

  mounted() {
    this.$nextTick(() => {});
  },
  computed: {
    dealBar() {
      return this.$store.state.system.dealBar;
    },
    dealVisible() {
      return this.dealBar.barShow;
    },
    accountData() {
      return this.$store.state.system.accountInfo;
    },
    ebokerUsername() {
      // console.log(this.$store.state.AccountLogin)
      let ebokerUsername = {
        id: this.accountData.ebokerUsername,
        name: this.accountData.enName
      };
      this.requestConfig.account[0] = ebokerUsername.id;
      return ebokerUsername;
    },
    orgUsername() {
      let orgUsername = {
        id: this.accountData.institutionalAccount,
        name: this.accountData.name
      };
      this.requestConfig.account[1] = localStorage.getItem("customerId");
      return orgUsername;
    },
    showErbroker() {
      // 是否登录了erbroker
      return this.dealBar.commonAccount;
    },
    showOrganization() {
      // 是否登录了机构账户
      return this.dealBar.organization;
    },
    isLogin() {
      if (this.dealBar.commonAccount || this.dealBar.organization) {
        return true;
      } else {
        return false;
      }
    },
    showMoreLogin() {
      // 两种账户没有全部登录时显示登录更多账户
      if (this.dealBar.commonAccount && this.dealBar.organization) {
        return false;
      } else {
        return true;
      }
    },
    currentType() {
      // 当前选择的账户  0 erbroker  1 博威
      let currentType = this.dealBar.currentType;
      if (currentType == 1) {
        // 当前账户为博威时， 不显示我的结单模块
        this.menuData[1].subMenu[5].show = false;
        this.menuData[2].subMenu[2].show = true;
        this.acrossMenu[9].show = "false";
        this.acrossMenu[11].show = "true";
      } else {
        this.menuData[1].subMenu[5].show = true;
        this.menuData[2].subMenu[2].show = false;
        this.acrossMenu[9].show = "true";
        this.acrossMenu[11].show = "false";
      }
      this.menuData.forEach(a => {
        // 计算左侧菜单高度
        let num = 1;
        a.subMenu.forEach(b => {
          if (b.show === undefined || b.show === true) {
            num++;
          }
        });
        a.height = num * 26;
      });
      return currentType;
    }
  },
  activated() {},
  watch: {
    currentType(val) {
      this.getBaseData();
      this.getTaxData();
      if (val == 1 && this.currentBlock === "statementField") {
        // 切换到博威时 如果显示的是结单模块，  切换到交易模块
        this.currentBlock = "indexField";
        this.targetMenu();
      } else if (val == 1 && this.currentBlock === "fundSaveField") {
        this.currentBlock = "indexField";
        this.targetMenu();
      }
    },
    dealVisible(val) {
      if (val) {
        // console.log(this.$route);
        this.getBaseData();
        this.getTaxData();
        // if (this.$route.fullPath.indexOf("code") === -1) {
        //   this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        //     a: 1,
        //     b: 0,
        //     timestamp: new Date().getTime() - 1000 * 11
        //   });
        // }
      }
    },
    "$store.state.DealBar.isAction"(val) {
      // 监听下单操作
      this.getBaseData();
    },
    "$store.state.system.targetDeal"(val) {
      this.targetMenu();
      setTimeout(() => {
        this.chooseAcross(this.acrossMenu[val.target]);
      }, 200);
    }
  },
  methods: {
    showMaxDeal() {
      this.$store.dispatch("SET_DEAL_BAR", { barShow: true });
    },
    chooseAcross(ac) {
      this.acrossMenu.forEach(a => {
        a.active = false;
      });
      ac.active = true;
      let target = {};
      this.menuData.forEach(a => {
        if (a.block === ac.value) {
          target = a;
          return false;
        }
        a.subMenu.forEach(b => {
          if (b.block === ac.value) {
            target = b;
            return false;
          }
        });
      });
      this.acrossMenuSingle = ac;
      if (target.subMenu) {
        this.isShow(target, ac);
      } else if (target.block) {
        this.subMenu(target);
      }
    },
    logOut(tar) {
      this.$confirm("确定退出当前交易账户？", "提示", {
        cancelButtonText: "取消"
      })
        .then(() => {
          // 退出对应账户
          this.$store.dispatch("SET_DEAL_BAR", {
            loginShow: false, // 交易弹框显示
            barShow: false, // 交易栏显示
            commonAccount: false, // 是否登录交易账户
            organization: false, // 是否登录机构账户
            currentType: 0 // 当前账户类型
          });

          clearCurStock();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getTaxData() {
      // ebroker/getCommissionInfo.do  hostEbroker
      let accountNo = this.requestConfig.account[this.currentType];
      this.axios({
        url: "/getCommissionInfo.do",
        method: "post",
        params: {
          userNo: accountNo,
          accountId: accountNo
        },
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        localStorage.setItem("taxData", JSON.stringify(res.data.data));
      });
    },
    changeCurrentType(flag) {
      this.$store.dispatch("SET_DEAL_BAR", {
        currentType: flag
      });
    },
    loginMore() {
      // let identityData = this.$store.state.AccountLogin.identityData;
      // console.log(identityData)
      // if (identityData.institutionalAccount) {
      this.$store.dispatch("SET_DEAL_BAR", {
        loginShow: true
      });
      // }
    },
    changeLockData() {
      if (!this.lockSwitch) {
        this.closeAll();
      }
    },
    getBaseData() {
      // let accountNo = this.$store.state.AccountLogin.accountData.LoginID;
      // console.log(this.$store.state.AccountLogin.accountData);
      let params = {};
      if (this.currentType == 0) {
        params.userNo = this.requestConfig.account[this.currentType];
      }
      params.market = "hk";
      this.axios({
        url: this.requestConfig.position[this.currentType],
        method: "post",
        params: params,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.accountInfo = res.data.data.accountInfo;
        this.curPosition = res.data.data.curPosition || [];
        this.curEntrust = res.data.data.curEntrust || [];
      });
    },
    showTrading() {
      this.lockSwitch = true;
      this.$refs.trading.addWin();
    },
    closeAll() {
      this.$refs.trading.closeAll();
    },
    isShow(menu, sub) {
      menu.show = !menu.show;
      if (menu.block) {
        this.currentBlock = menu.block;
        if (!sub) {
          this.acrossMenu.forEach(m => {
            m.active = false;
          });
          this.acrossMenu[0].active = true;
          this.acrossMenuSingle = this.acrossMenu[0];
        }

        this.menuData.forEach(a => {
          a.subMenu.forEach(b => {
            b.active = false;
          });
        });
      }
    },
    subMenu(sub) {
      // if (sub.extra && this.currentType == 1) {
      //   // 机构账户需要去官网入金
      //   shell.openExternal("http://www.13sec.cn/login.html");
      //   return false;
      // }
      this.menuData.forEach(a => {
        a.subMenu.forEach(b => {
          b.active = false;
        });
      });
      this.currentBlock = sub.block;
      sub.active = true;
      if (this.currentBlock === "fundField") {
        this.getBaseData();
      }
      this.acrossMenu.forEach(m => {
        m.active = false;
        if (m.value === sub.block) {
          m.active = true;
          this.acrossMenuSingle = m;
        }
      });
    },
    targetMenu() {
      this.isShow(this.menuData[0]);
    },
    closeDeal() {
      if (this.dealVisible) {
        this.$store.dispatch("SET_DEAL_BAR", { barShow: false });
      } else {
        this.$store.dispatch("SET_DEAL_BAR", { barShow: true });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$borColor: #323840;
.deal-cover {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #12181e;
  height: 0;
  opacity: 0;
  width: 100%;
  z-index: 999;
  box-shadow: 0px -4px 8px 0px rgba(19, 25, 32, 0.79);
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  .bar-top {
    @include flex-center;
    height: 34px;
    .account-btn {
      width: 320px;
      height: 26px;
      background-color: $blockBgColor;
      position: relative;
      @include flex-center;
      justify-content: center;
      cursor: pointer;
      border-radius: 4px;
      margin-right: 4px;
      position: relative;
      &.organization-account {
        i {
          margin-right: 12px;
        }
      }
      &:hover {
        color: $white;
      }
      &.active {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        color: $activeColor;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -4px;
          width: 100%;
          height: 4px;
          background-color: $blockBgColor;
        }
      }
      .logout-icon {
        position: absolute;
        top: 7px;
        right: 6px;
      }
    }
    .widgets {
      width: 120px;
      margin-left: 30px;
    }
    .widgets-close {
      width: 120px;
      margin-left: 30px;
      // padding-left: 8px;
      // background-color: inherit;
      // background-image: url("~@/assets/images/jy/deal-cover/close-all.png");
      // background-repeat: no-repeat;
      // background-size: 14px 14px;
      // background-position: 0 8px;
    }
    .logout {
      // width: 32px;
      // height: 32px;
      // margin-left: 10px;
      &:hover {
        color: $activeColor;
      }
    }
    .btn {
      &.set-min {
        margin-left: auto;
      }
      line-height: 32px;
      cursor: pointer;
      margin-right: 10px;
      &:hover {
        color: $activeColor;
      }
    }
    .min {
      width: 32px;
      height: 32px;
      margin-right: 10px;
      margin-left: auto;
      cursor: pointer;
      position: relative;
      background-image: url("~@/assets/images/base/min-icon-a.png");
      background-size: 13px 2px;
      background-position: center 16px;
      background-repeat: no-repeat;
      &:hover {
        background-image: url("~@/assets/images/base/min-icon-b.png");
        background-size: 32px 32px;
        background-position: center -6px;
        background-color: #455163;
        color: $activeColor;
      }
    }
  }

  .bar-content {
    width: 100%;
    height: calc(100% - 34px);
    background-color: $blockBgColor;
    @include flex-box;
    position: relative;

    .left-menu {
      width: 181px;
      height: 100%;
      overflow-y: auto;
      border-right: 1px solid $borColor;
      .menu-li {
        &:last-child {
          border-bottom: none;
        }
        overflow: hidden;
        border-bottom: 1px solid $borColor;
        transition: all 0.3s ease;
        &.active {
          .arrow {
            transform: rotate(180deg);
          }
        }
        &.index {
          .menu-label {
            background-color: #12171d;
          }
        }
        .menu-label {
          color: $white;
          @include flex-center;
          position: relative;
          line-height: 26px;
          padding-left: 36px;
          cursor: pointer;
          &:hover {
            background-color: #12171d;
          }
          &:before {
            content: "";
            position: absolute;
            left: 20px;
            top: 4px;
            width: 6px;
            height: 14px;
            background-color: $activeColor;
          }
          .arrow {
            margin-left: auto;
            margin-right: 6px;
            transition: all 0.3s ease;
          }
        }
        .sub-menu {
          .sub-menu-li {
            text-align: center;
            line-height: 26px;
            position: relative;
            cursor: pointer;
            &.active,
            &:hover {
              background-color: #12171d;
              color: $white;
              &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 0;
                width: 2px;
                height: 26px;
                background-color: $activeColor;
              }
            }
            &.hide {
              display: none;
            }
          }
        }
      }
    }
    .right-block {
      width: calc(100% - 181px);
      .across-menu {
        line-height: 22px;
        margin-top: 10px;
        width: auto;
        margin-left: 18px;
        @include flex-center;
        .across-menu-li {
          width: 80px;
          box-sizing: content-box;
          text-align: center;
          cursor: pointer;
          @include add-size($font_size_12);
          border-right: 1px solid $borderColor;
          border-top: 1px solid $borderColor;
          border-bottom: 1px solid $borderColor;
          &:first-child {
            border-left: 1px solid $borderColor;
          }
          &.active,
          &:hover {
            color: $white;
            background-color: #12171d;
          }
          &[data-show="false"] {
            display: none;
          }
        }
      }
    }
  }
  &.active {
    display: block;
    height: 320px;
    opacity: 1;
    overflow: visible;
  }
  &[data-mini] {
    display: block;
    height: 34px;
    opacity: 1;
    overflow: visible;
  }
  /deep/ .el-table {
    margin-top: 12px;
    .pr_40 {
      padding-right: 40px !important;
    }
    td {
      border: none;
      .cell {
        // @include add-size($font_size_14);
        line-height: 1 !important;
      }
      .fall {
        color: $green;
      }
      .rise {
        color: $red;
      }
    }
    td,
    th {
      padding: 7px 0;
    }
    // 表格头部加边框
    .el-table__header {
      th {
        border-right: 1px solid $borColor;
        border-top: 1px solid $borColor;
        border-bottom: 1px solid $borColor;
        padding: 4px;
        &:first-child {
          border-left: 1px solid $borColor;
        }
      }
    }
  }
  /deep/ .el-table {
    @include add-size($font_size_12);
    background-color: inherit;
    color: $defaultColor;
    td.fall {
      .cell {
        color: $green;
      }
    }
    td.raise {
      .cell {
        color: $red;
      }
    }
    .space {
      border: none;
    }
    &::before {
      display: none;
    }
    .cell {
      padding: 0;
      line-height: 1;
    }
    th {
      border-bottom: none;
      padding: 0;
    }
    td {
      border-bottom-color: #323840;
      .cell {
        color: $white;
        line-height: 29px;
      }
    }
    tr,
    th {
      background-color: inherit;
    }
    tr:hover {
      td {
        background-color: inherit;
        .cell {
          color: $activeColor;
        }
      }
    }
  }
}
.component-fade-enter-active,
.component-fade-leave-active {
  transition: opacity 0.3s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active for below version 2.1.8 */ {
  opacity: 0;
}
</style>
