import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    // name: "index",
    component: require("@/views/index").default,
    children: [
      {
        path: "/",
        component: require("@/views/modules/hk/hk-index").default,
        name: "hk-index",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "hk",
        component: require("@/views/modules/hk/hk").default,
        name: "hk",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "usIndex",
        component: require("@/views/modules/us/us-index").default,
        name: "usIndex",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "self",
        component: require("@/views/modules/self/self").default,
        name: "self",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "mine",
        component: require("@/views/modules/mine/mine").default,
        // name: "mine",
        meta: {
          keepAlive: true
        },
        children: [
          {
            path: "/",
            component: require("@/views/modules/mine/sub/purse").default,
            name: "mine"
          },
          {
            path: "/market",
            component: require("@/views/modules/mine/sub/market").default,
            name: "market"
          },
          {
            path: "/my-bank-card",
            component: require("@/views/modules/mine/sub/bank-card").default,
            name: "myBankCard"
          },
          {
            path: "/activity-center",
            component: require("@/views/modules/mine/sub/activity-center")
              .default,
            name: "activityCenter"
          },
          {
            path: "/reward-center",
            component: require("@/views/modules/mine/sub/reward-center")
              .default,
            name: "rewardCenter"
          },
          {
            path: "/invite-friend",
            component: require("@/views/modules/mine/sub/invite-friend")
              .default,
            name: "inviteFriend"
          },
          {
            path: "/school",
            component: require("@/views/modules/mine/sub/school").default,
            name: "school"
          },
          {
            path: "/partner",
            component: require("@/views/modules/mine/sub/partner").default,
            name: "partner"
          }
        ]
      },
      {
        path: "news",
        component: require("@/views/modules/news/news").default,
        name: "news",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "newStock",
        component: require("@/views/modules/newStock/newStock").default,
        name: "newStock",
        meta: {
          keepAlive: true
        },
        children: []
      },
      {
        path: "indexStock",
        component: require("@/views/modules/indexStock/index-stock").default,
        name: "indexStock",
        meta: {
          keepAlive: true
        },
        children: []
      }
    ]
  },
  {
    path: "/login",
    component: require("@/views/common/login").default,
    name: "login"
  }
];

const router = new VueRouter({
  routes
});

// 全局路由守卫
router.beforeEach((to, from, next) => {
  // const nextRoute = ['index', 'hq'] // 需要登录的页面
  // let token = localStorage.getItem("token");
  let token = localStorage.getItem("token");
  const loginStatus = !!token;
  if (!loginStatus) {
    // 如果未登录（本地存储无用户数据），并且要跳到登录页面
    if (to.name !== "login") {
      next("/login");
      return false;
    }
  }

  next(); // 必须使用 next ,执行效果依赖 next 方法的调用参数
});

export default router;
