<template>
  <div class="search">
    <!-- :autofocus="true" 自动聚焦 -->
    <el-autocomplete
      class="my-autocomplete"
      v-model="state"
      prefix-icon="el-icon-search"
      :highlight-first-item="true"
      clearable
      :fetch-suggestions="querySearch"
      placeholder="股票代码/名称/拼音首字母"
      :debounce="500"
      :trigger-on-focus="true"
      @select="handleSelect"
      ref="searchDiv"
    >
      <template slot-scope="{ item }">
        <div
          class="name"
          v-show="item.code.slice(0, 2) == 'hk'"
          :title="item.value"
        >
          <span class="hkSpan">
            <span>HK</span>
          </span>
          <span>{{ item.code.slice(2) }}</span>
          <span>{{ item.value }}</span>
        </div>
        <div
          class="name"
          v-show="item.code.slice(0, 2) == 'us'"
          :title="item.value"
        >
          <span class="usSpan">
            <span>US</span>
          </span>
          <span>{{ item.code.slice(2) }}</span>
          <span>{{ item.value }}</span>
        </div>
      </template>
    </el-autocomplete>
  </div>
</template>
<script>
export default {
  name: "search",
  data() {
    return {
      restaurants: [],
      state: "",
      historyList: [],
      focusType: false
    };
  },
  mounted() {
    let that = this;
    document.onkeyup = function(e) {
      if (
        e.keyCode > 47 &&
        e.keyCode < 106 &&
        document.querySelectorAll(":focus").length <= 0
      ) {
        that.$refs.searchDiv.focus();
        that.$refs.searchDiv.$el.querySelector(":focus").value = e.key;
      }
    };
  },
  methods: {
    querySearch(queryString, cb) {
      console.log(queryString);
      if (queryString) {
        this.loadAll(queryString, cb);
      } else {
        cb(this.fetch("itemData"));
      }
      // this.loadAll(queryString, cb);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // 获取数据
    fetch(code) {
      return JSON.parse(window.localStorage.getItem(code) || "[]");
    },
    // 保存数据
    save(code, items, callback) {
      let data = this.fetch(code);
      data.unshift(items);
      let dataAry = [];
      for (let i = 0; i < data.length; i++) {
        let isPush = true;
        for (let n = 0; n < dataAry.length; n++) {
          if (dataAry[n].code == data[i].code) {
            isPush = false;
          }
        }
        if (isPush) {
          if (dataAry.length < 5) {
            dataAry.push(data[i]);
          }
        }
      }
      window.localStorage.setItem(code, JSON.stringify(dataAry));
      if (callback) {
        callback();
      }
    },
    handleSelect(item) {
      this.save("itemData", item);
      setTimeout(() => {
        this.$refs.searchDiv.$el.querySelector("input").blur();
        this.$refs.searchDiv.handleChange("");
        this.$refs.searchDiv.close();
      }, 500);

      // let dom = this.$refs.searchDiv.handleClear()
      // setTimeout(() => {
      //   dom.value = ''
      // },500)
      // console.log(dom,9444444444)
      // this.historyList.unshift(item);
      // localStorage.setItem("itemData", JSON.stringify(this.historyList));
      if (item.code.length > 7) {
        // item.query = '90'
        this.$router.push({
          path: "zs"
        });
        this.$store.dispatch("SET_CURRENT_STOCK", {
          code: item.code,
          recover: true
        });
        return;
      }
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: item.code,
        recover: true
      });

      if (item.code.indexOf("hk") !== -1) {
        // 港股
        this.$store.dispatch("CHANGE_ROUTER_TARGET", {
          a: 1,
          b: 0,
          timestamp: new Date().getTime()
        });
      } else if (item.code.indexOf("us") !== -1) {
        // 美股
        this.$store.dispatch("CHANGE_ROUTER_TARGET", {
          a: 2,
          b: 0,
          timestamp: new Date().getTime()
        });
      }
    },
    loadAll(queryString, cb) {
      console.log(queryString);
      this.restaurants = [];
      if (queryString == "") {
        return;
      }
      let url = "/quote-service/api/v1/stocksearch";
      let data = {
        condition: queryString
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        console.log(res, 167);
        let data = res.data.Data;
        data.map((value, index) => {
          let dataAry = {};
          dataAry.value = value.StockInfo.name;
          dataAry.code = value.StockInfo.code;
          this.restaurants.push(dataAry);
        });
        // let restaurants = this.restaurants;
        // let results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // console.log(results)
        // 调用 callback 返回建议列表的数据
        cb(this.restaurants);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.search {
  width: 100%;
  /deep/.el-autocomplete {
    display: block;
    .el-input__inner {
      background-color: $blockBgColor;
      border: 1px solid $blockBgColor;
    }
  }
  /deep/.el-autocomplete-suggestion li.highlighted,
  .el-autocomplete-suggestion li {
    // background-color: red;
    color: $white;
  }
}
</style>
