<template>
  <div class="customer-info">
    <el-dialog
      class="customer-info-dialog"
      theame
      center
      title="我的客户"
      :modal="false"
      :visible="isShow"
      width="570px"
      top="15%"
      @close="close"
    >
      <div class="info-content">
        <el-tabs v-model="activeName" :stretch="true">
          <el-tab-pane label="账单" name="first"></el-tab-pane>
          <el-tab-pane label="统计" name="second"></el-tab-pane>
        </el-tabs>
        <div class="account-top">
          <div class="account-num">期间交易：0笔</div>
          <div class="account-num">期间收入：{{0 | moneyFormat}}</div>
          <label class="picker-label" for="monthPickerBook">
            {{dailyAccount}}
            <i class="el-icon-caret-bottom arrow" :class="{active: monthSelect}"></i>
            <el-date-picker
              value-format="yyyy-MM"
              class="picker"
              id="monthPickerBook"
              v-model="dailyAccount"
              type="month"
              placeholder="选择月"
              @blur="monthSelect = false"
              @focus="monthSelect = true"
            ></el-date-picker>
          </label>
        </div>
        <ul class="account-ul" v-if="activeName === 'first'">
          <li class="account-li" v-for="(acc, i) in accountUl" :key="i">
            <div class="account-li-item account-li_stock">
              <div>{{acc.stockName}}</div>
              <div class="stock-code">{{acc.stockCode}}</div>
            </div>
            <div class="account-li-item account-li_price">
              <div>佣金：{{acc.brokerage}}</div>
              <div>买入金额：{{acc.cost | moneyFormat}}</div>
            </div>
            <div class="account-li-item account-li_user">
              <div>{{acc.name}} {{acc.mobile}}</div>
              <div>{{acc.createTime}}</div>
            </div>
          </li>
        </ul>
        <div class="statistics" v-if="activeName === 'second'">
          <div class="category" ref="category"></div>
          <div class="sta-title">佣金类型</div>
          <div class="income-chart chart-canvas" ref="incomeEcharts"></div>
          <div class="chart-table">
            <ul class="chart-table-ul">
              <li class="chart-table-li">
                <div class="chart-table-li_name">名称</div>
                <div class="chart-table-li_value center">收入</div>
                <div class="chart-table-li_percent right">占比</div>
              </li>
              <li class="chart-table-li" v-for="li in chartData" :key="li.name">
                <div class="chart-table-li_name">{{li.name}}</div>
                <div class="chart-table-li_value center">{{li.value | moneyFormat}}</div>
                <div class="chart-table-li_percent right">{{li.percent}}</div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "customer-info",
  props: { chartData: Array },
  data() {
    return {
      isShow: false,
      isShowDetail: false,
      activeName: "first",
      monthSelect: false,
      dailyAccount: new Date().Format("yyyy-MM"),
      customerList: [
        {
          customerName: "某**",
          mobile: "133****4567",
          status: "已注册",
          createTime: "2019-10-28 10:30"
        }
      ],
      detailTab: "first",
      stockData: [
        // {
        //   stockName: "股票名称股票名称",
        //   stockCode: "001003",
        //   position: 2000,
        //   last: "123456.00",
        //   value: "123456.00"
        // },
        // {
        //   stockName: "股票名称股票名称",
        //   stockCode: "001003",
        //   position: 2000,
        //   last: "123456.00",
        //   value: "123456.00"
        // },
        // {
        //   stockName: "股票名称股票名称",
        //   stockCode: "001003",
        //   position: 2000,
        //   last: "123456.00",
        //   value: "123456.00"
        // }
      ],
      accountUl: [
       /*  {
          stockName: "股票名称股票名称",
          stockCode: "001003",
          brokerage: "99.00",
          cost: "8493",
          name: "某**",
