          createTime: "2019-10-28 19:28:12"
        } */
      ],
      bookData: [
        100,
        200,
        300,
        400,
        300,
        200,
        400,
        300,
        500,
        400,
        100,
        200,
        300,
        400,
        300,
        200,
        400,
        300,
        500,
        400,
        100,
        200,
        300,
        400,
        300,
        200,
        400,
        300,
        500,
        400
      ]
    };
  },
  mounted() {},
  computed: {},
  watch: {
    activeName(val) {
      if (val === "second") {
        this.$nextTick(() => {
          this.setCategory();
          this.setIncome();
        });
      }
    }
  },
  methods: {
    getData(){
      // GET /order/message/selectDetailedCommissionTb
      this.axios({
        url: "/order/message/selectDetailedCommissionTb",
        params: {
          month: this.dailyAccount + "-01"
        },
        type: "host2"
      }).then(res => {
        
      });
    },
    setIncome() {
      this.setChart(this.$refs.incomeEcharts, [
        { value: 1234, name: "其他" },
        { value: 1234, name: "窝轮" },
        { value: 1234, name: "牛熊证" },
        { value: 1234, name: "股票" }
      ]);
    },
    setChart(ref, data) {
      let incomeChart = this.$echarts.init(ref);
      incomeChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c} ({d}%)"
        },
        color: ["#1FFFA9", "#1F84FF", "#801FFF", "#FF1F57"],
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: ["40%", "80%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                formatter: "{d}%",
                borderBottomWidth: 1,
                color: "#fff"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: true,
                length2: 30
              }
            },
            data: data
          }
        ]
      });
    },
    setCategory() {
      let category = this.$echarts.init(this.$refs.category);
      category.setOption({
        title: {},
        tooltip: {
          trigger: "axis"
        },
        legend: {
          left: "30",
          itemWidth: 7,
          itemHeight: 7,
          data: [
            {
              name: "收入",
              icon: "circle",
              textStyle: {
                color: "#767A92"
              }
            },
            {
              name: "交易笔数",
              icon: "circle",
              textStyle: {
                color: "#767A92"
              }
            }
          ]
        },
        grid: {
          left: "30",
          right: "30",
          bottom: "3%",
          borderColor: "#767A92",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            // 1,
            // "",
            // 3,
            // "",
            // "",
            // 6,
            // "",
            // "",
            // 9,
            // "",
            // "",
            // 12,
            // "",
            // "",
            // 15,
            // "",
            // "",
            // 18,
            // "",
            // "",
            // 21,
            // "",
            // "",
            // 24,
            // "",
            // "",
            // 27,
            // "",
            // "",
            // 30
          ],
          axisLine: {
            lineStyle: {
              color: "#767A92"
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: [
          {
            name: "收入",
            type: "value",
            axisLine: {
              lineStyle: {
                color: "#767A92"
              }
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          },
          {
            name: "笔数",
            type: "value",
            axisLine: {
              lineStyle: {
                color: "#767A92"
              }
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        color: ["#1F84FF", "#DEA567"],
        series: [
          {
            name: "收入",
            type: "line",
            yAxisIndex: 0,
            data: [
              // 100,
              // 200,
              // 300,
              // 400,
              // 300,
              // 200,
              // 400,
              // 300,
              // 500,
              // 400,
              // 100,
              // 200,
              // 300,
              // 400,
              // 300,
              // 200,
              // 400,
              // 300,
              // 500,
              // 400,
              // 100,
              // 200,
              // 300,
              // 400,
              // 300,
              // 200,
              // 400,
              // 300,
              // 500,
              // 400
            ]
          },
          {
            name: "交易笔数",
            type: "line",
            yAxisIndex: 1,
            data: [
              // 1,
              // 2,
              // 3,
              // 4,
              // 3,
              // 1,
              // 4,
              // 3,
              // 5,
              // 4,
              // 1,
              // 2,
              // 3,
              // 4,
              // 3,
              // 20,
              // 4,
              // 3,
              // 5,
              // 4,
              // 1,
              // 2,
              // 3,
              // 4,
              // 3,
              // 2,
              // 4,
              // 3,
              // 5,
              // 4
            ]
          }
        ]
      });
    },
    rowSelect() {
      this.showDetail();
    },
    handleClick() {},
    show() {
      this.isShow = true;
      this.getData();
    },
    close() {
      this.isShow = false;
    },
    showDetail() {
      this.isShowDetail = true;
    },
    closeDetail() {
      this.isShowDetail = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.customer-info {
  .customer-info-dialog {
    /deep/ .el-dialog {
      .el-dialog__title {
        @include add-size($font_size_14);
        color: $defaultColor !important;
      }
    }
    .info-content {
      height: 398px;
      color: $white;
      .account-top {
        @include flex-center;
        height: 41px;
        padding: 0 30px;
        border-bottom: 1px solid #323840;
        .account-num {
          margin-right: 50px;
        }
      }
      .account-ul {
        padding: 20px 30px;
        .account-li {
          @include flex-center;
          justify-content: space-between;
          margin-bottom: 33px;
          .account-li-item {
            &.account-li_price {
              width: 140px;
            }
            &.account-li_stock {
              width: 130px;
            }
            div {
              &:nth-child(2) {
                margin-top: 6px;
              }
            }
            .stock-code {
              padding-left: 27px;
              position: relative;
              &::before {
                content: "";
                position: absolute;
                width: 20px;
                height: 14px;
                left: 0;
                top: 0;
                background: url(~@/assets/images/base/hk-icon.png) 0 0 no-repeat;
                background-size: 20px 14px;
              }
            }
          }
        }
      }
      .statistics {
        height: 300px;
        overflow-y: auto;
        .category {
          height: 250px;
          border-bottom: 1px solid #323840;
        }
        .sta-title {
          padding: 17px 30px;
        }
        .income-chart {
          height: 168px;
        }
        .chart-table {
          padding: 0 30px;
          .chart-table-ul {
            @include add-size($font_size_14);
            line-height: 26px;
            .chart-table-li {
              @include flex-center;
              justify-content: space-between;
              div {
                width: 30%;
                position: relative;
                &.right {
                  text-align: right;
                }
                &.center {
                  text-align: center;
                }
                &.chart-table-li_name {
                  padding-left: 16px;
                  &::before {
                    content: "";
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: 8px;
                  }
                }
              }
              &:nth-child(2) {
                .chart-table-li_name {
                  &::before {
                    background-color: #ff1f57;
                  }
                }
              }
              &:nth-child(3) {
                .chart-table-li_name {
                  &::before {
                    background-color: #801fff;
                  }
                }
              }
              &:nth-child(4) {
                .chart-table-li_name {
                  &::before {
                    background-color: #1f84ff;
                  }
                }
              }
              &:nth-child(5) {
                .chart-table-li_name {
                  &::before {
                    background-color: #1fffa9;
                  }
                }
              }
            }
          }
        }
      }
    }
    .picker-label {
      cursor: pointer;
      position: relative;
      margin-left: auto;
      .arrow {
        margin-left: 5px;
        transition: all 0.3s ease;
        &.active {
          transform: rotate(180deg);
        }
      }
      .picker {
        position: absolute;
        width: 0;
        height: 0;
        overflow: hidden;
      }
    }
  }
}
</style>
