/**
 * 计算价格涨跌幅百分比
 * @param {Object} price 当前价
 * @param {Object} yclose 昨收价
 */
function ratioCalculate(price, yclose) {
	// console.log(price,Number(yclose),((price - Number(yclose)) / Number(yclose) * 100),3150)
	return ((price - Number(yclose)) / Number(yclose) * 100).toFixed(3);
}
//数组处理
function splitData(rawData) {
	var datas = [];
	var times = [];
	var vols = [];
	var addTimes = [];
	var disLength = 40 - rawData.length
	for (var i = 0; i < rawData.length; i++) {
		datas.push(rawData[i]);
		times.push(rawData[i].splice(0, 1)[0]);
		vols.push(rawData[i][4]);
	}
	if (disLength > 0) {
		for (var k = 0; k < disLength; k++) {
			addTimes.push('2019-11-21')
		}
		times = times.concat(addTimes);
	}
	return {
		datas: datas,
		times: times,
		vols: vols
	};
}
//================================MA计算公式
function calculateMA(dayCount, data) {
	var result = [];
	//这里修改了不用data.times进行判断
	for (var i = 0, len = data.datas.length; i < len; i++) {
		if (i < dayCount) {
			result.push('-');
			continue;
		}
		var sum = 0;
		for (var j = 0; j < dayCount; j++) {
			sum += data.datas[i - j][1];
		}
		result.push((sum / dayCount).toFixed(3));
	}
	return result;
}


//=================================================MADC计算公式

var calcEMA, calcDIF, calcDEA, calcMACD;
/*
 * 计算EMA指数平滑移动平均线，用于MACD
 * @param {number} n 时间窗口
 * @param {array} data 输入数据
 * @param {string} field 计算字段配置
 */
calcEMA = function (n, data, field) {
	// console.log(data)
	// console.log(data[0][field],field,494)
	var i, l, ema, a;
	a = 2 / (n + 1);
	if (field) {
		//二维数组
		ema = [data[0][field]];
		for (i = 1, l = data.length; i < l; i++) {
			ema.push((a * data[i][field] + (1 - a) * ema[i - 1]).toFixed(3));
		}
	} else {
		//普通一维数组
		ema = [data[0]];
		for (i = 1, l = data.length; i < l; i++) {
			ema.push((a * data[i] + (1 - a) * ema[i - 1]).toFixed(3));
		}
	}
	return ema;
};

/*
 * 计算DIF快线，用于MACD
 * @param {number} short 快速EMA时间窗口
 * @param {number} long 慢速EMA时间窗口
 * @param {array} data 输入数据
 * @param {string} field 计算字段配置
 */
calcDIF = function (short, long, data, field) {
	var i, l, dif, emaShort, emaLong;
	dif = [];
	emaShort = calcEMA(short, data, field);
	emaLong = calcEMA(long, data, field);
	for (i = 0, l = data.length; i < l; i++) {
		dif.push((emaShort[i] - emaLong[i]).toFixed(3));
	}
	return dif;
};

/*
 * 计算DEA慢线，用于MACD
 * @param {number} mid 对dif的时间窗口
 * @param {array} dif 输入数据
 */
calcDEA = function (mid, dif) {
	return calcEMA(mid, dif);
};

/*
 * 计算MACD
 * @param {number} short 快速EMA时间窗口
 * @param {number} long 慢速EMA时间窗口
 * @param {number} mid dea时间窗口
 * @param {array} data 输入数据
 * @param {string} field 计算字段配置
 */
calcMACD = function (short, long, mid, data, field) {
	var i, l, dif, dea, macd, result;
	result = {};
	macd = [];
	dif = calcDIF(short, long, data, field);
	dea = calcDEA(mid, dif);
	for (i = 0, l = data.length; i < l; i++) {
		macd.push(((dif[i] - dea[i]) * 2).toFixed(3));
	}
	result.dif = dif;
	result.dea = dea;
	result.macd = macd;
	return result;
};


//=================================================MADC计算公式 end


