<template>
  <div class="stock-deal" :data-flag="showFlag">
    <el-form ref="dealForm" :model="dealForm">
      <el-form-item class="business-type" label="股票">
        <fillStock :currentStock="currentStock" @getMessage="stockSelect" />
      </el-form-item>
      <el-form-item label="交易类型">
        <el-select v-model="orderTypeTarget" placeholder="请选择">
          <el-option
            v-for="item in orderTypeFilter"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="价格">
        <el-input type="number" v-model="dealForm.price" placeholder="请输入价格" @blur="queryPrice"></el-input>
        <i class="el-icon-remove-outline minus" @click="priceMinus"></i>
        <i class="el-icon-circle-plus-outline plus" @click="pricePlus"></i>
      </el-form-item>
      <el-form-item class="space-between">
        <span>最小变动单位：</span>
        <span>{{perHandPrice}}港币</span>
      </el-form-item>
      <el-form-item label="数量">
        <el-input
          v-model="dealForm.quantity"
          type="number"
          placeholder="请输入数量"
          @blur="queryQuantity"
          @change="activeBtn = ''"
        ></el-input>
        <i class="el-icon-remove-outline minus" @click="quantityMinus"></i>
        <i class="el-icon-circle-plus-outline plus" @click="quantityPlus"></i>
      </el-form-item>
      <el-form-item class="space-between">
        <span>每手</span>
        <span>{{dealForm.hand}}股</span>
      </el-form-item>
      <!-- <el-form-item label=" ">
        <div class="num-bar">
          <div class="buy">可买数量：{{totalCount || '--'}}</div>
          <div class="sale">可卖数量：{{sellCount || '--'}}</div>
        </div>
      </el-form-item>-->
      <!-- <el-form-item label="金额">
        <div>{{totalPrice | moneyFormat(3)}}</div>
      </el-form-item>-->
      <el-form-item class="button-flex" label>
        <div class="buy-qut">
          <div class="buy-num">
            <span>可买数量：</span>
            <span>{{totalCount || 0}}</span>
          </div>
          <div class="btn-b">
            <el-button :class="{active: activeBtn === '3buy'}" @click="setQuantity(3,'buy')">1/3</el-button>
            <el-button :class="{active: activeBtn === '2buy'}" @click="setQuantity(2,'buy')">1/2</el-button>
            <el-button :class="{active: activeBtn === '1buy'}" @click="setQuantity(1,'buy')">全仓</el-button>
          </div>
        </div>
        <div class="sell-qut">
          <div class="sale-num">
            <span>可卖数量：</span>
            <span>{{sellCount || 0}}</span>
          </div>
          <div class="btn-b">
            <el-button :class="{active: activeBtn === '3sell'}" @click="setQuantity(3,'sell')">1/3</el-button>
            <el-button :class="{active: activeBtn === '2sell'}" @click="setQuantity(2,'sell')">1/2</el-button>
            <el-button :class="{active: activeBtn === '1sell'}" @click="setQuantity(1,'sell')">全仓</el-button>
          </div>
        </div>
      </el-form-item>
      <div class="action-btn">
        <el-button class="btn in" hover @click="needVerify(0)">买入</el-button>
        <el-button class="btn out" hover @click="needVerify(1)">卖出</el-button>
      </div>
    </el-form>
    <orderDetail ref="orderDetail" :orderData="dealForm" @setOrder="setOrder" :origin="origin" />
    <dealLock ref="dealLock" @verify="verify" />
  </div>
</template>

<script>
import fillStock from "./fill-stock";
import orderDetail from "./order-detail";
import dealLock from "./deal-lock";
import { sortStock } from "@/utils";
const PRICE_STEP_CONFIG = [
  {
    min: 0.01,
    max: 0.25,
    value: 0.001
  },
  {
    min: 0.25,
    max: 0.5,
    value: 0.005
  },
  {
    min: 0.5,
    max: 10.0,
    value: 0.01
  },
  {
    min: 10.0,
    max: 20.0,
    value: 0.02
  },
  {
    min: 20.0,
    max: 100.0,
    value: 0.05
  },
  {
    min: 100.0,
    max: 200.0,
    value: 0.1
  },
  {
    min: 200.0,
    max: 500.0,
    value: 0.2
  },
  {
    min: 500.0,
    max: 1000.0,
    value: 0.5
  },
  {
    min: 1000.0,
    max: 2000.0,
    value: 1.0
  },
  {
    min: 2000.0,
    max: 5000.0,
    value: 2.0
  },
  {
    min: 5000.0,
    max: 9995.0,
    value: 5.0
  }
];
export default {
  props: {
    currentStock: Object,
    accountInfo: Object,
    curPosition: Array,
    cutLink: Boolean,
    requestConfig: Object,
    currentType: [Number, String],
    orderPrice: [Number, String],
    origin: String,
    showFlag: {
      type: String,
      default: ""
    }
  },
  components: {
    fillStock,
    orderDetail,
    dealLock
  },
  data() {
    return {
      dealForm: {
        symbol: "",
        stockName: "",
        price: "",
        quantity: "",
        accountID: "",
        buySell: "",
        orderType: "",
        hand: "",
        tradeType: 0,
        type: 0
      },
      stockData: {},
      orderType: [
        {
          value: "1",
          label: "限价单",
          orderType: "L",
          limit: "0,1,2,3"
        },
        {
          value: "2",
          label: "竞价单",
          orderType: "A",
          tips: "不带价格",
          limit: "4,5"
        },
        {
          value: "3",
          label: "竞价限价单",
          orderType: "L",
          tips: "带价格",
          limit: "4,5"
        },
        {
          value: "4",
          label: "增强限价单",
          orderType: "E",
          limit: "0,1,2,3"
        },
        {
          value: "5",
          label: "特别限价单",
          orderType: "S",
          limit: "0,1,2,3"
        }
      ],
      orderTypeTarget: "",
      totalCount: "",
      totalPrice: 0,
      sellCount: "",
      activeBtn: "",
      dealWay: 0
    };
  },
  computed: {
    orderTypeFilter() {
      let list = this.orderType.filter(a => {
        return a.limit.indexOf(this.stockData.marketStatus) != -1;
      });
      if (list.length > 0) {
        this.orderTypeTarget = list[1].value;
      }
      return list;
    },
    perHandPrice() {
      let val = "0";
      PRICE_STEP_CONFIG.forEach(a => {
        if (this.dealForm.price > a.min && this.dealForm.price <= a.max) {
          val = a.value;
        }
      });
      return val;
    }
    // currentType() {
    //   // 当前选择的账户
    //   return this.$store.state.AccountLogin.currentType;
    // }
  },
  watch: {
    orderTypeTarget(v) {
      console.log(v);
    },
    currentStock(val) {
      this.dealForm.price = val.last;
      let hand =
        Number(val.hand) > 0
          ? Number(val.hand)
          : Number(val.warrant.hand) > 0
          ? Number(val.warrant.hand)
          : Number(val.inline.hand);
      this.dealForm.quantity = hand;
      this.dealForm.hand = hand;
      this.stockData = val;
      this.$emit("stockQuery", val);
      this.setPriceRange();
    },
    "dealForm.price"(price) {
      this.setPrice();
    },
    "dealForm.quantity"() {
      this.setTotalPrice();
    },
    "stockData.symbol"(val) {
      // 监听可卖数量
      // console.log(val);
      let num = "";
      this.curPosition.forEach(a => {
        if (a.StockCode === val.replace(/[a-zA-z]/g, "")) {
          num = a.StockQty;
        }
      });
      this.sellCount = num;
    },
    accountInfo() {
      this.setPrice();
    },
    orderPrice(val) {
      this.dealForm.price = val;
      this.queryPrice();
    }
  },
  methods: {
    verify() {
      this.stockOrder(this.dealWay);
    },
    needVerify(way) {
      this.dealWay = way;
      let lockTime = localStorage.getItem("lockTime");
      if (this.currentType == 0) {
        if (Date.now() - lockTime < 30 * 60 * 1000) {
          this.stockOrder(this.dealWay);
        } else {
          this.$refs.dealLock.show();
        }
      } else {
        this.stockOrder(this.dealWay);
      }
    },
    stockOrder(buySell) {
      // /addOrder.do
      try {
        let stockData = this.stockData;
        // console.log(stockData)
        let dealForm = this.dealForm;
        let zs = ["hk0000100", "hk0001400", "hk0001500"];
        if (zs.indexOf(stockData.symbol) >= 0) {
          throw new Error("指数类型无法进行买卖！");
        }
        // let accountID = this.$store.state.AccountLogin.accountData.LoginID;
        if (!stockData.name) {
          throw new Error("请选择股票！");
        }
        // 测试阶段  关闭价格区间验证
        // if (dealForm.price < this.minPrice || dealForm.price > this.maxPrice) {
        //   throw new Error(`该股票价格在${this.minPrice}-${this.maxPrice}之间`);
        // }
        // 股票代码只传数字部分
        dealForm.symbol = stockData.symbol;
        if (dealForm.symbol.indexOf("us") != -1) {
          dealForm.type = 1;
          throw new Error("暂不支持美股交易！");
        } else {
          dealForm.type = 0;
        }

        dealForm.stockName = stockData.name;
        dealForm.buySell = buySell;
        dealForm.price = Number(dealForm.price);
        // dealForm.accountID = accountID;
        // tradeType 0 股票 1 牛熊 2 窝轮

        dealForm.tradeType = this.$route.query.tradeType || 0;

        if (buySell == 0 && this.accountInfo.availableAmount <= 0) {
          throw new Error("账户余额不足！");
        }
        if (buySell != 0 && this.sellCount < dealForm.quantity) {
          throw new Error("当前持仓不足！");
        }
        if (!this.orderTypeTarget) {
          throw new Error("请选择交易类型！");
        } else {
          dealForm.orderType = this.orderType[
            this.orderTypeTarget - 1
          ].orderType;
        }
        // console.log(dealForm, 123123)
        this.$refs.orderDetail.show();
        // buySell == 0 ? "买入" : "卖出"
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    setOrder() {
      // this.requestConfig
      if (this.currentType == 0) {
        this.dealForm.accountID = this.requestConfig.account[this.currentType];
      } else {
        // delete this.dealForm.tradeType;
        delete this.dealForm.accountID;
      }
      this.dealForm.tradeType = sortStock(this.stockData);
      this.axios({
        url: this.requestConfig.addOrder[this.currentType],
        method: "post",
        params: this.dealForm,
        type: this.requestConfig.type[this.currentType]
      }).then(res => {
        this.$message({
          message: res.data.message,
          type: "success"
        });
        setTimeout(() => {
          this.$store.dispatch("SET_IS_ACTION");
        }, 1000);
        this.$emit("setOrder");
      });
    },

    setPrice() {
      let dealForm = this.dealForm;
      let availableAmount = Number(this.accountInfo.availableAmount);
      let totalCount = (availableAmount * 1000) / (dealForm.price * 1000);
      let multiple = parseInt(totalCount / dealForm.hand);
      this.totalCount = multiple * dealForm.hand;
      this.setTotalPrice();
    },
    setQuantity(multiple, flag) {
      if (flag === "sell") {
        this.dealForm.quantity = this.sellCount / multiple;
      } else {
        this.dealForm.quantity = this.totalCount / multiple;
      }
      this.activeBtn = multiple + flag;
      this.queryQuantity();
    },
    setTotalPrice() {
      let dealForm = this.dealForm;
      this.totalPrice = dealForm.price * dealForm.quantity;
    },
    stockSelect(e) {
      console.log(e);
      this.dealForm.symbol = e.code;
      this.dealForm.stockName = e.value;
      this.getStockPrice();
      // this.getStockOrder();
      this.$emit("getStockOrder", e.code);
    },
    getStockPrice() {
      // 获取股票快照
      let url = "/quote-service/api/v1/stockquery";
      if (this.dealForm.symbol.indexOf("us") != -1) {
        url = "/quote-service/api/us/v1/stockquery";
      }
      this.axios({
        url: url,
        method: "get",
        params: {
          codelist: this.dealForm.symbol
        },
        type: "HQ"
      }).then(res => {
        let stockData = res.data.Data[0];
        this.dealForm.price = stockData.last;
        let hand =
          Number(stockData.hand) > 0
            ? Number(stockData.hand)
            : Number(stockData.warrant.hand) > 0
            ? Number(stockData.warrant.hand)
            : Number(stockData.inline.hand);

        this.dealForm.quantity = hand;
        this.dealForm.hand = hand;

        this.stockData = stockData;
        this.$emit("stockQuery", stockData);
        this.setPriceRange();
      });
    },
    quantityMinus() {
      // 数量 减
      let dealForm = this.dealForm;
      dealForm.quantity = dealForm.quantity - dealForm.hand;
      this.queryQuantity();
    },
    quantityPlus() {
      // 数量 加
      let dealForm = this.dealForm;
      dealForm.quantity = dealForm.quantity + dealForm.hand;
      this.queryQuantity();
    },
    queryQuantity() {
      // 一手为最小单位
      let dealForm = this.dealForm;
      let multiple = parseInt(dealForm.quantity / dealForm.hand);
      dealForm.quantity = multiple * dealForm.hand;
      dealForm.quantity =
        dealForm.quantity >= dealForm.hand ? dealForm.quantity : dealForm.hand;
    },
    step(val) {
      // console.log(val);
      let price = Number(val);
      let o = {};
      for (let i = 0; i < PRICE_STEP_CONFIG.length; i++) {
        if (
          PRICE_STEP_CONFIG[i].min <= price &&
          PRICE_STEP_CONFIG[i].max > price
        ) {
          // 判断是否在边界
          let periphery = PRICE_STEP_CONFIG[i].min == price;
          o.periphery = periphery;
          o.index = i;
          o = Object.assign(o, PRICE_STEP_CONFIG[i]);
          break;
        }
      }
      if (o.value) {
        return o;
      }
      if (price < 0.01) {
        return {
          ...PRICE_STEP_CONFIG[0],
          index: 0,
          periphery: false
        };
      }
      if (price >= 9995.0) {
        return {
          ...PRICE_STEP_CONFIG[PRICE_STEP_CONFIG.length - 1],
          index: PRICE_STEP_CONFIG.length - 1,
          periphery: false
        };
      }
    },
    priceMinus() {
      let dealForm = this.dealForm;
      dealForm.price = this.changePrice(dealForm.price);
      this.queryPrice();
    },
    pricePlus() {
      let dealForm = this.dealForm;
      dealForm.price = this.changePrice(dealForm.price, "add");
      this.queryPrice();
    },
    changePrice(price, flag) {
      let step = this.step(price);
      if (flag === "add") {
        return (price * 1000 + step.value * 1000) / 1000;
      } else {
        // 处于边界时减上一级变动价位
        if (step.periphery) {
          let index = step.index <= 0 ? 0 : step.index - 1;
          let _step = PRICE_STEP_CONFIG[index];
          return (price * 1000 - _step.value * 1000) / 1000;
        } else {
          return (price * 1000 - step.value * 1000) / 1000;
        }
      }
    },
    queryPrice() {
      let dealForm = this.dealForm;
      dealForm.price = Number(dealForm.price);
      if (dealForm.price <= 0) {
        dealForm.price = "";
      }
      dealForm.price = dealForm.price.toFixed(3);
    },
    setPriceRange() {
      let price = this.stockData.last;

      let times = 24;
      this.minPrice = price;
      this.maxPrice = price;
      let _this = this;
      function cir() {
        _this.minPrice = _this.changePrice(_this.minPrice);
        _this.maxPrice = _this.changePrice(_this.maxPrice, "add");
        times--;
        if (times > 0) {
          cir();
        }
      }
      cir();
    }
  }
};
</script>

<style lang="scss" scoped>
$borColor: #576373;
.stock-deal {
  /deep/ .el-form {
    margin-top: 10px;
    padding: 0 20px;
    .el-form-item {
      @include flex-center;
      margin-bottom: 2px;
      position: relative;
      overflow: hidden;
      .el-form-item__content {
        line-height: 26px;
        width: 100%;
      }
    }
    .space-between {
      // padding-right: 20px;
      .el-form-item__content {
        &::after,
        &::before {
          display: none;
        }
        @include flex-center;
        justify-content: space-between;
      }
    }
    .minus,
    .plus {
      position: absolute;
      top: 8px;
      right: 30px;
      cursor: pointer;
      &:hover {
        color: $activeColor;
      }
    }
    .plus {
      right: 0;
    }
    .business-type {
      margin-bottom: 14px;
    }
    .num-bar {
      // width: 250px;
      @include flex-center;
      justify-content: space-between;
    }
    .el-form-item__label {
      color: $white;
      @include add-size($font_size_12);
      line-height: 26px !important;
      width: 58px;
      text-align: left;
      white-space: nowrap;
    }
    .el-input__inner {
      background-color: inherit;
      border-radius: 0;
      @include add-size($font_size_12);
      border: none;
      border-bottom: 1px solid $borColor;
      padding: 0;
      line-height: 26px;
      height: 26px;
      color: $white;
    }
    .el-input {
      width: 100%;
      line-height: 26px;
    }
    .el-select {
      background-color: #131920;
      height: 26px;
      width: 100%;
      .el-input__inner {
        border: none;
        line-height: 26px;
        height: 26px;
        padding-left: 23px;
        color: $white;
        text-align: center;
        padding-right: 23px;
      }
      .el-input__suffix {
        right: 15px;
      }
      .el-input__icon {
        line-height: 26px;
        &::before {
          color: $defaultColor;
          content: "\e78f";
        }
      }
    }

    .button-flex {
      margin-top: -10px;
      text-align: center;
      .buy-qut {
        padding-right: 10px;
        // margin-right: 10px;
        border-right: 1px solid $borderColor;
        width: 50%;
      }
      .sell-qut {
        width: 50%;
        padding-left: 10px;
      }
      .btn-b {
        @include flex-box;
        justify-content: space-around;
      }
      .el-form-item__content {
        @include flex-box;
      }
      .el-button {
        background-color: inherit;
        width: 30px;
        height: 20px;
        padding: 0;
        // margin-right: 25px;
        // margin-left: 0;
        border-radius: 4px;
        span {
          // line-height: 24px;
          @include add-size($font_size_12);
          color: $defaultColor;
        }
        &:hover,
        &.active {
          background-color: $activeColor;
          border-color: $activeColor;
          span {
            color: $white;
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .action-btn {
      @include flex-center;
      margin-top: 18px;
      .btn {
        width: 150px;
        height: 30px;
        border: none;
        border-radius: 6px;
        padding: 0;
        span {
          line-height: 30px;
          color: $white;
          @include add-size($font_size_14);
        }
        &.in {
          background-color: $red;
        }
        &.out {
          background-color: $green;
        }
      }
    }
  }
  &[data-flag="sale"] {
    .business-type {
      margin-bottom: 6px;
    }
    .button-flex {
      text-align: left;
      .buy-qut {
        display: none;
      }
      .sell-qut {
        padding: 0;
        width: 100%;
        .sale-num {
          @include flex-center;
          justify-content: space-between;
        }
      }
      .el-button {
        width: 60px;
      }
    }
    /deep/ .el-form {
      padding: 0 10px;
      margin-right: 10px;
      .el-form-item {
        margin-bottom: 0;
        width: 200px;
      }
      .el-input__suffix {
        right: 60px;
      }
      .action-btn {
        margin-top: 6px;
        .btn:first-child {
          display: none;
        }
        .btn:last-child {
          width: 200px;
          margin: 0;
        }
      }
    }
  }
  &[data-flag="buy"] {
    .business-type {
      margin-bottom: 6px;
    }
    .button-flex {
      text-align: left;
      .sell-qut {
        display: none;
      }
      .buy-qut {
        padding: 0;
        border: none;
        width: 100%;
        .buy-num {
          @include flex-center;
          justify-content: space-between;
        }
      }
      .el-button {
        width: 60px;
      }
    }
    /deep/ .el-form {
      padding: 0 10px;
      margin-right: 10px;
      .el-form-item {
        margin-bottom: 0;
        width: 200px;
      }
      .el-input__suffix {
        right: 60px;
      }
      .action-btn {
        margin-top: 6px;
        .btn:last-child {
          display: none;
        }
        .btn:first-child {
          width: 200px;
          margin: 0;
        }
      }
    }
  }
}
</style>


