<template>
  <div class="notice">
    <el-dialog
      theame
      center
      title="公告"
      :visible="dialogVisiblea"
      width="580px"
      top="15%"
      :modal="false"
      @close="closDialos"
    >
      <div class="menOrde">
        <el-tabs
          v-model="activeName"
          :tab-position="tabPosition"
          style="height: 366px;"
          @tab-click="changeNotice"
        >
          <el-tab-pane :name="tab.name" :label="tab.name" v-for="(tab, i) in sortList" :key="i">
            <div class="sub_body">
              <ul class="no-ul">
                <li
                  class="no-li"
                  :class="{active: no._check}"
                  v-for="(no, i) in tab.list"
                  :key="i"
                  :data-status="no.status"
                  @click="checkDetail(no)"
                >
                  <div class="li-title">{{no.title}}：</div>
                  <div class="li-desc">{{no.content}}</div>
                  <div class="li-date">{{no.createTime | resetTime }}</div>
                </li>
              </ul>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
    <noticeDetail ref="noticeDetail" />
  </div>
</template>

<script>
import noticeDetail from "./notice-detail";
export default {
  name: "notice",
  components: { noticeDetail },
  data() {
    return {
      dialogVisiblea: false,
      tabPosition: "left",
      activeName: "平台公告",
      sortList: [
        {
          name: "平台公告",
          active: false,
          list: [],
          page: 1,
          size: 10
        },
        {
          name: "出入金通知",
          active: false,
          list: [],
          page: 1,
          size: 10
        },
        {
          name: "提醒消息",
          active: false,
          list: [],
          page: 1,
          size: 10
        },
        {
          name: "交易通知",
          active: false,
          list: [],
          page: 1,
          size: 10
        }
      ],
      detailInfo: {}
    };
  },
  mounted() {},
  computed: {
    showList() {
      let current = {};
      this.sortList.forEach(a => {
        if (a.active) {
          current = a;
        }
      });
      return current.list;
    }
  },
  methods: {
    changeNotice() {
      let cur = this.sortList.filter(a => {
        return a.name === this.activeName;
      });
      this.sortChoose(cur[0]);
    },
    getShowList() {
      let current = {};
      this.sortList.forEach(a => {
        if (a.name === this.activeName) {
          current = a;
        }
      });

      // GET /msg/announce/user/selectAnnounceListForApp
      this.axios({
        url: "/msg/announce/user/selectAnnounceListForApp",
        method: "get",
        params: {
          page: current.page,
          size: current.size
        },
        type: "hostNews"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          res.data.ret.list.forEach(a => {
            a._check = false;
          });
          current.list = current.list.concat(res.data.ret.list);
          current.page++;
        } else {
        }
      });
    },
    getNoticeDetail() {},
    setRead(id) {
      // GET /msg/announce/user/updateAnnounceUserStatus
      this.axios({
        url: "/msg/announce/user/updateAnnounceUserStatus",
        method: "get",
        params: {
          id: id
        },
        type: "hostNews"
      }).then(res => {
        announceUserCount();
      });
    },
    checkDetail(no) {
      this.showList.forEach(a => {
        a._check = false;
      });
      no._check = true;
      this.detailInfo = no;
      if (no.status == -1) {
        this.setRead(no.id);
        no.status = 1;
      }
      this.$refs.noticeDetail.show(no);
    },
    sortChoose(sort) {
      if (sort.name != "平台公告") {
        this.$message({
          message: "该功能暂未开通，敬请期待~",
          type: "warning"
        });
        return false;
      }
      this.sortList.forEach(a => {
        a.active = false;
      });
      sort.active = true;
      this.sortTitle = sort.name;
      this.getShowList();
    },
    show() {
      this.dialogVisiblea = true;
      this.changeNotice();
    },

    closDialos() {
      this.dialogVisiblea = false;
      this.$emit("close");
    }
  },
  filters: {
    resetTime(val){
      return val.slice(0, 10)
    }
  }
};
</script>

<style lang="scss" scoped>
.notice {
  .menOrde {
    height: 100%;
    @include flex-box;
    .sub_body {
      // width: 460px;
      height: 100%;
      .no-ul {
        overflow-y: auto;
        max-height: 640px;
        .no-li {
          cursor: pointer;
          position: relative;
          height: 114px;
          padding-top: 20px;
          border-bottom: 1px solid #323840;
          &[data-status="-1"]::before {
            content: "";
            position: absolute;
            width: 8px;
            height: 8px;
            left: -14px;
            top: 3px;
            border-radius: 50%;
            background-color: $green;
          }
          .li-date {
            color: $defaultColor;
            margin-top: 15px;
          }
          .li-title {
            @include add-size($font_size_14);
            padding-bottom: 8px;
            color: $white;
          }
          .li-desc {
            color: $defaultColor;
            @include multi-line-ellipsis(2, 1.5);
          }
          &.active,
          &:hover {
            color: $white;
          }
        }
      }
    }
  }
  /deep/
    .el-dialog__wrapper[theame]
    .el-dialog
    .el-dialog__header
    .el-dialog__title {
    color: $defaultColor;
  }
  /deep/ .el-tabs__item.is-active {
    color: $activeColor;
    background-color: #1d242e;
    // margin-left: -20px;
  }
  /deep/.el-tabs__nav-scroll {
    background-color: #252e3b;
    width: 120px;
  }
  /deep/.el-tabs__item {
    color: $defaultColor;
  }
  /deep/ .el-tabs--left .el-tabs__item.is-left {
    text-align: left;
    padding-left: 30px;
  }
  /deep/.el-tabs__nav-wrap::after {
    display: none;
  }
  /deep/.el-tabs--left .el-tabs__active-bar.is-left,
  .el-tabs--left .el-tabs__nav-wrap.is-left::after {
    left: 0;
  }
  /deep/.el-tabs--left .el-tabs__active-bar.is-left {
    width: 4px;
    height: 20px !important;
    margin-left: 10px;
    margin-top: 10px;
  }
  /deep/ .el-tabs__content{
    height: 100%;
    .el-tab-pane{
      height: 100%;
      .sub_body{
        overflow: auto;
      }
    }
  }
}
</style>
