<template>
  <div class="card-box">
    <div class="car-content">
      <div class="card-title" v-if="!headType">
        <slot name="header"></slot>
        <div class="title-text" v-if="label">{{ label }}</div>
        <div class="more" v-if="more" @click="moreHandler">
          更多
          <i class="el-icon-arrow-right"></i>
        </div>
      </div>
      <div class="card-content">
        <div class="card-list" type="1" v-if="listData && listData.length > 0 && type == 1">
          <div
            class="card-li"
            v-for="(li, i) in listData"
            :key="i"
            hover
            @contextmenu.prevent="context($event, li)"
            @click="rowClick(li)"
          >
            <div class="li-icon TipBg-US">US</div>
            <div class="stock-name" v-if="li.query" :title="li.query.name">
              <span class="stock-code">{{ li.query.symbol.slice(2) }}</span>
              <span :data-selfStock="li.selfStock">{{ li.query.name }}</span>
            </div>
            <div class="stock-point" v-if="li.query">{{ li.query.last }}</div>
            <div
              class="percent"
              :class="{
                rise: li.query.chg_pct > 0,
                fall: li.query.chg_pct < 0
              }"
              v-if="li.query"
            >{{ li.query.chg_pct | addSymbol }}%</div>
          </div>
        </div>
        <!-- type="2" 普通4条数据 -->
        <div class="card-list" type="2" v-if="listData && listData.length > 0 && type == 2">
          <div
            class="card-li"
            v-for="li in listData"
            :key="li.id"
            hover
            @contextmenu.prevent="context($event, li)"
            @click="rowClick(li)"
          >
            <div class="stock-info">
              <div class="stock-name" :title="li.name" :data-selfStock="li.selfStock">{{ li.name }}</div>
              <div class="stock-code">
                <div class="li-icon TipBg-US">US</div>
                {{ li.symbol.slice(2) }}
              </div>
            </div>
            <div class="stock-line">
              <sCharts v-if="li.data" :sChartsData="li.data" />
            </div>
            <div
              v-if="li.yd_chg_pct"
              class="stock-point"
              :class="{ rise: li.yd_chg_pct > 0, fall: li.yd_chg_pct < 0 }"
            >{{ li.last }}</div>
            <div
              v-else
              class="stock-point"
              :class="{ rise: li.chg_pct > 0, fall: li.chg_pct < 0 }"
            >{{ li.last }}</div>
            <div
              v-if="li.yd_chg_pct"
              class="stock-point"
              :class="{ rise: li.yd_chg_pct > 0, fall: li.yd_chg_pct < 0 }"
              style="width: 95px;"
            >{{ li.yd_chg_pct | addSymbol }}%</div>
            <div
              v-else
              class="stock-point"
              :class="{ rise: li.chg_pct > 0, fall: li.chg_pct < 0 }"
              style="width: 95px;"
            >{{ li.chg_pct | addSymbol }}%</div>
            <div
              v-if="li.yd_chg_pct"
              class="stock-point"
              :class="{ rise: li.yd_chg_pct > 0, fall: li.yd_chg_pct < 0 }"
            >{{ li.yd_chg | addSymbol }}</div>
            <div
              v-else
              class="stock-point"
              :class="{ rise: li.chg_pct > 0, fall: li.chg_pct < 0 }"
            >{{ li.chg | addSymbol }}</div>
          </div>
        </div>
        <div class="no-data" v-if="listData && listData.length <= 0 && type == 2 && cardType != 'selfStock'">暂无数据</div>

        <!-- type="3"  产业模块 -->
        <div class="card-list" type="3" v-if="listData && listData.length > 0 && type == 3">
          <div class="card-li" v-for="li in listData" :key="li.id" hover @click="rowClick(li)">
            <!-- <div class="li-icon"></div> -->
            <div class="stock-name" :title="li.IndustryName">
              <!-- <span class="stock-code" v-if="li.StockCode">{{li.StockCode.slice(2)}}</span> -->
              {{ li.IndustryName }}
            </div>
            <div class="stock-point">{{ li.IndustryChgPct }}%</div>
            <div class="percent" :class="{ rise: li.StockChgPct > 0, fall: li.StockChgPct < 0 }">
              <span :title="li.StockName" >{{ li.StockName }}</span>
              <span>{{ li.StockPrice }}</span>
              <span>{{ li.StockChgPct }}%</span>
            </div>
          </div>
        </div>

        <slot name="content"></slot>
      </div>
    </div>
  </div>
</template> 

<script>
import sCharts from "@/components/common/sCharts_us";

export default {
  props: {
    label: String,
    more: Boolean,
    listData: Array,
    type: Number,
    headType: Boolean,
    cardType: String
  },
  components: { sCharts },
  methods: {
    moreHandler() {
      this.$emit("more");
    },
    rowClick(li) {
      this.$emit("rowClick", li);
    },
    context(e, li) {
      this.$emit("contextmenu", e, li);
    }
  }
};
</script>

<style lang="scss" scoped>
.card-box {
  width: 33.333%;
  color: $white;
  overflow: hidden;
  padding-right: 20px;
  @include add-size($font_size_14);
  // min-height: 30%;
  height: 280px;
  .car-content {
    border-radius: 4px;
    border: 1px solid #8a9099;
    overflow: hidden;
    height: 100%;
  }
  .card-title {
    @include flex-center;
    height: 32px;
    background-color: #2d3745;
    padding-left: 20px;
    .more {
      padding-right: 12px;
      margin-left: auto;
      @include flex-center;
      cursor: pointer;
      &:hover {
        color: $activeColor;
      }
    }
  }
  .card-content {
    .card-list {
      .card-li {
        line-height: 35px;
        color: $white;
        @include flex-center;
        padding-left: 20px;
        .rise {
          color: $red;
        }
        .fall {
          color: $green;
        }
        &:nth-child(even) {
          background-color: $subBlockColor;
        }
        .li-icon {
          // width: 18px;
          // height: 12px;
          margin-right: 10px;
        }
        .stock-code {
          margin-right: 16px;
        }
        .stock-name {
          width: 236px;
          @include single-line-ellipsis;
        }
        .stock-point {
          width: 68px;
        }
        .percent {
          // width: 250px;
          // padding-right: 37px;
          text-align: right;
          white-space: nowrap;
          @include flex-center;
          span {
            display: inline-block;
            margin-right: 10px;
            width: 60px;
          }
          span[title] {
            width: 160px;
            @include single-line-ellipsis;
          }
        }
      }
      &[type="2"] {
        .card-li {
          line-height: 1;
          height: 61px;
          .stock-name {
            @include single-line-ellipsis;
            width: 118px;
          }
          .stock-code {
            @include flex-center;
            margin-top: 6px;
          }
          .li-icon {
            margin-right: 4px;
          }
          .stock-point {
            width: 100px;
          }
          .stock-line {
            width: 182px;
            padding-right: 52px;
          }
        }
      }
    }
    .no-data{
      line-height: 100px;
      text-align: center;
    }
  }
}
</style>