<template>
  <div class="list-canvas">
    <div ref="smallChart" style="height: 40px;"></div>
    <!-- <span class="img-can" v-if="showImg">
      <img :src="src" alt />
    </span>-->
  </div>
</template>

<script>
import { initSMOption } from "@/utils/k-line";
export default {
  name: "sCharts",
  data() {
    return {
      src: "",
      showImg: false
    };
  },
  props: ["sChartsData"],
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.drawSmall();
      }, 300);
    });
  },
  methods: {
    drawSmall() {
      this.showImg = false;
      this.$nextTick(() => {
        let mChart = this.$echarts.init(this.$refs.smallChart);
        mChart.setOption(initSMOption(this.sChartsData, "hk"));
        // setTimeout(() => {
        //   this.src = mChart.getDataURL({
        //     pixelRatio: 2,
        //     backgroundColor: "transparent"
        //   });
        //   this.showImg = true;
        //   mChart.dispose();
        // }, 2000);
        // mChart.dispose();
      });

      //   setTimeout(() => {
      //     let mChart = this.$echarts.init(this.$refs.hahah);
      //     mChart.setOption(initMOption(this.testData, "hk"));
      //   }, 2000);
    }
  },
  watch: {
    sChartsData() {
      this.drawSmall();
    }
  }
};
</script>

<style lang="scss" scoped>
.list-canvas {
  max-width: 180px;
  .img-can {
    display: block;
    height: 40px;
    width: 129px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>