<template>
  <div class="field-main">
    <div class="base-info">
      <div class="info-li" v-for="(val, k) in baseData" :key="k">
        <div class="li-name" v-if="baseDataLabel[k]">{{baseDataLabel[k]}}</div>
        <div class="li-value" v-if="baseDataLabel[k]">{{val | moneyFormat}}</div>
      </div>
    </div>
    <div class="postion-stock">
      <el-table
        class="stock-table"
        :data="comuptedPosition"
        @row-contextmenu="selectRow"
        height="180"
      >
        <el-table-column prop="StockCode" label="股票代码" align="center">
          <template slot-scope="scope">
            <div>
              <span class="TipBg">HK</span>
              {{scope.row.StockCode}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="StockName" label="股票名称" align="center"></el-table-column>
        <el-table-column prop="StockQty" label="股票数量" align="right"></el-table-column>
        <el-table-column prop="sellNum" label="可卖数量" align="right"></el-table-column>
        <el-table-column prop="Last" label="最新市值" align="right">
          <template slot-scope="scope">
            <div>{{Number(scope.row.Last * scope.row.StockQty).toFixed(3)}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="Last" label="当前价" align="right">
          <template slot-scope="scope">
            <div>{{Number(scope.row.Last).toFixed(3)}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="CostNum" label="成本价" align="right">
          <template slot-scope="scope">
            <div>{{Number(scope.row.CostNum).toFixed(3)}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="freezeNum" label="冻结数量" align="right">
          <template slot-scope="scope">
            <div>{{scope.row.StockQty - scope.row.sellNum}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="CurProfitLoss" label="浮动盈亏" align="right">
          <template slot-scope="scope">
            <div
              :class="{rise: scope.row.CurProfitLoss > 0, fall: scope.row.CurProfitLoss < 0}"
            >{{scope.row.CurProfitLoss > 0 ?'+' + scope.row.CurProfitLoss : '-' + scope.row.CurProfitLoss}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="ProfitLossRatio" label="盈亏比例" align="right">
          <template slot-scope="scope">
            <div
              :class="{rise: scope.row.CurProfitLoss > 0, fall: scope.row.CurProfitLoss < 0}"
            >{{scope.row.ProfitLossRatio}}%</div>
          </template>
        </el-table-column>
        <el-table-column class-name="space" width="300"></el-table-column>
      </el-table>
    </div>
    <Contextmenu ref="Contextmenu" />
  </div>
</template>
<script>
import Contextmenu from "@/components/deal/contextmenu";
// import { basename } from "path";

export default {
  props: {
    accountInfo: Object,
    curPosition: Array,
    curEntrust: Array
  },
  components: {
    Contextmenu
  },
  data() {
    return {
      /* baseData: {
        totalAssets: 586324.59,
        availableAmount: 586324.59,
        withDrawCashBalance: 586324.59,
        frozenAmount: 586324.59,
        holdMarketValue: 586324.59,
        totalProfitLoss: 586324.59,
        curProfitLoss: 586324.59
      }, */
      baseDataLabel: {
        totalAssets: "总资产（HKD）",
        availableAmount: "当前可用",
        withDrawCashBalance: "当前可提",
        frozenAmount: "冻结金额",
        holdMarketValue: "持仓市值",
        totalProfitLoss: "持仓盈亏",
        curProfitLoss: "今日盈亏"
      },
      stockTable: []
    };
  },
  activated() {
    this.$nextTick(() => {
      // this.getQryQryPosition();
    });
  },
  computed: {
    baseData() {
      let baseData = {};
      for (let item in this.baseDataLabel) {
        baseData[item] = this.accountInfo[item];
      }
      return baseData;
    },
    comuptedPosition() {
      let position = this.curPosition;
      let entrust = this.curEntrust;
      position.forEach(a => {
        a.freezeNum = 0;
        a.sellNum = Number(a.StockQty);
        entrust.forEach(b => {
          if (a.StockCode === b.StockCode && b.OrderSide != "0") {
            a.freezeNum += Number(b.OrderQty);
            a.sellNum -= Number(b.OrderQty);
          }
        });
      });
      return position;
    },
    currentType() {
      // 当前选择的账户
      return this.$store.state.AccountLogin.currentType;
    }
  },
  methods: {
    getQryQryPosition() {
      // 获取当前持仓
      let accountNo = this.$store.state.AccountLogin.accountData.LoginID;
      this.axios({
        url: "/getQryCalculatePosition.do",
        method: "post",
        params: {
          accountNo: accountNo
        },
        type: "hostEbroker"
      }).then(res => {
        this.stockTable = res.data.data || [];
      });
    },
    selectRow(row, column, event) {
      let distanceX = event.clientX;
      let distanceY = event.clientY;
      this.$refs.Contextmenu.show({
        left: distanceX,
        top: distanceY,
        type: "deal",
        row: row
      });
      this.closeContextmenu();
    },
    closeContextmenu() {
      let html = document.getElementsByTagName("html")[0];
      let _this = this;
      html.onclick = function() {
        if (_this.$refs.Contextmenu) {
          _this.$refs.Contextmenu.close();
        }
      };
    }
  }
};
</script>

<style scoped lang="scss">
.field-main {
  padding: 20px 20px;
  width: 100%;
  @include flex-box;

  .base-info {
    @include flex-center;
    flex-wrap: wrap;
    width: 320px;
    border-right: 1px solid $borderColor;
    line-height: 20px;
    margin-right: 30px;
    .info-li {
      padding-bottom: 10px;
      &:first-child{
        width: 100%;
      }
      width: 159px;
      // @include flex-center;
      .li-name {
        @include add-size($font_size_12);
      }
      .li-value {
        color: $white;
        @include add-size($font_size_16);
        // margin-left: 10px;
      }
    }
  }
  .postion-stock {
      width: 1300px;

  }
  /deep/ .el-table {
    margin-top: 0 !important;
    .el-table__row td .cell {
      line-height: 18px;
    }
  }
}
</style>