<template>
  <div class="main">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive" />
    <KlineCharts />
    <StockQuery />
    <MainBot />
    <RightMenu />
    <Remind />
    <CompanyInfo />
    <Account />
    <DealBar />
  </div>
</template>

<script>
import StockQuery from "./common/stock-query"; // 股票详情
import MainBot from "./common/main-bot"; // 三大指数
import RightMenu from "@/components/common/right-menu"; // 右键菜单
import Remind from "@/components/common/remind"; // 股票提醒设置
import KlineCharts from "./common/kline-charts"; // 股票K线图
import CompanyInfo from "./common/company-info"; // 股票K线图
import Account from "./common/account-login/account-index" // 交易登录弹框
import DealBar from "./common/deal/deal-cover" // 交易栏
import { initSocket } from "@/utils";

import {
  getCustomerAcount,
  announceUserCount,
  closeSocket,
  indexFutures
} from "@/utils";

export default {
  data() {
    return {
      login: true
    };
  },
  components: {
    StockQuery,
    MainBot,
    RightMenu,
    Remind,
    KlineCharts,
    CompanyInfo,
    Account,
    DealBar
  },
  mounted() {
    this.$nextTick(() => {
      this.init();
    });
  },
  computed: {},
  methods: {
    queryState() {
      // 校验用户的开户状态
      let _this = this;
      getCustomerAcount(res => {
        _this.$store.dispatch("SET_ACCOUNT_INFO", res.data);
      });
    },
    init() {
      closeSocket();
      this.queryState();
      // announceUserCount();
      // this.$store.dispatch("SHOW_TIME_TURNOVER", { show: false, data: {} });
      let _this = this;
      // document.addEventListener("keyup", function(e) {
      //   if (e.keyCode === 27) {
      //     _this.$store.dispatch("closeDeal");
      //   }
      // });
      setTimeout(() => {
        indexFutures();
      }, 5000);
    },
    myQuotation() {
      // 获取我的行情数据
      this.boughtList = [];
      this.axios({
        url: "/customermarket/selectMarketSubscribe",
        params: {},
        type: "host3"
      }).then(res => {
        let dataset = res.data.ret;
        dataset.map((value, index) => {
          value.type = 1;
          this.boughtList.push(value);
        });
      });
    }
  },
  watch: {
    "$store.state.GlobalData.currentStock.code"(val) {
      initSocket(val);
    }
  }
};
</script>

<style scoped lang="scss">
.main {
  height: 100%;
  padding-bottom: 0;
  @include flex-box;
  position: relative;
  &[data-deal="true"] {
    padding-bottom: 34px;
  }
  /deep/ .action {
    width: 160px;
    height: 40px;
    border: none;
    border-radius: 4px;
    @include linear;
    margin-top: 41px;
    color: $white;
    @include medium;
    @include add-size($font_size_18);
    padding: 0;
  }
}
</style>
