<template>
  <div class="customer-info">
    <el-dialog
      class="customer-info-dialog"
      theame
      center
      title="我的客户"
      :modal="false"
      :visible="isShow"
      width="570px"
      top="15%"
      @close="close"
    >
      <div class="info-content">
        <el-tabs v-model="activeName" :stretch="true" @tab-click="handleClick">
          <el-tab-pane label="全部" name="first">
            <div slot="label">全部({{customerNum.totalCustomerNo}})</div>
          </el-tab-pane>
          <el-tab-pane label="未开户" name="second">
            <div
              slot="label"
            >未开户({{customerNum.registerCustomerNo}})</div>
          </el-tab-pane>
          <el-tab-pane label="开户中" name="third">
            <div slot="label">开户中({{customerNum.openningAcountCustomerNo}})</div>
          </el-tab-pane>
          <el-tab-pane label="已开户" name="fourth">
            <div slot="label">已开户({{customerNum.openAcountCustomerNo}})</div>
          </el-tab-pane>
          <el-tab-pane label="已入金" name="fifth">
            <div slot="label">已入金({{customerNum.depositCustomerNo}})</div>
          </el-tab-pane>
          <!-- <el-tab-pane label="已交易" name="fifth">
            <div slot="label">已交易({{dealCustomerList.length}})</div>
          </el-tab-pane>-->
        </el-tabs>
        <div class="customer-table">
          <el-table class :data="customerListShow" style @row-click="rowSelect" :max-height="320">
            <el-table-column prop="customerName" label="姓名" width="140">
              <template slot-scope="scope">
                <div class="name-label" :title="scope.row.customerName">{{scope.row.customerName}}</div>
              </template>
            </el-table-column>
            <el-table-column prop="mobile" label="电话" align="center"></el-table-column>
            <el-table-column prop="countState" label="状态" align="center"></el-table-column>
            <el-table-column prop="regTime" label="注册时间" width="180">
              <template slot-scope="scope">
                <div class="time-label">
                  {{scope.row.regTime}}
                  <i class="el-icon-arrow-right"></i>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      class="customer-info-dialog"
      theame
      center
      title="客户详情"
      :modal="false"
      :visible="isShowDetail"
      width="570px"
      top="15%"
      @close="closeDetail"
    >
      <div class="header" slot="title">
        <i class="el-icon-arrow-left back" @click="isShowDetail = false"></i>
        <div class="el-dialog__title">客户详情</div>
      </div>
      <div class="info-content">
        <el-tabs v-model="detailTab" :stretch="true" @tab-click="handleClick">
          <el-tab-pane label="账户信息" name="first">
            <ul class="account-info">
              <li class="account-info-li">
                <div class="account-info-li_label">姓名</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerCountInfoVo.customerName}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">电话</div>
                <div class="account-info-li_value">{{customerDetail.customerCountInfoVo.mobile}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">注册时间</div>
                <div class="account-info-li_value">{{customerDetail.customerCountInfoVo.regTime}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">账户状态</div>
                <div class="account-info-li_value">{{customerDetail.customerCountInfoVo.countState}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">交易次数</div>
                <div class="account-info-li_value">{{customerDetail.customerCountInfoVo.tradTimes}}</div>
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="资金存取" name="second">
            <ul class="account-info">
              <li class="account-info-li">
                <div class="account-info-li_label">总资产</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerAssetInfoVo.assets | moneyFormat}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">持股总价值</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerAssetInfoVo.marketValue | moneyFormat}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">可用资产</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerAssetInfoVo.availCapital | moneyFormat}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">冻结资金</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerAssetInfoVo.frozenCapital | moneyFormat}}</div>
              </li>
              <li class="account-info-li">
                <div class="account-info-li_label">可取资金</div>
                <div
                  class="account-info-li_value"
                >{{customerDetail.customerAssetInfoVo.extractValue | moneyFormat}}</div>
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="股票详情" name="third">
            <el-table class="stock-table" :data="customerDetail.customerStocksInfoVo">
              <el-table-column prop="stockName" label="股票名称">
                <template slot-scope="scope">
                  <div class="row-t">{{scope.row.stockName}}</div>
                  <div class="row-b stock-code">{{scope.row.stockCode}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="filledQty" label="持仓数量"></el-table-column>
              <el-table-column prop="curPirce" label="昨收价"></el-table-column>
              <el-table-column prop="marketValue" label="市值"></el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "customer-info",
  props: {
    customerList: Array,
    customerNum: Object
  },
  data() {
    return {
      isShow: false,
      isShowDetail: false,
      activeName: "first",
      detailTab: "first",
      stockData: [
        {
          stockName: "股票名称股票名称",
          stockCode: "001003",
          position: 2000,
          last: "123456.00",
          value: "123456.00"
        },
        {
          stockName: "股票名称股票名称",
          stockCode: "001003",
          position: 2000,
          last: "123456.00",
          value: "123456.00"
        },
        {
          stockName: "股票名称股票名称",
          stockCode: "001003",
          position: 2000,
          last: "123456.00",
          value: "123456.00"
        }
      ],
      dealCustomerList: [],
      customerDetail: {
        customerCountInfoVo: {},
        customerAssetInfoVo: {},
        customerStocksInfoVo: []
      }
    };
  },
  mounted() {},
  computed: {
    customerListShow() {
      if (this.activeName === "first") {
        return this.customerList.filter(a => {
          return a;
        });
      } else if (this.activeName === "second") {
        return this.customerList.filter(a => {
          return a.countState == "已注册";
        });
      } else if (this.activeName === "third") {
        return this.customerList.filter(a => {
          return a.countState == "开户申请中";
        });
      } else if (this.activeName === "fourth") {
        return this.customerList.filter(a => {
          return a.countState == "已开户";
        });
      } else if (this.activeName === "fifth") {
        return this.customerList.filter(a => {
          return a.countState == "已入金";
        });
      }
    }
  },
  watch: {},
  methods: {
    rowSelect(row) {
      console.log(row);
      let customerId = row.customerId;
      this.getCustomerDetail(customerId);
    },
    getCustomerDetail(customerId) {
      // GET /appcustomer/getMyCustomerDetail.do
      this.axios({
        url: "/appcustomer/getMyCustomerDetail.do",
        params: {
          customerId: customerId
        },
        type: "host"
      }).then(res => {
        this.customerDetail = res.data.data[0];
        this.showDetail();
      });
    },
    handleClick() {},
    show() {
      this.isShow = true;
      // this.getDealCustomerList();
    },
    getDealCustomerList() {
      // 已交易状态 去除
      // GET /appcustomer/getDealCustomerList.do
      this.axios({
        url: "/appcustomer/getDealCustomerList.do",
        params: {},
        type: "host"
      }).then(res => {
        this.dealCustomerList = res.data.data;
      });
    },
    close() {
      this.isShow = false;
    },
    showDetail() {
      this.isShowDetail = true;
    },
    closeDetail() {
      this.isShowDetail = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.customer-info {
  .customer-info-dialog {
    /deep/ .el-dialog {
      .el-dialog__title {
        @include add-size($font_size_14);
        color: $defaultColor !important;
      }
    }
    .info-content {
      height: 398px;
      .account-info {
        padding: 30px;
        color: $white;
        @include add-size($font_size_14);
        .account-info-li {
          @include flex-center;
          margin-bottom: 50px;
          .account-info-li_label {
            width: 224px;
          }
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
    .customer-table {
      padding: 0 20px;
      .name-label {
        @include single-line-ellipsis;
      }
      .time-label {
        @include flex-center;
        justify-content: space-between;
      }
    }
    .header {
      @include flex-center;
      position: relative;
      justify-content: center;
      .back {
        position: absolute;
        left: 0px;
        top: 9px;
        width: 30px;
        height: 16px;
        cursor: pointer;
        &:hover {
          color: $activeColor;
        }
      }
    }
    .stock-table {
      padding-left: 30px;
    }
  }
  /deep/ .el-table.stock-table {
    .stock-code {
      margin-top: 8px;
      padding-left: 27px;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 20px;
        height: 14px;
        left: 0;
        top: 0;
        background: url(~@/assets/images/base/hk-icon.png) 0 0 no-repeat;
        background-size: 20px 14px;
      }
    }
    &.el-table--medium td,
    &.el-table--medium th {
      padding: 14px 0;
    }
    .el-table__row {
      &:hover {
        td,
        th {
          background-color: inherit !important;
        }
      }
    }
  }
}
</style>
