<template>
  <div class="ipo-mask">
    <el-dialog theame center title="新股认购" :visible="isShow" width="690px" top="8%" @close="close">
      <div class="ipo-content">
        <ul class="subscribe-ul">
          <li class="subscribe-li">
            <div class="subscribe-li_name">新股名称</div>
            <div class="subscribe-li_c">{{ipoData.nameGb}} {{ipoData.productCode}}</div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name">认购账户</div>
            <div class="subscribe-li_c">{{ipoData.account}}</div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name">认购方式</div>
            <div class="subscribe-li_c">
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 0}"
                @click="applyWayStatus = 0"
                v-if="ipoData.financingStatus == 0"
              >融资认购</span>
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 1}"
                @click="applyWayStatus = 1"
                v-if="ipoData.international == 0"
              >国际配售</span>
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 2}"
                @click="applyWayStatus = 2"
                v-if="ipoData.cashStatus == 0"
              >现金认购</span>
            </div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus === 0">
            <div class="subscribe-li_name">融资比例</div>
            <div class="subscribe-li_c">{{ipoData.financeRate}}</div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus === 1">
            <div class="subscribe-li_name">认购金额</div>
            <el-input v-model="ipoData.actFinance" :placeholder="'最低配售金额' + ipoData.minFinance + '港币'"></el-input>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name">认购数量</div>
            <div class="subscribe-li_c">
              <el-select v-model="quantity" @change="changeQuantity">
                <el-option
                  v-for="item in ipoData.eipoListVoList"
                  :key="item.issuedShares"
                  :label="item.label"
                  :value="item.issuedShares"
                ></el-option>
              </el-select>
            </div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name">可用资金</div>
            <div class="subscribe-li_c">{{ipoData.cashBalance | moneyFormat}} 港币</div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name">认购金额</div>
            <div
              class="subscribe-li_c"
            >{{(ipoData.priceAllo - ipoData.proceduresMoney || 0) | moneyFormat}} 港币</div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name">手续费</div>
            <div
              class="subscribe-li_c"
            >{{( Number(ipoData.serviceCharge) + ipoData.proceduresMoney || 0) | moneyFormat}} 港币</div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name">认购总费用</div>
            <div
              class="subscribe-li_c"
            >{{( Number(ipoData.serviceCharge) + ipoData.priceAllo || 0) | moneyFormat}} 港币</div>
          </li>
        </ul>
        <div class="tips">
          <div class="tips-title">温馨提示：</div>
          <div class="tips-content">
            <p>1.新股认购提交后请预留对应认购金额，因账户金额不足扣款失败后果用户自行承担。无论中签与否，认购费100港币都将扣除。</p>
            <p>2.认购截止时间</p>
            <p>因需处理认购手续,易昇证券认购截止日期会稍早于交易所公布的截止时间,截止时间为认购截止日00:00,请确保在该时间点账户内有足够的资金,否则会导致认购失败。</p>
            <p>3.公布时间</p>
            <p>交易所公布中签结果后,中签的认购资金将在结果公布日期当天21:00前解冻。</p>
            <p>4.资金准备</p>
            <p>a)手续费包括1%经纪佣金+0.0027%证监会交易征费+0.005%联交所交易费+100港币认购费</p>
            <p>b)最低认购额预取发行价区间中最高价x最少认购股数,最终金额以实际扣款为准。</p>
            <p>c)资金冻结确定认购后,认购资金将被冻结,直至交易所公布中签结果。</p>
          </div>
        </div>
        <div class="submit-bar">
          <div class="protocol">
            <div class="radio" :class="{active: radio}" @click="radio = !radio"></div>
            <div class="protocol-text">
              我已阅读
              <span @click="showProtocol = true">《新股认购协议》</span>
            </div>
          </div>
          <button class="submit action" hover @click="submitIPO">确认提交</button>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      theame
      center
      title="新股认购协议"
      :visible="showProtocol"
      width="690px"
      top="11%"
      @close="showProtocol = false"
    >
      <div class="protocol-content">
        <div class="protocol-title">易昇证券新股认购协议</div>
        <p>本协议适用于要求易昇证券有限公司（本公司）代其认购在交易所上市之新发行证券（申购）的客户。</p>
        <p>客户授权易昇证券填写可能需要的申请表格，并向本公司陈述、声明及保证申购人必须做出在申购书上所载或包含之一切有关客户之陈述、声明、保证、确认及承诺均真实有效及准确。</p>
        <p>客户同意受新发行证券之条款所约束并保证及承诺申购乃在同一次证券发行中为客户利益而作出之唯一申购，及于该次证券发行，客户不可同时进行其他申购；</p>
        <p>授权本公司向交易所陈述、声明、及保证客户不会亦不作出其他申购，并且不会亦不拟为客户的利益而作出其他申购；</p>
        <p>确认本公司乃依据以上之保证、承诺及授权而进行申购；</p>
        <p>确认本公司没有任何责任把列明新发行之条款及条件上市档（招股书）交予客户。对于客户有关之申购，客户确认已从其他地方取得招股书，并已细阅及明白其中之条款及条件，而客户之申购亦不会违反该条款及条件。客户确认除非在适用之证券条例下其乃合乎资格，否则客户不会认购新发行证券；</p>
        <p>客户陈述、声明及保证并非有关新发行证券的证券发行者之关联人士（按监管规则下之定义）。</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      quantity: "",
      radio: true,
      showProtocol: false,
      ipoData: {},
      applyWayStatus: 2
    };
  },
  watch: {
    applyWayStatus(val) {
      this.applyWay(val);
    }
  },
  methods: {
    submitIPO() {
      try {
        if (!this.radio) {
          throw Error("请先阅读新股认购协议");
        }
        if (this.applyWayStatus != 1 && this.quantity <= 0) {
          throw Error("请选择认购数量");
        }
        let ipoData = this.ipoData;
        // console.log(ipoData);
        if (
          this.applyWayStatus == 1 &&
          Number(ipoData.actFinance || 0) < Number(ipoData.minFinance)
        ) {
          throw Error("不能低于最低配售金额");
        }
        if (
          this.applyWayStatus == 1 &&
          Number(ipoData.actFinance || 0) > ipoData.cashBalance
        ) {
          throw Error("可用资金不足");
        }

        if (ipoData.cashBalance < Number(ipoData.priceAllo)) {
          throw Error("可用资金不足");
        }
        // GET /eipo/eipoData
        return;
        this.axios({
          url: "/eipo/save",
          method: "post",
          params: {
            applyWay: this.applyWayStatus,
            account: ipoData.account,
            issuedShares: this.quantity,
            ipoId: this.liData.ipoId,
            nameGb: ipoData.nameGb,
            productCode: ipoData.productCode,
            serviceCharge: ipoData.serviceCharge,
            totalMoney:
              this.applyWayStatus == 1 ? ipoData.actFinance : ipoData.priceAllo,
            TransactionFee: ipoData.proceduresMoney
            // signatureUrl: ""
          },
          type: "host3"
        }).then(res => {
          this.$message({ message: "认购成功", type: "success" });
          this.close();
        });
      } catch (err) {
        this.$message({ message: err.message, type: "error" });
      }
    },
    show(row) {
      this.isShow = true;
      (this.quantity = ""), (this.liData = row);
      // this.getIpoInfo();
      this.applyWay(2);
    },
    getIpoInfo() {
      let liData = this.liData;
      // GET /eipo/selectEipo
      this.axios({
        url: "/eipo/selectEipoLever",
        params: {
          ipoId: liData.ipoId
        },
        type: "host3"
      })
        .then(res => {
          let data = res.data.ret;
          data.eipoListVoList.forEach(a => {
            a.label = a.issuedShares + " 股" + " （" + a.priceAllo + " 港币）";
          });
          this.ipoData = res.data.ret;
          this.applyWay(2);
        })
        .catch(err => {
          if (err.data.code === 9999) {
            this.$store.dispatch("updateAccountData", {});
            this.close();
          }
        });
    },
    applyWay(way) {
      // GET /eipo/applyWay
      let liData = this.liData;
      // 认购方式 0 融资认购 1 国际配售 2 现金申购
      this.axios({
        url: "/eipo/applyWay",
        params: {
          ipoId: liData.ipoId,
          channel: 4,
          applyWay: way
        },
        type: "host3"
      }).then(res => {
        let data = res.data.ret;
        if (data.eipoListVoList) {
          data.eipoListVoList.forEach(a => {
            a.label = a.issuedShares + " 股" + " （" + a.priceAllo + " 港币）";
          });
          this.ipoData = res.data.ret;
        }
      });
    },
    changeQuantity(e) {
      this.ipoData.eipoListVoList.forEach(a => {
        if (a.issuedShares === e) {
          this.ipoData.priceAllo = a.priceAllo;
          this.ipoData.proceduresMoney = a.proceduresMoney;
        }
      });
    },
    close() {
      this.isShow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.ipo-mask {
  /deep/ .el-dialog {
    .el-dialog__header {
      .el-dialog__title {
        color: $defaultColor !important;
        @include add-size($font_size_14);
      }
    }
  }
  .ipo-content {
    .subscribe-ul {
      @include add-size($font_size_14);
      padding: 30px 0 10px;
      @include flex-box;
      flex-wrap: wrap;
      .subscribe-li {
        padding: 10px 30px;
        width: 50%;
        @include flex-center;
        color: $white;
        .subscribe-li_name {
          width: 110px;
          color: $defaultColor;
        }
        .subscribe-li_c {
          @include flex-center;
          &.is-right {
            margin-left: auto;
          }
          .quantity {
            position: relative;
            /deep/ .el-input__inner {
              background-color: inherit;
              border: none;
              border-radius: 0;
              border-bottom: 1px solid #576373;
              width: 200px;
              color: $white;
              padding: 0;
              text-align: center;
              height: 28px;
            }
            i {
              @include add-size($font_size_14);
              position: absolute;
              color: $defaultColor;
              left: 15px;
              top: 6px;
              z-index: 10;
              cursor: pointer;
              &:hover {
                color: $activeColor;
              }
              &.plus {
                left: auto;
                right: 15px;
              }
            }
          }
          .per-hand {
            span {
              display: inline-block;
              width: 100px;
              text-align: center;
            }
          }
          /deep/ .el-button {
            width: 58px;
            height: 24px;
            background-color: inherit;
            border: 1px solid #767a92;
            padding: 0;
            @include add-size($font_size_12);
            span {
              line-height: 22px;
              color: $defaultColor;
            }
            &:hover {
              background-color: $activeColor;
              border-color: $activeColor;
              span {
                color: $white;
              }
            }
          }
          .flag-li {
            width: 70px;
            height: 24px;
            line-height: 22px;
            border: 1px solid $defaultColor;
            color: $defaultColor;
            text-align: center;
            margin-right: 6px;
            cursor: pointer;
            &:last-child {
              margin-right: 0;
            }
            &.active {
              color: $white;
              background: $activeColor;
            }
          }
        }
        &.line {
          padding: 0;
          div {
            height: 1px;
            width: 100%;
            background-color: #323840;
          }
        }
      }
      /deep/ .el-select {
        width: 190px;
        input {
          background: $baseBg;
          border: none;
          border-radius: 0;
          color: $white;
          height: 30px;
          line-height: 30px;
        }
        .el-select__caret {
          line-height: 30px;
          &::before {
            content: "\e78f";
          }
        }
      }
      /deep/ .el-input {
        background-color: inherit;
        .el-input__inner {
          background-color: inherit;
          border-radius: 0;
          height: 36px;
        }
      }
    }
    .tips {
      color: $defaultColor;
      padding: 0 30px 20px;
      @include add-size($font_size_14);
      .tips-title {
        line-height: 30px;
      }
      .tips-content {
        line-height: 24px;
      }
    }
    .submit-bar {
      @include flex-center;
      border-top: 1px solid #323840;
      height: 70px;
      padding: 0 30px;
      .protocol {
        @include flex-center;
        .radio {
          width: 12px;
          height: 12px;
          border-radius: 4px;
          border: 1px solid $activeColor;
          position: relative;
          cursor: pointer;
          &.active {
            &::before {
              content: "";
              position: absolute;
              left: 50%;
              top: 50%;
              width: 6px;
              height: 6px;
              background-color: $activeColor;
              margin-left: -3px;
              margin-top: -3px;
              border-radius: 2px;
            }
          }
        }
        .protocol-text {
          margin-left: 10px;
          span {
            color: $activeColor;
            cursor: pointer;
          }
        }
      }

      .submit {
        width: 100px;
        height: 30px;
        line-height: 30px;
        padding: 0;
        border-radius: 2px;
        cursor: pointer;
        margin: 0 0 0 auto;
        @include add-size($font_size_14);
      }
    }
  }
  .protocol-content {
    padding: 0 30px 60px;
    color: $white;
    .protocol-title {
      padding-top: 40px;
      padding-bottom: 23px;
      text-align: center;
    }
    p {
      @include add-size($font_size_12);
      text-indent: 2em;
      line-height: 24px;
    }
  }
}
</style>