<template>
  <div
    class="charts"
    ref="klineChart"
    v-show="isShow"
    :class="{ zsKline: zsIndex && zsPage }"
  >
    <div class="chartsTab">
      <span @click="selectChart(0)" :class="{ active: isActive == 0 }"
        >分时</span
      >
      <span @click="selectChart(1)" :class="{ active: isActive == 1 }"
        >5日</span
      >
      <span @click="selectChart(2)" :class="{ active: isActive == 2 }"
        >日K</span
      >
      <span @click="selectChart(3)" :class="{ active: isActive == 3 }"
        >周K</span
      >
      <span @click="selectChart(4)" :class="{ active: isActive == 4 }"
        >月K</span
      >
      <span @click="selectChart(5)" :class="{ active: isActive == 5 }"
        >年K</span
      >
      <el-dropdown trigger="click" @command="handleCommand">
        <span :class="{ active: isActive == 6 }" class="el-dropdown-link"
          >{{ moreBtn }}<i class="el-icon-arrow-down el-icon--right"></i
        ></span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="minute1">1分钟</el-dropdown-item>
          <el-dropdown-item command="minute5">5分钟</el-dropdown-item>
          <el-dropdown-item command="minute15">15分钟</el-dropdown-item>
          <el-dropdown-item command="minute30">30分钟</el-dropdown-item>
          <el-dropdown-item command="minute60">60分钟</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="Kline-divZs" v-if="zsIndex || changeIndex">
      <div class="minute" v-show="isActive == 0 || isActive == 1">
        <div class="m-line" v-show="isActive == 0" ref="mLine"></div>
        <div class="m-line" v-show="isActive == 1" ref="minCharts"></div>
      </div>
      <div
        class="newK"
        ref="newK"
        v-show="isActive != 0 && isActive != 1"
      ></div>
    </div>
    <div v-else class="Kline-div">
      <div class="minute" v-show="isActive == 0 || isActive == 1">
        <div class="m-line" v-show="isActive == 0" ref="mLine"></div>
        <div class="m-line" v-show="isActive == 1" ref="minCharts"></div>
      </div>
      <div
        class="newK"
        ref="newK"
        v-show="isActive != 0 && isActive != 1"
      ></div>
    </div>
  </div>
</template>

<script>
import {
  initMOption,
  init5MOption,
  initKOption,
  initHiddenDishOption
} from "@/utils/k-line";
export default {
  name: "klineCharts",
  data() {
    return {
      isActive: 0,
      stockCode: "", //股票代码
      webSocket: "",
      dataset: {
        data: [],
        yestclose: 330
      },
      datasetFive: {
        data: [],
        yestclose: 330
      },
      kLineData: [],
      mChart: "",
      commandVal: "",
      changeIndex: false,
      opacityType: false,
      moreBtn: "更多",
      stockData: {},
      isShow: false,
      zsPage: false,
      zsIndex: false
    };
  },
  // props: ["zsIndex"],
  created() {},
  mounted() {
    // this.$nextTick(() => {
    //   window.mChart = this.$echarts.init(this.$refs.mLine);
    //   window.minCharts = this.$echarts.init(this.$refs.minCharts);
    //   // console.log(33333333)
    //   if (!this.isShow) {
    //     this.stockCode = this.$store.state.GlobalData.currentStock;
    //     this.initWatch();
    //     this.isShow = true;
    //   }
    //   setTimeout(() => {
    //     console.log(this.$store.state.GlobalData.stockData)
    //     initSocket(this.stockCode)
    //   }, 10000)
    // });
  },
  activated() {
    // this.isActive = 0;
    // this.isShow = true;
    // window.mChart = this.$echarts.init(this.$refs.mLine);
    // window.minCharts = this.$echarts.init(this.$refs.minCharts);
  },
  deactivated() {
    this.isShow = false;
    // this.$echarts.dispose(window.mChart);
    // console.log("clear")
  },
  computed: {
    minutely() {
      return this.$store.state.GlobalData.stockData.minutely;
    },
    queryData() {
      return this.$store.state.GlobalData.stockData.query;
    }
  },
  methods: {
    init() {
      window.mChart = this.$echarts.init(this.$refs.mLine);
      window.minCharts = this.$echarts.init(this.$refs.minCharts);
      this.stockCode = this.$store.state.GlobalData.currentStock.code;
      this.initWatch();
    },
    //选择
    selectChart(num) {
      this.isActive = num;
      if (num == 0) {
        //切换到K线选择其他股票时，点击分时数据不变了
        this.getStock(num);
        // console.log(this.dataset.data.length,544444444)
        // if (this.dataset.data.length > 0) {
        //     this.$nextTick(() => {
        //         // this.mChart.resize();
        //         this.$echarts.init(this.$refs.mLine).resize();
        //     })
        // } else {
        //     this.getStock(num)
        // }
      } else if (num == 1) {
        if (this.datasetFive.data.length > 0) {
          this.$nextTick(() => {
            window.minCharts.resize();
          });
        } else {
          this.getStock(num);
        }
      } else {
        this.klineData(num);
      }
    },
    drawMinute() {
      //订阅事件a
      if (this.isActive == 1) {
        return;
      }
      let dataset = this.dataset;
      // console.log(data, '订阅者模式');
      // var time = data.Minute
      let minutely = this.minutely || [];
      minutely.forEach(data => {
        if (data.symbol != this.stockCode) {
          return false;
        }
        if (data.Price < "0.001" || data.Avg_price < "0.001") {
          return;
        }
        if (!data) {
          return false;
        }
        let t = new Date(data.Minute * 60 * 1000);
        let time = t.getHours().toString() + t.getMinutes().toString();
        if (
          dataset.data.length > 0 &&
          Number(time) <= Number(dataset.data[dataset.data.length - 1][0])
        ) {
          // console.log("时间相等");
          dataset.data[dataset.data.length - 1][1] = data.Price;
          dataset.data[dataset.data.length - 1][2] = data.Avg_price;
          dataset.data[dataset.data.length - 1][3] = data.Volume;
          dataset.data[dataset.data.length - 1][4] = data.Minute;
        } else {
          let curData = new Array();
          curData.push(time);
          curData.push(data.Price);
          curData.push(data.Avg_price);
          curData.push(data.Volume);
          curData.push(data.Minute);
          dataset.data.push(curData);
        }
      });
      if (this.queryData.marketStatus == "8") {
        window.mChart.setOption(initHiddenDishOption(dataset, "hiddenDisk"));
      } else {
        window.mChart.setOption(initMOption(dataset, "hk"));
      }
      clearTimeout(window.timeOut || {});
      this.addSetTime();
    },
    addSetTime() {
      let _this = this;
      window.timeOut = setTimeout(() => {
        let dataset = _this.dataset;
        // let marketStatus = this.$store.state.GlobalData.minutelyCharts.data.query.marketStatus
        _this.addSetTime();
        // && marketStatus != '0'
        if (_this.isOutTime()) {
          return;
        }
        let newDataset = dataset.data[dataset.data.length - 1];
        let t1 = new Date();
        let minute =
          t1.getMinutes().toString().length === 2
            ? t1.getMinutes().toString()
            : "0" + t1.getMinutes().toString();
        // 分钟为个位数时得到的值前面补0
        let time1 = t1.getHours().toString() + minute;
        // console.log(Number(time1), Number(dataset.data[dataset.data.length - 1][0]));
        if (Number(time1) <= Number(dataset.data[dataset.data.length - 1][0])) {
          return false;
        }
        let curData = new Array();
        curData.push(time1);
        curData.push(newDataset[1]);
        curData.push(newDataset[2]);
        curData.push(0);
        curData.push(newDataset[4]);
        dataset.data.push(curData);
        if (_this.queryData.marketStatus == "8") {
          window.mChart.setOption(initHiddenDishOption(dataset, "hiddenDisk")); //暗盘数据补点
        } else {
          window.mChart.setOption(initMOption(dataset, "hk"));
        }
      }, 60000);
    },
    isOutTime() {
      let now = new Date();
      let nowTime = now.getTime();
      let year = now.getFullYear();
      let month = now.getMonth() + 1; //js从0开始取
      let date = now.getDate();
      let deadlineStr = year + "/" + month + "/" + date + " " + "09:30:00";
      let deadline = Date.parse(deadlineStr);
      if (now.getHours() >= 16 || nowTime < deadline || now.getHours() == 12) {
        return true;
      } else {
        return false;
      }
    },
    getStock(num) {
      if (this.$route.name === "indexStock") {
        document.getElementsByClassName("charts")[0].style.width =
          "width: calc(100% - 397px);";
        this.zsPage = true;
      }
      let url = "/quote-service/api/v1/minutely";
      let day;
      let dataset;
      if (num == 0) {
        day = 1;
        dataset = this.dataset;
      } else {
        day = 5;
        dataset = this.datasetFive;
      }
      this.axios({
        url: url,
        method: "get",
        params: {
          code: this.stockCode,
          day: day
        },
        type: "HQ"
      })
        .then(data => {
          this.stockData = data.data.query;
          let newData = data.data.Data;
          dataset.data = [];
          dataset.yestclose = this.stockData.hst_close;
          clearTimeout(window.timeOut || {});
          if (newData.length <= 0) {
            if (num == 0) {
              window.mChart.setOption(initMOption(dataset, "hk"));
            } else {
              window.minCharts.setOption(init5MOption(dataset, "hk"));
            }
            return false;
          }
          newData.map((value, index) => {
            if (value.Price < "0.001" || value.Avg_price < "0.001") {
              return;
            }
            if (value.symbol.length > 7) {
              value.Avg_price = 0;
            }
            var listAry = new Array();
            var t = new Date(value.Minute * 60 * 1000);
            listAry.push(t.getHours().toString() + t.getMinutes().toString());
            listAry.push(value.Price);
            listAry.push(value.Avg_price);
            listAry.push(value.Volume);
            listAry.push(value.Minute);
            dataset.data.push(listAry);
          });
          if (num == 0) {
            window.mChart.resize();
            window.mChart.clear();
            if (this.stockData.marketStatus == "8") {
              window.mChart.setOption(
                initHiddenDishOption(dataset, "hiddenDisk")
              );
            } else {
              window.mChart.setOption(initMOption(dataset, "hk"));
            }
          } else {
            window.minCharts.resize();
            window.minCharts.setOption(init5MOption(dataset, "hk"));
          }
          this.addSetTime();
        })
        .catch(function(err) {
          console.log(err);
        });

      // window.onresize = function() {
      //     this.mChart.resize();
      //   };
    },
    klineData(type) {
      this.kLineData = [];
      let cycleNum;
      if (type == 2) {
        cycleNum = "day";
        this.moreBtn = "更多";
      } else if (type == 3) {
        cycleNum = "week";
        this.moreBtn = "更多";
      } else if (type == 4) {
        cycleNum = "month";
        this.moreBtn = "更多";
      } else if (type == 5) {
        cycleNum = "year";
        this.moreBtn = "更多";
      } else if (type == "minute1") {
        cycleNum = "minute1";
        this.moreBtn = "1分钟";
      } else if (type == "minute5") {
        cycleNum = "minute5";
        this.moreBtn = "5分钟";
      } else if (type == "minute15") {
        cycleNum = "minute15";
        this.moreBtn = "15分钟";
      } else if (type == "minute30") {
        cycleNum = "minute30";
        this.moreBtn = "30分钟";
      } else if (type == "minute60") {
        cycleNum = "minute60";
        this.moreBtn = "60分钟";
      }
      let kLineData = this.kLineData;
      let kUrl = "/quote-service/api/v1/kline";
      window.newKChart = this.$echarts.init(this.$refs.newK);
      this.axios({
        url: kUrl,
        method: "get",
        params: {
          code: this.stockCode,
          cycle: cycleNum,
          page: 1
        },
        type: "HQ"
      })
        .then(data => {
          console.log(data);
          var dataK = data.data.Kline;
          dataK.map(function(value, index) {
            var KlineAry = new Array();
            var dateTime =
              value.date.slice(0, 4) +
              "-" +
              value.date.slice(4, 6) +
              "-" +
              value.date.slice(6, 8);
            // console.log(dateTime)
            KlineAry.push(dateTime);
            // KlineAry.push(value.open);
            KlineAry.push(Number(value.open));
            KlineAry.push(Number(value.close));
            KlineAry.push(Number(value.low));
            KlineAry.push(Number(value.hight));
            KlineAry.push(Number(value.volume));
            kLineData.push(KlineAry);
          });
          // console.log(kdata);
          window.newKChart.setOption(initKOption(kLineData));
          window.newKChart.resize();
        })
        .catch(function(err) {
          console.log(err);
        });
      window.onresize = null;
      window.onresize = function() {
        window.newKChart.resize();
      };
    },
    // 分钟K
    handleCommand(command) {
      this.isActive = 6;
      this.commandVal = command;

      this.klineData(command);
    },
    initWatch() {
      if (this.stockCode.length > 7) {
        this.changeIndex = true;
        this.$nextTick(() => {
          if (window.mChart) window.mChart.resize();
          if (window.minCharts) window.minCharts.resize();
        });
      } else {
        this.changeIndex = false;
        this.$nextTick(() => {
          if (window.mChart) window.mChart.resize();
          if (window.minCharts) window.minCharts.resize();
        });
      }
      if (this.isActive == 0 || this.isActive == 1) {
        this.datasetFive.data = [];
        this.getStock(this.isActive);
      } else if (this.isActive == 6) {
        this.klineData(this.commandVal);
      } else {
        this.klineData(this.isActive);
      }
    }
  },
  watch: {
    $route() {
      this.zsPage = this.$route.name === "indexStock";
    },
    "$store.state.GlobalData.currentStock.code"(val) {
      if (!this.isShow) {
        return;
      }
      this.stockCode = val;
      this.initWatch();
      this.zsIndex = val.length > 7;
    },
    minutely(val) {
      if (!this.isShow) {
        return;
      }
      if (this.dataset.data.length > 0) {
        this.drawMinute();
      }
    },
    "$store.state.system.showStockKline"(val) {
      if (val) {
        this.init();
      }
      this.isShow = val;
    }
  }
};
</script>
>
<style lang="scss" scoped>
.charts {
  // width: 100%;
  width: 1022px;
  position: absolute;
  right: 397px;
  top: 0;
  z-index: 101;
  &.zsKline {
    width: calc(100% - 397px);
  }
  .chartsTab {
    /deep/ .el-dropdown {
      color: #9195ac;
    }
    @include add-size($font_size_14);
    width: 100%;
    height: 32px;
    line-height: 32px;
    background: #2d3745;
    > span {
      padding: 0 16px;
      cursor: pointer;
    }
    .active {
      color: $activeColor;
    }
  }
  .Kline-div {
    // position: fixed;
    width: 1022px;
    height: 576px;
    // bottom: 0;
    // top: 0;
    .minute {
      width: 1022px;
      height: 576px;
      .m-line {
        height: 576px;
        width: 1022px;
        // margin-bottom: 1%;
      }
      .opacity {
        opacity: 0;
      }
      .K-line {
        height: 30%;
        width: 100%;
      }
    }

    .newK {
      width: 100%;
      height: 576px;
    }
  }
  .Kline-divZs {
    // position: fixed;
    width: 100%;
    height: 868px;
    // bottom: 0;
    // top: 0;
    .minute {
      width: 100%;
      height: 868px;
      .m-line {
        height: 868px;
        width: 100%;
        // margin-bottom: 1%;
      }
      .opacity {
        opacity: 0;
      }
      .K-line {
        height: 30%;
        width: 100%;
      }
    }

    .newK {
      width: 100%;
      height: 868px;
    }
  }
}
</style>
