<template>
  <div
    class="contextmenu"
    :style="{left: left+'px',top: top+'px'}"
    v-if="visible"
    @click.stop="stop"
  >
    <ul class="menu-ul">
      <li v-for="(li, i) in menu" :key="i" @click="menuAction(li)">{{li.label}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    menu: Array,
    distance: { type: [Number, String], default: 0 }
  },
  data() {
    return {
      left: 0,
      top: 0,
      visible: false,
      target: {}
    };
  },
  computed: {
    currentType() {
      // 当前选择的账户
      return this.$store.state.AccountLogin.currentType;
    }
  },
  mounted() {
    let _this = this;
    document.addEventListener("click", function() {
      _this.close();
    });
  },
  methods: {
    show(target) {
      this.left = target.left;
      this.top = target.top;
      this.type = target.type;
      this.visible = true;
      this.target = target.row;
      if (window.innerHeight - this.top < this.distance) {
        this.top -= this.distance;
      }
    },
    menuAction(menu) {
      this.$emit("menuSelect", menu, this.target);
      this.close();
    },
    close() {
      this.visible = false;
    },
    stop() {}
  }
};
</script>


<style lang="scss" scoped>
.contextmenu {
  position: fixed;
  left: 100px;
  top: 100px;
  border: 1px solid $borderColor;
  background-color: $blockBgColor;
  width: 100px;
  z-index: 999999;
  .menu-ul {
    li {
      line-height: 30px;
      cursor: pointer;
      @include add-size($font_size_14);
      text-align: center;
      &:hover {
        color: $white;
        background-color: $activeColor;
      }
    }
  }
}
</style>