<template>
  <div class="notice">
    <el-dialog
      theame
      center
      title="修改登录密码"
      :visible="dialogVisiblea"
      width="350px"
      top="16%"
      :modal="false"
      @close="closeDiv"
    >
      <div class="email-sub">
        <div class="email-title">
          <span>旧密码</span>
        </div>
        <div class="email-inpt-number">
          <el-input :type="showPassword1" v-model="odenumber" maxlength="20" placeholder="请输入旧的登录密码"></el-input>
          <i
            class="icon"
            :class="{password:showPassword1 ==='password'}"
            @click="changeShow('showPassword1')"
          ></i>
        </div>
        <div class="email-title">
          <span>新密码</span>
        </div>
        <div class="email-inpt-number">
          <el-input :type="showPassword2" v-model="newnumber" maxlength="20" placeholder="请输入8-20位新的登录密码"></el-input>
          <i
            class="icon"
            :class="{password:showPassword2 ==='password'}"
            @click="changeShow('showPassword2')"
          ></i>
        </div>
        <div class="email-title">
          <span>再次确认</span>
        </div>
        <div class="email-inpt-number">
          <el-input :type="showPassword3" v-model="againNumber" maxlength="20" placeholder="请确认您的登录密码"></el-input>
          <i
            class="icon"
            :class="{password:showPassword3 ==='password'}"
            @click="changeShow('showPassword3')"
          ></i>
        </div>
        <div class="btn-sue-q">
          <el-button hover @click="SureModify" :class="{action: canWriteFlag}">确定修改</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { reLogin } from "@/utils";
export default {
  name: "notice",
  components: {},
  data() {
    return {
      dialogVisiblea: false,
      odenumber: "",
      newnumber: "",
      againNumber: "",
      showPassword1: "password",
      showPassword2: "password",
      showPassword3: "password",
      canWriteFlag: false
    };
  },
  mounted() {},
  watch: {
    odenumber() {
      this.canWrite();
    },
    newnumber() {
      this.canWrite();
    },
    againNumber() {
      this.canWrite();
    }
  },
  methods: {
    canWrite() {
      if (this.odenumber && this.newnumber && this.againNumber) {
        this.canWriteFlag = true;
      } else {
        this.canWriteFlag = false;
      }
    },
    changeShow(tar) {
      let target = this[tar];
      target === "password" ? (target = "text") : (target = "password");
      this[tar] = target;
    },
    show() {
      this.dialogVisiblea = true;
    },
    closeDiv() {
      this.dialogVisiblea = false;
      this.odenumber = "";
      this.newnumber = "";
      this.againNumber = "";
    },
    SureModify() {
      try {
        if (!this.newnumber || !this.odenumber || !this.againNumber) {
          throw new Error("请输入密码！");
        }
        if (this.newnumber.length < 8) {
          throw new Error("密码长度在8-20之间~");
        }
        if (this.newnumber != this.againNumber) {
          throw new Error("两次输入密码不一致~");
        }
        this.axios({
          url: "/app/resetPassword.do",
          method: "post",
          params: {
            newPassword1: this.newnumber,
            newPassword2: this.againNumber,
            oldPassword: this.odenumber
          }
        }).then(res => {
          this.$message({
            message: "修改成功，请重新登录",
            type: "success"
          });
          setTimeout(() => {
            reLogin();
          }, 2000);
        });
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error",
          showClose: true
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/.el-dialog__title {
  @include add-size($font_size_14);
}
/deep/.el-dialog {
  left: 58px;
}
.email-sub {
  .email-title {
    width: 100%;
    height: 40px;
    padding-top: 16px;
    padding-left: 17px;
    > span {
      color: $white;
      @include add-size($font_size_14);
    }
  }
  .email-inpt-number {
    position: relative;
    width: 100%;
    height: 40px;
    padding-left: 17px;
    /deep/.el-input--medium .el-input__inner {
      width: 314px;
      height: 35px;
      background-color: #1d242e;
      border-top: 1px solid #1d242e;
      border-left: 1px solid #1d242e;
      border-right: 1px solid #1d242e;
      border-bottom: 1px solid #576373;
      padding-left: 0;
      border-radius: 0;
      color: $white;
      @include add-size($font_size_14);
    }
    /deep/.el-input__inner:hover {
      border-top: 1px solid #1d242e;
      border-left: 1px solid #1d242e;
      border-right: 1px solid #1d242e;
      border-bottom: 1px solid #576373;
    }
  }
  .btn-sue-q {
    width: 100%;
    height: 78px;
    padding-top: 20px;
    /deep/.el-button {
      width: 250px;
      height: 30px;
      background-color: #9195ac;
      margin-left: 50px;
      border: 1px solid #1d242e;
      color: $white;
      border: none;
      line-height: 30px;
      padding: 0;
      @include medium;
    }
    .action {
      @include linear;
    }
  }
}
.icon {
  position: absolute;
  width: 18px;
  height: 11px;
  background: url("~@/assets/images/login/show-icon.png") center no-repeat;
  background-size: 18px 11px;
  right: 30px;
  top: 50%;
  margin-top: -4px;
  cursor: pointer;
  &.password {
    background: url("~@/assets/images/login/hide-icon.png") center no-repeat;
    background-size: 18px 8px;
  }
}
</style>