<template>
  <div class="purse">
    <div class="base-info">
      <div class="draw-bar">
        <div class="unit">单位：港元</div>
        <div class="draw-btn" @click="draw">
          <img src="~@/assets/images/personal/draw-icon.png" class="draw-icon" alt />
          提现
        </div>
      </div>
      <div class="info-bar">
        <div class="info-li">
          <div class="li-name">钱包余额</div>
          <div class="li-value">{{baseInfo.extractValue | moneyFormat}}</div>
        </div>
        <div class="info-li hover" @click="showAccountBook">
          <div class="li-name">
            今日收入 {{baseInfo.commissionDayCount || 0}} 笔
            <i class="el-icon-arrow-right"></i>
          </div>
          <div class="li-value">{{(baseInfo.commissionDay || 0) | moneyFormat}}</div>
        </div>
        <div class="info-li hover">
          <div class="li-name">
            累计收入
            <!-- 3 笔
            <i class="el-icon-arrow-right"></i>-->
          </div>
          <div class="li-value">{{baseInfo.commissionall | moneyFormat}}</div>
        </div>
      </div>
    </div>
    <div class="table-area">
      <div class="income f-sub">
        <div class="chart">
          <div class="chart-title">
            <div class="chart-title-t">我的收入</div>
            <label class="picker-label" for="monthPicker">
              {{dailyAccount}}
              <i class="el-icon-caret-bottom arrow" :class="{active: monthSelect}"></i>
              <el-date-picker
                value-format="yyyy-MM"
                class="picker"
                id="monthPicker"
                v-model="dailyAccount"
                type="month"
                placeholder="选择月"
                @blur="monthSelect = false"
                @focus="monthSelect = true"
                @change="incomeMonthChange"
              ></el-date-picker>
            </label>
          </div>
          <div class="chart-total">
            <div class="chart-total-li">收入：{{cueData.count || 0}}笔</div>
            <div class="chart-total-li">金额：{{ cueData.commissionUser | moneyFormat}}</div>
          </div>
          <div class="chart-canvas">
            <div class="income-chart chart-canvas" v-if="showCanvas" ref="incomeChart"></div>
          </div>
          <div class="chart-table">
            <ul class="chart-table-ul">
              <li class="chart-table-li">
                <div class="chart-table-li_name">名称</div>
                <div class="chart-table-li_value center">收入</div>
                <div class="chart-table-li_percent right">占比</div>
              </li>
              <li class="chart-table-li" v-for="li in baseInfo.incomeData" :key="li.name">
                <div class="chart-table-li_name">{{li.name}}</div>
                <div class="chart-table-li_value center">{{li.value | moneyFormat}}</div>
                <div class="chart-table-li_percent right">{{li.percent}}%</div>
              </li>
            </ul>
          </div>
        </div>
        <div class="quantity-count">
          <div class="quantity-count-title">
            <div class="quantity-count-title-t">人数统计</div>
            <label class="picker-label" for="monthPicker2">
              {{dailyAccountCustomer}}
              <i
                class="el-icon-caret-bottom arrow"
                :class="{active: monthSelect}"
              ></i>
              <el-date-picker
                value-format="yyyy-MM"
                class="picker"
                id="monthPicker2"
                v-model="dailyAccountCustomer"
                type="month"
                placeholder="选择月"
                @blur="monthSelect = false"
                @focus="monthSelect = true"
                @change="getCustomerDataMonth"
              ></el-date-picker>
            </label>
          </div>
          <ul class="quantity-ul">
            <li class="quantity-li">
              <div class="quantity-li_name">注册人数</div>
              <div class="quantity-li_value">{{customerDataMonth.registerCustomerNo}}人</div>
            </li>
            <li class="quantity-li">
              <div class="quantity-li_name">开户人数</div>
              <div class="quantity-li_value">{{customerDataMonth.openAcountCustomerNo}}人</div>
            </li>
            <li class="quantity-li">
              <div class="quantity-li_name">入金人数</div>
              <div class="quantity-li_value">{{customerDataMonth.depositCustomerNo}}人</div>
            </li>
            <li class="quantity-li">
              <div class="quantity-li_name">活跃人数</div>
              <div class="quantity-li_value">{{customerDataMonth.customerOnlineNo}}人</div>
            </li>
          </ul>
        </div>
      </div>
      <div class="customer f-sub">
        <div class="chart">
          <div class="chart-title">
            <div class="chart-title-t">我的客户</div>
            <div class="customer-count hover" @click="showCustomerMask">
              {{customerNum.totalCustomerNo}}人
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
          <div class="chart-total">
            <div class="chart-total-li">总资产：{{customerNum.totalAssets | moneyFormat}}</div>
            <div class="chart-total-li">可用：{{ customerNum.totalAvailCapital | moneyFormat}}</div>
            <div class="chart-total-li">持仓：{{ customerNum.totalMarketValue | moneyFormat}}</div>
          </div>
          <div class="income-chart chart-canvas" v-if="showCanvas2" ref="customerChart"></div>
          <!-- <div class="chart-canvas"> -->
          <!-- </div> -->
          <div class="chart-table">
            <ul class="chart-table-ul">
              <li class="chart-table-li">
                <div class="chart-table-li_name">名称</div>
                <div class="chart-table-li_value center">人数</div>
                <div class="chart-table-li_percent right">占比</div>
              </li>
              <li class="chart-table-li" v-for="li in customerData" :key="li.name">
                <div class="chart-table-li_name">{{li.name}}</div>
                <div class="chart-table-li_value center">{{li.value}}</div>
                <div class="chart-table-li_percent right">{{li.percent}}%</div>
              </li>
            </ul>
          </div>
        </div>
        <div class="quantity-count">
          <div class="quantity-count-title">
            <div class="quantity-count-title-t">在线客户</div>
            <div class="online-count hover" @click="showOnlineCustomer">
              {{customerOnlineList.length}}人
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
          <ul class="quantity-ul">
            <li class="quantity-li">
              <div class="quantity-li_name">总资产</div>
              <div class="quantity-li_value">{{onlineAssets | moneyFormat}}</div>
            </li>
            <li class="quantity-li">
              <div class="quantity-li_name">可用</div>
              <div class="quantity-li_value">{{onlineAvailCapital | moneyFormat}}</div>
            </li>
            <li class="quantity-li">
              <div class="quantity-li_name">持仓</div>
              <div class="quantity-li_value">{{onlineMarketValue | moneyFormat}}</div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <el-dialog
      class="earning-dialog"
      theame
      center
      title="收益详情"
      :visible="earningDetailMask"
      width="620"
      top="15%"
      @close="earningDetailMask = false"
    >
      <div class="earning-content">
        <div class="export">导出</div>
        <el-table class="report-detail" :data="reportDetail.list" style>
          <el-table-column prop="customerName" label="客户"></el-table-column>
          <el-table-column prop="transactionAll" label="交易金额" align="center"></el-table-column>
          <el-table-column prop="count" label="交易次数" align="center"></el-table-column>
          <el-table-column prop="commissionUser" label="产生佣金" align="right"></el-table-column>
          <template slot="append">
            <infinite-loading
              ref="infinite"
              @infinite="getEarningDetail"
              :distance="20"
              spinner="bubbles"
              forceUseInfiniteWrapper=".report-detail .el-table__body-wrapper"
            >
              <loading></loading>
              <span slot="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results"></span>
            </infinite-loading>
          </template>
        </el-table>
      </div>
    </el-dialog>
    <!-- 提现弹框 -->
    <el-dialog
      class="draw-dialog"
      theame
      center
      title="提现"
      :visible="drawMask"
      width="455px"
      top="15%"
      @close="drawMask = false"
    >
      <div class="draw-header" slot="title">
        <div class="draw-record" @click="showDrawRecordList">提现记录</div>
        <div class="title-text">提现</div>
      </div>
      <div class="draw-content">
        <bank-select wd :bankDataArr="bankDataArr" v-model="currentBank" @add-card="addCard"></bank-select>
        <div class="money-input">
          <div class="input-t">
            <div class="input-t_text">提现金额</div>
            <div class="draw-type">
              <span>提现币种</span>
              <el-select v-model="drawData.type">
                <el-option
                  :label="op.label"
                  :value="op.value"
                  v-for="op in drawData.typeSelect"
                  :key="op.value"
                ></el-option>
              </el-select>
            </div>
          </div>
          <el-input
            class="money-in rmb"
            v-if="drawData.type == 2"
            v-model="drawData.money"
            type="number"
            maxlength="10"
          ></el-input>
          <el-input
            class="money-in hk"
            v-if="drawData.type == 0"
            v-model="drawData.money"
            type="number"
            maxlength="10"
          ></el-input>
          <div class="res-money">
            <div
              class="total-money"
            >可提金额 {{(drawData.type == 0 ? baseInfo.extractValue : baseInfo.extractValue * drawData.rate) | moneyFormat}}{{drawData.type == 0 ? '港币':'元'}}</div>
            <span class="tips" @click="drawTips = true">提现说明</span>
          </div>
        </div>
        <el-button class="action" @click="drawMoneyRequset">提现</el-button>
      </div>
    </el-dialog>
    <!-- 提现记录 -->
    <el-dialog
      class="draw-dialog"
      theame
      center
      title="提现记录"
      :visible="drawRecordMask"
      width="455px"
      top="15%"
      @close="drawRecordMask = false"
    >
      <div class="draw-header" slot="title">
        <div class="draw-record" @click="drawRecordMask = false">
          <i class="el-icon-arrow-left"></i>
        </div>
        <div class="title-text">提现记录</div>
      </div>
      <div class="draw-record-content">
        <ul class="record-ul">
          <li class="record-li title">
            <div class="record-li-money">金额</div>
            <div class="record-li-time">申请时间</div>
            <div class="record-li-status">状态</div>
          </li>
          <div class="scroll-area">
            <li class="record-li" v-for="(record, i) in drawRecordRequest.list" :key="i">
              <div class="record-li-money">{{record.money}}</div>
              <div class="record-li-time">{{record.createTime}}</div>
              <div class="record-li-status">{{record.examine | examine}}</div>
            </li>
            <infinite-loading
              slot="append"
              @infinite="getDrawRecordList"
              force-use-infinite-wrapper=".scroll-area"
              spinner="bubbles"
              :identifier="drawRecordRequest.infiniteId"
              class="loading"
              ref="infiniteLoading"
            >
              <span slot="no-more" class="no-more">—————— 加载完成 ———————</span>
              <span slot="no-results" class="no-result">暂无数据</span>
            </infinite-loading>
          </div>
        </ul>
      </div>
    </el-dialog>
    <!-- 提现说明 -->
    <el-dialog
      class="draw-dialog"
      theame
      center
      title="提现说明"
      :visible="drawTips"
      width="455px"
      top="15%"
      @close="drawTips = false"
    >
      <div class="draw-header" slot="title">
        <div class="draw-record" @click="drawTips = false">
          <i class="el-icon-arrow-left"></i>
        </div>
        <div class="title-text">提现说明</div>
      </div>
      <div class="draw-tips">
        <p class="tips-title">温馨提示：</p>
        <p>1、港股交易日12：00前提交出金申请，当天处理；12：00以后提交出金申请，下一个交易日处理。</p>
        <!-- <p>2、香港本地银行出金不收取手续费。</p> -->
        <p v-if="drawData.type == 0">2、海外银行账户出金不论币种收取50港币手续费，不包括银行收费。</p>
        <p v-if="drawData.type == 2">2、大陆银行账户出金收取10人民币手续费。</p>
        <!-- <p v-if="drawData.type == 0">4、港币不支持大陆银行账户出金。</p> -->
      </div>
    </el-dialog>

    <customerMask ref="customerMask" :customerList="customerList" :customerNum="customerNum" />
    <customerOnline ref="customerOnline" :customerOnlineList="customerOnlineList" />
    <accountBook :chartData="baseInfo.incomeData" ref="accountBook" />
  </div>
</template>

<script>
import bankSelect from "@/components/deal/bank-select";
import infiniteLoading from "vue-infinite-loading";
import customerMask from "@/components/mine/customer-mask";
import customerOnline from "@/components/mine/customer-online";
import accountBook from "@/components/mine/account-book";
export default {
  components: {
    bankSelect,
    infiniteLoading,
    customerMask,
    customerOnline,
    accountBook
  },
  data() {
    return {
      baseInfo: {
        commissionDay: 0,
        commissionall: 0,
        commissionuser: 0,
        extractValue: 0,
        commissionList: [],
        list: []
      },
      activeName: "first",
      value1: 0,
      dailyAccount: new Date().Format("yyyy-MM"),
      dailyAccountCustomer: new Date().Format("yyyy-MM"),
      monthSelect: false,
      option1: [
        {
          label: "全部奖励",
          value: 0
        },
        {
          label: "邀请人奖励",
          value: 1
        },
        {
          label: "佣金奖励",
          value: 2
        },
        {
          label: "合伙人奖励",
          value: 3
        },
        {
          label: "其他奖励",
          value: 4
        }
      ],
      recordData: [
        {
          name: "佣金奖励",
          money: "100",
          status: "已到账",
          time: "2019-08-30 10:30"
        },
        {
          name: "佣金奖励",
          money: "100",
          status: "已到账",
          time: "2019-08-30 10:30"
        },
        {
          name: "佣金奖励",
          money: "100",
          status: "已到账",
          time: "2019-08-30 10:30"
        },
        {
          name: "佣金奖励",
          money: "100",
          status: "已到账",
          time: "2019-08-30 10:30"
        }
      ],
      earningDetailMask: false,
      drawMask: false,
      bankDataArr: [],
      reportForm: {
        page: 1,
        size: 10,
        list: []
      },
      reportDetail: {
        id: "",
        page: 1,
        size: 10,
        list: []
      },
      rewardData: {
        rewardsId: "",
        createTime: "",
        page: 1,
        size: 10,
        list: []
      },
      pickerData: [],
      brokerageData: {
        // 佣金明细
        page: 1,
        size: 10,
        startTime: "",
        endTime: "",
        channel: 0
      },
      brokerageList: [],
      customerData: [
        { value: 0, name: "未开户", percent: 0 },
        { value: 0, name: "开户中", percent: 0 },
        { value: 0, name: "已开户", percent: 0 },
        { value: 0, name: "已入金", percent: 0 }
      ],
      currentBank: {},
      drawRecordMask: false,
      drawRecordRequest: {
        page: 1,
        size: 10,
        list: [],
        channel: 0,
        infiniteId: 0
      },
      drawTips: false,
      drawData: {
        type: 0,
        money: 0,
        typeSelect: [
          {
            label: "港币",
            value: 0
          },
          {
            label: "人民币",
            value: 2
          }
        ],
        rate: ""
      },
      showCanvas: false,
      showCanvas2: false,
      customerList: [],
      customerNum: {},
      customerDataMonth: {},
      customerOnlineList: []
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  activated() {
    this.$nextTick(() => {
      this.getBaseInfo();
      this.getCustomerData();
      this.getCustomerDataMonth();
      this.getCustomerOnline();
      // this.getBrokerageList();
    });
  },
  computed: {
    cueData() {
      return this.baseInfo.commissionList[0] || {};
    },
    onlineAssets() {
      let total = 0;
      this.customerOnlineList.forEach(a => {
        total += Number(a.assets);
      });
      return total;
    },
    onlineAvailCapital() {
      let total = 0;
      this.customerOnlineList.forEach(a => {
        total += Number(a.availCapital);
      });
      return total;
    },
    onlineMarketValue() {
      let total = 0;
      this.customerOnlineList.forEach(a => {
        total += Number(a.marketValue);
      });
      return total;
    }
  },
  methods: {
    incomeMonthChange(e) {
      // 收入月份选择
      // console.log(e)
      this.getBaseInfo();
    },
    showDrawRecordList() {
      this.drawRecordRequest.infiniteId++;
      this.drawRecordMask = true;
      this.drawRecordRequest.list = [];
      this.drawRecordRequest.page = 1;
    },
    getDrawRecordList($state) {
      // GET /order/message/selectRecordList
      let drawRecordRequest = this.drawRecordRequest;
      this.axios({
        url: "/order/message/selectRecordList",
        params: {
          page: drawRecordRequest.page,
          size: drawRecordRequest.size,
          channel: drawRecordRequest.channel
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (drawRecordRequest.page == 1) {
            drawRecordRequest.list = res.data.ret.list;
          } else {
            drawRecordRequest.list = drawRecordRequest.list.concat(
              res.data.ret.list
            );
          }
          drawRecordRequest.page++;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    drawMoneyRequset() {
      if (this.drawData.money <= 0) {
        this.$message({
          message: "请输入提现金额！",
          type: "error"
        });
        return false;
      }
      if (!this.currentBank.accountNo) {
        this.$message({
          message: "请选择银行账户",
          type: "error"
        });
        return false;
      }
      if (!this.currentBank.isHKCard == "0" && this.drawData.money <= 10) {
        this.$message({
          message: "大陆银行账户出金收取10人民币手续费",
          type: "error"
        });
        return false;
      }
      console.log(this.currentBank);
      // /order/message/saveRecord
      this.axios({
        url: "/order/message/saveRecord",
        method: "post",
        params: {
          money:
            this.drawData.type == 2
              ? Number(this.drawData.money / this.drawData.rate).toFixed(3)
              : this.drawData.money,
          bankAccount: this.currentBank.accountNo,
          companyBankId: this.currentBank.cardBindId,
          incomeType: 3, // 钱包提现
          isHKCard: this.currentBank.isHKCard,
          currency: 0,
          rate: this.drawData.type == 2 ? this.drawData.rate : 1,
          chooseCurrency: this.drawData.type
        },
        type: "host2"
      }).then(res => {
        this.$message({
          message: "提交成功",
          type: "success"
        });
        this.drawMask = false;
        this.drawData.money = 0;
        this.getBaseInfo();
      });
    },
    getBankCard() {
      this.axios({
        url: "/order/message/selectBankBind",
        method: "get",
        params: {
          type: 1
        },
        type: "host2"
      }).then(res => {
        this.bankDataArr = res.data.ret;
      });
    },
    showAccountBook() {
      this.$refs.accountBook.show();
    },
    showOnlineCustomer() {
      this.$refs.customerOnline.show();
    },
    showCustomerMask() {
      this.$refs.customerMask.show();
      this.getCustomerList();
    },
    setChart(ref, data) {
      if (!ref) {
        return false;
      }
      let incomeChart = this.$echarts.init(ref);
      incomeChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c} ({d}%)"
        },
        color: ["#FF1F57", "#801FFF", "#1F84FF", "#1FFFA9"],
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                formatter: "{d}%",
                borderBottomWidth: 1,
                color: "#fff"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: true,
                length2: 30
              }
            },
            data: data
          }
        ]
      });
    },
    changeDate(e) {
      this.brokerageData.startTime = e[0];
      this.brokerageData.endTime = e[1];
      this.brokerageData.page = 1;
      this.getBrokerageList();
    },

    getBrokerageList($state) {
      // 佣金明细
      let brokerageData = this.brokerageData;
      let proxyNumberId = localStorage.getItem("customerId");
      // console.log(proxyNumberId);
      this.axios({
        url: "/order/message/selectDetailedCommission",
        method: "get",
        params: {
          ...brokerageData,
          proxyNumberId
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.list && res.data.ret.list.length > 0) {
          if (this.brokerageData.page === 1) {
            this.brokerageList = res.data.ret.list;
          } else {
            this.brokerageList = this.brokerageList.concat(res.data.ret.list);
          }
          this.brokerageData.page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    },
    getBaseInfo() {
      // GET /order/message/selectListInfo
      this.showCanvas = false;
      this.axios({
        url: "/order/message/selectListInfo",
        params: {
          month: this.dailyAccount + "-01"
        },
        type: "host2"
      }).then(res => {
        this.showCanvas = true;
        this.$nextTick(() => {
          this.setIndexData(res.data.ret);
        });
      });
    },
    getCustomerData() {
      // 获取客户数据
      // GET /appcustomer/getMyCustomerCount2.do  客户数据统计
      /* 
      各个类型用户数量:totalCustomerNo    2未开户:registerCustomerNo  3开户中  openningAcountCustomerNo  4 已开户:openAcountCustomerNo 5 已入金:  depositCustomerNo
      */
      this.showCanvas2 = false;
      this.axios({
        url: "/appcustomer/getMyCustomerCount2.do",
        params: {},
        type: "host"
      })
        .then(res => {
          this.showCanvas2 = true;
          let data = res.data.data;
          this.customerNum = data;
          let total =
            data.totalCustomerNo +
              data.openningAcountCustomerNo +
              data.openAcountCustomerNo +
              data.depositCustomerNo || 1;
          if (total <= 0) {
            this.setChart(this.$refs.customerChart, this.customerData);
            return false;
          }
          this.customerData[0].value = data.registerCustomerNo;
          this.customerData[1].value = data.openningAcountCustomerNo;
          this.customerData[2].value = data.openAcountCustomerNo;
          this.customerData[3].value = data.depositCustomerNo;
          this.customerData.forEach(a => {
            a.percent = ((a.value * 100) / total).toFixed(2);
          });
          setTimeout(() => {
            this.setChart(this.$refs.customerChart, this.customerData);
          }, 500);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getCustomerDataMonth() {
      // 获取客户数据
      // GET /appcustomer/getMyCustomerCount2.do  客户数据统计
      /* 
      各个类型用户数量:totalCustomerNo    2未开户:registerCustomerNo  3开户中  openningAcountCustomerNo  4 已开户:openAcountCustomerNo 5 已入金:  depositCustomerNo
      */
      let dailyAccountCustomer = this.dailyAccountCustomer;
      this.axios({
        url: "/appcustomer/getMyCustomerCount2.do",
        params: {
          year: dailyAccountCustomer.slice(0, 4),
          month: dailyAccountCustomer.slice(5)
        },
        type: "host"
      })
        .then(res => {
          this.customerDataMonth = res.data.data;
        })
        .catch(err => {});
    },
    getCustomerList(data) {
      // GET /appcustomer/getMyTotalCustomerList2.do
      this.axios({
        url: "/appcustomer/getMyTotalCustomerList2.do",
        params: {},
        type: "host"
      }).then(res => {
        this.customerList = res.data.data || [];
      });
    },
    getCustomerOnline() {
      this.axios({
        url: "/appcustomer/getMyCustomerOnlineList.do",
        params: {
          page: 1,
          size: 1000000
        },
        type: "host"
      }).then(res => {
        this.customerOnlineList = res.data.data.list || [];
      });
    },
    setIndexData(baseInfo) {
      // let baseInfo = this.baseInfo;
      baseInfo.incomeData = [
        {
          value: 0,
          name: "股票"
        },
        { value: 0, name: "牛熊证" },
        { value: 0, name: "窝轮" },
        { value: 0, name: "其他" }
      ];
      /* 
           { value: 1234, name: "股票", percent: "25.00%" },
        { value: 1234, name: "牛熊证", percent: "25.00%" },
        { value: 1234, name: "窝轮", percent: "25.00%" },
        { value: 1234, name: "其他", percent: "25.00%" }
      */
      let total = 0;
      // 0  股票   1 牛熊    2 窝轮  4 其他
      baseInfo.commissionList.forEach(a => {
        if (a.tradeType == 0) {
          baseInfo.incomeData[0].value += a.commissionUser;
        }
        if (a.tradeType == 1) {
          baseInfo.incomeData[1].value += a.commissionUser;
        }
        if (a.tradeType == 2) {
          baseInfo.incomeData[2].value += a.commissionUser;
        }
        if (a.tradeType == 4) {
          baseInfo.incomeData[3].value += a.commissionUser;
        }
        total += a.commissionUser;
      });
      baseInfo.incomeData.forEach(b => {
        b.percent = ((b.value / (total || 1)) * 100).toFixed(2);
      });
      this.baseInfo = baseInfo;
      this.setChart(this.$refs.incomeChart, baseInfo.incomeData);
    },
    getReportFormList($state) {
      // GET /order/message/selectReportForm
      let reportForm = this.reportForm;
      this.axios({
        url: "/order/message/selectReportForm",
        method: "get",
        params: {
          size: reportForm.size,
          page: reportForm.page
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          reportForm.list = reportForm.list.concat(res.data.ret.list);
          reportForm.page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    },
    earningDetail(row) {
      this.reportDetail.id = row.time;
      this.reportDetail.page = 1;
      this.getEarningDetail();
      this.earningDetailMask = true;
    },
    getEarningDetail($state) {
      let reportDetail = this.reportDetail;
      // GET /order/message/selectReportInfo
      this.axios({
        url: "/order/message/selectReportInfo",
        method: "get",
        params: {
          size: reportDetail.size,
          page: reportDetail.page,
          time: reportDetail.id
        },
        type: "host2"
      }).then(res => {
        if (res.data.ret.list.length > 0) {
          if (reportDetail.page == 1) {
            reportDetail.list = [];
          }
          reportDetail.list = reportDetail.list.concat(res.data.ret.list);
          reportDetail.page++;
          if ($state) $state.loaded();
        } else {
          if ($state) $state.complete();
        }
      });
    },
    draw() {
      this.drawMask = true;
      this.getBankCard();
      this.getRate();
    },
    getRate() {
      // 获取汇率
      this.axios({
        url: "/fund/selectRate",
        method: "get",
        params: {},
        type: "host2"
      }).then(res => {
        this.drawData.rate = res.data.ret.ukToRmb;
      });
    },
    handleClick() {
      if (this.activeName === "third") {
        if (this.reportForm.list.length <= 0) {
          this.getReportFormList();
        }
      }
    },

    addCard() {
      this.drawMask = false;
      // this.$router.push({ path: "jy", query: { target: 7 } });
      this.$store.dispatch("CHANGE_MENU", {
        target: 2,
        timestamp: new Date().getTime()
      });
    }
  },
  filters: {
    examine(val) {
      let ex = ["待审核", "通过", "驳回"];
      return ex[val];
    }
  }
};
</script> 

