                    <span
                      v-show="item.value == 'yes' + item.index || item.newName"
                      >买{{ item.index }}</span
                    >
                  </span>
                  <span v-if="item.newName">{{ item.newName }}</span>
                  <span v-else-if="item.name">{{ item.id }}</span>
                  <span v-if="item.newName">{{ item.newName }}</span>
                  <span v-else>{{ item.name }}</span>
                </div>
              </div>
              <div class="allBuyBroker">
                <div
                  v-for="(item, index) in brokerSellList"
                  :key="index"
                  :title="item.name"
                >
                  <span>
                    <span
                      v-show="item.value == 'yes' + item.index || item.newName"
                      >卖{{ item.index }}</span
                    >
                  </span>
                  <span v-if="item.newName">{{ item.newName }}</span>
                  <span v-else-if="item.name">{{ item.id }}</span>
                  <span v-if="item.newName">{{ item.newName }}</span>
                  <span v-else>{{ item.name }}</span>
                </div>
              </div>
            </div>
          </div>

          <div class="moreBroker" @click="getMore">
            <span>收起</span>
          </div>
        </div>
        <div v-show="showDynamic && isVIP" class="dynamic">
          <div class="dynamicTit">
            <span>时间</span>
            <span>成交价</span>
            <span>成交量</span>
          </div>
          <div class="dynamicTxt" ref="dynamicTxt">
            <div class="dynamicLeft">
              <div class="dynamicCon" v-for="(item, i) in newTrade" :key="i">
                <span>
                  {{ item.time | timeSplit }}
                </span>
                <span
                  :class="
                    Number(item.price) > Number(bidAskData.hst_close)
                      ? 'red'
                      : Number(item.price) < Number(bidAskData.hst_close)
                      ? 'green'
                      : ''
                  "
                >
                  {{ item.price }}
                  <i>
                    <img
                      v-if="i > 0 && item.price != '--'"
                      :src="
                        item.direction == '1' ||
                        newTrade[i].price > newTrade[i - 1].price
                          ? require('@/assets/images/hq/up.png')
                          : item.direction == '0' ||
                            newTrade[i].price < newTrade[i - 1].price
                          ? require('@/assets/images/hq/down.png')
                          : ''
                      "
                      alt
                    />
                  </i>
                </span>
                <span
                  :class="
                    item.buysellflag == 'B'
                      ? 'red'
                      : item.buysellflag == 'S'
                      ? 'green'
                      : ''
                  "
                  >{{ item.volume | digitUnit }}</span
                >
              </div>
            </div>

            <div class="moreBroker" @click="showTimeTurnover">
              <span>更多</span>
            </div>
          </div>
        </div>
        <!-- <vipBtn v-if="!isVIP" /> -->
      </div>
    </div>
  </div>
</template>

<script>
// import vipBtn from "@/components/hq/vip-btn";
export default {
  name: "bidAsk",
  components: {
    // vipBtn
  },
  data() {
    return {
      // bidList: "", //买十档
      // askList: "", //卖十档
      // brokerSellList: "", //卖盘经纪队列
      // brokerBuyList: "", //买盘经纪队列
      // weibi: 0, //委比
      // tradeData: [], //逐笔成交数据
      showDynamic: true, //是否显示逐笔数据
      showImg: false, //自选
      chines: ["一", "二", "三", "四", "五", "六", "七", "八", "九", "十"],
      // length: 17,
      marketData: [
        "交易中",
        "午间休市",
        "停牌",
        "已收盘",
        "早盘竞价",
        "收盘竞价",
        "待上市",
        "未开盘",
        "暗盘交易中"
      ],
      isShow: false
    };
  },
  // props: ["bidAskData", "selfStock"],
  mounted() {
    this.$nextTick(() => {
      // if(this.$store.state.DealBar.dealVisible){
      //   this.length = 2
      // }else{
      //   if(this.isLogin){
      //     this.length = 15
      //   }else{
      //     this.length = 17
      //   }
      // }
      // this.show();
    });
  },
  computed: {
    selfStock() {
      return this.stockData.selfSelect;
    },
    stockData() {
      return this.$store.state.GlobalData.stockData;
    },
    bidAskData() {
      return this.stockData.query;
    },
    length() {
      let dealBar = this.$store.state.system.dealBar;
      if (dealBar.barShow) {
        return 2;
      } else if (dealBar.commonAccount || dealBar.organization) {
        return 15;
      } else {
        return 17;
      }
    },
    newTrade() {
      let wrapHeight = 0;
      let data = this.stockData.trade || [];
      if (data.length > this.length) {
        return data.slice(data.length - this.length);
      } else {
        let arr = [];
        for (let i = 0; i < this.length; i++) {
          if (data[i]) {
            arr.push(data[i]);
          } else {
            arr.push({
              direction: "--",
              kind: "--",
              price: "--",
              symbol: "--",
              ticker_id: "--",
              time: "--",
              volume: "--"
            });
          }
        }
        return arr;
      }
    },
    brokerSellList() {
      let broderSell = this.stockData.brokerSell.list || [];
      let ary = true;
      let i = 1;
      let list = broderSell.filter((a, b) => {
        if (a.id.indexOf("+") > -1) {
          a.value = a.id.slice(1);
          a.index = i;
          if (ary) {
            a.newName = "--";
          } else {
            a.newName = "";
          }
          ary = true;
          i++;
        } else {
          if (ary) {
            a.value = "yes" + i;
          } else {
            a.value = "";
          }
          ary = false;
          a.index = i;
        }
        return a;
      });
      // return list.reverse();
      //卖盘经纪队列不应该反转，第一条数据要放在第一位
      return list;
    }, //卖盘经纪队列
    brokerBuyList() {
      let broderBuyList = this.stockData.brokerBuy.list || [];
      let ary = true;
      let i = 1;
      let list = broderBuyList.filter((a, b) => {
        if (a.id.indexOf("-") > -1) {
          a.value = a.id.slice(1);
          a.index = i;
          if (ary) {
            a.newName = "--";
          } else {
            a.newName = "";
          }
          ary = true;
          i++;
        } else {
          if (ary) {
            a.value = "yes" + i;
          } else {
            a.value = "";
          }
          ary = false;
          a.index = i;
        }
        return a;
      });
      return list;
    }, //买盘经纪队列
    bidList() {
      if (this.isVIP && this.stockData.bidlist.length >= 5) {
        return this.stockData.bidlist;
      } else {
        let arr = [];
        for (let i = 0; i < 10; i++) {
          arr.push({
            num: "--",
            price: "0",
            qty: "--"
          });
        }
        return arr;
      }
    },
    askList() {
      let asklist = this.stockData.asklist.filter(a => {
        return a;
      });
      if (this.isVIP && this.stockData.asklist.length >= 5) {
        return asklist.reverse();
      } else {
        let arr = [];
        for (let i = 0; i < 10; i++) {
          arr.push({
            num: "--",
            price: "0",
            qty: "--"
          });
        }
        return arr;
      }
    },
    weibi() {
      let bidNum = 0;
      let askNum = 0;
      this.bidList.map((value, index) => {
        bidNum = bidNum + Number(value.qty);
      });
      this.askList.map((value, index) => {
        askNum = askNum + Number(value.qty);
      });
      if (bidNum >= 0) {
        return Number((bidNum / (bidNum + askNum)).toFixed(2));
      } else {
        return "0.50";
      }
    },
    isVIP() {
      // 港股行情VIP
      return true;
      return this.$store.state.AccountLogin.identityData.hasHkValue ? 1 : 0;
    },
    currentStock() {
      return this.$store.state.GlobalData.currentStock.code;
    }
  },
  methods: {
    gotoWolun() {
      let stockCodeList = {
        stockCode: this.stockData.symbol,
        timestamp: new Date().getTime()
      };
      window.stockCodeList = stockCodeList;
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 5,
        timestamp: new Date().getTime() - 11 * 1000
      });
    },
    gotoNiuxiong() {
      let stockCodeList = {
        stockCode: this.stockData.symbol,
        timestamp: new Date().getTime()
      };
      window.stockCodeList = stockCodeList;
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 6,
        timestamp: new Date().getTime() - 11 * 1000
      });
    },
    gotoJie() {
      let stockCodeList = {
        stockCode: this.stockData.symbol,
        timestamp: new Date().getTime()
      };
      window.stockCodeList = stockCodeList;
      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 1,
        b: 7,
        timestamp: new Date().getTime() - 11 * 1000
      });
    },
    trade() {},
    showRemind() {
      // this.$emit("remind");
      this.$refs.remind.show();
    },
    //自选
    selfSelect() {
      // this.showImg = !this.showImg;
      let time = new Date().getTime();
      if (!this.selfStock) {
        this.selfData(1);
      } else {
        this.selfData(0);
      }
      this.$store.dispatch("CHANGE_SelfStatus_DATA", { timestamp: time });
    },
    linkMarket() {
      // this.$store.dispatch("CHANGE_ROUTER_TARGET", {
      //   a: 10,
      //   timestamp: new Date().getTime() - 1000 * 10
      // });
      // setTimeout(() => {
      this.$router.push({ path: "mine", query: { target: "market" } });
      // }, 100);
    },
    getData() {
      this.bidAskList();
      this.getStockQuery();
      this.brokerList();
      this.tradeList();
    },
    showTimeTurnover() {
      this.$store.dispatch("SHOW_TIME_TURNOVER", {
        show: true,
        data: this.bidAskData
      });
    },
    // 获取快照数据
    getStockQuery() {
      let url = "/quote-service/api/v1/stockquery";
      let data = {
        codelist: this.currentStock
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        this.$store.dispatch("SET_STOCK_DATA", {
          query: res.data.Data[0]
        });
      });
    },
    //买卖十档
    bidAskList() {
      let url = "/quote-service/api/v1/order";
      let data = {
        code: this.currentStock
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        if (res.data.Data) {
          this.$store.dispatch("SET_STOCK_DATA", {
            asklist: res.data.Data[0].asklist,
            bidlist: res.data.Data[0].bidlist
          });
        } else {
          let obj = {
            asklist: [],
            bidlist: []
          };
          this.$store.dispatch("SET_STOCK_DATA", obj);
        }
      });
    },
    bidAskRender(bidList, askList) {
      // console.log(res);
      this.bidList = bidList; //买十档
      this.askList = askList.reverse(); //卖十档
      let bidNum = 0;
      let askNum = 0;
      this.bidList.map((value, index) => {
        bidNum = bidNum + Number(value.qty);
      });
      this.askList.map((value, index) => {
        askNum = askNum + Number(value.qty);
      });
      this.weibi = Number((bidNum / (bidNum + askNum)).toFixed(2));

      // 同步交易窗口买卖五档数据
      let ul = this.askList.slice(0, 5).concat(this.bidList.slice(0, 5));
      this.$store.dispatch("UPDATE_BUY_SELL", ul);
      this.$store.dispatch("UPDATE_CURRENT_STOCK", this.bidAskData);
    },
    // 获取经纪队列
    brokerList() {
      let url = "/quote-service/api/v1/broker";
      let data = {
        code: this.currentStock
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        if (res.data.Data) {
          this.$store.dispatch("SET_STOCK_DATA", {
            brokerBuy: res.data.Data[0] || {}
          });
          this.$store.dispatch("SET_STOCK_DATA", {
            brokerSell: res.data.Data[1] || {}
          });
        } else {
          this.$store.dispatch("SET_STOCK_DATA", {
            brokerBuy: { list: [] }
          });
          this.$store.dispatch("SET_STOCK_DATA", {
            brokerSell: { list: [] }
          });
        }
      });
    },
    // 逐笔成交数据
    tradeList() {
      let url = "/quote-service/api/v1/trade";
      let data = {
        code: this.currentStock,
        ticker_id: 0
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        this.$store.dispatch("SET_STOCK_DATA", {
          trade: res.data.Data || []
        });
      });
    },
    //获取更多经纪队列
    getMore() {
      this.showDynamic = !this.showDynamic;
    },
    //默认滚动到底部
    scrollToBottom() {
      let dom = this.$refs.sellDom;
      this.$nextTick(() => {
        //页面渲染完之后的操作(如果不使用$nextTick方法则scrollTop一直为0，无法赋值)
        dom.scrollTop = dom.scrollHeight;
      });
    }
  },
  activated() {
    this.active = true;
  },
  deactivated() {
    this.active = false;
  },
  watch: {
    "$store.state.system.showStockQuery"(val) {
      if (val) {
        this.getData();
      }
      this.isShow = val;
    },
    "$store.state.GlobalData.currentStock.code"(val) {
      if (!this.isShow) {
        return false;
      }
      if (val.indexOf("hk") === -1) {
        return false;
      }
      if (this.isShow) {
        this.getData();
      }
    },
    "$store.state.DealBar.dealVisible"(val) {
      console.log(val, 1053);
      if (val) {
        this.length = 2;
      } else {
        if (this.isLogin) {
          this.length = 15;
        } else {
          this.length = 17;
        }
      }
    },
    isLogin(val) {
      if (val) {
        this.length = 2;
      } else {
        this.length = 17;
      }
    }
  },
  filters: {
    timeSplit(val) {
      // item.time.slice(8, 10) }}:{{ item.time.slice(10, 12) }}:{{ item.time.slice(12, 14)
      if (val == "--") {
        return val;
      } else {
        return `${val.slice(8, 10)}:${val.slice(10, 12)}:${val.slice(12, 14)}`;
      }
    }
  }
};
</script>

