    border-top: 1px solid #323840;
    .allBrokerDivBg {
      position: relative;
      .allBrokerDiv {
        @include flex-box;
        justify-content: space-between;
        overflow: auto;
        height: 490px;
        position: relative;
        z-index: 7;
        .allBuyBroker {
          height: 490px;
