                width: 90px;
                // overflow: hidden;
                // white-space: nowrap;
                // text-overflow: ellipsis;
              }
            }
          }
        }
      }
      &:before {
        position: absolute;
        width: 50%;
        height: 100%;
        left: 0;
        top: 0;
        background: $red;
        content: "";
      }
      &:after {
        position: absolute;
        width: 50%;
        height: 100%;
        right: 0;
        top: 0;
        background: $green;
        content: "";
      }
    }

    .green {
      background: $green;
