      }

      .moreBroker {
        width: 24px;
        position: relative;
        background: #252e3b;
        color: #fff;
        cursor: pointer;
        > span {
          width: 12px;
          display: -webkit-box;
          position: absolute;
          top: 44%;
          right: 6px;
        }
