<template>
  <div class="news-detail">
    <el-dialog
      class="news-detail-dialog"
      theame
      center
      title
      :visible="isShow"
      width="991px"
      top="36px"
      @close="close"
    >
      <div class="news-content">
        <div class="news-title">{{newsData.newsTitle}}</div>
        <div class="base-info">
          <div class="news-origin">来源：{{newsData.newsSource}}</div>
          <div class="news-time">{{newsData.newsPubTime}}</div>
        </div>
        <!-- <div class="news-article" v-html="newsData.article"></div> -->
        <div
          class="news-article text01"
          v-html="newsData._content"
          ref="newsContent"
          v-if="showType === 1"
        ></div>
        <webview
          class="webview"
          :src="newsData._content"
          style="display:inline-flex;"
          v-if="showType === 0"
        ></webview>
        <div class="prev">
          <div class="line-btn" @click="initMask(newsData.prev)">上一篇</div>
          <div
            class="l-title"
            @click="initMask(newsData.prev)"
            :title="newsData.prev.newsTitle"
          >{{newsData.prev.newsTitle || '无'}}</div>
        </div>
        <div class="next">
          <div class="line-btn" @click="initMask(newsData.next)">下一篇</div>
          <div
            class="l-title"
            @click="initMask(newsData.next)"
            :title="newsData.next.newsTitle"
          >{{newsData.next.newsTitle || '无'}}</div>
        </div>
      </div>
    </el-dialog>
    <el-image-viewer
      v-if="showViewer"
      :on-close="closeViewer"
      :zIndex="100000"
      :url-list="imgUrls"
    />
  </div>
</template>

<script>
import ElImageViewer from "@/components/element-ui/image/image-viewer";

export default {
  name: "news-detail",
  components: {
    ElImageViewer
  },
  props: {
    newsList: Array
  },
  data() {
    return {
      isShow: false,
      newsId: "",
      newsData: {
        prev: {},
        next: {}
      },
      listData: {},
      showViewer: false,
      imgUrls: [],
      showType: 1
    };
  },
  mounted() {},
  watch: {},
  methods: {
    setBrother() {
      // 设置上一篇、下一篇
      let newsList = this.newsList;
      newsList.forEach((a, b) => {
        if (a.newsId === this.listData.newsId) {
          if (b === 0) {
            this.newsData.prev = {};
          } else {
            this.newsData.prev = newsList[b - 1];
          }
          if (b === newsList.length - 1) {
            this.newsData.next = {};
          } else {
            this.newsData.next = newsList[b + 1];
          }
        }
      });
    },
    onPreview() {
      this.showViewer = true;
    },
    closeViewer() {
      this.showViewer = false;
    },
    initMask(news) {
      if (news.newsId) {
        this.listData = news;
        this.getNewsDetail();
      }
    },
    prev() {
      this.initMask(this.newsData.prev);
    },
    next() {
      this.initMask(this.newsData.next);
    },
    getNewsDetail() {
      // GET /news/title/selectNewListInfo
      let listData = this.listData;
      this.axios({
        url: "/news/title/selectNewListInfo",
        method: "get",
        params: {
          newsOwnId: listData.newsId
        },
        type: "hostNews"
      }).then(res => {
        if (res.data.ret.content.indexOf("<!DOCTYPE html>") >= 0) {
          res.data.ret._content = res.data.ret.newsContent;
        } else {
          res.data.ret._content = res.data.ret.content;
        }

        this.newsData = { ...listData, ...res.data.ret };
        if (this.newsData._content.indexOf("mp.weixin.qq.com") != -1) {
          this.showType = 0;
        } else {
          this.showType = 1;
        }
        this.$nextTick(() => {
          this.$refs.newsContent.scrollTop = 0;
          this.setImgPreview();
        });
        this.setBrother();
      });
    },
    setImgPreview() {
      let imgs = this.$refs.newsContent.getElementsByTagName("img");
      let _this = this;
      for (let i = 0; i < imgs.length; i++) {
        imgs[i].setAttribute("index", i);
        // 处理图片域名重复问题
        let src = imgs[i].src;
        let o = src.split("://");
        imgs[i].src = "http://" + o[o.length - 1];

        imgs[i].onclick = function() {
          let index = Number(this.getAttribute("index"));
          let src = this.src;
          let left = [];
          let right = [];
          // 重置当前显示图片
          for (let m = 0; m < imgs.length; m++) {
            let _index = Number(imgs[m].getAttribute("index"));
            if (_index > index) {
              left.push(imgs[m].src);
            } else if (_index < index) {
              right.push(imgs[m].src);
            }
          }
          _this.imgUrls = [src, ...left, ...right];
          _this.onPreview();
        };
      }
    },
    show(news) {
      this.isShow = true;
      this.initMask(news);
    },
    close() {
      this.isShow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.news-detail {
  outline: none;
  .news-detail-dialog {
    /deep/ .el-dialog {
      height: calc(100% - 76px);
      margin-bottom: 0;
      .news-content {
        padding: 30px 20px 10px;
        @include add-size($font_size_14);
        color: $defaultColor;
        .news-title {
          @include add-size($font_size_20);
          color: $white;
        }
        .base-info {
          @include flex-center;
          margin-top: 24px;
          .news-origin {
            margin-right: 59px;
          }
          .news-time {
            color: $white;
          }
        }
        .news-article {
          margin-top: 24px;
          height: 672px;
          margin-bottom: 30px;
          line-height: 30px;
          overflow-y: auto;
        }
        .prev,
        .next {
          @include flex-center;
          margin-bottom: 30px;
          cursor: pointer;
          .line-btn {
            width: 72px;
            height: 28px;
            border: 1px solid $defaultColor;
            border-radius: 6px;
            text-align: center;
            line-height: 26px;
            margin-right: 27px;
          }
          .l-title {
            @include add-size($font_size_18);
            color: $white;
            @include single-line-ellipsis;
            max-width: 800px;
            &:hover {
              color: $activeColor;
            }
          }
        }
        .text01 {
          img {
            max-width: 100%;
          }
        }
        .webview {
          width: 950px;
          height: 700px;
          padding: 20px 0;
        }
      }
    }
  }
}
</style>
