<template>
  <div class="account-login">
    <div class="step-item">
      <el-dialog
        theame
        title="提示：需登录交易账户，才能进行交易操作"
        :visible="accountLogin && step === 0"
        width="470px"
        height="350px"
        top="15%"
        :modal="false"
        class="account-dialog"
        :close-on-click-modal="false"
        @close="closeLoginMask"
      >
        <div class="account-content">
          <div class="step-title">交易账号登录</div>
          <el-form :model="loginData" :rules="formRules" ref="loginData">
            <el-form-item label="账号" class="account" prop="account">
              <!-- <el-input
                class="mobile-value"
                v-model="loginData.account"
                placeholder="请输入账户"
                maxlength="6"
                readonly
              ></el-input>-->
              <el-select
                placeholder="请先开户"
                v-model="loginData.account"
                @change="loginData.password = ''"
              >
                <el-option
                  :label="op.value"
                  :value="op.value"
                  v-for="op in accountSelectData"
                  :key="op.value"
                >
                  <span class="option-value">{{ op.value }}</span>
                  <span class="option-label">{{ op.label }}</span>
                </el-option>
              </el-select>

              <!-- <i class="icon"></i> -->
            </el-form-item>
            <el-form-item label="密码" prop="password" class="password">
              <el-input
                class="password-value"
                :type="showPassword"
                v-model="loginData.password"
                placeholder="请输入登录密码"
                @keyup.enter.native="loginSubmit"
              ></el-input>
              <i
                class="icon"
                :class="{ password: showPassword === 'password' }"
                @click="
                  showPassword === 'password'
                    ? (showPassword = 'text')
                    : (showPassword = 'password')
                "
              ></i>
            </el-form-item>
            <el-button class="action login-submit" hover @click="loginSubmit"
              >确定</el-button
            >
            <div class="forget">
              <span class="register" @click="showForget('ewm')">马上开户</span>
              <span @click="showForget('forget')">忘记密码?</span>
            </div>
            <div class="login-tips">
              <span class="tips-t">点击登录按钮，即表示同意</span>
              <span class="tips-protocol" @click="showForget('statement')"
                >《交易免责声明》</span
              >
            </div>
          </el-form>
        </div>
      </el-dialog>
    </div>
    <Forget ref="forget" />
    <sendCode ref="sendCode" @login="codeLogin" />
    <tionPassword ref="tionPassword" />
  </div>
</template>

<script>
import Forget from "./forget";
import sendCode from "./send-code";
import tionPassword from "../setting/subfile/tionPassword";

export default {
  components: { Forget, sendCode, tionPassword },
  data() {
    return {
      step: 0,
      loginData: {
        account: "",
        password: ""
      },
      showPassword: "password",
      formRules: {
        mobile: [
          { required: false, message: "请输入正确的手机号", trigger: "blur" },
          {
            pattern: /^[1][3-9]\d{9}$|^([6|9])\d{7}$|^[0][9]\d{8}$|^[6]([8|6])\d{5}$/,
            message: "请输入正确的手机号",
            trigger: "blur"
          }
        ],
        vcode: [
          {
            required: true,
            message: "请输入验证码",
            trigger: "blur"
          }
        ],
        password: [{ required: false, message: "请输入密码", trigger: "blur" }]
      },
      accountSelectData: []
    };
  },
  computed: {
    accountLogin() {
      return this.$store.state.system.dealBar.loginShow;
    },
    accountData() {
      return this.$store.state.system.accountInfo;
    },
    dealBar() {
      return this.$store.state.system.dealBar;
    }
  },
  watch: {
    accountLogin(v) {
      if (v) {
        this.setAccount();
      }
    }
  },
  activated() {},
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    // changeAccount(e){
    //   console.log(e)
    //   // SET_CURRENT_TYPE
    //   // this.accountSelectData
    // },
    setAccount() {
      // institutionalAccount 机构账号  ebokerUsername 个人账号
      let identityData = this.accountData;
      if (identityData.idCardCode) {
        this.accountSelectData = [];
        this.loginData.account =
          identityData.ebokerUsername || identityData.institutionalAccount;
        if (identityData.ebokerUsername) {
          this.accountSelectData.push({
            value: identityData.ebokerUsername,
            label: "标准账户"
          });
        }
        if (identityData.institutionalAccount) {
          this.accountSelectData.push({
            value: identityData.institutionalAccount,
            label: "机构账户"
          });
        }
      } else {
        // 无交易账户，提示在app上申请
        this.$message({
          message: "未开通交易账户，请在APP上申请！",
          type: "error"
        });
      }
    },
    closeLoginMask() {
      this.$store.dispatch("SET_DEAL_BAR", { loginShow: false });
    },
    organizationLogin() {
      let loginData = this.loginData;
      this.axios({
        url: "/tradeAccountLogin.do",
        method: "post",
        params: {
          appUserName: loginData.account,
          appPassword: loginData.password
        },
        type: "organization"
      }).then(res => {
        // this.$store.dispatch("SET_ORGANIZATION_DATA", res.data.data);
        this.$store.dispatch("SET_DEAL_BAR", { organization: true });

        this.closeLoginMask();
        this.loginData.password = "";
        this.$message({
          message: "登录成功",
          type: "success"
        });
        setTimeout(() => {
          this.$store.dispatch("SET_DEAL_BAR", {barShow: true});
          this.$store.dispatch("SET_DEAL_BAR", { currentType: 1 });
        }, 500);
      });
    },
    loginSubmit() {
      let loginData = this.loginData;
      let currentType = 0;
      if (!loginData.password) {
        this.$message({
          message: "请输入密码",
          type: "error"
        });
        return false;
      }
      this.accountSelectData.forEach(a => {
        if (a.value === loginData.account) {
          if (a.label == "标准账户") {
            currentType = 0;
          } else {
            currentType = 1;
          }
        }
      });
      if (currentType == 0) {
        if (this.dealBar.commonAccount) {
          this.$message({
            message: "标准账户已经登录，请切换机构账户",
            type: "error"
          });
          return false;
        }
      } else {
        if (this.dealBar.organization) {
          this.$message({
            message: "机构账户已经登录，请切换标准账户",
            type: "error"
          });
          return false;
        } else {
          // 登录机构账户
          this.organizationLogin();
          return false;
        }
      }

      this.$loading({
        text: "loading...",
        fullscreen: true,
        background: "transparent"
      });

      this.axios({
        url: "/tradeLogin.do",
        method: "post",
        params: {
          userNo: loginData.account,
          password: loginData.password,
          type: "0"
        },
        type: "hostEbroker",
        hideMessage: true
      })
        .then(res => {
          console.log(res);
          // clearTimeout(timer);
          this.$loading().close();
          this.$store.dispatch("SET_DEAL_BAR", {
            commonAccount: true
          });
          // this.$refs.sendCode.close();
          this.closeLoginMask();
          this.loginData.password = "";
          this.$message({
            message: "登录成功",
            type: "success"
          });
          localStorage.setItem("lockTime", Date.now());
          setTimeout(() => {
            this.$store.dispatch("SET_DEAL_BAR", {barShow: true});
            this.$store.dispatch("SET_DEAL_BAR", { currentType: 0 });
          }, 500);
          // if (res.data.code == 84) {
          //   let mobile = res.data.data.NotifyAddress;
          //   this.$message({
          //     message: "验证码发送成功！",
          //     type: "success"
          //   });
          //   this.$refs.sendCode.show(mobile);
          // } else {
          //   this.$message({
          //     message: "网络异常，请重新登录！",
          //     type: "error"
          //   });
          // }
        })
        .catch(err => {
          console.log(err);
          this.$loading().close();
          if (err.data.code == 12) {
            // 用户首次登录  需修改密码
            this.$message({
              showClose: true,
              message: "首次登录需修改密码！",
              type: "error"
            });
            this.$refs.tionPassword.show();
          } else {
            this.$message({
              showClose: true,
              message: err.data.message || err.data.msg,
              type: "error"
            });
          }
        });
    },
    codeLogin(code) {
      let loginData = this.loginData;
      this.$loading({
        text: "loading...",
        fullscreen: true,
        background: "transparent"
      });
      this.axios({
        url: "/twoLogin.do",
        method: "post",
        params: {
          loginID: loginData.account,
          tokenCode: code
        },
        type: "hostEbroker"
      })
        .then(res => {
          this.$loading().close();
          this.$store.dispatch("updateAccountData", res.data.data);
          this.$refs.sendCode.close();
          this.closeLoginMask();
          this.loginData.password = "";
          this.$message({
            message: "登录成功",
            type: "success"
          });
          setTimeout(() => {
            this.$store.dispatch("SET_DEAL_BAR", {barShow: true});
            this.$store.dispatch("SET_CURRENT_TYPE", 0);
          }, 500);
        })
        .catch(err => {
          this.$loading().close();
        });
    },
    showForget(flag) {
      this.$refs.forget.show(flag);
    }
  }
};
</script>

<style lang="scss" scoped>
.account-login {
  .account-dialog {
    /deep/ .el-dialog {
      .el-dialog__title {
        @include add-size($font_size_12);
        padding-left: 15px;
      }
      .step-title {
        @include add-size($font_size_18);
        color: $white;
        text-align: center;
        padding-top: 30px;
        padding-bottom: 10px;
      }
      .el-form-item {
        @include flex-center;
        width: 230px;
        border-bottom: 1px solid #576373;
        margin: 0 auto;
        height: 48px;
        position: relative;
      }
      .el-input__inner {
        background-color: inherit;
        border: none;
        width: 190px;
        border-radius: 0;
        padding: 0 10px;
        color: $white;
      }
      .login-submit {
        width: 230px;
        height: 30px;
        line-height: 30px;
        margin: 36px auto 0;
        padding: 0;
        display: block;
        @include add-size($font_size_14);
      }
      .account {
        .icon {
          position: absolute;
          width: 14px;
          height: 13px;
          background: url("~@/assets/images/login/user-icon.png") 0 center
            no-repeat;
          background-size: 14px 13px;
          right: 6px;
          top: 50%;
          margin-top: -6px;
        }
      }
      .password {
        .icon {
          position: absolute;
          width: 18px;
          height: 11px;
          background: url("~@/assets/images/login/show-icon.png") 0 center
            no-repeat;
          background-size: 18px 11px;
          right: 5px;
          top: 50%;
          margin-top: -4px;
          cursor: pointer;
          &.password {
            background: url("~@/assets/images/login/hide-icon.png") 0 center
              no-repeat;
            background-size: 18px 8px;
            right: 5px;
          }
        }
      }
      .forget {
        @include add-size($font_size_14);
        width: 230px;
        text-align: right;
        margin: 14px auto 0;
        @include flex-center;
        justify-content: space-between;
        span {
          cursor: pointer;
          color: $activeColor;
          opacity: 0.8;
          &:hover {
            opacity: 1;
          }
        }
      }
      .login-tips {
        @include add-size($font_size_14);
        padding-left: 18px;
        .tips-protocol {
          color: $activeColor;
          cursor: pointer;
          opacity: 0.8;
          &:hover {
            opacity: 1;
          }
        }
        margin-top: 39px;
        margin-bottom: 15px;
      }
    }
  }
  /deep/ .notice {
    .el-dialog {
      left: auto;
    }
  }
}
.option-value {
  width: 70px;
  display: inline-block;
}
</style>
