<template>
  <div class="part-list">
    <div class="part-ul" v-show="!showDetail">
      <div class="pro-li" v-for="(li, i) in listData" :key="i" @click="checkDetail(li)">
        <div class="li-title">{{li.title}}</div>
      </div>
    </div>
    <div class="li-detail" v-show="showDetail">
      <div class="back">
        <i class="el-icon-arrow-left arrow" @click="showDetail = false"></i>
        <div class="sub-title">{{currentTitle}}</div>
      </div>
      <div class="current" v-html="currentDetail"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    listData: Array
  },
  data() {
    return {
      showDetail: false,
      currentDetail: "",
      currentTitle: ""
    };
  },
  methods: {
    close(e) {},
    checkDetail(li) {
      this.showDetail = true;
      this.currentDetail = li.content;
      this.currentTitle = li.title;
    }
  }
};
</script>

<style lang="scss" scoped>
.part-list {
  padding-left: 30px;
  .part-ul {
    .pro-li {
      cursor: pointer;
      color: $white;
      height: 61px;
      border-bottom: 1px solid #323840;
      line-height: 60px;
      &:hover {
        color: $activeColor;
      }
    }
  }
  .li-detail {
    .back {
      @include flex-center;
      .arrow {
        color: $defaultColor;
        cursor: pointer;
        line-height: 32px;
        padding: 20px 0;
        @include add-size($font_size_20);
        &:hover {
          color: $activeColor;
        }
      }
      .sub-title{
        color: $white;
        padding-left: 20px;
      }
    }
    .current {
      max-height: 516px;
      overflow-y: auto;
      line-height: 24px;
      color: $defaultColor;
      padding-right: 24px;
      margin-right: 5px;
      /deep/span {
        color: $white;
        display: block;
        padding: 10px 0;
        &:first-child{
          padding-top: 0;
        }
      }
      /deep/ table{
        td{
          outline: 0;
        }
      }
    }
  }
}
</style>


