
// 过滤器
function moneyFormat(val, num = 2) {
  // 返回处理后的值, num 大于20会报错
  let value = Number(val) || 0;
  if(value < 0){
    value = Math.abs(value);
  }
  value = parseFloat((value + "").replace(/[^\d\.-]/g, "")).toFixed(num) + ""; //将金额转成比如 123.45的字符串
  let valueArr = value.split(".")[0].split("") //将字符串的数变成数组
  const valueFloat = value.split(".")[1]; // 取到 小数点后的值
  let valueString = "";
  for (let i = 0; i < valueArr.length; i++) {
    valueString = ((i + 1) % 3 == 0 && (i + 1) != valueArr.length ? "," : "") + valueArr[valueArr.length - i - 1] + valueString; //循环 取数值并在每三位加个','
  }
  let money = valueString.split("").join("") + "." + valueFloat; //拼接上小数位
  if(Number(val) < 0){
    money = '-' + money;
  }
  return money
}

// 时间格式化过滤器
// 例子：   
// {new Date() | dateFormat("yyyy-MM-dd hh:mm:ss.S")} ==> 2006-07-02 08:09:04.423   
// {new Date() | dateFormat("yyyy-M-d h:m:s.S")}      ==> 2006-7-2 8:9:4.18   
function dateFormat(value, fmt) {
  let val = "";
  if(typeof value === "number"){
    val = value;
  } else {
    val = value.replace(/-/g, '/');
  }
  let _this = new Date(val);
  var o = {
    "M+": _this.getMonth() + 1,                 //月份   
    "d+": _this.getDate(),                    //日   
    "h+": _this.getHours(),                   //小时   
    "m+": _this.getMinutes(),                 //分   
    "s+": _this.getSeconds(),                 //秒   
    "q+": Math.floor((_this.getMonth() + 3) / 3), //季度   
    "S": _this.getMilliseconds()             //毫秒   
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

function digitUnit(value) {
  if(value == "--" || !Number(value)){
    return "--";
  }
  let num;
  let unit, unitRange;
  let symbol = "";
  if (value > 1000000000000) {
    unitRange = 1000000000000;
    unit = "万亿";
    num = symbol + (Math.round((value / unitRange) * 100) / 100) + unit
  } else if (value >= 100000000) {
    unitRange = 100000000;
    unit = "亿";
    num = symbol + (Math.round((value / unitRange) * 100) / 100) + unit
  } else if (value > 10000) {
    unitRange = 10000;
    unit = "万";
    num = symbol + (Math.round((value / unitRange) * 100) / 100) + unit
  } else {
    num = symbol + value
  }
  return num
}
//添加加号
function addSymbol(value) {
  let newVal;
  if (value > 0) {
    newVal = '+' + value;
  } else {
    newVal = value;
  }
  return newVal;
}
function bidask(value) {
  let num;
  if (value >= 100000000) {
    num = Number(value / 100000000).toFixed(2) + '亿'
  } else if (value >= 10000) {
    // num = Math.round(value / 10000) + '万'
    num = (value / 10000).toFixed(0) + '万'
  } else {
    num = value
  }
  return num
}

// 银行卡卡号过滤
function bankCardFilter(val) {
  if (val == "点击绑定银行卡") {
    return val;
  } else if (val) {
    return "**** **** **** " + String(val).slice(val.length - 4 > 0 ? val.length - 4 : 0);
  }
}

function dateTimeString(val) {
  if (val.length > 10) {
    return val.slice(10);
  } else {
    return val;
  }
}

module.exports = {
  moneyFormat,
  dateFormat,
  digitUnit,
  addSymbol,
  bidask,
  bankCardFilter,
  dateTimeString
}