<template>
  <div class="mainBot" v-if="isShow">
    <div
      v-for="(item, index) in indexStockData"
      :key="index"
      :class="item.chg > 0 ? 'upColor' : item.chg < 0 ? 'downColor' : ''"
      @click="goIndex(item)"
      hover
    >
      <span>
        <!-- <i class="el-icon-d-caret"></i> -->
        {{ item.name }}
      </span>
      <span>{{ Number(item.last).toFixed(2) }}</span>
      <span
        >{{ item.chg > 0 ? "+" : "" }}{{ Number(item.chg).toFixed(2) }}</span
      >
      <span>{{ item.chg > 0 ? "+" : "" }}{{ item.chg_pct }}%</span>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(() => {
      // this.stockquery();
    });
  },
  computed: {
    indexStockData() {
      let data = this.$store.state.GlobalData.indexStockData;
      let res = {};
      for (let key in data) {
        if (key.indexOf("hk") != -1) {
          res[key] = data[key];
        }
      }
      return res;
    },
    isShow() {
      return this.$store.state.system.showIndexStock;
    }
  },
  methods: {
    // 获取指数接口
    stockquery() {
      let url = "/quote-service/api/v1/stockquery";
      let data = {
        codelist: "hk0000100,hk0001400,hk0001500"
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        let stockData = {};
        res.data.Data.forEach(a => {
          stockData[a.symbol] = a;
        });
        this.$store.dispatch("SET_INDEX_STOCK_DATA", stockData);
      });
    },
    goIndex(item) {
      if (this.$route.name != "indexStock") {
        this.$router.replace({
          path: "indexStock"
        });
      }
      this.$store.dispatch("SET_CURRENT_STOCK", {
        code: item.symbol
      });
      // setTimeout(() => {
      //   this.$store.dispatch("CHANGE_SEARCH_DATA", {
      //     code: item.symbol,
      //     timestamp: new Date().getTime()
      //   });
      //   // 延迟是为了确保要监听股票代码改变的组件加载完成
      // }, 500);
    }
  },
  watch: {}
};
</script>

<style lang="scss" scoped>
.mainBot {
  @include flex-box;
  justify-content: flex-start;
  // width: 1835px;
  background: $blockBgColor;
  height: 30px;
  margin-top: 4px;
  position: absolute;
  left: 0;
  bottom: 0;
  width: calc(100% - 397px);
  z-index: 99;
  > div {
    @include add-size($font_size_14);
    width: 475px;
    line-height: 30px;
    padding-left: 20px;
    cursor: pointer;
    > span {
      width: 100px;
      text-align: center;
      display: -webkit-inline-box;
      &:first-child {
        color: #fff;
      }
    }
  }
  .upColor {
    color: $green;
  }
  .downColor {
    color: $red;
  }
}
</style>
