<template>
  <div class="trading-ul" onselectstart="return false;" @click.stop="stop">
    <transition-group name="list">
      <div
        class="mask-cover"
        :style="{zIndex: tr.zIndex,left: tr.left,top: tr.top}"
        :id="tr.id"
        v-for="tr in tradingList"
        :key="tr.id"
        @mousedown="resetIndex(tr.id)"
      >
        <div class="mask-header" @mousedown="mousedown($event, tr.id)">
          <div class="mask-title">{{tr.currentType == 0 ? "标准账户" : "机构账户"}}{{tr.title || "交易"}}</div>
          <div class="mask-close btn close" @click="close(tr.id)"></div>
        </div>
        <div class="mask-content">
          <stockDeal
            :accountInfo="accountInfo"
            :curPosition="curPosition"
            :requestConfig="requestConfig"
            :currentType="tr.currentType"
            :orderPrice="tr.orderPrice"
            :origin="'win'"
            @setOrder="setOrder($event, tr)"
            @getStockOrder="getStockOrder($event, tr)"
            @stockQuery="getStockQuery($event, tr)"
          />
          <div class="deal-five">
            <div
              class="deal-li"
              v-for="(sale, index) in tr.asklist"
              :key="index"
              @click="setOrderPrice(sale, tr)"
              hover
            >
              <div class="li-name">卖{{ capital[9 - index] }}</div>
              <div
                class="li-price"
                :class="{rise: sale.price > tr.hstClose, fall: sale.price < tr.hstClose}"
              >{{Number(sale.price).toFixed(3)}}</div>
              <div class="li-num">{{ sale.qty | bidask }}({{ sale.num }})</div>
            </div>
            <div class="deal-li peecent">
              <div class="percent-l" :style="{width: tr.widthPercent+'%'}">
                <span>{{tr.widthPercent}}%</span>
              </div>
              <div class="percent-r" :style="{width: (100 - tr.widthPercent)+'%'}">
                <span>{{100 - tr.widthPercent}}%</span>
              </div>
            </div>
            <div
              class="deal-li buy"
              v-for="(buy, io) in tr.bidlist"
              :key="io+'a'"
              @click="setOrderPrice(buy, tr)"
              hover
            >
              <div class="li-name">买{{ capital[io] }}</div>
              <div
                class="li-price"
                :class="{rise: buy.price > tr.hstClose, fall: buy.price < tr.hstClose}"
              >{{Number(buy.price).toFixed(3)}}</div>
              <div class="li-num">{{ buy.qty | bidask }}({{ buy.num }})</div>
            </div>
          </div>
          <!-- <div class="deal-five" v-if="!isVIP">
            <vipBtn :min="true" />
          </div>-->
        </div>
      </div>
    </transition-group>
  </div>
</template>
 
<script>
import stockDeal from "./stock-deal";
import vipBtn from "@/components/common/vip-btn";

import { buySellSocket } from "@/utils";
export default {
  name: "trading",
  props: {
    accountInfo: Object,
    curPosition: Array,
    requestConfig: Object
  },
  components: {
    stockDeal,
    vipBtn
  },
  data() {
    return {
      selectElement: "",
      tradingList: [],
      len: 0,
      limit: 6,
      capital: ["一", "二", "三", "四", "五", "六", "七", "八", "九", "十"]
    };
  },
  watch: {
    "$store.state.GlobalData.dealBuySell"(val) {
      let dealBuySell = val;
      for (let code in dealBuySell) {
        this.tradingList.forEach(a => {
          if (a.stockCode === code) {
            if (this.isVIP) {
              a.asklist = dealBuySell[code].data.asklist;
              a.bidlist = dealBuySell[code].data.bidlist;
            } else {
              let arr = [];
              for (let i = 0; i < 10; i++) {
                arr.push({
                  num: "--",
                  price: "0",
                  qty: "--"
                });
              }
              a.asklist = arr;
              a.bidlist = arr;
            }

            this.widthPercent(a);
          }
        });
      }
      // console.log(this.tradingList, "00000000000");
    }
  },
  computed: {
    currentStock() {
      return this.$store.state.DealBar.currentStock;
    },
    isVIP() {
      // 港股行情VIP
      // return false;
      return this.$store.state.system.accountInfo.hasHkValue || false;
    }
  },
  mounted() {
    this.$nextTick(() => {});
  },

  methods: {
    getStockQuery(stock, win) {
      win.hstClose = Number(stock.hst_close);
    },
    setOrderPrice(li, win) {
      // 点击买卖十档获取对应价格
      win.orderPrice = li.price;
    },
    setOrder(e, win) {
      // 成功下单操作
      // console.log(e, win)
      this.close(win.id);
    },
    //买卖十档
    getStockOrder(code, tr) {
      let url = "/quote-service/api/v1/order";
      let data = {
        code: code
      };
      this.axios({
        url: url,
        method: "get",
        params: data,
        type: "HQ"
      }).then(res => {
        // console.log(res);
        let bidList = res.data.Data[0].bidlist; //买十档
        let askList = res.data.Data[0].asklist.reverse(); //卖十档
        let bidNum = 0;
        let askNum = 0;
        bidList.map((value, index) => {
          bidNum = bidNum + Number(value.qty);
        });
        askList.map((value, index) => {
          askNum = askNum + Number(value.qty);
        });
        // 同步交易窗口买卖五档数据
        tr.bidlist = bidList;
        tr.asklist = askList;
        tr.stockCode = code;
        this.widthPercent(tr);
        // let ul = askList.concat(bidList);
        // this.$store.dispatch("UPDATE_BUY_SELL", ul);
        // 同步websocket数据
        let val = {};
        val[code] = {
          data: res.data.Data[0],
          timestamp: new Date().getTime()
        };
        this.$store.dispatch("SET_DEAL_BUY_SELL", val);
        buySellSocket(code);
      });
    },
    widthPercent(tr) {
      let bidNum = 0;
      let askNum = 0;
      tr.bidlist.map((value, index) => {
        bidNum = bidNum + Number(value.qty);
      });
      tr.asklist.map((value, index) => {
        askNum = askNum + Number(value.qty);
      });
      let percent = (
        Number((bidNum / (bidNum + askNum)).toFixed(2)) * 100
      ).toFixed(0);
      if (bidNum > 0) {
        tr.widthPercent = percent;
      } else {
        tr.widthPercent = "50";
      }
      // console.log(tr);
    },

    closeAll() {
      this.tradingList = [];
    },
    close(id) {
      this.tradingList = this.tradingList.filter(a => {
        return a.id !== id;
      });
    },
    resetIndex(id) {
      this.tradingList.forEach(a => {
        a.zIndex = 0;
        if (a.id === id) {
          a.zIndex = 1;
        }
      });
    },
    addWin() {
      if (this.tradingList.length > this.limit - 1) {
        this.$message({
          message: `最多打开${this.limit}个窗口`,
          type: "warning"
        });
        return false;
      }
      let len = this.len;
      this.resetIndex();
      let title = this.getTitle();
      // console.log(title);
      let orderlist = [];
      for (let i = 0; i < 10; i++) {
        orderlist.push({
          num: 0,
          price: 0,
          qty: 0
        });
      }
      let win = {
        id: `w${len}`,
        zIndex: 1,
        title: title,
        left: `calc(50% + ${this.tradingList.length * 10}px)`,
        top: `calc(20% + ${this.tradingList.length * 10}px)`,
        bidlist: orderlist,
        asklist: orderlist,
        widthPercent: "",
        currentType: this.$store.state.system.dealBar.currentType,
        stockCode: "",
        orderPrice: "",
        hstClose: ""
      };
      this.tradingList.push(win);
      this.len++;
      this.widthPercent(win);
      // console.log(this.currentStock);
      // this.getStockOrder(this.currentStock.symbol, win);
    },
    getTitle() {
      let title = "";
      let list = this.tradingList;
      if (list.length <= 0) {
        return "交易1";
      }
      for (let i = 0; i < 6; i++) {
        let flag = false;
        let title = `交易${i + 1}`;
        for (let j = 0; j < list.length; j++) {
          if (list[j].title == `交易${i + 1}`) {
            flag = false;
            break;
          } else {
            flag = true;
          }
        }
        if (flag) {
          return title;
        }
      }
    },
    stop() {},
    mousedown(event, id) {
      var div1 = document.getElementById(id);
      // div1.style.cursor = "move";
      let distanceX = event.clientX - div1.offsetLeft;
      let distanceY = event.clientY - div1.offsetTop;
      // console.log(distanceX);
      // console.log(distanceY);
      document.onmousemove = function(ev) {
        let oevent = ev || event;
        div1.style.left = oevent.clientX - distanceX + "px";
        div1.style.top = oevent.clientY - distanceY + "px";
      };
      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
        // div1.style.cursor = "default";
      };
    }
  }
};
</script>
 
<style scoped lang="scss">
.mask-cover {
  position: fixed;
  left: 50%;
  top: 10%;
  width: 476px;
  // height: 300px;
  // padding-bottom: 30px;
  transform: translateX(-180px);
  background: $blockBgColor;
  border: 1px solid $borderColor;
  box-shadow: 0 0 50px rgba($color: #000000, $alpha: 1);
  .mask-header {
    height: 32px;
    background: #2d3745;
    @include flex-center;
    .mask-title {
      width: calc(100% - 32px);
      padding-left: 32px;
      line-height: 32px;
      text-align: center;
      @include add-size($font_size_14);
      user-select: none;
      cursor: move;
    }
    .mask-close {
      cursor: pointer;
      width: 32px;
      height: 32px;
    }
  }
  .mask-content {
    @include flex-box;
    /deep/.el-form {
      padding: 0 16px;
      .el-form-item {
        margin-bottom: 20px;
        &:nth-child(2) {
          margin-bottom: 18px;
        }
        &:nth-child(3) {
          margin-bottom: 18px;
        }
        &:nth-child(4) {
          margin-bottom: 4px;
        }
        &:nth-child(5) {
          margin-bottom: 10px;
        }
        &:nth-child(6) {
          margin-bottom: 14px;
        }
      }
     
      .num-bar {
        width: 190px;
      }
      .button-flex {
        margin-top: 10px;
        .el-button {
          width: 30px;
          margin-right: 0;
          &:last-child {
            margin-right: 0;
          }
        }
      }
      .action-btn {
        .btn {
          width: 120px;
        }
        .el-button + .el-button {
          margin-left: 18px;
        }
      }
    }
    .deal-five {
      width: 180px;
      margin-top: 2px;
      height: 386px;
      // @include add-size($font_size_14);
      .deal-li {
        @include flex-center;
        padding: 0 8px;
        justify-content: space-between;
        line-height: 18px;
        color: $white;
        background: rgba(255, 169, 64, 0.2);
        @include add-size($font_size_14);
        white-space: nowrap;
        .li-name {
          // padding-right: 28px;
          width: 32px;
        }
        .li-price {
          // color: $red;
          // width: 50px;
          text-align: center;
          &.rise {
            color: $red;
          }
          &.fall {
            color: $green;
          }
        }
        .li-num {
          // width: 70px;
          text-align: right;
        }
        &:nth-child(10) {
          background: rgba(255, 169, 64, 0.5);
        }
        &:nth-child(12) {
          background: rgba(105, 192, 255, 0.5);
        }
        &:nth-child(12 + n) {
          background: rgba(105, 192, 255, 0.2);
        }
        &:last-child {
          padding-bottom: 8px;
        }
        &.peecent {
          padding: 0;
          .percent-l {
            // padding-left: 20px;
            background: $red;
            position: relative;
            height: 20px;
            span {
              position: absolute;
              left: 10px;
              z-index: 1;
            }
          }
          .percent-r {
            // padding-right: 20px;
            background: $green;
            position: relative;
            text-align: right;
            height: 20px;
            span {
              position: absolute;
              right: 10px;
              z-index: 1;
            }
          }
        }
      }
    }
  }
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to
/* .list-leave-active for below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px) translateX(-180px);
}
</style>