      <div class="way-detail" v-if="radio === 2">
        <div class="es-tips">请将外汇网银“境外汇款”功能汇至易昇证券账户</div>
        <div class="attention">注：只接受同名账户入金</div>
        <div class="tab-bar">
          <div class="tab-ul" :data-tab="tabLabel">
            <div
