            </div>
          </div>
          <div class="rule-right">
            <p>温馨提示：</p>
            <p>1、登录银行App，网银或第三方支付工具；</p>
            <p>2、选择“转账”或带有FPS标识的功能；</p>
            <p>3、把易昇证券的转速快识别码（PFS ID)：“3202058”填入收款人栏；</p>
            <p>4、网银提示收款人账户名“Easy Securitise Limited”,点击确认；</p>
            <p>5、完成转账并截图，上传截图凭证和通知易昇证券。</p>
          </div>
        </div>
        <div class="tab-content second" v-show="tabLabel === 2">
          <div class="rule-left">
            <div class="item">
