              <div class="item-value lh_24">
                <p>BANK OF COMMUNICATIONS</p>
                <p>CO,LTD.HONGD KONG</p>
                <p>BRANCH(交通银行)</p>
              </div>
            </div>
          </div>
          <div class="rule-left mt">
            <div class="item">
              <div class="item-label">收款人名称</div>
              <div class="item-value">EASY SECURITIES LTD</div>
            </div>
            <div class="item">
              <div class="item-label">收款账户号码</div>
              <div class="item-value">02841100871901</div>
            </div>
            <div class="item">
              <div class="item-label">银行编码</div>
              <div class="item-value">027</div>
            </div>
            <div class="item">
              <div class="item-label">分行号</div>
              <div class="item-value">无需填写</div>
            </div>
            <div class="item">
              <div class="item-label">SWIFT代码</div>
              <div class="item-value">COMMHKHH</div>
            </div>
          </div>
          <div class="rule-left">
            <!-- <div class="item">
              <div class="item-label">收款人地址</div>
              <div class="item-value">无需填写</div>
            </div>-->
            <div class="item">
              <div class="item-label">收款人地址</div>
              <div class="item-value lh_24">
                WHEELOCK HOUSE,20 PEDDER STREET,CENTRAL, HONG KONG
              </div>
            </div>
          </div>
        </div>
        <div class="tab-content second" v-show="tabLabel === 3">
          <div class="rule-left">
            <div class="item">
              <div class="item-label">到账时间</div>
              <div class="item-value">预计一个工作日</div>
            </div>
            <div class="item">
              <div class="item-label">手续费</div>
              <div class="item-value">0-15港币</div>
            </div>
            <div class="item">
              <div class="item-label">存入币种</div>
              <div class="item-value">港币</div>
            </div>
            <div class="item">
              <div class="item-label">存入银行</div>
              <div class="item-value">任意银行</div>
            </div>
            <div class="item">
              <div class="item-label">收款银行名称</div>
              <div class="item-value lh_24">
                <p>BANK OF COMMUNICATIONS</p>
                <p>CO,LTD.HONGD KONG</p>
                <p>BRANCH(交通银行)</p>
              </div>
            </div>
          </div>
          <div class="rule-left mt">
            <div class="item">
              <div class="item-label">收款人名称</div>
              <div class="item-value">EASY SECURITIES LTD</div>
            </div>
            <div class="item">
              <div class="item-label">收款账户号码</div>
              <div class="item-value">027841100871901</div>
            </div>
            <div class="item">
              <div class="item-label">银行编码</div>
              <div class="item-value">027</div>
            </div>
            <div class="item">
              <div class="item-label">分行号</div>
              <div class="item-value">无需填写</div>
            </div>
            <div class="item">
              <div class="item-label">SWIFT代码</div>
              <div class="item-value">COMMHKHH</div>
            </div>
          </div>
          <div class="rule-left">
            <!-- <div class="item">
              <div class="item-label">收款人地址</div>
              <div class="item-value">无需填写</div>
            </div>-->
            <div class="item">
              <div class="item-label">收款人地址</div>
              <div class="item-value lh_24">
                WHEELOCK HOUSE,20 PEDDER STREET,CENTRAL, HONG KONG
              </div>
            </div>
          </div>
        </div>
        <el-button class="notice submit" hover @click="showVoucher(2)"
          >已汇缴，通知易昇证券</el-button
        >
      </div>
    </div>
    <!-- 选择机构账号时可以存入资金 -->
    <div class="organization" v-if="currentType === 1">
      <div class="save-form">
        <div class="money-input">
          <div class="input-t">
            <div class="input-t_text">入金金额</div>
            <div class="draw-type">
              <span>入金币种</span>
              <el-select v-model="currency">
                <el-option
                  :label="op.label"
                  :value="op.value"
                  v-for="op in typeSelect"
                  :key="op.value"
                ></el-option>
              </el-select>
            </div>
          </div>
          <el-input
            class="money-in hk"
            v-model="originNum"
            type="number"
            maxlength="10"
            placeholder="请输入提现金额"
          ></el-input>
          <div class="res-money">
            <div class="total-money">
              支付金额： {{ rmbNum | moneyFormat }}元
            </div>
          </div>
        </div>
        <!--    <div class="currency">
          <div class="hk" :class="{active: currency === 'hk' }" @click="currency = 'hk'">存入港币</div>
          <div
            class="dollar"
            :class="{active: currency === 'dollar' }"
            @click="currency = 'dollar'"
          >存入美元</div>
        </div>
        <div class="input-item recharge">
          <div class="label">充值金额：</div>
          <el-input
            type="number"
            class="input"
            v-model="originNum"
            :placeholder="currency == 'hk'?'至少入金10000港币':'至少入金1500美金'"
          ></el-input>
        </div>
        <div class="input-item cost">
          <div class="label">支付金额：</div>
          <el-input class="input" v-model="rmbNum" placeholder="0.00" readonly></el-input>
          <span class="unit">元</span>
        </div>-->
        <!-- <div class="pay-type">支付宝支付（单笔限额30000人民币）</div> -->
        <el-button class="submit" hover @click="saveSubmit">入金</el-button>
      </div>
      <div class="save-tips">
        <p class="save-tips-title">温馨提示：</p>
        <p>1.根据香港证监会要求，入金需不少于1万元港币以激活账户，特请注意。</p>
        <p>2.汇率为实时汇率，由第三方服务公司提供。</p>
        <p>3.到账时间：1-3个工作日。</p>
      </div>
    </div>
    <el-dialog
      class="notice-dialog"
      theame
      center
      title="通知易昇证券"
      :visible="voucherShow"
      width="485px"
      top="10%"
      :modal="false"
      @close="voucherShow = false"
    >
      <div class="abroad-notice-content">
        <el-form v-model="noticeData">
          <el-form-item label="汇款银行信息">
            <el-select v-model="companyBankId" v-if="bankData.length > 0">
              <el-option
                :label="op.bankName"
                :value="op.cardBindId"
                v-for="op in bankData"
                :key="op.cardBindId"
              ></el-option>
            </el-select>
            <div class="no-card" v-else>
              <span>请添加银行卡</span>
              <span class="add-card" hover @click="linkAddCard">添加</span>
            </div>
          </el-form-item>
          <el-form-item label="存入币种">
            <el-input v-model="noticeData.type" readonly></el-input>
          </el-form-item>
          <el-form-item label="存入金额">
            <el-input
              v-model="noticeData.money"
              type="number"
              placeholder="请输入您的入金金额"
              @blur="checkMoney"
            ></el-input>
          </el-form-item>
          <el-form-item label="备注信息">
            <el-input v-model="noticeData.remark" placeholder="选填"></el-input>
          </el-form-item>
        </el-form>
        <div class="upload-img">
          <div
            class="card-inner"
            v-for="(img, index) in voucherData"
            :key="index"
          >
            <img :src="img.imgSrc" alt />
            <i class="delete-icon" @click="deleteImg(img)"></i>
          </div>
          <label
            for="voucher"
            class="upload-label card-inner"
            v-if="voucherData.length < 6"
          >
            <div class="card-label">点击上传汇款凭证</div>
            <imgUpload
              :id="'voucher'"
              :multiple="true"
              @change="uploadVoucher"
            />
          </label>
        </div>
      </div>
      <div class="mask-tips">
        <p>温馨提示</p>
        <p>
          上传带有汇款人姓名、账号、金额、交易编码的汇款凭证与带有姓名实际支付账号的银行卡月结单截图；
        </p>
        <p>入金金额首次超10万港币需上传护照首页或者港澳通行证正面。</p>
      </div>

      <el-button class="submit no-confirm" @click="saveInOtherwise" hover
        >确认提交</el-button
      >
    </el-dialog>
    <Contextmenu :menu="rightMenu" ref="Contextmenu" @menuSelect="CopyText" />
  </div>
</template>

<script>
import imgUpload from "@/components/common/img-upload";
import { uploadImg } from "@/utils";
import Contextmenu from "@/components/common/contextmenu";

const { shell, clipboard } = require("electron");

export default {
  components: {
    imgUpload,
    Contextmenu
  },
  data() {
    return {
      rightMenu: [
        {
          label: "复制",
          value: "copy"
        }
      ],
      showType: 1,
      radio: 1,
      originNum: "",
      rmbNum: "",
      currency: "hk",
      esData: {},
      noticeShow: false,
      noticeData: {
        account: "809-810559-838",
        money: "",
        type: "港币",
        businessAccount: "",
        remark: "",
        file: "",
        src: "",
        incomeType: 1
      },
      tabLabel: 1,
      voucherShow: false,
      companyBankId: "",
      voucherData: [],
      bankData: [],
      organization: 0, // 0 ebroker 1机构
      rateData: {},
      typeSelect: [
        {
          label: "港币",
          value: "hk"
        }
        // {
        //   label: "美元",
        //   value: "dollar"
        // }
      ]
    };
  },
  computed: {
    // receiveName() {
    //   return this.$store.state.UserInfo.userInfo.enName;
    // },
    currentType() {
      // 当前选择的账户  0 erbroker  1 博威
      return this.$store.state.system.dealBar.currentType;
    },
    orgUsername() {
      let orgUsername = {
        id: this.$store.state.system.accountInfo.organizationData.institutionalAccount,
        name: this.$store.state.AccountLogin.organizationData.name
      };
      return orgUsername;
    }
  },
  watch: {
    voucherShow(val) {
      if (val) {
        this.getBankCardList();
      }
    },
    originNum(val) {
      if (this.currency === "hk") {
        this.rmbNum = (
          (Number(val) / Number(this.rateData.rmbToUk)) *
          1.02
        ).toFixed(2);
      } else {
        this.rmbNum = (
          (Number(val) / Number(this.rateData.rmbToDollar)) *
          1.02
        ).toFixed(2);
      }
    },
    currency() {
      let val = this.originNum;
      if (this.currency === "hk") {
        this.rmbNum = (Number(val) * Number(this.rateData.ukToRmb)).toFixed(2);
      } else {
        this.rmbNum = (Number(val) * Number(this.rateData.dollarToRmb)).toFixed(
          2
        );
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.getEsData();
      this.getRate();
    });
  },
  methods: {
    deleteImg(img) {
      this.voucherData = this.voucherData.filter((i, m) => {
        return i.imgSrc !== img.imgSrc;
      });
    },
    showCopy(e) {
      let text = window.getSelection().toString();
      if (text) {
        this.copyText = text;
        this.$refs.Contextmenu.show({
          left: e.x,
          top: e.y,
          text: text
        });
      }
    },
    CopyText(menu) {
      clipboard.writeText(this.copyText);
    },
    saveSubmit() {
      // if (this.rmbNum > 30000) {
      //   this.$message({
      //     message: "单笔限额30000人民币",
      //     type: "error"
      //   });
      //   return false;
      // }
      if (this.rmbNum <= 0) {
        this.$message({
          message: "请输入金额",
          type: "error"
        });
        return false;
      }
      let mobile =
        this.$store.state.UserInfo.personalInfo.mobile ||
        localStorage.getItem("lastMobile");
      let rate = this.rateData.ukToRmb;
      if (this.currency === "dollar") {
        rate = this.rateData.dollarToRmb;
      }
      let customerId = localStorage.getItem("customerId");
      let params = {
        customerId: customerId,
        currency: 0,
        mobile: mobile,
        money: this.originNum,
        rmb: this.rmbNum,
        rate: rate
      };
      let data = encodeURIComponent(JSON.stringify(params));
      shell.openExternal(
        `http://h5.lc913.com/web/newWebPayment/indexPcEnter.html?form=${data}`
      );

      // /payFund/alipayPay.do
      // this.axios({
      //   url: "/payFund/alipayPay.do",
      //   method: "post",
      //   params: params,
      //   type: "host3"
      // }).then(res => {
      //   let forms = encodeURIComponent(res.data.ret);
      //   // $("#zhuce_body").html(forms);
      // });
    },
    getRate() {
      // 获取汇率
      this.axios({
        url: "/fund/selectRate",
        method: "get",
        params: {},
        type: "host2"
      }).then(res => {
        this.rateData = res.data.ret;
      });
    },
    showVoucher(flag) {
      this.noticeData.incomeType = flag;
      this.voucherShow = true;
    },
    checkMoney() {
      if (Number(this.noticeData.money) > 0) {
        this.noticeData.money = Number(this.noticeData.money).toFixed(2);
      } else {
        this.noticeData.money = "";
        this.$message({
          message: "请输入正确的金额！",
          type: "error"
        });
      }
    },
    uploadImgArr(index, cb) {
      let imgArr = this.voucherData;
      let _this = this;
      // 依次上传图片
      uploadImg({
        file: imgArr[index].file,
        type: 4,
        cb(res) {
          imgArr[index].webUrl = res.data.body;
          index++;
          if (index <= imgArr.length - 1) {
            _this.uploadImgArr(index, cb);
          } else {
            cb();
          }
        }
      });
    },
    saveInOtherwise() {
      //  /fund/saveFastFund
      //  子账户入金  海外或者香港入金
      try {
        if (this.voucherData.length <= 0) {
          throw new Error("请上传汇款凭证！");
        }
        if (!this.noticeData.money) {
          throw new Error("请输入存入金额！");
        }
        if (!this.companyBankId) {
          throw new Error("请选择银行！");
        }
        let _this = this;
        let esData = this.esData;
        let bankAccount = "";
        this.bankData.forEach(a => {
          if (a.cardBindId === this.companyBankId) {
            bankAccount = a.accountNo;
          }
        });
        let url = "";
        if (this.tabLabel == 1 && this.radio == 2) {
          url = "/fund/saveFastFund";
          _this.noticeData.incomeType = 0;
        } else {
          url = "/fund/saveFund";
        }
        this.uploadImgArr(0, () => {
          let voucher = "";
          this.voucherData.forEach(i => {
            voucher += i.webUrl + ",";
          });
          voucher = voucher.substring(0, voucher.length - 1);
          this.axios({
            url: url,
            method: "post",
            params: {
              companyBankId: _this.companyBankId,
              payeeNo: esData.payeeNo,
              type: 0,
              money: _this.noticeData.money,
              ebrokerUserName: esData.ebrokerUserName,
              remarks: _this.noticeData.remark,
              incomeType: _this.noticeData.incomeType,
              voucher: voucher,
              typ: _this.organization,
              bankAccount: bankAccount
            },
            type: "host2"
          }).then(res => {
            _this.voucherShow = false;
            _this.$message({
              message: "提交成功！",
              type: "success"
            });
            _this.noticeData.money = "";
            _this.noticeData.src = "";
            _this.noticeData.remark = "";
            _this.voucherData = [];
          });
        });
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    saveInMainland() {
      // 资金在大陆  子账户入金
      // POST /fund/saveFund
      if (!this.noticeData.src) {
        this.$message({
          message: "请上传支付凭证！",
          type: "error"
        });
        return false;
      }
      if (this.noticeData.money <= 0) {
        this.$message({
          message: "存入金额不能为空！",
          type: "error"
        });
        return false;
      }
      let esData = this.esData;
      let _this = this;
      uploadImg({
        ...this.noticeData,
        cb(re) {
          _this
            .axios({
              url: "/fund/saveFund",
              method: "post",
              params: {
                payeeNo: esData.payeeNo,
                type: 0,
                money: _this.noticeData.money,
                ebrokerUserName: esData.ebrokerUserName,
                remarks: _this.noticeData.remark,
                incomeType: 1,
                voucher: re.data.body,
                typ: _this.organization
              },
              type: "host2"
            })
            .then(res => {
              _this.voucherShow = false;
              _this.$message({
                message: "提交成功！",
                type: "success"
              });
              _this.noticeData.money = "";
              _this.noticeData.src = "";
            });
        }
      });
    },

    linkAddCard() {
      this.voucherShow = false;
      this.$router.push({ path: "wd", query: { target: "myBankCard" } });
    },
    uploadVoucher(files) {
      try {
        if (this.voucherData.length + files.length > 6) {
          throw new Error("最多上传6张图片！");
        }
        this.voucherData = this.voucherData.concat(files);
        console.log(this.voucherData);
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    uploadVoucherNotice(e) {
      this.noticeData.file = e.file;
      this.noticeData.src = e.src;
    },
    getEsData() {
      // /fund/selectListInfo?customerId=432459347709407232
      this.axios({
        url: "/fund/selectListInfo",
        method: "get",
        params: {},
        type: "host2"
      }).then(res => {
        this.esData = res.data.ret;
        this.esData.address = "无需填写";
        this.noticeData.businessAccount = this.esData.ebrokerUserName;
      });
    },
    getBankCardList() {
      // GET /order/message/selectBankBind
      this.axios({
        url: "/order/message/selectBankBind",
        method: "get",
        params: {
          type: 1
        },
        type: "host2"
      }).then(res => {
        this.bankData = res.data.ret || [];
      });
    }
  }
};
</script>

