<template>
  <div class="customer-info">
    <el-dialog
      class="customer-info-dialog"
      theame
      center
      title="在线客户"
      :modal="false"
      :visible="isShow"
      width="570px"
      top="15%"
      @close="close"
    >
      <div class="info-content">
        <div class="total-account">
          <div class="total-account-li">
            <div class="total-account-li_label">在线客户：</div>
            <div class="total-account-li_value">{{totalData.quantity}}人</div>
          </div>
          <div class="total-account-li">
            <div class="total-account-li_label">总资产：</div>
            <div class="total-account-li_value">{{totalData.assets | moneyFormat}}</div>
          </div>
          <div class="total-account-li">
            <div class="total-account-li_label">可用：</div>
            <div class="total-account-li_value">{{totalData.availCapital | moneyFormat}}</div>
          </div>
          <div class="total-account-li">
            <div class="total-account-li_label">持仓：</div>
            <div class="total-account-li_value">{{totalData.marketValue | moneyFormat}}</div>
          </div>
        </div>
        <div class="customer-table">
          <el-table class :data="customerOnlineList">
            <el-table-column prop="customerName" label="客户" width="150">
              <template slot-scope="scope">
                {{scope.row.name}}&nbsp;&nbsp;
                {{scope.row.mobile}}
              </template>
            </el-table-column>
            <el-table-column prop="assets" label="总资产"></el-table-column>
            <el-table-column prop="availCapital" label="可用"></el-table-column>
            <el-table-column prop="marketValue" label="持仓"></el-table-column>
          </el-table>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "customer-info",
  props: {
    customerOnlineList: Array
  },
  data() {
    return {
      isShow: false,
      isShowDetail: false,
      activeName: "first",
      totalData: {
        quantity: 0,
        assets: 0,
        availCapital: 0,
        marketValue: 0
      }
    };
  },
  mounted() {},
  computed: {
  },
  watch: {},
  methods: {
    initData() {
      let totalData = this.totalData;
      totalData.quantity += this.customerOnlineList.length;
      this.customerOnlineList.forEach(a => {
        totalData.assets += Number(a.assets);
        totalData.availCapital += Number(a.availCapital);
        totalData.marketValue += Number(a.marketValue);
      });
    },
    handleClick() {},
    show() {
      this.isShow = true;
      this.initData();
    },
    close() {
      this.isShow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.customer-info {
  .customer-info-dialog {
    /deep/ .el-dialog {
      .el-dialog__title {
        @include add-size($font_size_14);
        color: $defaultColor !important;
      }
    }
    .info-content {
      height: 398px;
      .account-info {
        padding: 30px;
        color: $white;
        @include add-size($font_size_14);
        .account-info-li {
          @include flex-center;
          margin-bottom: 50px;
          .account-info-li_label {
            width: 224px;
          }
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      .total-account {
        @include flex-center;
        height: 51px;
        border-bottom: 1px solid #323840;
        padding: 0 25px;
        justify-content: space-between;
        @include add-size($font_size_12);
        color: $defaultColor;
        .total-account-li {
          @include flex-center;
        }
      }
    }
    .customer-table {
      padding: 0 30px;
      .time-label {
        @include flex-center;
        justify-content: space-between;
      }
    }
  }
}
</style>
