          },
          {
            title: "如何办理香港银行卡？",
            content: `<span>必备资料</span>
            <p>1.内地居民身份证</p>
            <p>2.三个月或半年以上有效签注的港澳通行证或护照</p>
            <p>3.住址证明（申请人最近三个月内收到的可证明住址的单据，可为公共事业单位所发账单，如水、电、煤气、网络、电话等账单；银行信用卡账单；其他金融或公用机构所发出的加盖业务章的单据。需为纸质文件，收信人姓名与申请人一致，且地址为个人住宅地址而非公司地址）</p>
            <span>支持办理香港银行卡的大陆银行</span>
            <p>民生银行、恒生银行、工商银行（工银亚洲）、建设银行（建银亚洲）、中国银行、招商银行、永隆银行、汇丰银行、渣打银行、花旗银行。</p>
            <span>温馨提示</span>
            <p>1.失败风险：即便是资料和证件齐备，基于具体网点的自主裁量，仍然存在开户失败的风险，建议开户前咨询具体客户经理。</p>
            <p>2.账户类型：银行可能设立不同档次的银行账户，并要求客户适用于某具体类别，需于具体开设时咨询及确认，届时请留意有关服务及费用情况。</p>
            <p>3.权限开通：办理香港银行卡时，建议要求对方开通账户的第三方转账权限，并提高转账的限额，以便日后使用（此功能往往默认不开通），视需要可考虑同时开通网上理财功能。</p>
            <p>4.防止冻结：请咨询开户行是否有账户冻结条例。在新卡开立以及后期使用中，均可能有冻结条例。</p>
            <p>5.账户管理费：香港银行有最低存款要求，低于一定的存款金额会引起收取账户管理费用，请留意咨询开户行有关条例，予以应对。</p>`
          },
          {
            title: "入金有什么门槛？",
            content: `<p>在易昇证券入金无任何门槛。</p>`
          }
        ],
        sfbz: [
          {
            title: "交易费用",
            content: ` <table border="1" cellspacing="0" style="width: 100%;text-align:center;" class="detailTab">
                <tr>
                    <td>类型</td>
                    <td>收费标准</td>
                    <td>收费方</td>
                </tr>
                <tr>
                    <td>交易佣金</td>
                    <td>0.05%交易值（最低50港币）</td>
                    <td>易昇证券</td>
                </tr>
                <tr>
                    <td>印花税</td>
                    <td>0.1%交易值（最低1港币）</td>
                    <td>香港政府</td>
                </tr>
                <tr>
                    <td>交易费</td>
                    <td>0.005%交易值</td>
                    <td>香港交易所</td>
                </tr>
                <tr>
                    <td>交易征费</td>
                    <td>0.0027%交易值</td>
                    <td>香港证监会</td>
                </tr>
                <tr>
                    <td>结算交收费</td>
                    <td>0.004%交易值（最低5港币）</td>
                    <td>香港结算所</td>
                </tr>
            </table>`
          },
          {
            title: "新股认购费用",
            content: `<table border="1" cellspacing="0" style="width: 100%;text-align:center;" class="detailTab">
                <tr>
                    <td>类型</td>
                    <td>收费标准</td>
                    <td>收费方</td>
                </tr>
                <tr>
                    <td>认购手续费</td>
                    <td>融资申购100港币/笔<br/>（发起认购即会收取）</td>
                    <td>易昇证券</td>
                </tr>
                <tr>
                    <td>经纪佣金</td>
                    <td>1%交易值<br>（中签才会收取）</td>
                    <td>经纪公司</td>
                </tr>
                <tr>
                    <td>交易征费</td>
                    <td>0.0027%交易值</td>
                    <td>香港证监会</td>
                </tr>
                <tr>
                    <td>交易费</td>
                    <td>0.005%交易值</td>
                    <td>香港交易所</td>
                </tr>
            </table>`
          },
          {
            title: "出金收费标准",
            content: `<p>易昇证券平台不收取任何出金费用，银行可能会收取小额手续费用，具体收费情况以银行实际收取为准。</p>`
          }
        ],
        gpzh: [
          {
            title: "如何从其他券商转入股票到易昇？",
            content: `            <span>准备工作</span>
            <p>1.打开易昇证券APP</p>
            <p>2.登录易昇证券</p>
            <p>3.点击“交易-转入股票”</p>
            <p>4.填写转户申请信息（即填写转出券商及您在转出券商的信息、转出类型、转出金额、转入至易昇证券的股票代码及数量）</p>
            <p>5.确认信息并通知转出券商（确认无误后点击“确认提交”，转出方的转户申请表格便将发送至您的开户邮箱，供您参考。烦请尽快联系转出券商转出股票，若已向转出方提交转户申请，则请耐心等待客服联系。一旦确认转股信息，则易昇证券便将在1-2个工作日内存入股票至您的账户）</p>`
          }
          // {
          //   title: "如何从国金转户到易昇？",
          //   content: ``
          // },
          // {
          //   title: "如何从老虎转户到易昇？",
          //   content: ``
          // }
        ],
        zhaq: [
          {
            title: "如何保障资金安全？",
            content: `<p>客户资产托管在清算机构的账户内，同时受香港交易所（HKEX）监管。未经客户授权，易昇证券无法处置客户账户内的任何资产。并且，客户资金的转入与转出均需由客户的同名账户执行。客户资产与易昇证券企业资产是隔离的，易昇证券无权也无法处置客户账户内的任何资产。即使易昇证券在经营上出现问题，客户资金仍然是安全的，不会受到影响。</p>`
          },
          {
            title: "如何保障账户安全？",
            content: `  <span>多重要素验证</span>
            <p>如果您从新地址登录或更改敏感账号信息，我们可能会要求您提供一些信息，以帮助确认您的身份。具体来说，我们可能会要求您输入发送到您手机或邮箱的验证码，或者验证您的一些详细身份信息。</p>
            <span>密码强度</span>
            <p>请确保您的密码长度至少为8个字符，并且尽量使用字母、数字的组合。您的账号密码应不同于您在其他网站使用的密码（如邮箱账号、银行账号或社交媒体账号）。这样即使您的其他账号收到攻击，您的账号受到影响也会降低。</p>`
          },
          {
            title: "如何保障隐私安全？",
            content: `<p>为了保护您的个人信息，防范未经授权的访问与使用，我们将采取法律所要求的安全措施，这些措施具体包括电脑防护、加密文件、安全办公楼。除非法律法规要求，否则我们不会将个人信息与非公开信息披露给第三方。另外，易昇证券也采用IPSec世界领先的标准安全与加密技术，拥有银行级的安全体系，严格防止外部攻击者对用户隐私信息的获取，以此来确保您的个人信息、账户信息、交易资产数据、交易记录的安全。</p>`
          }
        ],
        xgrg: [
          {
            title: "如何进行新股申购",
            content: `<span>现金认购</span>
            <p>1.打开易昇证券APP</p>
            <p>2.登录易昇证券</p>
            <p>3.点击“交易-新股认购”</p>
            <p>4.选择您想要认购的新股及其数量</p>
            <p>5.点击“现金认购”</p>
            <p>6.点击“认购”</p>
            <span>融资认购</span>
            <p>1.打开易昇证券APP</p>
            <p>2.登录易昇证券</p>
            <p>3.点击“交易-新股认购”</p>
            <p>4.选择您想要认购的新股及其数量</p>
            <p>5.点击“确定”</p>
            <p>6.认购完成</p>
            <span>温馨提示</span>
            <p>1.认购结果一般会于公布售股结果日期当天21:00之后公布。若中签，则您可以在自己的持仓内查看所获得分配新股的股数，同时您亦可通过系统消息、短信、邮件的方式收到中签通知，当日日结单中也会显示中签新股及股数。股票上市后，即可正常交易。</p>
            <p>2.认购手续费、认购资金、中签费用均将在认购截止日当天从账户内扣除。</p>
            <p>3.若客户未能获得分配新股（即认购失败或未中签），则认购资金与将在新股结果公布日期当天21:00之后退回至客户账户。该笔资金到账后，客户便可用于股票交易、申请提取资金、货币兑换。</p>`
          },
          {
            title: "已认购的新股可以撤回吗？",
            content: `<p>在认购截止之前，可以修改与撤销。</p>`
          },
          {
            title: "新股什么时候截止认购？",
            content: `<p>新股认购一般在新股截止日当日早上10:00关闭认购通道。</p>`
          }
        ],
        ggjy: [
          {
            title: "港股交易时间是什么时候？",
            content: `<span>港股市场在周一至周五进行交易，交易时间如下：</span>
            <p>【早盘集合竞价时段】</p>
            <p>上午09:00～09:30</p>
            <p>【持续交易时段】</p>
            <p>上午09:30～12:00</p>
            <p>下午13:00～16:00</p>
            <p>【收市集合竞价时段】</p>
            <p>下午16:00～16:10</p>
            <span>温馨提示</span>
            <p>周六周日与公众假日休市。此外，圣诞前夕、新年前夕、农历新年前夕均为半日交易，即中午12:00～12:10结束当日交易，没有下午的午市交易。最后，若有特殊休市日，则易昇证券APP会提前以系统通知及客户端通告告知您休市情况。</p>`
          },
          {
            title: "港股的买卖类型有哪些？",
            content: `<p>在早盘集合竞价时段与收市集合竞价时段，港交所系统接受的订单类型包括竞价单与竞价限价单。而在持续交易时段，港交所系统接受的订单类型包括增强限价单、特别限价单、限价单。</p>
            <span>竞价单</span>
            <p>竞价单是指无指定价格的买卖单，在输入系统后，按最终参考平衡价格进行对盘。竞价单享有较竞价限价单优先的对盘次序及根据时间先后次序按最终参考平衡价格顺序对盘。在早盘集合竞价时段结束后，任何未完成的竞价单会于持续交易时段开始前自动取消。</p>
            <span>竞价限价单</span>
            <p>竞价限价单是指有指定价格的买卖单。指定价格等同最终参考平衡价格或较最终参考平衡价格更具竞争力（即指定价格等同或高于最终参考平衡价格的买盘，或指定价格等同或低于最终参考平衡价格的卖盘），或可按最终参考平衡价格进行对盘。竞价限价单会根据价格及时间先后次序按最终参考平衡价格顺序对盘，且其不会以差于最终参考平衡价格的价格对盘。在早盘集合竞价时段结束后，任何未完成而输入价格不偏离按盘价九倍或以上的竞价限价单将自动转至持续交易时段，并一概视为限价单，存于所输入价格的轮候队伍中。</p>
            <span>增强限价单</span>
            <p>增强限价单最多可同时与十条轮候队伍进行配对（即最佳的价格队伍至距离9个价位的第10条轮候队伍），只要成交价格不差于输入价格。卖盘的输入价格不可低于最佳买入价10个价位（或以外），而买盘的输入价格则不可高于最佳卖出价10个价位（或以外）。任何未完成的增强限价单将一概视为限价单，并存于所输入价格的轮候队伍中。</p>
            <span>特别限价单</span>
            <p>特别限价单最多可同时与十条轮候队伍进行配对（即最佳的价格队伍至距离9个价位的第10条轮候队伍），只要成交价格不差于输入价格。特别限价单没有输入价格的限制，只要卖盘的价格等于或低于最佳买入价，亦或买盘的价格等于或高于最佳卖出价。任何未完成的特别限价单将会被取消，而不会保留在系统内。</p>
            <span>限价单</span>
            <p>限价单只可指定价格进行配对，卖盘的输入价格不可低于最佳买入价（如有），而买盘的输入价格则不可高于最佳卖出价（如有）。任何未完成的限价单将存于所输入价格的轮候队伍中。</p>`
          },
          {
            title: "港股报价的交易规则有哪些？",
            content: `<p>每个交易日，首个输入交易系统的买盘与卖盘，均是受开市报价规则所监管的。按照此规则，无论是买盘或卖盘，首个挂盘在任何情况下均不得偏离上个交易日收市价9倍或以上，也不得低于九分之一或以下。这里的“价位”是指证券交易可允许的最小价格变动单位。
            </p>
            <p>而非首个输入交易系统的买盘或卖盘的报价规则则为：买盘或卖盘的价格均不得偏离按盘价（即上个交易日的收市价）9倍或以上，也不得低于九分之一或以下。
                此外，在持续交易时段，若首个挂牌是买盘，则其价格必须高于或等于上个交易日收市价格之下24个价位的价格，而若首个挂盘为卖盘，则其价格必须低于或等于上个交易日收市价格之上24个价位的价格。
            </p>`
          }
        ]
      }
    };
  },
  methods: {
    close(e) {},
    show() {
      this.helpMask = true;
    }
  }
};
</script>

<style lang="scss" scoped>
